import {MapUtils} from "../util/MapUtils"
export class Icon {

  private readonly width: number

  private readonly height: number

  private readonly url: string

  constructor(width: number, height: number, url: string) {
    this.width = width
    this.height = height
    this.url = url
  }

  clone(): Icon {
    return new Icon(this.width, this.height, this.url)
  }

  getWidth(): number {
    return this.width
  }

  getHeight(): number {
    return this.height
  }

  getUrl(): string {
    if (this.url == null) {
      return null
    }
    return this.url.replace("@1x", `@${MapUtils.resourceScale}x`)
  }
}
