import {ResourceManager, ShaderEnum} from "../util/ResourceManager"
import {MapState} from "../map/MapState"
import {LatLng} from "../public/LatLng"
import {GeometryUtils} from "../util/GeometryUtils"
import {Matrix4} from "../engine/Matrix"
import {UniformInfo} from "../engine/Shader"
import {Color} from "../engine/Color"
import {MapUtils} from "../util/MapUtils"
import {TextLabelData} from "../text/TextLabelData"
import {Vector2} from "../engine/Vector2"
import {ProjectionMercator} from "../map/ProjectionMercator"

export class TextLabelRenderer {

  private constructor() {
  }

  static renderCurved(gl: WebGLRenderingContext, mapState: MapState, textLabelData: TextLabelData, position: LatLng,
                      textColor: Color = Color.fromHex("#000000"), halo: boolean = true,
                      haloColor: Color = Color.fromHex("#FFFFFF")) {
    let resourceManager = ResourceManager.getInstance(gl)
    let shader = resourceManager.getShader(ShaderEnum.TEXT_LABEL_SHADER)
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    shader.bind()

    let glyphsTexture = textLabelData.font.texture
    textLabelData.model.bind()
    shader.setAttributes(textLabelData.model.attributeInfos)

    let coordinate =
      GeometryUtils.pixelCoordinateToCameraCoordinate(
        ProjectionMercator.projectScale(position, mapState.scale), mapState.cameraPosition)
    let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0.0)
    let mvpMatrix = modelMatrix.concat(mapState.getViewProjectionMatrix())

    shader.setUniforms([
      new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix),
      new UniformInfo("u_texture", "texture", glyphsTexture),
      new UniformInfo("u_pixel_scale", "1f", MapUtils.pixelScale),
      new UniformInfo("u_text_size", "1f", textLabelData.size),
      new UniformInfo("u_texsize", "2fv",
        new Float32Array([glyphsTexture.width, glyphsTexture.height]) )
    ])

    if (halo) {
      shader.setUniforms([
        new UniformInfo("u_color", "4fv", haloColor.buffer()),
        new UniformInfo("u_is_halo", "1i", true)
      ])
      textLabelData.model.render(gl.TRIANGLES)
    }

    shader.setUniforms([
      new UniformInfo("u_color", "4fv", textColor.buffer()),
      new UniformInfo("u_gamma_scale", "1f", 1.0),
      new UniformInfo("u_is_halo", "1i", false)
    ])
    textLabelData.model.render(gl.TRIANGLES)
  }

  static render(gl: WebGLRenderingContext, mapState: MapState,
    textLabelData: TextLabelData, position: LatLng | Vector2,
    textColor: Color = Color.fromHex("#000000"),
    halo: boolean = true,
    haloColor: Color = Color.fromHex("#FFFFFF")) {
    let resourceManager = ResourceManager.getInstance(gl)
    let shader = resourceManager.getShader(ShaderEnum.TEXT_LABEL_SHADER)
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    shader.bind()

    let glyphsTexture = textLabelData.font.texture
    textLabelData.model.bind()
    shader.setAttributes(textLabelData.model.attributeInfos)

    let coordinate = null
    if (position instanceof LatLng) {
      coordinate =  GeometryUtils.latLngToScreenCoordinate(position, 0, mapState.cameraPosition, mapState.getScreenSize())
    }
    else {
      coordinate = position
    }
    let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0)
    let mvpMatrix = modelMatrix.concat(mapState.getOrthoProjectionMatrix())

    shader.setUniforms([
      new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix),
      new UniformInfo("u_texture", "texture", glyphsTexture),
      new UniformInfo("u_pixel_scale", "1f", MapUtils.pixelScale),
      new UniformInfo("u_text_size", "1f", textLabelData.size),
      new UniformInfo("u_texsize", "2fv",
        new Float32Array([glyphsTexture.width, glyphsTexture.height]) )
    ])

    if (halo) {
      shader.setUniforms([
        new UniformInfo("u_color", "4fv", haloColor.buffer()),
        new UniformInfo("u_is_halo", "1i", true)
      ])
      textLabelData.model.render(gl.TRIANGLES)
    }

    shader.setUniforms([
      new UniformInfo("u_color", "4fv", textColor.buffer()),
      new UniformInfo("u_gamma_scale", "1f", 1.0),
      new UniformInfo("u_is_halo", "1i", false)
    ])
    textLabelData.model.render(gl.TRIANGLES)
  }
}
