import {Layer, LayerType} from "./Layer"
import {TileManager} from "../tile/TileManager"
import {TileCoordinate} from "../tile/TileCoordinate"
import {RasterTile} from "../tile/RasterTile"
import {EffectManager} from "../effect/EffectManager"
import {SkyObject} from "../map/SkyObject"
import {SkyRenderer} from "../renderer/SkyRenderer"
import {TimeEffectManager} from "../effect/TimeEffectManager"
import {LightingManager} from "../lighting/LightingManager"

export class RasterLayer extends Layer {

  //TODO: Refactor: EffectManager should not be here
  private _effectManager: EffectManager

  //TODO: Refactor: TimeEffectManager should not be here
  private _timeEffectManager: TimeEffectManager

  //TODO: Refactor: LightingManager should not be here
  lightManager: LightingManager

  //TODO: Refactor: Sky object should not be here
  skyObject: SkyObject

  //TODO: Refactor: Sky renderer should not be here
  skyRenderer: SkyRenderer

  constructor(layerId: string, order: number, tileManager: TileManager) {
    super(layerId, order, LayerType.RASTER, tileManager)
  }

  update(): void {

    this.tileManager.update((tileCoordinate: TileCoordinate) => {
      return new RasterTile(tileCoordinate, this.tileManager.mapView.gl)
    })
  }

  get effectManager(): EffectManager {
    return this._effectManager
  }

  set effectManager(value: EffectManager) {
    this._effectManager = value
  }

  get timeEffectManager(): TimeEffectManager {
    return this._timeEffectManager
  }

  set timeEffectManager(timeEffectManager: TimeEffectManager) {
    this._timeEffectManager = timeEffectManager
  }

  setLightingManager(lightingManager: LightingManager) {
    this.lightManager = lightingManager
  }
}

