import {ParticleSystem} from "./ParticleSystem"
import {Texture} from "../engine/Texture"
import {MapState} from "../map/MapState"
import {Shader, UniformInfo} from "../engine/Shader"
import {ModelEnum, ResourceManager} from "../util/ResourceManager"
import {Matrix4} from "../engine/Matrix"
import {ParticlePool} from "./ParticlePool"

export class ParticleSystemQuad extends ParticleSystem{

  protected _shader: Shader

  protected _particleTexture: Texture

  protected _gl: WebGLRenderingContext

  protected constructor(gl: WebGLRenderingContext,
    shader: Shader,
    particleTexture:
    Texture,
    particlePool: ParticlePool) {
    super(particlePool)
    this._shader = shader
    this._particleTexture = particleTexture
    this._gl = gl
  }

  render(mapState: MapState): void {
    let model = ResourceManager.getInstance(this._gl).getModel(ModelEnum.PARTICLE_QUAD_MODEL)
    let orthoProjectionMatrix = mapState.getOrthoProjectionMatrix()
    this._shader.bind()
    model.bind()
    this._shader.setAttributes(model.attributeInfos)
    this._shader.setUniforms([
      new UniformInfo("u_texture", "texture", this._particleTexture),
    ])
    for (let particle = this._activeParticlesFirst; particle; particle = particle.next) {
      let position = particle.position
      let scale = particle.scale
      let rotation = particle.rotation
      let modelMatrix = Matrix4.translate(position.x, position.y, 0)
      let matrixScale = Matrix4.scale(this._particleTexture.width * scale,
        this._particleTexture.height * scale,
        1)
      let matrixRotate = Matrix4.rotateZ(rotation)
      modelMatrix = matrixRotate.concat(modelMatrix)
      modelMatrix = matrixScale.concat(modelMatrix)
      let mvpMatrix = modelMatrix.concat(orthoProjectionMatrix)
      this._shader.setUniforms([
        new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix)
      ])

      model.render(this._gl.TRIANGLES)
    }
    model.unbind()
  }

}