import {GeometryTileData, GeometryTileFeature, GeometryTileLayer, GeometryType} from "./GeometryTileData"
import {GeometryCollection} from "./GeometryCollection"

export class VectorTileFeature extends GeometryTileFeature {

  private id: string

  constructor(id: string, type:  GeometryType, geometries: GeometryCollection,
    properties: Map<string, string | boolean | number>) {
    super(type, geometries, properties)
    this.id = id
  }

  getId(): string {
    return this.id
  }
}

export class VectorTileLayer extends  GeometryTileLayer {
  constructor(name: string) {
    super(name)
  }

  addFeature(id: string, geometryType: GeometryType, geometries: GeometryCollection,
    properties: Map<string, string | boolean | number> = new Map<string, string | boolean | number>()): void {
    this.features.push(new VectorTileFeature(id, geometryType, geometries, properties))
  }
}

export class VectorTileData extends GeometryTileData {

  layers: Map<string, VectorTileLayer>

  layerIds: string[] = []

  constructor() {
    super()
    this.layers = new Map<string, VectorTileLayer>()
  }

  addLayer(name: string): VectorTileLayer {
    if (!this.layers.has(name)) {
      this.layerIds.push(name)
      this.layers.set(name, new VectorTileLayer(name))
    }
    return this.layers.get(name)
  }

  getLayerIds(): string[] {
    return this.layerIds
  }

  getLayer(name: string): GeometryTileLayer {
    if (this.layers.has(name)) {
      return this.layers.get(name)
    }
    else {
      return null
    }
  }
}