import {MapView} from "../map/MapView"
import {MouseState} from "./MouseEventHandler"
import {Vector2} from "../engine/Vector2"
import {MathUtils} from "../engine/MathUtils"

export class DragRotateHandler {

  private _mapView: MapView

  private inertia: Array<[number, number]> = []

  private lastDragRotatePosition: Vector2;

  constructor(mapView: MapView) {
    this._mapView = mapView
  }

  onDragStart(mousePosition: Vector2) {
    this.lastDragRotatePosition = new Vector2(mousePosition.x, mousePosition.y)
    this.inertia = [[Date.now(), this._mapView.mapState.getBearing()]]
  }

  onDrag(data: MouseState) {
    this.inertia.push([Date.now(), -data.x])

    let bearingDiff = (this.lastDragRotatePosition.x - data.x) * 0.4
    let tiltDiff = (this.lastDragRotatePosition.y - data.y) * -0.5
    this._mapView.setBearing(this._mapView.mapState.getBearing() - bearingDiff, 0, true)
    this._mapView.setTilt(this._mapView.mapState.cameraPosition.getTilt() - tiltDiff, true)
    this.lastDragRotatePosition = new Vector2(data.x, data.y)
  }

  onDragEnd() {
    this.drainInertiaBuffer()
    if (this.inertia.length < 2) {
      this._mapView.notifyCameraDidChange()
      return
    }
    const inertia = this.inertia
    let length = inertia.length
    const last = inertia[length - 1], previous = inertia[length - 2]
    let  direction = last[1] - previous[1] > 0 ? 1 : -1
    const bearingDiff = last[1] - previous[1]
    let offset = Math.abs(bearingDiff) * 3.0
    if (Math.abs(offset) > 360){
      offset = 360
    }
    offset *= direction
    let duration = Math.abs(offset) / 90.0
    duration = MathUtils.clamp(duration, 0.5, 1.0)
    this._mapView.setBearing(this._mapView.mapState.getBearing() - offset, duration, true)
  }

  private drainInertiaBuffer() {
    const inertia = this.inertia, now = Date.now(), cutoff = 160  /* msec */
    while (inertia.length > 0 && now - inertia[0][0] > cutoff)
      inertia.shift()
  }
}
