import {Box} from "./Box"
import {Point} from "../public/Point"
import {GeometryUtils} from "./GeometryUtils"
import {Vector2} from "../engine/Vector2"
import {MapState} from "../map/MapState"
import {LatLng} from "../public/LatLng"

type Line = Array<Point>
type MultiLine = Array<Line>
type Ring = Array<Point>
type Polygon = Array<Point>
type MultiPolygon = Array<Polygon>

export class CollisionUtils {

  static boxIntersectsSymbol(touchBox: Box,
                             symbolPosition: LatLng, symbolElevation: number,
                             symbolWidth: number, symbolHeight: number,
                             symbolAnchor: Point, mapState: MapState): boolean {
    symbolElevation = mapState.is3dMode() ? symbolElevation : 0.0
    let position =
      GeometryUtils.latLngToScreenCoordinate(symbolPosition, symbolElevation, mapState.cameraPosition, mapState.getScreenSize())
    let positionInScreenCoordinate =
      new Vector2(position.x + (0.5 - symbolAnchor.x) * symbolWidth,
        position.y + (0.5 - symbolAnchor.y) * symbolHeight)
    let box = new Box(
      new Point(positionInScreenCoordinate.x - symbolWidth / 2, positionInScreenCoordinate.y - symbolHeight / 2),
      new Point(positionInScreenCoordinate.x + symbolWidth / 2, positionInScreenCoordinate.y + symbolHeight / 2))
    return CollisionUtils.boxIntersectsBox(touchBox, box)
  }

  static pointIntersectsBox(p: Point, box: Box): boolean {
    return  p.x <= box.max.x &&
            p.y <= box.max.y &&
            p.x >= box.min.x &&
            p.y >= box.min.y
  }

  static boxIntersectsBox(box1: Box, box2: Box): boolean {
    return !(box2.min.x > box1.max.x
          || box2.max.x < box1.min.x
          || box2.min.y > box1.max.y
          || box2.max.y < box1.min.y)
  }

  static polygonIntersectsPolygon(polygonA: Polygon, polygonB: Polygon): boolean  {
    for (let i = 0; i < polygonA.length; i++) {
      if (CollisionUtils.polygonContainsPoint(polygonB, polygonA[i])) return true
    }

    for (let i = 0; i < polygonB.length; i++) {
      if (CollisionUtils.polygonContainsPoint(polygonA, polygonB[i])) return true
    }

    if (CollisionUtils.lineIntersectsLine(polygonA, polygonB)) return true

    return false
  }

  static multiPolygonIntersectsBufferedPoint(multiPolygon: MultiPolygon, point: Point, radius: number): boolean  {
    for (let j = 0; j < multiPolygon.length; j++) {
      const polygon = multiPolygon[j]
      if (CollisionUtils.polygonContainsPoint(polygon, point)) return true
      if (CollisionUtils.pointIntersectsBufferedLine(point, polygon, radius)) return true
    }
    return false
  }

  static multiPolygonIntersectsBufferedMultiPoint(multiPolygon: MultiPolygon, rings: Array<Ring>,
    radius: number): boolean  {
    for (let i = 0; i < rings.length; i++) {
      const ring = rings[i]
      for (let k = 0; k < ring.length; k++) {
        if (CollisionUtils.multiPolygonIntersectsBufferedPoint(multiPolygon, ring[k], radius)) return true
      }
    }
    return false
  }

  static multiPolygonIntersectsMultiPolygon(multiPolygonA: MultiPolygon, multiPolygonB: MultiPolygon): boolean {

    if (multiPolygonA.length === 1 && multiPolygonA[0].length === 1) {
      return CollisionUtils.multiPolygonContainsPoint(multiPolygonB, multiPolygonA[0][0])
    }

    for (let m = 0; m < multiPolygonB.length; m++) {
      const ring = multiPolygonB[m]
      for (let n = 0; n < ring.length; n++) {
        if (CollisionUtils.multiPolygonContainsPoint(multiPolygonA, ring[n])) return true
      }
    }

    for (let j = 0; j < multiPolygonA.length; j++) {
      const polygon = multiPolygonA[j]
      for (let i = 0; i < polygon.length; i++) {
        if (CollisionUtils.multiPolygonContainsPoint(multiPolygonB, polygon[i])) return true
      }

      for (let k = 0; k < multiPolygonB.length; k++) {
        if (CollisionUtils.lineIntersectsLine(polygon, multiPolygonB[k])) return true
      }
    }

    return false
  }

  static multiPolygonIntersectsBufferedMultiLine(multiPolygon: MultiPolygon, multiLine: MultiLine,
    radius: number): boolean {
    for (let i = 0; i < multiLine.length; i++) {
      const line = multiLine[i]

      for (let j = 0; j < multiPolygon.length; j++) {
        const polygon = multiPolygon[j]

        if (polygon.length >= 3) {
          for (let k = 0; k < line.length; k++) {
            if (CollisionUtils.polygonContainsPoint(polygon, line[k])) return true
          }
        }

        if (CollisionUtils.lineIntersectsBufferedLine(polygon, line, radius)) return true
      }
    }
    return false
  }

  static lineIntersectsBufferedLine(lineA: Line, lineB: Line, radius: number): boolean {

    if (lineA.length > 1) {
      if (CollisionUtils.lineIntersectsLine(lineA, lineB)) return true

      // Check whether any point in either line is within radius of the other line
      for (let j = 0; j < lineB.length; j++) {
        if (CollisionUtils.pointIntersectsBufferedLine(lineB[j], lineA, radius)) return true
      }
    }

    for (let k = 0; k < lineA.length; k++) {
      if (CollisionUtils.pointIntersectsBufferedLine(lineA[k], lineB, radius)) return true
    }

    return false
  }

  static lineIntersectsLine(lineA: Line, lineB: Line): boolean {
    if (lineA.length === 0 || lineB.length === 0) return false
    for (let i = 0; i < lineA.length - 1; i++) {
      const a0 = lineA[i]
      const a1 = lineA[i + 1]
      for (let j = 0; j < lineB.length - 1; j++) {
        const b0 = lineB[j]
        const b1 = lineB[j + 1]
        if (CollisionUtils.lineSegmentIntersectsLineSegment(a0, a1, b0, b1)) return true
      }
    }
    return false
  }

  static lineSegmentIntersectsLineSegment(a0: Point, a1: Point, b0: Point, b1: Point): boolean {
    return CollisionUtils.isCounterClockwise(a0, b0, b1) !== CollisionUtils.isCounterClockwise(a1, b0, b1) &&
      CollisionUtils.isCounterClockwise(a0, a1, b0) !== CollisionUtils.isCounterClockwise(a0, a1, b1)
  }

  static pointIntersectsBufferedMultiLine(p: Point, lines: MultiLine, radius: number): boolean {
    return lines.some(line => CollisionUtils.pointIntersectsBufferedLine(p, line, radius))
  }

  static polygonIntersectsBufferedMultiLine(polygon: Polygon, lines: MultiLine, radius: number): boolean {
    let result = false
    lines.forEach(line => {
      if (polygon.length >= 3) {
        line.forEach(p => {
          if (this.polygonContainsPoint(polygon, p)) {
            result = true
            return
          }
        })
      }
      if (this.lineIntersectsBufferedLine(polygon, line, radius)) {
        result = true
        return
      }
    })
    return result
  }

  static pointIntersectsBufferedLine(p: Point, line: Line, radius: number): boolean {
    const radiusSquared = radius * radius
    let mbp = Point.convertPointToMBPoint(p)
    if (line.length === 1) return mbp.distSqr(line[0]) < radiusSquared

    for (let i = 1; i < line.length; i++) {

      // Find line segments that have a distance <= radius^2 to p
      // In that case, we treat the line as "containing point p".
      const v = line[i - 1], w = line[i]
      if (CollisionUtils.distToSegmentSquared(p, v, w) < radiusSquared) return true
    }
    return false
  }

  static boxIntersectsCircle(minBox: Vector2, maxBox: Vector2, c: Point, radius: number): boolean {
    let deltaX = c.x - Math.max(minBox.x, Math.min(c.x, minBox.x + (maxBox.x - minBox.x)))
    let deltaY = c.y - Math.max(minBox.y, Math.min(c.y, minBox.y + (maxBox.y - minBox.y)))
    return (deltaX * deltaX + deltaY * deltaY) < (radius * radius)
  }

  // point in polygon ray casting algorithm
  static multiPolygonContainsPoint(rings: Array<Ring>, p: Point): boolean {
    let c = false,
      ring, p1, p2

    for (let k = 0; k < rings.length; k++) {
      ring = rings[k]
      for (let i = 0, j = ring.length - 1; i < ring.length; j = i++) {
        p1 = ring[i]
        p2 = ring[j]
        if (((p1.y > p.y) !== (p2.y > p.y)) && (p.x < (p2.x - p1.x) * (p.y - p1.y) / (p2.y - p1.y) + p1.x)) {
          c = !c
        }
      }
    }
    return c
  }

  static polygonContainsPoint(ring: Ring, p: Point): boolean  {
    let c = false
    for (let i = 0, j = ring.length - 1; i < ring.length; j = i++) {
      const p1 = ring[i]
      const p2 = ring[j]
      if (((p1.y > p.y) !== (p2.y > p.y)) && (p.x < (p2.x - p1.x) * (p.y - p1.y) / (p2.y - p1.y) + p1.x)) {
        c = !c
      }
    }
    return c
  }

  private static isCounterClockwise(a: Point, b: Point, c: Point): boolean {
    return (c.y - a.y) * (b.x - a.x) > (b.y - a.y) * (c.x - a.x)
  }

  // Code from http://stackoverflow.com/a/1501725/331379.
  private static distToSegmentSquared(p: Point, v: Point, w: Point): number {
    let mbv = Point.convertPointToMBPoint(v)
    let mbw = Point.convertPointToMBPoint(w)
    let mbp = Point.convertPointToMBPoint(p)
    const l2 = mbv.distSqr(mbw)
    if (l2 === 0) return mbp.distSqr(mbv)
    const t = ((p.x - v.x) * (w.x - v.x) + (p.y - v.y) * (w.y - v.y)) / l2
    if (t < 0) return mbp.distSqr(mbv)
    if (t > 1) return mbp.distSqr(mbw)
    return mbp.distSqr(mbw.sub(mbv)._mult(t)._add(mbv))
  }
}
