import * as createRBTree from "functional-red-black-tree"
import {MapState} from "../map/MapState"
import {compareLayerId, Layer, LayerId} from "../layer/Layer"
import {LayerRenderFactory} from "./LayerRenderFactory"
import {EffectManager} from "../effect/EffectManager"

export class MapRenderer {

  gl: WebGLRenderingContext

  mapState: MapState

  layerRenderers: any

  private effectManager: EffectManager

  constructor(gl: WebGLRenderingContext, mapState: MapState) {
    this.mapState = mapState
    this.gl = gl

    this.layerRenderers = createRBTree(compareLayerId)
  }

  render(): void {

    // We need to render the shadow mapping first
    if (this.mapState.is3dMode() && this.effectManager.enableShadowEffect) {
      this.layerRenderers.forEach((layerId, layer) => {
        layer.renderShadowMapping()
      })
    }

    this.layerRenderers.forEach((layerId, layer) => {
      layer.render()
    })
  }

  renderSelection(): void {
    this.layerRenderers.forEach((layerId, layer) => {
      layer.renderSelection()
    })
  }

  addLayer(layer: Layer): void {
    this.layerRenderers = this.layerRenderers.insert(new LayerId(layer.getLayerId(), layer.getOrder()),
      LayerRenderFactory.create(this.gl, this.mapState, layer))
  }

  setEffectManager(effectManager: EffectManager): void {
    this.effectManager = effectManager
  }

  removeLayer(layerId: string, layerOrder: number): void {
    let id = new LayerId(layerId, layerOrder)
    this.layerRenderers = this.layerRenderers.remove(id)
  }
}
