import {TileCoordinate} from "./TileCoordinate"
import {LatLng} from "../public/LatLng"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {Vector2} from "../engine/Vector2"
import {MapUtils} from "../util/MapUtils"

export class UnwrappedTileCoordinate {
  wrap: number;

  tileCoordinate: TileCoordinate;

  static createWithXYZoom(x: number, y: number, zoom: number, mode3D: boolean = false): UnwrappedTileCoordinate {
    let unwrappedTileCoordinate = new UnwrappedTileCoordinate()
    unwrappedTileCoordinate.wrap = ((x < 0 ? x - (1 << zoom) + 1 : x) / (1 << zoom)) >> 0
    unwrappedTileCoordinate.tileCoordinate = new TileCoordinate(x & ((1 << zoom) - 1),
      y < 0 ? 0 : Math.min(y, (1 << zoom) - 1),
      zoom, mode3D)
    return unwrappedTileCoordinate
  }

  static createWithWrapAndTileCoordinate(wrap: number, tileCoordinate: TileCoordinate): UnwrappedTileCoordinate {
    let unwrappedTileCoordinate = new UnwrappedTileCoordinate()
    unwrappedTileCoordinate.wrap = wrap
    unwrappedTileCoordinate.tileCoordinate = tileCoordinate
    return unwrappedTileCoordinate
  }

  static fromLatLng(latLng: LatLng, zoom: number): UnwrappedTileCoordinate {
    let tile = ProjectionMercator.projectScale(latLng, Math.pow(2, zoom)).multiply(
      1 / MapUtils.getTileSize())
    return UnwrappedTileCoordinate.createWithXYZoom(Math.floor(tile.x), Math.floor(tile.y), zoom)
  }

  toVec2(): Vector2 {
    let x = this.tileCoordinate.getX() + this.wrap * (1 << this.tileCoordinate.getZoom())
    return new Vector2(x, this.tileCoordinate.getY())
  }

  parent(): UnwrappedTileCoordinate {
    let parentZ = this.tileCoordinate.zoom - 1
    let parentX = (this.tileCoordinate.x / 2) >> 0
    let parentY = (this.tileCoordinate.y / 2) >> 0
    return UnwrappedTileCoordinate.createWithWrapAndTileCoordinate(
      this.wrap, new TileCoordinate(parentX, parentY, parentZ, this.tileCoordinate.is3DMode()))
  }

  children(): UnwrappedTileCoordinate[] {
    let childZ = this.tileCoordinate.zoom + 1
    let childX = this.tileCoordinate.x * 2
    let childY = this.tileCoordinate.y * 2
    return [
      UnwrappedTileCoordinate.createWithWrapAndTileCoordinate(
        this.wrap, new TileCoordinate(childX, childY, childZ, this.tileCoordinate.is3DMode())),
      UnwrappedTileCoordinate.createWithWrapAndTileCoordinate(
        this.wrap, new TileCoordinate(childX, childY + 1, childZ, this.tileCoordinate.is3DMode())),
      UnwrappedTileCoordinate.createWithWrapAndTileCoordinate(
        this.wrap, new TileCoordinate(childX + 1, childY, childZ, this.tileCoordinate.is3DMode())),
      UnwrappedTileCoordinate.createWithWrapAndTileCoordinate(
        this.wrap, new TileCoordinate(childX + 1, childY + 1, childZ, this.tileCoordinate.is3DMode()))
    ]
  }

  getWrap(): number {
    return this.wrap
  }

  getTileCoordinate(): TileCoordinate {
    return this.tileCoordinate
  }

  equal(rhs: UnwrappedTileCoordinate): boolean {
    return this.wrap == rhs.getWrap() && this.tileCoordinate.equal(rhs.getTileCoordinate())
  }

  notEqual(rhs: UnwrappedTileCoordinate): boolean {
    return this.wrap != rhs.getWrap()
  }

  lessThan(rhs: UnwrappedTileCoordinate): boolean {
    if (this.wrap == rhs.getWrap()) {
      return this.tileCoordinate.lessThan(rhs.getTileCoordinate())
    }
    return this.wrap < rhs.getWrap()
  }
}
