import {ParticleSystem} from "./ParticleSystem"
import {ParticleRain} from "./ParticleRain"
import {ParticleSnow} from "./ParticleSnow"
import {MapState} from "../map/MapState"
import {Vector2} from "../engine/Vector2"
import {Particle} from "./Particle"
import {MapConstants, MathConstants, ParticleConstants, WeatherConstants} from "../map/MapConstants"
import {Weather} from "../public/Weather"
import {ParticlePool} from "./ParticlePool"
import {UrlUtils} from "../util/UrlUtils"
import {RequestTask, Resource} from "../worker/RequestTask"
import {TaskManager} from "../worker/TaskManager"
import {MapSettings} from "../map/MapSettings"
import {LatLng} from "../public/LatLng"

export class WeatherManager {

  private mapState: MapState

  private _weather: Weather

  private _particlePool: ParticlePool

  private _particleSystems: ParticleSystem[] = []

  private _lastTime: number

  private _lastTimeGetLive: number

  private _lastLocationGetLive: LatLng

  private _liveMode: boolean

  private weatherTaskManager: TaskManager

  private requestTask: RequestTask

  constructor(gl: WebGLRenderingContext, mapState: MapState, weatherTaskManager: TaskManager) {
    this.weatherTaskManager = weatherTaskManager
    this.mapState = mapState
    this._liveMode = false
    this._weather = Weather.Sunny
    this._particlePool = new ParticlePool()
    let particleRain = new ParticleRain(gl, this._particlePool)
    particleRain.init()
    this._particleSystems.push(particleRain)

    let particleSnow = new ParticleSnow(gl, this._particlePool)
    particleSnow.init()
    this._particleSystems.push(particleSnow)

    this._lastTime = NaN
  }

  setWeather(weather: Weather): void {
    if (weather === this._weather || (this._liveMode && weather === Weather.Live)) {
      return
    }

    if (weather == Weather.Live) {
      this._liveMode = true
      this._lastTimeGetLive = Date.now()
      this._lastLocationGetLive = this.mapState.getTarget()
      this.getLiveInfomation()
    }
    else {
      this._liveMode = false
    }

    if (weather < Weather.Sunny) {

      // Clean up the previous weather
      this._particleSystems[weather].cleanup()
    }
    this._weather = weather
    this._lastTime = NaN
  }

  private setWeatherFromLive(weather: Weather): void {
    if (weather < Weather.Sunny) {

      // Clean up the previous weather
      this._particleSystems[weather].cleanup()
    }
    this._weather = weather
    this._lastTime = NaN
  }

  getWeather(): Weather {
    return this._weather
  }

  dirty(): boolean {
    return this.mapState.is3dMode() &&
      (this._liveMode ||
      this.getWeather() != Weather.None &&
      this.getWeather() != Weather.Sunny &&
      this.getWeather() != Weather.Cloudy)
  }

  getLiveInfomation(): void {
    let weatherRequestUrl =  MapConstants.getWeatherDataUrl(
      this.mapState.getTarget().lat,
      this.mapState.getTarget().lng)
    if (this.requestTask) {
      this.weatherTaskManager.cancelTask(this.requestTask)
    }
    this.requestTask = new RequestTask(Resource.Json)
    let requestTask = this.requestTask
    requestTask.url = weatherRequestUrl
    requestTask.completion = (response: any) => {
      if (response.data != null && response.data.result != null && response.data.result.type != null) {
        this.setWeatherFromLive(WeatherManager.convertToWeather(response.data.result.type))
      }
      else {
        console.error("Can not get weather from server because response data invalid !")
      }
    }
    this.weatherTaskManager.postTask(requestTask)
  }

  update(): void {
    if (!this.dirty()) {
      return
    }
    if (this._weather < Weather.Sunny) {
      let currentTime = Date.now()
      let delta = !isNaN(this._lastTime) ? currentTime - this._lastTime : 0
      this._lastTime = currentTime
      this._particleSystems[this._weather].update(delta / 1000.0 /* Millisecond to second */)
    }

    if (this._liveMode) {
      let currentTime = Date.now()
      let currentLocation = this.mapState.getTarget()
      let distance = currentLocation.distanceTo(this._lastLocationGetLive)
      if (distance > WeatherConstants.refreshDistance
      || currentTime - this._lastTimeGetLive > WeatherConstants.refreshTime) {
        this._lastLocationGetLive = currentLocation
        this._lastTimeGetLive = currentTime
        this.getLiveInfomation()
      }
    }
  }

  render(mapState: MapState): void {
    if (!this.dirty()) {
      return
    }
    if (this._weather < Weather.Sunny) {
      this._particleSystems[this._weather].render(mapState)
    }
  }

  resize(width: number, height: number): void {
    for (let i = 0; i < Weather.Sunny; ++i) {
      let x = 0
      if (i == Weather.Rainy) {
        x = Math.abs(Math.tan(ParticleConstants.rainParticleRotation * MathConstants.deg2Rad)) * height
      }
      let spawnRectWidth = width + x
      let spawnRectHeight = 2
      this._particleSystems[i].spawnRect = {
        "width": spawnRectWidth,
        "height": spawnRectHeight
      }
      this._particleSystems[i].emitterPosition = new Vector2(width / 2.0 - x / 2.0, 0)
      this._particleSystems[i].lifetimeFunc = (particle: Particle) => {
        return particle.position.y > height || particle.position.x > width
      }
    }
  }

  private static convertToWeather(type: number): Weather {
    let weather = NaN
    switch (type) {
    case 0:
      weather = Weather.Sunny
      break
    case 1:
      weather = Weather.Rainy
      break
    case 2:
      weather = Weather.Snowy
      break
    case 3:
      weather = Weather.Cloudy
      break
    default:
      weather = Weather.None
    }
    return weather
  }

  destroy(): void {
    for (let i = 0; i < Weather.Sunny; ++i) {
      this._particleSystems[i].destroy()
    }
    while (this._particleSystems.length != 0)  {
      this._particleSystems.pop()
    }
    this._particleSystems = null

    // wipe the pool clean
    this._particlePool.destroy()
    this._particlePool = null
  }
}
