import {GeometryTileData, GeometryTileFeature, GeometryTileLayer, GeometryType} from "./GeometryTileData"
import {AnnotationId} from "../../annotation/Annotation"
import {GeometryCollection} from "./GeometryCollection"

export class AnnotationTileFeature extends GeometryTileFeature {
  private _id: AnnotationId

  constructor(id: AnnotationId, type:  GeometryType, geometries: GeometryCollection,
    properties: Map<string, string | boolean | number>) {
    super(type, geometries, properties)
    this._id = id
  }

  get id(): AnnotationId {
    return this._id
  }
}

export class AnnotationTileLayer extends  GeometryTileLayer {
  constructor(name: string) {
    super(name)
  }

  addFeature(annotationId: AnnotationId, geometryType: GeometryType, geometries: GeometryCollection,
    properties: Map<string, string | boolean | number> = new Map<string, string | boolean | number>()): void {
    this.features.push(new AnnotationTileFeature(annotationId, geometryType, geometries, properties))
  }
}

export class AnnotationTileData extends GeometryTileData {

  layers: Map<string, AnnotationTileLayer>

  layerIds: string[] = []

  constructor() {
    super()
    this.layers = new Map<string, AnnotationTileLayer>()
  }

  addLayer(name: string): AnnotationTileLayer {
    if (!this.layers.has(name)) {
      this.layerIds.push(name)
      this.layers.set(name, new AnnotationTileLayer(name))
    }
    return this.layers.get(name)
  }

  getLayerIds(): string[] {
    return this.layerIds
  }

  getLayer(name: string): GeometryTileLayer {
    if (this.layers.has(name)) {
      return this.layers.get(name)
    }
    else {
      return null
    }
  }
}
