import geojsonvt from "geojson-vt"
import * as RBush from "rbush"
import * as LinkedMap from "linked-map"
import {LayerType} from "../layer/Layer"
import {MapConstants} from "./MapConstants"
import {MapView} from "./MapView"
import {VectorTile} from "../tile/VectorTile"
import {TileCoordinate} from "../tile/TileCoordinate"
import {VectorTileData, VectorTileFeature} from "../tile/data/VectorTileData"
import {GeometryCollection} from "../tile/data/GeometryCollection"
import {RenderTile} from "../renderer/RenderTile"
import {UnwrappedTileCoordinate} from "../tile/UnwrappedTileCoordinate"
import {LatLng} from "../public/LatLng"
import {GeometryTileData, GeometryType} from "../tile/data/GeometryTileData"
import {UUID} from "../util/UUID"
import {Vector2} from "../engine/Vector2"
import {GeometryUtils} from "../util/GeometryUtils"
import {ProjectionMercator} from "./ProjectionMercator"
import {Point} from "../public/Point"
import {CollisionUtils} from "../util/CollisionUtils"
import {MapUtils} from "../util/MapUtils"
import {Data} from "../public/MapData"
import {LineLayerData} from "../renderer/data/LineLayerData"
import {GeoJsonLayer} from "../layer/GeoJsonLayer"
import {Box} from "../util/Box"

class PointGeoJsonData {

  private readonly _id: string

  private readonly _coordinate: LatLng

  private readonly _properties: Map<string, string | boolean | number>

  private readonly _order

  // Rbush properties
  private readonly _minX: number

  private readonly _minY: number

  private readonly _maxX: number

  private readonly _maxY: number

  constructor(id: string, coordinate: LatLng, properties: Map<string, string | boolean | number>, order: number) {
    this._id = id
    this._coordinate = coordinate
    this._properties = properties
    this._order = order
    this._minX = coordinate.lng
    this._minY = coordinate.lat
    this._maxX = coordinate.lng
    this._maxY = coordinate.lat
  }

  get id(): string {
    return this._id
  }

  get coordinate(): LatLng {
    return this._coordinate
  }

  get properties(): Map<string, string | boolean | number> {
    return this._properties
  }

  get order(): number {
    return this._order
  }

  get minX(): number {
    return this._minX
  }

  get minY(): number {
    return this._minY
  }

  get maxX(): number {
    return this._maxX
  }

  get maxY(): number {
    return this._maxY
  }
}

export class GeoJsonManager {

  private static readonly geojsonvtOptions = {
    maxZoom: MapConstants.defaultMaxNativeZoom,
    buffer: 0,
    extent: MapConstants.tileExtend,
    tolerance: 4,
  }

  public static readonly geoJsonLayerId = "3.maplink.geojson.layer"

  private geoJsonIndexer: geojsonvt

  private pointTree: rbush.RBush<PointGeoJsonData>

  private tiles: Map<string, VectorTile>

  private mapView: MapView

  private features: LinkedMap<string, Data.Feature>

  private dirty: boolean

  constructor(mapView: MapView) {
    this.pointTree = RBush<PointGeoJsonData>()
    this.tiles = new Map<string, VectorTile>()
    this.features = new LinkedMap<string, Data.Feature>()
    this.mapView = mapView
    this.dirty = false
  }

  update(): void {
    if (this.dirty) {
      this.tiles.forEach(tile => {
        let vectorTileData = this.getTileData(tile.getTileCoordinate())
        this.updateRenderTile(vectorTileData, tile)
        tile.setData(vectorTileData)
      })
      this.dirty = false
    }
  }

  addFeature(feature: Data.Feature |  Data.FeatureOptions):  Data.Feature {
    let internalFeature = feature instanceof Data.Feature ? feature :
      new Data.Feature(feature.id, feature.geometry, feature.properties)
    let featureId = internalFeature.getId()
    this.features.push(typeof featureId === "string" ? featureId : featureId.toString(), internalFeature)
    this.reloadIndexer()
    return internalFeature
  }

  removeFeature(feature: Data.Feature): void {
    if (feature == null) {
      return
    }
    let featureId = feature.getId()
    if (featureId && this.features.remove(typeof featureId === "string" ? featureId : featureId.toString())) {
      this.reloadIndexer()
    }
  }

  removeAllFeatures(): void {
    this.features.clear()
    this.reloadIndexer()
  }

  addGeoJson(geoJson: string): Data.Feature[] {
    let features: Data.Feature[] = []
    let geoJsonObject = JSON.parse(geoJson)
    geoJsonObject.features.forEach(feature => {
      let geometries: LatLng[] = []
      let linearRings: Data.LinearRing[] = []
      let geometryType = feature.geometry.type
      let geometry = null
      if (geometryType === "Point") {
        let point = feature.geometry.coordinates
        geometry = new Data.Point(point)
      }
      else if (geometryType === "MultiPoint") {
        let points = feature.geometry.coordinates
        let coordinates: LatLng[] = []
        points.forEach(point => {
          coordinates.push(LatLng.convert(point))
        })
        geometry = new Data.MultiPoint(coordinates)
      }
      else if (geometryType === "Polygon") {
        let polygon = feature.geometry.coordinates
        for(let i = 0; i < polygon.length; ++i) {
          let ring = polygon[i]
          ring.forEach(point => {
            geometries.push(LatLng.convert(point))
          })
          linearRings.push(new Data.LinearRing(geometries))
          geometries = []
        }
        geometry = new Data.Polygon(linearRings)
      }
      else if (geometryType === "MultiPolygon") {
        let polygons : Data.Polygon[] = []
        feature.geometry.coordinates.forEach(polygon => {
          let linearRings: Data.LinearRing[] = []
          for(let i = 0; i < polygon.length; ++i) {
            let ring = polygon[i]
            let ringElements: LatLng[] = []
            ring.forEach(point => {
              ringElements.push(LatLng.convert(point))
            })
            linearRings.push(new Data.LinearRing(ringElements))
          }
          polygons.push(new Data.Polygon(linearRings))
        })
        geometry = new Data.MultiPolygon(polygons)
      }
      else if (geometryType === "LineString") {
        feature.geometry.coordinates.forEach(point => {
          geometries.push(LatLng.convert(point))
        })
        geometry = new Data.LineString(geometries)
      }
      else if (geometryType === "MultiLineString") {
        let lineStrings : Data.LineString[] = []
        feature.geometry.coordinates.forEach(lineString => {
          let lineStringElements: LatLng[] = []
          lineString.forEach(point => {
            lineStringElements.push(LatLng.convert(point))
          })
          lineStrings.push(new Data.LineString(lineStringElements))
        })
        geometry = new Data.MultiLineString(lineStrings)
      }
      else {
        console.warn("Geometry type not supported: " + geometryType)
        return
      }
      let featureId =
        feature.id ? (typeof feature.id === "string" ? feature.id : feature.id.toString()) : UUID.generate()
      if (!this.features.get(featureId)) {
        let ft = new Data.Feature(featureId, geometry, feature.properties)
        this.features.push(featureId, ft)
        features.push(ft)
      }
      else {
        console.warn("Feature id already exist! This feature will be ignored: " + featureId)
      }
    })
    this.reloadIndexer()
    return features
  }

  addTile(tile: VectorTile): void {
    let tileCoordinate = tile.getTileCoordinate()
    let vectorTileData = this.getTileData(tileCoordinate)
    this.updateRenderTile(vectorTileData, tile)
    tile.setData(vectorTileData)
    this.tiles.set(tileCoordinate.toString(), tile)
  }

  removeTile(tileCoordinate: TileCoordinate): void {
    let tile = this.tiles.get(tileCoordinate.toString())
    if (!tile) {
      return
    }
    let geometryTileData = tile.getTileData()
    if (geometryTileData != null) {
      let vectorTileData = geometryTileData as VectorTileData
      let layerIds = vectorTileData.getLayerIds()
      let tileCoordinateKey = tile.getTileCoordinate().toString()
      layerIds.forEach(layerId => {
        let layer = this.mapView.getLayer(layerId)
        if (layer != null) {
          let layerType = layer.getLayerType()
          if (layerType == LayerType.GEOJSON) {
            let geoJsonLayer = layer as GeoJsonLayer
            geoJsonLayer.removeRenderTile(tileCoordinateKey)
          }
        }
      })
    }
    tile.destroy()
    this.tiles.delete(tile.getTileCoordinate().toString())
  }

  removeAllTiles(): void {
    Array.from(this.tiles.values()).forEach(tile=> this.removeTile(tile.tileCoordinate))
  }

  queryFeature(touchPoint: Vector2): Data.Feature {
    let mapState = this.mapView.mapState
    let latlng = GeometryUtils.screenCoordinateToLatLng(touchPoint, mapState.cameraPosition, mapState.getScreenSize())
    if (!latlng.valid()) {
      return null
    }
    let touchBox = new Box(
      new Point(touchPoint.x - MapUtils.touchBoxSize / 2, touchPoint.y - MapUtils.touchBoxSize / 2),
      new Point(touchPoint.x + MapUtils.touchBoxSize / 2, touchPoint.y + MapUtils.touchBoxSize / 2))

    let tileCoordinate =
      TileCoordinate.fromLatLng(latlng,
        mapState.getZoom(), mapState.is3dMode())
    let tileScale = 1.0
    let maxNativeZoom = mapState.getMaxNativeZoom()
    if (mapState.getZoom() > maxNativeZoom) {
      tileCoordinate = TileCoordinate.fromLatLng(latlng,
        mapState.getMaxNativeZoom(), mapState.is3dMode())
      tileScale = Math.pow(2.0, Math.round(mapState.getZoom()) - maxNativeZoom)
    }
    if (this.tiles.has(tileCoordinate.toString())) {

      // Query point feature first
      let bounds = GeometryUtils.tileCoordinateToLatLngBounds(tileCoordinate)
      let bBox = {
        minX: bounds.getSouthwest().lng,
        minY: bounds.getSouthwest().lat,
        maxX: bounds.getNortheast().lng,
        maxY: bounds.getNortheast().lat
      }
      let symbols = this.pointTree.search(bBox)
      symbols.sort((a: PointGeoJsonData, b: PointGeoJsonData) => {
        return b.order - a.order
      })
      for (let i = 0; i < symbols.length; ++i) {
        let symbol = symbols[i]
        let anchor = MapConstants.pointGeoJsonAnchor
        let anchorX = anchor[0]
        let anchorY = anchor[1]
        if (CollisionUtils.boxIntersectsSymbol(touchBox, symbol.coordinate,
          0, MapConstants.pointGeoJsonWidth, MapConstants.pointGeoJsonHeight,
          new Point(anchorX, anchorY),
          mapState)) {

          // Clicked on point
          return this.features.get(symbol.id)
        }
      }

      let vectorTile = this.tiles.get(tileCoordinate.toString())
      let geometryTileData = vectorTile.getTileData()
      if (geometryTileData == null || geometryTileData.getLayerIds().length == 0) {
        return null
      }
      let scale = mapState.scale
      let tileCoordinateX = tileCoordinate.getX()
      let tileCoordinateY = tileCoordinate.getY()

      let minBoxLatLng = GeometryUtils.screenCoordinateToLatLng(new Vector2(touchBox.min.x, touchBox.min.y),
        mapState.cameraPosition, mapState.getScreenSize())
      let maxBoxLatLng = GeometryUtils.screenCoordinateToLatLng(new Vector2(touchBox.max.x, touchBox.max.y),
        mapState.cameraPosition, mapState.getScreenSize())

      let minBoxVectorTileCoordinate =
        GeometryUtils.pixelCoordinateToVectorTileCoordinate(
          ProjectionMercator.projectScale(
            minBoxLatLng,
            scale),
          tileCoordinateX, tileCoordinateY, tileScale)
      let maxBoxVectorTileCoordinate =
        GeometryUtils.pixelCoordinateToVectorTileCoordinate(
          ProjectionMercator.projectScale(
            maxBoxLatLng,
            scale),
          tileCoordinateX, tileCoordinateY, tileScale)

      let clickedFeature =
        this.queryShapeAnnotationInternal(minBoxVectorTileCoordinate, maxBoxVectorTileCoordinate, geometryTileData)
      if (clickedFeature) {
        return clickedFeature
      }
    }
    return null
  }

  private queryShapeAnnotationInternal(minBoxVectorTileCoordinate: Vector2,
                                       maxBoxVectorTileCoordinate: Vector2,
                                       geometryTileData: GeometryTileData): Data.Feature {
    let tileSize = MapUtils.getTileSize()
    let layerIds = geometryTileData.getLayerIds()

    // Sort by name so that we can query line layer first
    layerIds.sort()
    let layerIdsSize = geometryTileData.getLayerIds().length
    let mapState = this.mapView.mapState
    let touchBoxPolygon = [
      new Point(minBoxVectorTileCoordinate.x, maxBoxVectorTileCoordinate.y),
      new Point(maxBoxVectorTileCoordinate.x, maxBoxVectorTileCoordinate.y),
      new Point(maxBoxVectorTileCoordinate.x, minBoxVectorTileCoordinate.y),
      new Point(minBoxVectorTileCoordinate.x, minBoxVectorTileCoordinate.y)
    ]

    for (let i = 0; i < layerIdsSize; ++i) {
      let layerId = layerIds[i]
      let geometryLayer = geometryTileData.getLayer(layerId)

      // Revert the query order we will always get the feature in the top
      for (let j = geometryLayer.featureCount() - 1; j >= 0 ; --j) {
        let feature = geometryLayer.getFeature(j)
        let featureType = feature.getType()
        if (!(feature instanceof VectorTileFeature) || featureType == GeometryType.Point ||
          featureType == GeometryType.Unknown) {
          continue
        }
        let vectorTileFeature = feature as VectorTileFeature
        let geometries = vectorTileFeature.getGeometries().geometryCoordinates
        let layer = this.mapView.getLayer(layerId)
        if (layer == null) {
          console.error("There is problem while querying shape annotation")
          return null
        }
        if (featureType == GeometryType.LineString) {
          let haftLineWidth =
            (feature.properties.get("stroke-width") as number || LineLayerData.defaultStrokeWidth)
            * MapUtils.pixelScale / 2.0
          if (mapState.getZoom() > mapState.getMaxNativeZoom()) {
            haftLineWidth /= Math.pow(2.0, Math.round(mapState.getZoom()) - mapState.getMaxNativeZoom())
          }
          let lines = []
          geometries.forEach(geometry => {
            let line = []
            geometry.forEach(point => line.push(new Point(point[0], point[1])))
            lines.push(line)
          })
          haftLineWidth *= MapUtils.pixelScale
          let testHit =
            CollisionUtils.polygonIntersectsBufferedMultiLine(touchBoxPolygon, lines, haftLineWidth / tileSize)
          if (testHit) {
            return this.features.get(vectorTileFeature.getId())
          }
        }
        else if (featureType == GeometryType.Polygon) {
          let hitPolygon = false
          for (let i = 0; i < geometries.length; ++i) {
            let ring = geometries[i]
            let polygon = []
            ring.forEach(point => polygon.push(new Point(point[0], point[1])))
            if (i == 0) {

              // Extrude
              hitPolygon = CollisionUtils.polygonIntersectsPolygon(touchBoxPolygon, polygon)
              if (!hitPolygon) {
                break
              }
            }
            else {

              // Holes
              let testHitHole = CollisionUtils.polygonIntersectsPolygon(touchBoxPolygon, polygon)
              if (testHitHole) {
                if (!CollisionUtils.polygonIntersectsBufferedMultiLine(touchBoxPolygon, [polygon], 0)) {
                  hitPolygon = false
                  break
                }
              }
            }
          }
          if (hitPolygon) {
            return this.features.get(vectorTileFeature.getId())
          }
        }
      }
    }
    return null
  }

  private updateRenderTile(vectorTileData: VectorTileData, tile: VectorTile) {
    if (vectorTileData != null) {
      let tileCoordinate = tile.getTileCoordinate()
      let tileCoordinateKey = tileCoordinate.toString()
      let layerIds = vectorTileData.getLayerIds()
      layerIds.forEach(layerId => {
        let layer = this.mapView.getLayer(layerId)
        if (layer != null) {
          let layerType = layer.getLayerType()
          let renderTile = new RenderTile(
            UnwrappedTileCoordinate.createWithXYZoom(
              tileCoordinate.getX(),
              tileCoordinate.getY(),
              tileCoordinate.getZoom()),
            tile)

          if (layerType == LayerType.GEOJSON) {
            let geoJsonLayer = layer as GeoJsonLayer
            geoJsonLayer.addRenderTile(tileCoordinateKey, renderTile)
          }
        }
      })
    }
  }

  private reloadIndexer(): void {
    let dataFeatures = {
      "type": "FeatureCollection",
      "features": [
      ]
    }
    this.pointTree.clear()
    let order = 0
    this.features.each((key, feature) => {
      let geometryType = feature.getGeometry().getType()
      let properties = feature.getProperties()
      let coordinates = []
      let fillOutlined = false
      let fillOutlinedCoordinates = []

      if (geometryType === "Point") {
        let pointGeometry = feature.getGeometry() as Data.Point
        this.pointTree.insert(new PointGeoJsonData(key, pointGeometry.get(), properties, order++))
      }
      else if (geometryType === "MultiPoint") {
        let multiPointGeometry = feature.getGeometry() as Data.MultiPoint
        multiPointGeometry.forEachLatLng(coordinate => {
          this.pointTree.insert(new PointGeoJsonData(key, coordinate, properties, order++))
        })
      }
      else if (geometryType === "Polygon") {
        let polygonGeometry = feature.getGeometry() as Data.Polygon
        for (let i = 0; i < polygonGeometry.getLength(); ++i) {
          let pathCoordinates = []
          let ring = polygonGeometry.getAt(i)
          ring.elements.forEach(element => pathCoordinates.push([element.lng, element.lat]))
          coordinates.push(pathCoordinates)
        }
        if ((properties == null) ||
          (!properties["stroke-width"]) ||
          (properties["stroke-width"] as number > 0)) {
          fillOutlined = true
          fillOutlinedCoordinates = coordinates
        }
      }
      else if (geometryType === "MultiPolygon") {
        let multiPolygonGeometry = feature.getGeometry() as Data.MultiPolygon
        let size = multiPolygonGeometry.getLength()
        if ((properties == null) ||
          (!properties["stroke-width"]) ||
          (properties["stroke-width"] as number > 0)) {
          fillOutlined = true
        }

        for (let i = 0; i < size; ++i) {
          let coordinates = []
          let polygonGeometry = multiPolygonGeometry.getAt(i) as Data.Polygon
          for (let i = 0; i < polygonGeometry.getLength(); ++i) {
            let pathCoordinates = []
            let ring = polygonGeometry.getAt(i)
            ring.elements.forEach(element => pathCoordinates.push([element.lng, element.lat]))
            coordinates.push(pathCoordinates)
          }
          if (fillOutlined) {
            coordinates.forEach(coordinate => fillOutlinedCoordinates.push(coordinate))
          }
          let dataFeature = {
            "type": "Feature",
            "id": feature.getId(),
            "properties": properties,
            "geometry": {
              "type": "Polygon",
              "coordinates":
              coordinates
            }
          }
          dataFeatures.features.push(dataFeature)
        }
      }
      else if (geometryType === "LineString") {
        feature.getGeometry().forEachLatLng((point => {
          coordinates.push([point.lng, point.lat])
        }))
      }
      else if (geometryType === "MultiLineString") {
        let multiLineStringGeometry = feature.getGeometry() as Data.MultiLineString
        let size = multiLineStringGeometry.getLength()
        for (let i = 0; i < size; ++i) {
          let lineString = []
          multiLineStringGeometry.getAt(i).forEachLatLng(point => {
            lineString.push([point.lng, point.lat])
          })
          coordinates.push(lineString)
        }
      }
      else {
        throw new Error("Not reachable")
      }
      if (geometryType !== "MultiPolygon" && geometryType !== "Point") {
        let dataFeature = {
          "type": "Feature",
          "id": feature.getId(),
          "properties": properties,
          "geometry": {
            "type": geometryType,
            "coordinates":
            coordinates
          }
        }
        dataFeatures.features.push(dataFeature)
      }
      if (fillOutlined) {
        let stroke = "#555555"
        let strokeOpacity = 1
        if (properties) {
          stroke = properties["stroke"] || "#555555"
          strokeOpacity = properties["stroke-opacity"] ? properties["stroke-opacity"] : 1
        }
        for (let i = 0; i < fillOutlinedCoordinates.length; ++i) {
          let coordinates = fillOutlinedCoordinates[i]
          let fillOutlineFeature = {
            "type": "Feature",
            "id": feature.getId(),
            "properties": {
              "fill-outlined": true,
              "stroke": stroke,
              "stroke-opacity": strokeOpacity
            },
            "geometry": {
              "type": "LineString",
              "coordinates":
              coordinates
            }
          }
          dataFeatures.features.push(fillOutlineFeature)
        }
      }

    })
    this.geoJsonIndexer = geojsonvt(JSON.parse(JSON.stringify(dataFeatures, null, 2)),
      GeoJsonManager.geojsonvtOptions)
    this.dirty = true
    this.mapView.dirty = true
    let clonedTiles: VectorTile[] = []
    Array.from(this.tiles.values())
      .forEach(tile => clonedTiles.push(new VectorTile(tile.tileCoordinate, tile.gl)))
    this.removeAllTiles()
    clonedTiles.forEach(tile => this.addTile(tile))
  }

  private getTileData(tileCoordinate: TileCoordinate): VectorTileData {
    if (this.features.size() == 0) {
      return null
    }
    let vectorTileData = new VectorTileData()
    let tile = this.geoJsonIndexer.getTile(tileCoordinate.getZoom(), tileCoordinate.getX(), tileCoordinate.getY())
    tile != null && tile.features != null && tile.features.forEach(feature => {
      let geometryType = feature.type
      let geometries = new GeometryCollection(feature.geometry)
      let properties =  new Map<string, string | boolean | number>()
      let featureTags = feature.tags
      for (const fieldName in featureTags) {
        properties.set(fieldName, featureTags[fieldName])
      }
      let geojsonLayer = vectorTileData.addLayer(GeoJsonManager.geoJsonLayerId)
      if (geometryType === GeometryType.Polygon) {
        geojsonLayer.addFeature(feature.id, geometryType, geometries, properties)
      }
      else if (geometryType === GeometryType.LineString) {
        if (properties.has("fill-outlined")) {
          geojsonLayer.addFeature(feature.id, geometryType, geometries, properties)
        }
        else {
          geojsonLayer.addFeature(feature.id, geometryType, geometries, properties)
        }
      }
    })
    let bounds = GeometryUtils.tileCoordinateToLatLngBounds(tileCoordinate)
    let bBox = {
      minX: bounds.getSouthwest().lng,
      minY: bounds.getSouthwest().lat,
      maxX: bounds.getNortheast().lng,
      maxY: bounds.getNortheast().lat
    }
    let symbols = this.pointTree.search(bBox)
    let geojsonLayer = vectorTileData.addLayer(GeoJsonManager.geoJsonLayerId)
    symbols.sort((a: PointGeoJsonData, b: PointGeoJsonData) => {
      return a.order - b.order
    })
    symbols.forEach(symbol => {
      let properties = new Map<string, string | boolean | number>()
      properties.set("coordinate-lat", symbol.coordinate.lat)
      properties.set("coordinate-lng", symbol.coordinate.lng)
      geojsonLayer.addFeature(symbol.id, GeometryType.Point, null, properties)
    })
    return vectorTileData
  }
}
