import {Tile} from "./Tile"
import {MapSettings} from "../map/MapSettings"
import {TaskManager} from "../worker/TaskManager"
import {MapState} from "../map/MapState"
import {TileCache} from "./TileCache"
import {TileCoordinate} from "./TileCoordinate"
import {RenderTile} from "../renderer/RenderTile"
import {UnwrappedTileCoordinate} from "./UnwrappedTileCoordinate"
import {GeometryUtils} from "../util/GeometryUtils"
import {Vector2} from "../engine/Vector2"
import {Rasterize} from "../util/Rasterize"
import {MapConstants, MathConstants} from "../map/MapConstants"
import {UrlUtils} from "../util/UrlUtils"
import {RequestTask, Resource} from "../worker/RequestTask"
import {TileArea, TileAreaId} from "../public/TileArea"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {MapView} from "../map/MapView"
import {MapUtils} from "../util/MapUtils"
import {AnnotationTile} from "./AnnotationTile"
import {TileRange} from "./TileRange"
import {BuildingResourceManager} from "../building/BuildingResourceManager"
import {Building} from "../building/Building"
import {BuildingType} from "../building/BuildingType"
import {Vector3} from "../engine/Vector3"
import {RasterTile} from "./RasterTile"
import {BuildingJson} from "../building/BuildingJson"
import {ModelJson} from "../building/ModelJson"
import {Point} from "../public/Point"
import {LatLng} from "../public/LatLng"
import {BuildingExtrudeUtils} from "../util/BuildingExtrudeUtils"
import {Geometry2DUtils} from "../util/Geometry2DUtils"
import {BuildingIconSelectionManager} from "../building/BuildingIconSelectionManager"
import {CustomBuildingManager} from "../building/CustomBuildingManager"
import {ReflectUtils} from "../util/ReflectUtils"
import {VectorTile} from "./VectorTile"
import {MapEvent} from "../public/MapEvent"
import {PlaceJson} from "../map/PlaceJson"
import {VectorTileData, VectorTileFeature} from "./data/VectorTileData"
import {SymbolResourceManager} from "../annotation/SymbolResourceManager"
import {GeometryType} from "./data/GeometryTileData"
import {MarkerLabel} from "../public/MarkerLabel"
import {TextLabelResourceManager} from "../text/TextLabelResourceManager"
import {SymbolLayer} from "../layer/SymbolLayer"
import {LayerOverlay, LayerOverlayId, OverlayType} from "../public/LayerOverlay"
import {TileOverlay} from "../public/TileOverlay"
import {BuildingOverlay} from "../public/BuildingOverlay"
import {POIOverlay} from "../public/POIOverlay"

export interface Id {
  x: number;
  y: number;
  sqDist: number;
}

export class TileManager {

  static pointLayerId = "2.maplink.vector.tile.points"

  static userPointLayerId = "1.maplink.vector.tile.user.points"

  static overlayPointLayerId = "3.maplink.vector.tile.overlay.points"

  mapView: MapView

  private mapSettings: MapSettings

  private tileTaskManager: TaskManager

  private mapState: MapState

  /** The tiles cache*/
  private tileCache: TileCache

  /** The map which contains render, parent, children tiles. */
  private tiles: Map<string, {tileCoordinate: TileCoordinate, tile: Tile}>

  /** The map which contains annotation tiles. */
  private annotationTiles: Map<string, {tileCoordinate: TileCoordinate, tile: AnnotationTile}>

  /** FIXME: Is there any better way to handle it? */
  private userVectorTiles: Map<string, {tileCoordinate: TileCoordinate, tile: VectorTile}>

  /**
   * Current tiles ready for rendering.
   * Be careful: this list borrow tile pointers from tiles map.
   * Make sure that delete tiles from tiles map correctly so that renderTiles don't contains dangling pointer
   */
  private renderTiles: RenderTile[] = []

  /** The list of tiles which is visible on the current zoom level sorted by the distance from the center. */
  private visibleTiles: UnwrappedTileCoordinate[] = []

  /** The last zoom value. This value indicates the map is zoom in or zoom out. */
  private lastZoom: number

  /** The last zoom value. This value indicates the map is zoom in or zoom out for update visible tile. */
  private lastZoomUpdateVisibleTiles: number

  /** The flag indicates that there is nothing to do for updating. */
  finishedLoad: boolean

  private tilesLoaded: boolean

  /** Tile areas */
  private tileAreas: Map<TileAreaId, TileArea>

  /** All Layer overlays*/
  private layerOverlays: Map<LayerOverlayId, LayerOverlay>

  createAnnotationTile: (tileCoordinate: TileCoordinate) => AnnotationTile

  private tileRequestsMap: Map<string, RequestTask>

  private layerOverlayRequestsMap: Map<{tileCoordinate: string, layerOverlayId: LayerOverlayId}, RequestTask>

  private buildingResourceManager: BuildingResourceManager

  private buildingIconSelectionManager: BuildingIconSelectionManager

  private  customBuildingManager: CustomBuildingManager

  private readonly requestRender: () => void

  constructor(mapView: MapView, tileTaskManager: TaskManager, mapState: MapState,
    mapSettings: MapSettings, buildingResourceManager: BuildingResourceManager,
    buildingIconSelectionManager: BuildingIconSelectionManager,
    customBuildingManager: CustomBuildingManager,
    createAnnotationTile: (tileCoordinate: TileCoordinate) => AnnotationTile, requestRender: () => void) {
    this.mapView = mapView
    this.tileTaskManager = tileTaskManager
    this.mapState = mapState
    this.tileCache = new TileCache(MapConstants.tileCacheSize)
    this.tiles = new Map<string, {tileCoordinate: TileCoordinate, tile: Tile}>()
    this.annotationTiles = new Map<string, {tileCoordinate: TileCoordinate, tile: AnnotationTile}>()
    this.userVectorTiles = new Map<string, {tileCoordinate: TileCoordinate, tile: VectorTile}>()
    this.finishedLoad = true
    this.tilesLoaded = true
    this.mapSettings = mapSettings
    this.lastZoom = this.mapState.getZoom()
    this.lastZoomUpdateVisibleTiles = this.mapState.getZoom()
    this.tileAreas = new Map<TileAreaId, TileArea>()
    this.layerOverlays = new Map<LayerOverlayId, LayerOverlay>()
    this.tileRequestsMap = new Map<string, RequestTask>()
    this.layerOverlayRequestsMap = new Map<{tileCoordinate: string, layerOverlayId: LayerOverlayId}, RequestTask>()
    this.buildingResourceManager = buildingResourceManager
    this.buildingIconSelectionManager = buildingIconSelectionManager
    this.customBuildingManager = customBuildingManager
    this.createAnnotationTile = createAnnotationTile
    this.requestRender = requestRender
  }

  private isDirty(): boolean {
    let currentZoom = this.mapState.getZoom()
    let isDirty = true
    if (this.finishedLoad ||
      this.mapState.isZoomIn() ||
      this.mapState.isZoomOut() ||
      (!this.mapState.isFlyingTo()
        && (Math.ceil(currentZoom - MathConstants.epsilon) > currentZoom
          || Math.floor(currentZoom + MathConstants.epsilon) < currentZoom))) {
      isDirty = false
    }
    return isDirty
  }

  update(createTile: (tileCoordinate: TileCoordinate) => Tile): void {
    if (!this.isDirty()) {
      return
    }
    this.updateRenderableTiles(createTile)
    if (!this.tileRequestsMap.size) {
      if (this.tilesLoaded == false) {
        this.mapView.mapsEventManager.notify(MapEvent.tilesLoaded)
      }
      this.tilesLoaded = true
      this.finishedLoad = true
      this.lastZoom = this.mapState.getZoom()
    }
  }

  reload(): void {
    this.mapView.getGeoJsonManager().removeAllTiles()
    this.tiles.forEach(tile => {
      if (tile.tile instanceof RasterTile) {
        this.removePlaces(tile.tile)
      }
      tile.tile.destroy()
    })
    this.mapSettings.placesEnabled && this.mapView.getPlacement().updatePlacement()
    this.annotationTiles.forEach(annotationTile => {
      annotationTile.tile.destroy()
    })
    this.tiles.clear()
    this.annotationTiles.clear()
    this.renderTiles = []
    this.tileCache.clear()
    this.finishedLoad = false
    this.mapView.dirty = true
    this.tileRequestsMap.forEach(tileRequest => {
      this.tileTaskManager.cancelTask(tileRequest)
    })
    this.tileRequestsMap.clear()

    this.layerOverlayRequestsMap.forEach(tileRequest => {
      this.tileTaskManager.cancelTask(tileRequest)
    })
    this.layerOverlayRequestsMap.clear()
  }

  destroy(): void {
    this.reload()
    this.visibleTiles = []
    this.buildingResourceManager.removeHiddenBuildings()
    this.buildingResourceManager.removeAllResources()
  }

  getRenderTiles(): RenderTile[] {
    return this.renderTiles
  }

  getVisibleTiles(): UnwrappedTileCoordinate[] {
    return this.visibleTiles
  }

  /**
   * ok for test
   * @param zoom
   */
  private updateVisibleTilesForZoom(zoom: number): void {
    if (this.mapState.getZoom() > this.mapSettings.getMaxNativeZoom()) {
      zoom = this.mapSettings.getMaxNativeZoom()
    }
    let cameraPosition = this.mapState.cameraPosition
    let screenSize = this.mapState.getScreenSize()
    let scale = Math.pow(2.0, zoom)

    /** Save last tilt to restore if tilt > maxTiltIntersectWithScreen */
    let lastTilt = this.mapState.getTilt()
    let needRestoreTilt = false
    if (lastTilt > MapConstants.maxTiltIntersectWithScreen) {
      this.mapState.setTilt(MapConstants.maxTiltIntersectWithScreen)
      needRestoreTilt = true
    }

    /** Top Left */
    let latLngTopLeft = GeometryUtils.screenCoordinateToLatLngZoom(
      new Vector2(0, 0), cameraPosition, screenSize, zoom)
    let tl = ProjectionMercator.projectScale(latLngTopLeft, scale).multiply(1 / MapUtils.getTileSize())

    /** Top Right */
    let latLngTopRight = GeometryUtils.screenCoordinateToLatLngZoom(
      new Vector2(screenSize.x, 0), cameraPosition, screenSize, zoom)
    let tr = ProjectionMercator.projectScale(latLngTopRight, scale).multiply(1 / MapUtils.getTileSize())

    /** Restore last tilt */
    if (needRestoreTilt) {
      this.mapState.setTilt(lastTilt)
    }

    /** Bottom Right */
    let latLngBottomRight = GeometryUtils.screenCoordinateToLatLngZoom(
      new Vector2(screenSize.x, screenSize.y), cameraPosition, screenSize, zoom)
    let br = ProjectionMercator.projectScale(latLngBottomRight, scale).multiply(1 / MapUtils.getTileSize())

    /** Bottom Left */
    let latLngBotLeft = GeometryUtils.screenCoordinateToLatLngZoom(
      new Vector2(0, screenSize.y), cameraPosition, screenSize, zoom)
    let bl = ProjectionMercator.projectScale(latLngBotLeft, scale).multiply(1 / MapUtils.getTileSize())

    /** Center */
    let centerScreen = GeometryUtils.screenCoordinateToLatLngZoom(
      new Vector2(screenSize.x / 2, screenSize.y / 2), cameraPosition, screenSize, zoom)
    let c = ProjectionMercator.projectScale(centerScreen, scale).multiply(
      1 / MapUtils.getTileSize())

    if (this.mapState.is3dMode()) {
      this.mapState.setSkyRatio(GeometryUtils.latLngToScreenCoordinate(
        latLngTopLeft, 0.0, cameraPosition, screenSize).y / screenSize.y)
      this.mapState.setDistanceToSky(
        Geometry2DUtils.calculateDistanceBetweenPointToLineBy2Point(tr, bl, br)* MapUtils.getTileSize())
    }

    let maxTiles = 1 << Math.floor(zoom)
    let t: Id[] = []

    let scanLine = (x0: number, x1: number, y: number) => {
      if (y >= 0 && y <= maxTiles) {
        for (let x = x0; x < x1; ++x) {
          let dx = x + 0.5 - c.x
          let dy = y + 0.5 - c.y
          let temp = {x: x, y: y, sqDist: (dx * dx + dy * dy)}
          t.push(temp)
        }
      }
    }

    Rasterize.scanTriangle(tl, tr, br, 0, maxTiles, scanLine)
    Rasterize.scanTriangle(br, bl, tl, 0, maxTiles, scanLine)

    // Sort first by distance, then by x/y.
    t.sort((a: Id, b: Id) => {
      if (a.sqDist == b.sqDist) {
        if (a.x == b.x) {
          return a.y - b.y
        }
        return a.x - b.x
      }
      return a.sqDist - b.sqDist
    })

    // Erase duplicate tile ids and add to visible tiles
    t.filter((value, index, array) => index == 0 ||
      (value.x != array[index - 1].x || value.y != array[index - 1].y))
      .forEach(value => this.visibleTiles.push(
        UnwrappedTileCoordinate.createWithXYZoom(value.x, value.y, zoom, this.mapState.is3dMode())))

  }

  updateVisibleTiles(): void {
    this.finishedLoad = false
    this.visibleTiles = []

    let zoom = this.mapState.getZoom()

    if (this.lastZoomUpdateVisibleTiles < zoom) {
      this.updateVisibleTilesForZoom(Math.ceil(zoom))
    }
    else {
      this.updateVisibleTilesForZoom(Math.floor(zoom))
    }

    this.lastZoomUpdateVisibleTiles = zoom

    // Remove outdated tiles
    this.cancelOutdatedTasks()
  }

  private cancelOutdatedTasks(): void {
    this.tileRequestsMap.forEach((value, key, map) => {

      // Check if key valid or not
      let validTile = this.visibleTiles.some(tile => {
        return tile.getTileCoordinate().toString() == key
      })
      if (!validTile) {
        map.delete(key)
        this.tileTaskManager.cancelTask(value)
      }
    })

    this.layerOverlayRequestsMap.forEach((value, key, map) => {

      // Check if key valid or not
      let validTile = this.visibleTiles.some(tile => {
        return tile.getTileCoordinate().toString() == key.tileCoordinate
      })
      if (!validTile) {
        map.delete(key)
        this.tileTaskManager.cancelTask(value)
      }
    })
  }

  private updateRenderableTiles(createTile: (tileCoordinate: TileCoordinate) => Tile) {
    let retainTiles: TileCoordinate[] = []
    let parentRenderTiles: RenderTile[] = []
    let previousChildTiles: RenderTile[] = []

    // Get all current child tiles when camera is zoom out
    if (this.mapState.getZoom() < this.lastZoom && this.visibleTiles.length != 0) {
      let targetZoom = this.visibleTiles[0].getTileCoordinate().getZoom()
      this.renderTiles.forEach(renderTile => {
        if (renderTile.getUnwrappedTileCoordinate().getTileCoordinate().getZoom() > targetZoom) {
          previousChildTiles.push(renderTile)
        }
      })
    }
    this.renderTiles = []
    this.visibleTiles.forEach(unwrappedVisibleTile =>{
      let tileCoordinate = unwrappedVisibleTile.getTileCoordinate()
      tileCoordinate.set3DMode(this.mapState.is3dMode())

      let tile = this.getTile(tileCoordinate)
      let hasValidTile = (tile != null && (tile.isRenderable() || this.tileRequestsMap.has(tileCoordinate.toString())))
      if (!hasValidTile) {
        // create tile if not exist
        tile = this.createTile(tileCoordinate, createTile)
        let vectorTile = new VectorTile(tileCoordinate, this.mapView.gl)
        if (tile instanceof RasterTile) {
          let rasterTile = tile as RasterTile
          rasterTile.setVectorTile(vectorTile)
        }
        this.mapView.getGeoJsonManager().addTile(new VectorTile(tileCoordinate, this.mapView.gl))
      }

      let rasterTile = tile as RasterTile
      if ((rasterTile.isRenderable() && !rasterTile.isOverlayBuilding())
          || (rasterTile.isRenderable() && rasterTile.isOverlayBuilding() && rasterTile.overlayBuildingLoaded())) {
        this.renderTiles.push(new RenderTile(unwrappedVisibleTile, tile))
        retainTiles.push(tileCoordinate)
      }
      else {

        // The tile isn't loaded yet, but retain it anyway
        retainTiles.push(tileCoordinate)
        if (this.mapState.isFlyingTo()) {

          /** We just only check the parent/children tiles when map state is not flying-to */
          return
        }

        if (this.mapState.getZoom() > this.lastZoom) {

          // Check parent
          let parentTile = unwrappedVisibleTile.parent()
          while (parentTile.getTileCoordinate().getZoom() >= this.lastZoom) {
            tileCoordinate = parentTile.getTileCoordinate()
            tile = this.getTile(tileCoordinate)
            if (tile != null && tile.isRenderable()) {
              let inList = retainTiles.some(retainTile => {
                return retainTile.equal(tileCoordinate)
              })
              if (!inList) {
                retainTiles.push(tileCoordinate)
                parentRenderTiles.push(new RenderTile(parentTile, tile))
              }
              break
            }
            parentTile = parentTile.parent()
          }
        }
        else if (this.mapState.getZoom() < this.lastZoom) {

          // Check children
          previousChildTiles.forEach(previousChildTile => {
            let childTile = previousChildTile.getUnwrappedTileCoordinate()
            if (childTile.getTileCoordinate().isChildOf(unwrappedVisibleTile.getTileCoordinate())) {
              tileCoordinate = childTile.getTileCoordinate()
              tile = this.getTile(tileCoordinate)
              if (tile != null && tile.isRenderable()) {
                let inList = retainTiles.some(retainTile => {
                  return retainTile.equal(tileCoordinate)
                })
                if (!inList) {
                  retainTiles.push(tileCoordinate)
                  this.renderTiles.push(new RenderTile(childTile, tile))
                }
              }
            }
          })
        }
      }
    })
    if (parentRenderTiles.length != 0) {

      // Parent tiles should be rendered first
      this.renderTiles.forEach(renderTile => {
        parentRenderTiles.push(renderTile)
      })
      this.renderTiles = parentRenderTiles
    }

    // Remove outdated tiles
    retainTiles.sort((a: TileCoordinate, b: TileCoordinate) => {
      if (a.lessThan(b))
        return -1
      else if (!a.lessThan(b))
        return 1
      else return 0
    })

    // Erase duplicate tiles
    let newRetainTiles = []
    for (let i = 0; i < retainTiles.length; ++i) {

      //out of range of array will return undefined
      if (retainTiles[i] !== retainTiles[i + 1]) {
        newRetainTiles.push(retainTiles[i])
      }
    }

    retainTiles = newRetainTiles

    let tilesArray = Array.from(this.tiles)
    let annotationTilesArray = Array.from(this.annotationTiles)
    tilesArray.sort((a: [string, {tileCoordinate: TileCoordinate, tile: Tile}],
      b: [string, {tileCoordinate: TileCoordinate, tile: Tile}]) => {
      if (a[1].tileCoordinate.lessThan(b[1].tileCoordinate))
        return -1
      else if (!a[1].tileCoordinate.lessThan(b[1].tileCoordinate))
        return 1
      else return 0
    })

    annotationTilesArray.sort((a: [string, {tileCoordinate: TileCoordinate, tile: AnnotationTile}],
      b: [string, {tileCoordinate: TileCoordinate, tile: AnnotationTile}]) => {
      if (a[1].tileCoordinate.lessThan(b[1].tileCoordinate))
        return -1
      else if (!a[1].tileCoordinate.lessThan(b[1].tileCoordinate))
        return 1
      else return 0
    })

    let tilesIterator = 0
    let retainIterator = 0
    let annotationTilesIterator = 0
    let shouldUpdatePlacement = false
    while (tilesIterator < tilesArray.length) {
      if (retainIterator == retainTiles.length ||
        tilesArray[tilesIterator][1].tileCoordinate.lessThan(retainTiles[retainIterator])) {
        let rasterTile = tilesArray[tilesIterator][1].tile as RasterTile
        let buildingObjects = rasterTile.getBuildings() as any
        buildingObjects.forEach(building => {
          this.buildingResourceManager.decreaseBuildingReference(building.id)
        })
        this.removePlaces(rasterTile)
        shouldUpdatePlacement = true
        this.mapView.getGeoJsonManager().removeTile(rasterTile.tileCoordinate)
        this.tileCache.add(tilesArray[tilesIterator][0], tilesArray[tilesIterator][1].tile)
        this.tiles.delete(tilesArray[tilesIterator][0].toString())
        let tile = annotationTilesArray[annotationTilesIterator][1].tile
        this.annotationTiles.delete(annotationTilesArray[annotationTilesIterator][0])
        tile.destroy()
        ++tilesIterator
        ++annotationTilesIterator
      }
      else {
        if (!(retainTiles[retainIterator].lessThan(tilesArray[tilesIterator][1].tileCoordinate))) {
          ++tilesIterator
          ++annotationTilesIterator
        }
        ++retainIterator
      }
    }

    if (shouldUpdatePlacement && (this.mapSettings.placesEnabled || this.mapView.hasPOIOverlay())) {
      this.mapView.getPlacement().updatePlacement()
    }
  }

  private getTile(tileCoordinate: TileCoordinate): Tile {
    if (!this.tiles.has(tileCoordinate.toString())) {

      // Load from cache
      let tile = this.tileCache.pop(tileCoordinate.toString())
      if (tile != null) {
        let rasterTile = tile as RasterTile

        // Check overlayTile loaded or not to re create Tile
        if (rasterTile.isOverlayTile()) {
          let overlayTileLoaded = true
          rasterTile.getOverlayTiles().forEach(overlayTile => {
            if (overlayTile.getTexture() == null) {
              overlayTileLoaded = false
              return
            }
          })
          if (!overlayTileLoaded) {
            return null
          }
        }
        this.addBuildings(rasterTile.getBuildings())
        this.addPlaces(rasterTile)
        this.addOverlayPlaces(rasterTile)
        this.tiles.set(tileCoordinate.toString(), {tileCoordinate: tileCoordinate, tile: tile})
        this.annotationTiles.set(tileCoordinate.toString(),
          {tileCoordinate: tileCoordinate, tile: this.createAnnotationTile(tileCoordinate)})
        let vectorTile = new VectorTile(tileCoordinate, this.mapView.gl)
        this.mapView.getGeoJsonManager().addTile(vectorTile)
        return tile
      }
      return null
    }
    return this.tiles.get(tileCoordinate.toString()).tile
  }

  /**
   * Clear tiles cache when switch 3D to 2D
   */
  clearTileCache(): void {
    this.tileCache.clear()
  }

  private createTile(tileCoordinate: TileCoordinate, createTile: (tileCoordinate: TileCoordinate) => Tile): Tile {

    /** Get from cache first */
    let tile = this.tileCache.pop(tileCoordinate.toString())
    if (tile == null) {

      this.tilesLoaded = false
      let mapUrl = this.mapSettings.getMapUrl()
      let zoom = tileCoordinate.getZoom()
      let stop = false

      this.tileAreas.forEach(tileArea => {
        if (stop) {
          return
        }
        if (zoom <= tileArea.getMaxZoom() && zoom >= tileArea.getMinZoom() &&
          TileRange.fromLatLngBounds(tileArea.getBounds(), zoom, zoom).contains(tileCoordinate)) {
          mapUrl = MapConstants.baseMapUrl + "?tileUrl=" + tileArea.getUrl()
          if (this.mapSettings.is3dMode()) {
            mapUrl += "&mode=3d"
          }
          mapUrl += "&key=" + MapSettings.getAccessKey()
          stop = true
        }
      })
      let url = UrlUtils.getTileUrl(mapUrl,
        tileCoordinate.getX(),
        tileCoordinate.getY(),
        tileCoordinate.getZoom())
      tile = createTile(tileCoordinate)
      let requestTask = new RequestTask(Resource.RasterTile)
      requestTask.url = url
      requestTask.completion = (response: any) => {
        if (!response.success) {
          return
        }
        let initCallBack = () => {
          this.requestRender()
          this.tileRequestsMap.delete(tileCoordinate.toString())
        }
        if (response.data != null) {
          tile.init(response.data.result, initCallBack)
          let rasterTile = tile as RasterTile

          let objects = (response.data.result || {}).objects || []
          objects && objects.forEach(object => {
            object.model = ReflectUtils.renameProp({"textureName": "texture", "objName": "obj"}, object.model)
          })
          let buildingJson: BuildingJson[] = objects || []

          // Check time
          let time = this.mapView.mapSettings.getDate().getTime()

          let i = buildingJson.length

          while(i--) {
            let building = buildingJson[i]
            let startDate = building.startDate != null ? building.startDate : Number.NEGATIVE_INFINITY
            let endDate = building.endDate != null ? building.endDate : Number.POSITIVE_INFINITY
            if (startDate > time || endDate < time) {
              buildingJson.splice(i, 1)
            }
          }

          this.addBuildings(buildingJson)
          rasterTile.addBuildings(buildingJson)

          if (this.mapState.is3dMode()) {
            let mapObjects = this.customBuildingManager.getObjectsForTile(tile.getTileCoordinate())
            if (mapObjects && mapObjects.length != 0) {
              let buildingJsonArray = []
              mapObjects.forEach(mapObject => {
                let buildingJson = this.customBuildingManager.getJsonForMapObject(mapObject)
                buildingJsonArray.push(buildingJson)
                rasterTile.getBuildings().push(buildingJson)
              })
              this.addBuildings(buildingJsonArray)
            }
          }

          // place data
          let places: PlaceJson[] = (response.data.result || {}).places || []
          places = places.slice(0, MapConstants.maxPlacePerTile)
          rasterTile.addPlaces(places)
          this.addPlaces(rasterTile)
        }
      }
      this.tileRequestsMap.set(tileCoordinate.toString(), requestTask)
      this.tileTaskManager.postTask(requestTask)

      // get overlay data
      this.layerOverlays.forEach(layerOverlay => {
        if (zoom <= layerOverlay.getMaxZoom() && zoom >= layerOverlay.getMinZoom()) {
          if (layerOverlay.getType() == OverlayType.Tile) {
            let tileOverlay = layerOverlay as TileOverlay
            url = tileOverlay.getUrl(tileCoordinate.getX(),
              tileCoordinate.getY(),
              tileCoordinate.getZoom(),
              this.mapState.is3dMode())
            if (!url) {
              return
            }
            let overlayTile = createTile(tileCoordinate) as RasterTile
            let rasterTile = tile as RasterTile
            rasterTile.setOverlayTile(overlayTile)
            let requestTask = new RequestTask(Resource.TileOverlay)
            requestTask.url = url
            requestTask.completion = (response: any) => {
              if (!response.success) {
                return
              }
              let initCallBack = () => {
                this.requestRender()
                this.layerOverlayRequestsMap.delete(
                  {tileCoordinate: tileCoordinate.toString(), layerOverlayId: tileOverlay.getId()})
              }
              if (response.data != null) {
                overlayTile.init(response, initCallBack)
              }
            }
            this.layerOverlayRequestsMap.set(
              {tileCoordinate: tileCoordinate.toString(), layerOverlayId: tileOverlay.getId()}, requestTask)
            this.tileTaskManager.postTask(requestTask)
          }
          else if (layerOverlay.getType() == OverlayType.Building) {
            if (this.mapState.is3dMode()) {
              let buildingOverlay = layerOverlay as BuildingOverlay
              url = buildingOverlay.getUrl(
                tileCoordinate.getX(),
                tileCoordinate.getY(),
                tileCoordinate.getZoom())
              if (!url) {
                return
              }

              let rasterTile = tile as RasterTile
              rasterTile.setOverlayBuilding()
              let requestTask = new RequestTask(Resource.TileData)
              requestTask.url = url
              requestTask.completion = (response: any) => {
                this.layerOverlayRequestsMap.delete(
                  {tileCoordinate: tileCoordinate.toString(), layerOverlayId: buildingOverlay.getId()})
                if (!response.success) {
                  return
                }
                if (response.data != null) {
                  let objects = (response.data.result || {}).objects || []
                  objects && objects.forEach(object => {
                    object.model = ReflectUtils.renameProp({"textureName": "texture", "objName": "obj"}, object.model)
                  })
                  let buildingJsonArray: BuildingJson[] = objects || []
                  buildingJsonArray.forEach(buildingJson => {
                    buildingJson.id += MapConstants.buildingOverlayIdPrefix
                    rasterTile.getBuildings().push(buildingJson)
                  })
                  this.addBuildings(buildingJsonArray)
                  rasterTile.setOverlayBuildingLoaded()
                  this.requestRender()
                }
              }
              this.layerOverlayRequestsMap.set(
                {tileCoordinate: tileCoordinate.toString(), layerOverlayId: buildingOverlay.getId()}, requestTask)
              this.tileTaskManager.postTask(requestTask)
            }
          }
          else if (layerOverlay.getType() == OverlayType.POI) {
            let poiOverlay = layerOverlay as POIOverlay
            url = poiOverlay.getUrl(
              tileCoordinate.getX(),
              tileCoordinate.getY(),
              tileCoordinate.getZoom())
            if (!url || tileCoordinate.is3DMode()) {
              return
            }

            let requestTask = new RequestTask(Resource.TileData)
            requestTask.url = url
            requestTask.completion = (response: any) => {
              this.layerOverlayRequestsMap.delete(
                {tileCoordinate: tileCoordinate.toString(), layerOverlayId: poiOverlay.getId()})
              if (!response.success) {
                return
              }
              if (response.data != null) {
                let rasterTile = tile as RasterTile
                let places: PlaceJson[] = (response.data.result || {}).places || []
                places = places.slice(0, MapConstants.maxPlacePerTile)
                rasterTile.addOverlayPlaces(places)
                this.addOverlayPlaces(rasterTile)
              }
            }
            this.layerOverlayRequestsMap.set(
              {tileCoordinate: tileCoordinate.toString(), layerOverlayId: poiOverlay.getId()}, requestTask)
            this.tileTaskManager.postTask(requestTask)
          }
        }
      })
    }

    if (this.tiles != null) {
      this.tiles.set(tileCoordinate.toString(), {tileCoordinate: tileCoordinate, tile: tile})
    }
    this.annotationTiles.set(tileCoordinate.toString(),
      {tileCoordinate: tileCoordinate, tile: this.createAnnotationTile(tileCoordinate)})
    return tile
  }

  addTileArea(tileAreaId: TileAreaId, tileArea: TileArea): void {
    this.tileAreas.set(tileAreaId, tileArea)
    this.reload()
  }

  updateTileArea(tileAreaId: TileAreaId, tileArea: TileArea): void {
    if (!this.tileAreas.has(tileAreaId)) {
      return
    }
    this.tileAreas.delete(tileAreaId)
    this.addTileArea(tileAreaId, tileArea)
  }

  removeTileArea(tileAreaId: TileAreaId): void {
    if (!this.tileAreas.has(tileAreaId)) {
      return
    }
    this.tileAreas.delete(tileAreaId)

    // Remove tile area successful
    this.reload()
  }

  addLayerOverlay(overlayId: LayerOverlayId, layerOverlay: LayerOverlay): void {
    this.layerOverlays.set(overlayId, layerOverlay)
    if (this.mapState.is3dMode() || layerOverlay.getType() != OverlayType.Building) {
      this.reload()
    }
  }

  removeLayerOverlay(overlayId: LayerOverlayId): void {
    let overlayObject = this.layerOverlays.get(overlayId)
    if (!overlayObject) {
      return
    }
    if (overlayObject.getType() == OverlayType.Building) {
      this.buildingResourceManager.removeAllResources()
      this.mapView.buildingLayer.clear()
    }

    if (this.mapState.is3dMode() || overlayObject.getType() != OverlayType.Building) {
      this.reload()
    }
    this.layerOverlays.delete(overlayId)
  }

  /**
   * Add building again after User set objectsEnabled from false to true
   * @param filterFunc to filter building
   */
  enableBuilding(filterFunc: (building: BuildingJson) => boolean) {
    this.tiles.forEach(tile => {
      let rasterTile = tile.tile as RasterTile
      let buildings = rasterTile.getBuildings()
      buildings = buildings.filter(building => filterFunc != null ? filterFunc(building) : true)
      this.addBuildings(buildings, true)
    })
  }

  removeUserPlaces(tile: Tile): void {
    if (!(tile instanceof RasterTile)) {
      return
    }
    let rasterTile = tile as RasterTile
    let tileCoordinate = rasterTile.tileCoordinate

    let placement = this.mapView.getPlacement()
    let vectorPointLayer = this.mapView.getLayer(TileManager.userPointLayerId) as SymbolLayer
    vectorPointLayer.renderTiles.delete(tileCoordinate.toString())
    let featureRemoves = []

    // Query user's feature
    this.mapView.getFeatureManager().queryFeatures(tileCoordinate).filter(feature => {
      return feature.getType() == GeometryType.Point
    }).forEach(feature => {
      featureRemoves.push(feature.getId())
    })
    placement.removeFeatures(featureRemoves)
  }

  removePlaces(tile: Tile): void {
    if (!(tile instanceof RasterTile)) {
      return
    }
    let rasterTile = tile as RasterTile
    let places = rasterTile.getPlaces()
    let tileCoordinate = rasterTile.tileCoordinate
    if (!rasterTile.isInitialized()) {
      return
    }
    let placement = this.mapView.getPlacement()
    let vectorPointLayer = this.mapView.getLayer(TileManager.pointLayerId) as SymbolLayer
    vectorPointLayer.renderTiles.delete(tileCoordinate.toString())
    //let resourceManager = SymbolResourceManager.getInstance(this.mapView.gl)
    let featureRemoves = []
    places.forEach(place => {
      featureRemoves.push(place.id)
      //TODO: refactor later
      //let iconUrl = (place.icon || {}).url || "iconDefault"
      //resourceManager.release(iconUrl)
      if (place.name != null) {
        let markerLabel = new MarkerLabel({
          text: place.name,
          fontSize: MapConstants.placeFontSize,
          fontWeight: MapConstants.placeFontWeight,
          line: MapConstants.placeNumLine,
          maxWidth: MapConstants.placeMaxWidth
        })
        TextLabelResourceManager.getInstance(this.mapView.gl).release(markerLabel)
      }
    })
    placement.removeFeatures(featureRemoves)

    // Remove user places
    this.removeUserPlaces(tile)

    // Remove overlay places
    this.removeOverlayPlaces(tile)
  }

  private removeOverlayPlaces(tile: Tile): void {
    if (!(tile instanceof RasterTile)) {
      return
    }
    let rasterTile = tile as RasterTile
    let places = rasterTile.getOverlayPlaces()
    let tileCoordinate = rasterTile.tileCoordinate
    if (!rasterTile.isInitialized()) {
      return
    }
    let placement = this.mapView.getPlacement()
    let overlayVectorPointLayer = this.mapView.getLayer(TileManager.overlayPointLayerId) as SymbolLayer
    overlayVectorPointLayer.renderTiles.delete(tileCoordinate.toString())
    //let resourceManager = SymbolResourceManager.getInstance(this.mapView.gl)
    let featureRemoves = []
    places.forEach(place => {
      featureRemoves.push(place.id)
      //TODO: refactor later
      //let iconUrl = (place.icon || {}).url || "iconDefault"
      //resourceManager.release(iconUrl)
      if (place.name != null) {
        let markerLabel = new MarkerLabel({
          text: place.name,
          fontSize: MapConstants.placeFontSize,
          fontWeight: MapConstants.placeFontWeight,
          line: MapConstants.placeNumLine,
          maxWidth: MapConstants.placeMaxWidth
        })
        TextLabelResourceManager.getInstance(this.mapView.gl).release(markerLabel)
      }
    })
    placement.removeFeatures(featureRemoves)
  }

  addUserPlaces(tile: Tile): void {
    if (!(tile instanceof RasterTile)) {
      return
    }
    let rasterTile = tile as RasterTile
    if (!rasterTile.isInitialized()) {
      return
    }
    let tileCoordinate = rasterTile.tileCoordinate

    //TODO: Considering for avoid creating vector tile in the case tile has no point data
    let vectorTile = null
    if (!this.userVectorTiles.has(tileCoordinate.toString())) {
      vectorTile = new VectorTile(tileCoordinate, this.mapView.gl)
      this.userVectorTiles.set(tileCoordinate.toString(), {tileCoordinate: tileCoordinate, tile: vectorTile})
    }
    else {
      vectorTile = this.userVectorTiles.get(tileCoordinate.toString()).tile
    }
    let vectorTileData = new VectorTileData()
    vectorTile.setData(vectorTileData)
    let pointTileLayer = vectorTileData.addLayer(TileManager.pointLayerId)
    let features: VectorTileFeature[] = []

    // Query user's feature
    this.mapView.getFeatureManager().queryFeatures(tileCoordinate).filter(feature => {
      return feature.getType() == GeometryType.Point
    }).forEach(feature => {
      pointTileLayer.addFeature(feature.getId(), GeometryType.Point, null, feature.getProperties())
      features.push(feature)
    })
    let placement = this.mapView.getPlacement()
    let renderTile = new RenderTile(UnwrappedTileCoordinate.createWithXYZoom(
      tileCoordinate.getX(),
      tileCoordinate.getY(),
      tileCoordinate.getZoom()),
    vectorTile)
    let vectorPointLayer = this.mapView.getLayer(TileManager.userPointLayerId) as SymbolLayer
    vectorPointLayer.addRenderTile(tileCoordinate.toString(), renderTile)
    placement.addFeatures(features)
  }

  addPlaces(tile: Tile): void {
    if (!(tile instanceof RasterTile)) {
      return
    }
    let rasterTile = tile as RasterTile
    if (!rasterTile.isInitialized()) {
      return
    }
    let places = rasterTile.getPlaces()
    let tileCoordinate = rasterTile.tileCoordinate
    let vectorTile = rasterTile.getVectorTile()
    if (vectorTile == null) {
      vectorTile = new VectorTile(tileCoordinate, this.mapView.gl)
      rasterTile.setVectorTile(vectorTile)
    }
    let vectorTileData = new VectorTileData()
    vectorTile.setData(vectorTileData)
    let pointTileLayer = vectorTileData.addLayer(TileManager.pointLayerId)
    let resourceManager = SymbolResourceManager.getInstance(this.mapView.gl)
    places && places.forEach(place => {
      let properties = new Map<string, string | boolean | number>()
      let location = LatLng.convert(place.location)

      let url = null
      let color = null

      /** Get color from place.icon.color first **/
      if ((place.icon || {}).color) {
        color = place.icon.color
      }

      /** Get url from place.icon.url first **/
      if ((place.icon || {}).url) {
        url = place.icon.url
      }
      else if (place.types && place.types.length > 0) {
        let poiType = place.types[0]
        url = UrlUtils.getPOIImageUrl(MapConstants.poiImageUrl, 0, poiType, "1x")
      }
      else {
        url = UrlUtils.getPOIImageUrl(MapConstants.poiImageUrl, 0, MapConstants.placeDefaultIcon, "1x")
      }

      properties.set("color", color)
      properties.set("url", url)
      properties.set("lat", location.lat)
      properties.set("lng", location.lng)
      properties.set("name", place.name)
      properties.set("address", place.address)
      properties.set("types", place.types != null ? place.types.join() : "")
      pointTileLayer.addFeature(place.id, GeometryType.Point, null, properties)
      let iconUrl = properties.get("url") as string
      resourceManager.acquire(iconUrl)
      if (place.name != null) {
        let markerLabel = new MarkerLabel({
          text: place.name,
          fontSize: MapConstants.placeFontSize,
          fontWeight: MapConstants.placeFontWeight,
          line: MapConstants.placeNumLine,
          maxWidth: MapConstants.placeMaxWidth
        })
        TextLabelResourceManager.getInstance(this.mapView.gl).acquire(markerLabel)
      }
    })

    //TODO: Considering for avoid creating vector tile/ renderTile in the case tile has no point data
    let renderTile = new RenderTile(
      UnwrappedTileCoordinate.createWithXYZoom(
        tileCoordinate.getX(),
        tileCoordinate.getY(),
        tileCoordinate.getZoom()),
      vectorTile)
    let vectorPointLayer = this.mapView.getLayer(TileManager.pointLayerId) as SymbolLayer
    vectorPointLayer.addRenderTile(tileCoordinate.toString(), renderTile)
    let placement = this.mapView.getPlacement()
    let features: VectorTileFeature[] = []
    pointTileLayer.features.forEach(feature => {
      features.push(feature as VectorTileFeature)
    })
    placement.addFeatures(features)

    // Add user place
    this.addUserPlaces(tile)
  }

  addOverlayPlaces(tile: Tile): void {
    if (!(tile instanceof RasterTile)) {
      return
    }
    let rasterTile = tile as RasterTile
    let places = rasterTile.getOverlayPlaces()
    let tileCoordinate = rasterTile.tileCoordinate
    let vectorTile = rasterTile.getPlacesVectorTile()
    if (vectorTile == null) {
      vectorTile = new VectorTile(tileCoordinate, this.mapView.gl)
      rasterTile.setPlacesVectorTile(vectorTile)
    }
    let vectorTileData = new VectorTileData()
    vectorTile.setData(vectorTileData)
    let overlayPointTileLayer = vectorTileData.addLayer(TileManager.overlayPointLayerId)
    let resourceManager = SymbolResourceManager.getInstance(this.mapView.gl)
    places && places.forEach(place => {
      let properties = new Map<string, string | boolean | number>()
      let location = LatLng.convert(place.location)
      properties.set("color", place.icon && place.icon.color ? place.icon.color : MapConstants.placeDefaultColor)
      properties.set("url", (place.icon || {}).url || "iconDefault")
      properties.set("lat", location.lat)
      properties.set("lng", location.lng)
      properties.set("name", place.name)
      properties.set("address", place.address)
      properties.set("types", place.types != null ? place.types.join() : "")
      properties.set("overlayPlaces", true)
      overlayPointTileLayer.addFeature(place.id, GeometryType.Point, null, properties)
      let iconUrl = properties.get("url") as string
      resourceManager.acquire(iconUrl)
      if (place.name != null) {
        let markerLabel = new MarkerLabel({
          text: place.name,
          fontSize: MapConstants.placeFontSize,
          fontWeight: MapConstants.placeFontWeight,
          line: MapConstants.placeNumLine,
          maxWidth: MapConstants.placeMaxWidth
        })
        TextLabelResourceManager.getInstance(this.mapView.gl).acquire(markerLabel)
      }
    })

    //TODO: Considering for avoid creating vector tile/ renderTile in the case tile has no point data
    let renderTile = new RenderTile(
      UnwrappedTileCoordinate.createWithXYZoom(
        tileCoordinate.getX(),
        tileCoordinate.getY(),
        tileCoordinate.getZoom()),
      vectorTile)
    let overlayPointLayer = this.mapView.getLayer(TileManager.overlayPointLayerId) as SymbolLayer
    overlayPointLayer.addRenderTile(tileCoordinate.toString(), renderTile)
    let placement = this.mapView.getPlacement()
    let features: VectorTileFeature[] = []
    overlayPointTileLayer.features.forEach(feature => {
      features.push(feature as VectorTileFeature)
    })
    placement.addFeatures(features)
  }

  addBuildings(buildingJson: BuildingJson[], excludeExternalBuildings: boolean = false): void {
    buildingJson.forEach(json => {
      let buildingType = this.getBuildingType(json.model)
      if (!this.mapSettings.objectsEnabled && buildingType != BuildingType.External
        && buildingType != BuildingType.ExternalExtrude) {
        return
      }
      if (excludeExternalBuildings &&
        (buildingType == BuildingType.External || buildingType == BuildingType.ExternalExtrude)) {
        return
      }

      if (!this.buildingResourceManager.hasBuilding(json.id)) {
        let building = new Building(this.buildingIconSelectionManager)
        building.setId(json.id)
        if (json.name == null) {
          building.setName("Unknown name")
        }
        else {
          building.setName(json.name)
        }
        building.setPlaces(json.places)

        //TODO: Only apply height scale on extrude buildings
        let height = json.model.height || 0.0

        let isExtrude = buildingType == BuildingType.ExternalExtrude || buildingType == BuildingType.Extrude
        let scaleZ = json.scale
        if (isExtrude) {
          scaleZ = json.scale * height
        }
        building.setScale(new Vector3(json.scale, json.scale, scaleZ ))
        building.setBearing(json.bearing)
        building.setLocation(LatLng.convert(json.location))
        building.setElevation(json.elevation)
        building.setHeight(height)
        building.setCamera(json.camera)
        building.setTypes(json.types)
        building.setVerified(json.isVerified)
        building.setMinZoom(json.minZoom)
        building.setMaxZoom(json.maxZoom)
        building.setStartDate(json.startDate)
        building.setEndDate(json.endDate)
        building.setDraggable(json.draggable || false)

        if (typeof json.model.obj === "string") {
          building.setObjName(json.model.obj)
        }
        else if (json.model.obj) {
          building.setObjName(json.model.obj.reuseId || json.id)
          building.setObjData(json.model.obj.data)
        }

        if (typeof json.model.texture === "string") {
          building.setTextureName(json.model.texture)
        }
        else if (json.model.texture) {
          building.setTextureName(json.model.texture.reuseId || json.id)
          building.setTextureData(json.model.texture.data)
        }
        //TODO: handle objUrl & textureUrl only
        if (json.model.objUrl != null) {
          building.setObjName(json.model.objUrl)
        }

        if (json.model.textureUrl != null) {
          building.setTextureName(json.model.textureUrl)
        }

        building.setBuildingType(buildingType)
        if (building.getBuildingType() == BuildingType.Extrude || buildingType == BuildingType.ExternalExtrude) {

          //TODO: build extrude building data first
          let locationInMeters = ProjectionMercator.convertToMeterFromLatLng(building.getLocation())
          let buildingGeometry: Point[][] = []
          let polygon: Point[] = []
          let coordinates: LatLng[] = []
          for (let j = 0; j < json.model.coordinates.length; ++j) {
            let point = json.model.coordinates[j]
            let coordinate = LatLng.convert(point)
            coordinates.push(coordinate)
            let pointInMeters = ProjectionMercator.convertToMeterFromLatLng(coordinate)
            polygon.push(new Point(pointInMeters.x - locationInMeters.x,
              locationInMeters.y - pointInMeters.y))
          }
          building.setCoordinates(coordinates)
          buildingGeometry.push(polygon)
          let modelExtrude = BuildingExtrudeUtils.extrudePolygon(buildingGeometry, 1)
          this.buildingResourceManager.addBuildingWithData(
            json.id, building, modelExtrude.vertices, modelExtrude.indices)
        }
        else {
          this.buildingResourceManager.addBuilding(json.id, building)
        }
      }
      else {
        let building = this.buildingResourceManager.getBuilding(json.id)
        let newLocation = LatLng.convert(json.location)
        let oldLocation = building.getLocation()
        if (newLocation && oldLocation && !newLocation.equals(oldLocation)) {
          let coordinates = building.getCoordinates()
          if (coordinates && coordinates.length > 0) {
            let translateVectorX = newLocation.lng - oldLocation.lng
            let translateVectorY = newLocation.lat - oldLocation.lat
            let newCoordinates = []
            coordinates.forEach(coordinate => {
              newCoordinates.push(new LatLng(coordinate.lat + translateVectorY,
                coordinate.lng + translateVectorX))
            })
            building.setCoordinates(newCoordinates)
          }
          building.setLocation(LatLng.convert(json.location))
        }
        this.buildingResourceManager.increaseBuildingReference(json.id)
      }
    })
  }

  getBuildingType(model: ModelJson): BuildingType {
    let type = model.type
    if (type == "Object") {
      return BuildingType.Texture
    }
    else if (type == "Polygon") {
      return BuildingType.Extrude
    }
    else if (type == "External") {
      return BuildingType.External
    }
    else if (type == "ExternalExtrude") {
      return BuildingType.ExternalExtrude
    }
    else {
      console.warn("Invalid building type: " + type + " of model: " + model.obj)
    }
  }

  getTiles(): Map<string, {tileCoordinate: TileCoordinate, tile: Tile}> {
    return this.tiles
  }

  getTileCache(): TileCache {
    return this.tileCache
  }

  reloadTileData(tileCoordinate: TileCoordinate): void {
    let tile = this.tiles.get(tileCoordinate.toString())
    if (tile == null) {
      return
    }
    this.addPlaces(tile.tile)
    this.addOverlayPlaces(tile.tile)
  }

  getCustomBuildingManager(): CustomBuildingManager {
    return this.customBuildingManager
  }
}
