export class  TextLabel {

  private readonly _text: string

  private readonly _size: number

  constructor(text: string, size: number) {
    this._text = text
    this._size = size
  }

  get text(): string {
    return this._text
  }

  get size(): number {
    return this._size
  }

  toString(): string {
    return "text: " + this._text + " size: " + this._size
  }
}