import {TimeEffect} from "../public/TimeEffect"
import {Vector3} from "../engine/Vector3"
import {ColorConstants, TimeConstants} from "../map/MapConstants"
import {Color} from "../engine/Color"
import {MapState} from "../map/MapState"

export class TimeEffectManager {

  private _time: number

  private _timeEffect: TimeEffect

  private mapState: MapState

  private defaultTimeByString: string

  private readonly _timeArray: number[] = [
    TimeConstants.morningTime,
    TimeConstants.noonTime,
    TimeConstants.afterNoonTime,
    TimeConstants.eveningTime
  ]

  constructor(mapState: MapState) {
    this.defaultTimeByString = "Noon"
    this.mapState = mapState
    this._timeEffect = TimeEffect.Noon
  }

  dirty(): boolean {
    return this.mapState.is3dMode() && this._timeEffect == TimeEffect.Live
  }

  getTimeEffect(): TimeEffect {
    return this._timeEffect
  }

  getTime(): number {
    if (this._timeEffect == TimeEffect.Live  || this._time == null) {
      return new Date().getTime()
    }
    return this._time
  }

  setTimeEffect(option: TimeEffect): void {
    if (option == null) {
      console.error("TimeEffect value is invalid, please set again !")
      return
    }
    if (this._timeEffect == option) {
      return
    }
    this._timeEffect = option
    let live = new Date()
    switch (option) {
    case TimeEffect.Live:
      this._time = null
      break
    case TimeEffect.Morning:
      this._time =  live.setHours(TimeConstants.morningTime, 0, 0, 0)
      break
    case TimeEffect.Noon:
    case TimeEffect.None:
      this._time =  live.setHours(TimeConstants.noonTime, 0, 0, 0)
      break
    case TimeEffect.AfterNoon:
      this._time =  live.setHours(TimeConstants.afterNoonTime, 0, 0, 0)
      break
    case TimeEffect.Evening:
      this._time =  live.setHours(TimeConstants.eveningTime, 0, 0, 0)
      break
    }
  }

  getSkyColor(): Color {
    if (this._timeEffect == TimeEffect.None) {
      return ColorConstants.getSkyColor(this.defaultTimeByString)
    }
    let color = null
    let stringTimeEffect = TimeEffectManager.getStringByTimeEffect(this._timeEffect)
    switch (this._timeEffect) {
    case TimeEffect.Live:
      color = this.getSkyColorByTime()
      break
    case TimeEffect.Morning:
    case TimeEffect.AfterNoon:
    case TimeEffect.Evening:
      color = ColorConstants.getSkyColor(stringTimeEffect)
      break
    default:
      color = ColorConstants.getSkyColor(this.defaultTimeByString)
      break
    }
    return color
  }

  getFogColor(): Color {
    if (this._timeEffect == TimeEffect.None) {
      return ColorConstants.getFogColor(this.defaultTimeByString)
    }
    let color = null
    let stringTimeEffect = TimeEffectManager.getStringByTimeEffect(this._timeEffect)
    switch (this._timeEffect) {
    case TimeEffect.Live:
      color = this.getFogColorByTime()
      break
    case TimeEffect.Morning:
    case TimeEffect.Noon:
    case TimeEffect.AfterNoon:
    case TimeEffect.Evening:
      color = ColorConstants.getFogColor(stringTimeEffect)
      break
    default:
      color = ColorConstants.getFogColor(this.defaultTimeByString)
      break
    }
    return color
  }

  getSkyColorByTime(): Color {
    let day = new Date(this.getTime())
    let hour = day.getHours()
    let color = null
    for (let i = 0; i < this._timeArray.length - 1; ++i) {
      if (hour > this._timeArray[i] && hour <= this._timeArray[i + 1]) {
        color = Color.mix(
          ColorConstants.getSkyColor(
            TimeEffectManager.getStringByTimeEffect(TimeEffect.Morning + i)),
          ColorConstants.getSkyColor(
            TimeEffectManager.getStringByTimeEffect(TimeEffect.Morning + i + 1)),
          (this._timeArray[i + 1] - hour) / (this._timeArray[i + 1] - this._timeArray[i]))
        break
      }
    }
    if ((color == null && hour > TimeConstants.eveningTime)
    || (color == null && hour <= TimeConstants.morningTime)) {
      if (hour >= TimeConstants.eveningTime || hour < TimeConstants.morningTime - 2){
        return ColorConstants.getSkyColor(TimeEffectManager.getStringByTimeEffect(TimeEffect.Evening))
      }
      let hourNormalize = hour
      if (hour <= TimeConstants.morningTime) {
        hourNormalize += 24
      }
      color = Color.mix(
        ColorConstants.getSkyColor("Morning"),
        ColorConstants.getSkyColor("Evening"),
        (hourNormalize - TimeConstants.eveningTime) / (24 - (TimeConstants.eveningTime - TimeConstants.morningTime)))
    }
    return color
  }

  getFogColorByTime(): Color {
    let day = new Date(this.getTime())
    let hour = day.getHours()
    let color = null
    for (let i = 0; i < this._timeArray.length - 1; ++i) {
      if (hour > this._timeArray[i] && hour <= this._timeArray[i + 1]) {
        color = Color.mix(
          ColorConstants.getFogColor(
            TimeEffectManager.getStringByTimeEffect(TimeEffect.Morning + i)),
          ColorConstants.getFogColor(
            TimeEffectManager.getStringByTimeEffect(TimeEffect.Morning + i + 1)),
          (this._timeArray[i + 1] - hour) / (this._timeArray[i + 1] - this._timeArray[i]))
        break
      }
    }
    if ((color == null && hour > TimeConstants.eveningTime)
      || (color == null && hour <= TimeConstants.morningTime)) {
      if (hour >= TimeConstants.eveningTime || hour < TimeConstants.morningTime - 2){
        return ColorConstants.getFogColor(TimeEffectManager.getStringByTimeEffect(TimeEffect.Evening))
      }
      let hourNormalize = hour
      if (hour <= TimeConstants.morningTime) {
        hourNormalize += 24
      }
      color = Color.mix(
        ColorConstants.getFogColor("Morning"),
        ColorConstants.getFogColor("Evening"),
        (hourNormalize - TimeConstants.eveningTime) / (24 - (TimeConstants.eveningTime - TimeConstants.morningTime)))
    }
    return color
  }

  static getStringByTimeEffect(timeEffect: TimeEffect): string {
    switch (timeEffect) {
    case TimeEffect.Live:
      return "Live"
    case TimeEffect.Morning:
      return "Morning"
    case TimeEffect.Noon:
      return "Noon"
    case TimeEffect.AfterNoon:
      return "AfterNoon"
    case TimeEffect.Evening:
      return "Evening"
    default:
      return null
    }
  }

  isEveningTime(): boolean {
    let time = new Date(this.getTime())
    let hour = time.getHours()
    if (hour >= TimeConstants.eveningTime || hour < TimeConstants.morningTime) {
      return true
    }
    return false
  }

  getBuildingColor(): Color {
    if (this._timeEffect == TimeEffect.None) {
      return ColorConstants.getSkyColor(this.defaultTimeByString)
    }
    let color = null
    let stringTimeEffect = TimeEffectManager.getStringByTimeEffect(this._timeEffect)
    switch (this._timeEffect) {
    case TimeEffect.Live:
      color = this.getLightColorByTime()
      break
    case TimeEffect.Morning:
    case TimeEffect.AfterNoon:
    case TimeEffect.Evening:
      color = ColorConstants.getLightColor(stringTimeEffect)
      break
    default:
      color = ColorConstants.getLightColor(this.defaultTimeByString)
      break
    }
    return color
  }

  private getLightColorByTime(): Color {
    let day = new Date(this.getTime())
    let hour = day.getHours()
    let color = null
    for (let i = 0; i < this._timeArray.length - 1; ++i) {
      if (hour > this._timeArray[i] && hour <= this._timeArray[i + 1]) {
        color = Color.mix(
          ColorConstants.getLightColor(
            TimeEffectManager.getStringByTimeEffect(TimeEffect.Morning + i)),
          ColorConstants.getLightColor(
            TimeEffectManager.getStringByTimeEffect(TimeEffect.Morning + i + 1)),
          (this._timeArray[i + 1] - hour) / (this._timeArray[i + 1] - this._timeArray[i]))
        break
      }
    }
    if ((color == null && hour > TimeConstants.eveningTime)
        || (color == null && hour <= TimeConstants.morningTime)) {
      if (hour >= TimeConstants.eveningTime || hour < TimeConstants.morningTime - 2){
        return ColorConstants.getLightColor(TimeEffectManager.getStringByTimeEffect(TimeEffect.Evening))
      }
      let hourNormalize = hour
      if (hour <= TimeConstants.morningTime) {
        hourNormalize += 24
      }
      color = Color.mix(
        ColorConstants.getSkyColor("Morning"),
        ColorConstants.getSkyColor("Evening"),
        (hourNormalize - TimeConstants.eveningTime) / (24 - (TimeConstants.eveningTime - TimeConstants.morningTime)))
    }
    return color
  }
}
