import {CameraPosition} from "../public/CameraPosition"
import {MapUtils} from "./MapUtils"
import {CameraConstants} from "../map/MapConstants"
import {Matrix4} from "../engine/Matrix"
import {Vector3} from "../engine/Vector3"
import {Vector2} from "../engine/Vector2"

export class CameraUtils {

  private constructor() { }

  public static calculateViewMatrix(cameraPosition: CameraPosition, screenHeight: number) {
    let screenTileSize = MapUtils.getTileSize()

    // Calculate view matrix
    let scale = (CameraConstants.height * Math.tan(CameraConstants.fov / 2)
      * screenTileSize / screenHeight) / (screenTileSize / 2)
    let bearing = cameraPosition.getBearing()
    let tempVector = Matrix4.rotateX(cameraPosition.getTilt())
      .transform(new Vector3(0, 0, CameraConstants.height), 0)
    let eyePosition = Matrix4.rotateZ(-bearing)
      .transform(tempVector, 0)

    tempVector = Matrix4.rotateX(cameraPosition.getTilt()).transform(new Vector3(0, 1, 0), 0)
    let up = Matrix4.rotateZ(-bearing).transform(tempVector, 0)

    let viewMatrix = Matrix4.lookAt(eyePosition, new Vector3(0, 0, 0), 1, up)
    viewMatrix = Matrix4.scale(scale, scale, scale).concat(viewMatrix)
    return viewMatrix
  }

  public static calculateProjectionMatrix(screenWidth: number, screenHeight: number) {
    return Matrix4.perspective(CameraConstants.fov, screenWidth / screenHeight, CameraConstants.nearPlane, CameraConstants.farPlane)
  }

  public static calculateOrthoProjectionMatrix(screenWidth: number, screenHeight: number) {
    return Matrix4.ortho(0.0, screenWidth, screenHeight, 0.0)
  }

  public static calculateViewProjectionMatrix(cameraPosition: CameraPosition, screenWidth: number, screenHeight: number) {
    let viewMatrix = CameraUtils.calculateViewMatrix(cameraPosition, screenHeight)
    let projectionMatrix = CameraUtils.calculateProjectionMatrix(screenWidth, screenHeight)
    return viewMatrix.concat(projectionMatrix)
  }

  public static calculateInverseViewProjectionMatrix(cameraPosition: CameraPosition, screenWidth: number, screenHeight: number) {
    return this.calculateViewProjectionMatrix(cameraPosition, screenWidth, screenHeight).inverse()
  }
}
