import {AnnotationId} from "./Annotation"
import {InfoWindow} from "./InfoWindow"
import {DOMUtils} from "../util/DOMUtils"
import {MapView} from "../map/MapView"
import {Marker} from "../public/Marker"
import {SymbolAnnotationData} from "./SymbolAnnotationData"

export class InfoWindowManager {

  private infoWindows: Map<AnnotationId, InfoWindow>

  private selectedMarkerId: AnnotationId

  private infoWindowContainer: HTMLElement

  private infoContentsContainer: HTMLElement

  private map: MapView

  private symbolAnnotations: Map<AnnotationId, SymbolAnnotationData>

  constructor(map: MapView, symbolAnnotations: Map<AnnotationId, SymbolAnnotationData>) {
    this.map = map
    this.symbolAnnotations = symbolAnnotations
    this.infoWindows = new Map<AnnotationId, InfoWindow>()
    this.createInfoWindowContainer()
  }

  createInfoWindowContainer(): void {
    if (this.infoWindowContainer) {
      return
    }
    this.infoWindowContainer = DOMUtils.create("div",
      "mf-info-window-container",
      this.map.getMapContainer())
  }

  createInfoContentsContainer(): void {
    if (this.infoContentsContainer) {
      return
    }
    this.createInfoWindowContainer()
    this.infoContentsContainer = DOMUtils.create("div", "mf-info-window-content mf-scrollbar", this.infoWindowContainer)
  }

  createInfoWindow(marker: Marker): HTMLElement {
    let infoWindow = this.getInfoWindow(marker)
    if (infoWindow != null) {
      if (this.infoContentsContainer == null) {
        this.infoContentsContainer = DOMUtils.create("div", null, this.infoWindowContainer)
      }
      this.setContentsHTMLElement(infoWindow)
      return this.infoContentsContainer
    }
    return null
  }

  createInfoContents(marker: Marker): HTMLElement {
    let infoWindowContents = this.getInfoContents(marker)
    if (infoWindowContents != null) {
      this.createInfoContentsContainer()
      this.setContentsHTMLElement(infoWindowContents)
      return this.infoContentsContainer
    }
    return null
  }

  createDefaultInfoContents(marker: Marker): HTMLElement {
    let title = marker.getTitle()
    let snippet = marker.getSnippet()
    if ((title === "" || title == null) && (snippet === "" || snippet == null)) {
      return null
    }
    this.createInfoContentsContainer()
    let defaultContents = this.getDefaultContents(marker)
    this.setContentsHTMLElement(defaultContents)
    return this.infoContentsContainer
  }

  deselectMarker(): void {
    if (this.selectedMarkerId != null) {
      this.hideInfoWindow(this.selectedMarkerId)
    }
  }

  getDefaultContents(marker: Marker): string {
    let title = marker.getTitle() || ""
    let snippet = marker.getSnippet() || ""

    return ((title.length > 0) ? `<h4>${title}</h4>` : "") +
      ((snippet.length > 0) ? `<p>${marker.getSnippet()}</p>`: "")
  }

  getInfoContents(marker: Marker): string | Node {
    let infoContents = marker.getInfoContents()
    if(infoContents != null) {
      return infoContents
    }
    return null
  }

  getInfoWindow(marker: Marker): string | Node {
    let infoWindow = marker.getInfoWindow()
    if (infoWindow != null) {
      return infoWindow
    }
    return null
  }

  getInfoWindowHTMLElement(marker: Marker): HTMLElement {
    let infoWindow = this.createInfoWindow(marker)
    if (infoWindow != null) {
      return infoWindow
    }
    let infoContents = this.createInfoContents(marker)
    if (infoContents != null) {
      return infoContents
    }
    return this.createDefaultInfoContents(marker)
  }

  isInfoWindowShown(annotationId: AnnotationId): boolean {
    if (this.selectedMarkerId == null) {
      return false
    }
    return this.selectedMarkerId == annotationId
  }

  removeContent(): void {
    if (this.infoContentsContainer) {
      DOMUtils.remove(this.infoContentsContainer)
      this.infoContentsContainer = null
    }
  }

  render(visibleIds: AnnotationId[]): void {
    if (this.selectedMarkerId == null) {
      return
    }
    if (visibleIds.indexOf(this.selectedMarkerId) > -1) {
      this.update()
    }
    else {
      this.infoWindowContainer.style.visibility = "hidden"
    }
  }

  update(): void {
    let symbolAnnotationData = this.symbolAnnotations.get(this.selectedMarkerId)
    if (symbolAnnotationData != null) {
      let infoWindow = this.infoWindows.get(this.selectedMarkerId)
      let position = infoWindow.getPositionBySymbolAnnotationData(symbolAnnotationData, this.map.mapState)
      if (position == null) {
        return
      }
      let left = position.x
      let top = position.y
      let style = this.infoWindowContainer.style
      style.top = top.toString() + "px"
      style.left = left.toString() + "px"
      if (style.visibility == "hidden") {
        style.visibility = "visible"
      }
    }
  }

  hideInfoWindow(annotationId?: AnnotationId): void {
    let id = annotationId || this.selectedMarkerId
    if (id == null) {
      return
    }

    this.removeContent()
    this.infoWindows.delete(id)
    this.selectedMarkerId = null
  }

  showInfoWindow(annotationId: AnnotationId): void {
    if (annotationId === this.selectedMarkerId) {
      return
    }

    this.deselectMarker()
    let symbolAnnotationData = this.symbolAnnotations.get(annotationId)
    if (symbolAnnotationData != null && symbolAnnotationData.symbolAnnotation.isVisible()) {
      let marker = symbolAnnotationData.symbolAnnotation

      let content = this.getInfoWindowHTMLElement(marker)
      if (content && content.children.length > 0) {
        let allChildContent = ""
        for (let i = 0; i < content.children.length; ++i) {
          allChildContent += content.children[i].innerHTML
        }
        if (allChildContent == "") {
          console.error("All content you set for Info Window is NULL, Please check your custom setInfoContents() method.")
          content = null
        }
      }
      let infoWindow = new InfoWindow(content, marker.getWindowAnchor())
      this.infoWindows.clear()
      this.infoWindows.set(annotationId, infoWindow)
      this.selectedMarkerId = annotationId
      this.update()
    }
  }

  setContentsHTMLElement(content: string | Node): void {
    if (this.infoContentsContainer) {
      this.infoContentsContainer.innerHTML = content.toString()
    }
  }

  updateInfoContents(annotationId: AnnotationId): void {
    let symbolAnnotationData = this.symbolAnnotations.get(annotationId)
    let marker = symbolAnnotationData.symbolAnnotation
    let infoWindow = this.infoWindows.get(annotationId)
    if (infoWindow != null) {
      infoWindow.setContent(this.getInfoWindowHTMLElement(marker))
      this.update()
    }
  }
}
