import {LatLng, ILatLng} from "./LatLng"
import {MapConstants} from "../map/MapConstants"

export class LatLngBounds {

  private northeast: LatLng

  private southwest: LatLng

  constructor(sw?: any, ne?: any) {
    if (!sw) {

      // Noop
    }
    else if (ne) {
      this.southwest = sw instanceof LatLng ? new LatLng(sw.lat, sw.lng) : LatLng.convert(sw)
      this.northeast = ne instanceof LatLng ? new LatLng(ne.lat, ne.lng) : LatLng.convert(ne)
    }
    else if (sw.length === 4) {
      this.southwest = LatLng.convert([sw[1], sw[0]])
      this.northeast = LatLng.convert([sw[3], sw[2]])
    }
    else {
      this.southwest = sw[0] instanceof LatLng ? new LatLng(sw[0].lat, sw[0].lng) : LatLng.convert(sw[0])
      this.northeast = sw[1] instanceof LatLng ? new LatLng(sw[1].lat, sw[1].lng) : LatLng.convert(sw[1])
    }

    /** Arrange southwest and northeast in the right position */
    if (!(this.southwest && this.northeast)) {
      return
    }
    if (this.southwest.lat > this.northeast.lat || this.southwest.lng > this.northeast.lng) {
      let sw = this.southwest
      let ne = this.northeast
      this.southwest = new LatLng(Math.min(sw.lat, ne.lat), Math.min(sw.lng, ne.lng))
      this.northeast = new LatLng(Math.max(sw.lat, ne.lat), Math.max(sw.lng, ne.lng))
    }
  }

  extend(point: ILatLng): LatLngBounds{
    let location = LatLng.convert(point)
    if (!this.southwest || !this.northeast) {
      this.southwest = new LatLng(location.lat, location.lng)
      this.northeast = new LatLng(location.lat, location.lng)
    }
    else {
      this.southwest =
          new LatLng(Math.min(location.lat,
            this.southwest.lat), Math.min(this.southwest.lng, location.lng))
      this.northeast =
          new LatLng(Math.max(location.lat,
            this.northeast.lat), Math.max(this.northeast.lng, location.lng))
    }
    return this
  }

  /**
   * Computes the center of this LatLngBounds
   */
  getCenter(): LatLng {
    if (!this.northeast || !this.southwest) {
      return new LatLng(0, 0)
    }
    return new LatLng((this.northeast.lat + this.southwest.lat) / 2,
      (this.northeast.lng + this.southwest.lng) / 2)
  }

  /**
   * Returns the north-east corner of this bounds.
   */
  getNortheast(): LatLng {
    return this.northeast || LatLng.convert([MapConstants.maxLongitude, MapConstants.maxLatitude])
  }

  /**
   * Returns the south-west corner of this bounds.
   */
  getSouthwest(): LatLng {
    return this.southwest || LatLng.convert([MapConstants.minLongitude, MapConstants.minLatitude])
  }

  static world(): LatLngBounds {
    return new LatLngBounds([MapConstants.minLongitude, MapConstants.minLatitude], [MapConstants.maxLongitude, MapConstants.maxLatitude])
  }

  static convert(input: ILatLngBounds): LatLngBounds {
    if (input instanceof LatLngBounds) {
      return input
    }

    return new LatLngBounds(input)
  }
}

export type ILatLngBounds = LatLngBounds | [ILatLng, ILatLng] | [number, number, number, number]
