import {Vector3} from "../engine/Vector3"
import {BuildingType} from "./BuildingType"
import {MapState} from "../map/MapState"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {GeometryUtils} from "../util/GeometryUtils"
import {Vector2} from "../engine/Vector2"
import {Object3D} from "./Object3D"
import {Matrix4} from "../engine/Matrix"
import {LatLng} from "../main"
import {CameraPosition} from "../public/CameraPosition"
import {BuildingIconSelectionManager} from "./BuildingIconSelectionManager"
import {MapConstants} from "../map/MapConstants"

export class Building extends Object3D {

  private static readonly buildingRotationX: number = 90.0

  private id: string

  private name: string

  private places: string[]

  private scale: Vector3

  private bearing: number

  private location: LatLng

  private elevation: number

  private camera: CameraPosition

  private types: string[]

  private _isVerified: boolean

  private minZoom: number

  private maxZoom: number

  private startDate: number

  private endDate: number

  private textureName: string

  private textureData: string

  private objName: string

  private objData: string

  private coordinates: LatLng[] = []

  private draggable: boolean

  private buildingType: BuildingType

  private selectionCode: number

  private highestPosition: LatLng

  private height: number

  private selected: boolean

  private scaledModelMatrix: Matrix4

  private scaledMvpMatrix: Matrix4

  private buildingIconSelectionManager: BuildingIconSelectionManager

  highestModelPosition: Vector3

  constructor(buildingIconSelectionManager: BuildingIconSelectionManager) {
    super()
    this.location = new LatLng(0.0, 0.0)
    this.scale = new Vector3(1.0, 1.0, 1.0)
    this.bearing = 0.0
    this.name = "Unknown name"
    this.elevation = 0.0
    this.selectionCode = 0.0
    this.objData = null
    this.textureData = null
    this.selected = false
    this.buildingIconSelectionManager = buildingIconSelectionManager
  }

  getId(): string {
    return this.id
  }

  setId(value: string): void {
    this.id = value
  }

  getName(): string {
    return this.name
  }

  setName(value: string): void {
    this.name = value
  }

  setPlaces(places: string[]): void{
    this.places = places
  }

  getPlaces(): string[]{
    return this.places
  }

  getScale(): Vector3 {
    return this.scale
  }

  setScale(value: Vector3): void {
    this.scale = value
  }

  setBearing(bearing: number): void {
    this.bearing = bearing
  }

  getBearing(): number{
    return this.bearing
  }

  setLocation(location: LatLng): void {
    this.location = LatLng.convert(location)
  }

  getLocation(): LatLng {
    return this.location
  }

  getElevation(): number {
    return this.elevation
  }

  setElevation(value: number): void {
    this.elevation = value
  }

  setCoordinates(coordinates: LatLng[]): void {
    this.coordinates = coordinates
  }

  getCoordinates() : LatLng[] {
    return this.coordinates
  }

  setCamera(camera: CameraPosition): void {
    this.camera = camera
  }

  getCamera(): CameraPosition {
    return this.camera
  }

  setTypes(types: string[]){
    this.types = types
  }

  getTypes(): string[]{
    return this.types
  }

  setVerified(isVerified: boolean): void {
    this._isVerified = isVerified
  }

  isVerified(): boolean {
    return this._isVerified
  }

  setMinZoom(minZoom: number) : void {
    this.minZoom = minZoom
  }

  getMinZoom(): number {
    return this.minZoom
  }

  setMaxZoom(maxZoom: number){
    this.maxZoom = maxZoom
  }

  getMaxZoom(): number {
    return this.maxZoom
  }

  setStartDate(startDate: number): void {
    this.startDate = startDate
  }

  getStartDate(): number {
    return this.startDate
  }

  setEndDate(endDate: number): void {
    this.endDate = endDate
  }

  getEndDate(): number {
    return this.endDate
  }

  getTextureName(): string {
    return this.textureName
  }

  setTextureName(value: string): void {
    this.textureName = value
  }

  getTextureData(): string {
    return this.textureData
  }

  setTextureData(textureData: string): void {
    this.textureData = textureData
  }

  getObjName(): string {
    return this.objName
  }

  setObjName(value: string): void {
    this.objName = value
  }

  getObjData(): string {
    return this.objData
  }

  setObjData(objData: string): void {
    this.objData = objData
  }

  setDraggable(draggable: boolean): void {
    this.draggable = draggable
  }

  isDraggable(): boolean {
    return this.draggable
  }

  getBuildingType(): BuildingType {
    return this.buildingType
  }

  setBuildingType(value: BuildingType): void {
    this.buildingType = value
  }

  getSelectionCode(): number {
    return this.selectionCode
  }

  setSelectionCode(value: number): void {
    this.selectionCode = value
  }

  setHighestPosition(highestPosition: LatLng) {
    this.highestPosition = highestPosition
  }

  getHighestPosition(): LatLng {
    return this.highestPosition
  }

  setHeight(height: number) {
    this.height = height
  }

  getHeight(): number {
    return this.height
  }

  setSelected(selected: boolean): void {
    if (selected == this.selected) {
      return
    }
    if (selected && this.getHighestPosition() /* Model was downloaded */) {
      this.buildingIconSelectionManager.addBuilding(this)
    }
    else if (!selected && this.selected && this.getHighestPosition() /* Model was downloaded */) {

      //deselected
      this.buildingIconSelectionManager && this.buildingIconSelectionManager.removeBuilding(this.id)
    }
    this.selected = selected
  }

  isSelected(): boolean {
    return this.selected
  }

  getScaledModelMatrix(): Matrix4 {
    return this.scaledModelMatrix
  }

  getScaledMvpMatrix(): Matrix4 {
    return this.scaledMvpMatrix
  }

  notifyModelDownloaded(): void {
    if (this.selected) {
      this.buildingIconSelectionManager.addBuilding(this)
    }
  }

  destroy() : void {
    this.buildingIconSelectionManager && this.selected && this.buildingIconSelectionManager.removeBuilding(this.id)
    this.buildingIconSelectionManager = null

    // Just only handle polygon building. Another building types already handled in BuildingResourceManager
    if (this.buildingType == BuildingType.Extrude || this.buildingType == BuildingType.ExternalExtrude  ) {
      this.model && this.model.destroy()
    }
  }

  updateMatrices(mapState: MapState): void {
    if (this.model == null) {
      console.warn("Model of " + this.getId() + " is null!")
      return
    }
    let metersPerPixel = ProjectionMercator.getMetersPerPixelAtLatitude(MapConstants.metersPerPixelAtLatitude, mapState.getZoom())

    let buildingScale = 1.0 / metersPerPixel
    let position = ProjectionMercator.projectScale(this.location, mapState.getScale())
    let coordinate = GeometryUtils.pixelCoordinateToCameraCoordinate(new Vector2(position.x, position.y), mapState.cameraPosition)

    let elevationPixel = mapState.is3dMode() ? GeometryUtils.getElevationPixelFromMeter(this.getElevation(), mapState.cameraPosition) : 0.0

    this.modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, elevationPixel)
    this.modelMatrix = Matrix4.scale(
      this.scale.x * buildingScale,
      this.scale.y * buildingScale,
      this.scale.z * buildingScale).concat(this.modelMatrix)

    this.modelMatrix = Matrix4.rotateZ(this.bearing).concat(this.modelMatrix)
    this.modelMatrix = Matrix4.rotateX(Building.buildingRotationX).concat(this.modelMatrix)
    this.mvpMatrix = this.modelMatrix.concat(mapState.getViewProjectionMatrix())

    let scaleX = 0.1
    let scaleY = 0.1
    let scaleZ = 0.015
    if (this.model.minX ) {
      let scaleValue = 100 * 0.05 / (this.model.maxX - this.model.minX)
      scaleValue = scaleValue == Infinity ? 0.1 : scaleValue
      scaleX = Math.max(scaleX, scaleValue)
      scaleValue = 100 * 0.05 / (this.model.maxY - this.model.minY)
      scaleValue = scaleValue == Infinity ? 0.1 : scaleValue
      scaleY = Math.max(scaleY, scaleValue)
      scaleValue = 100 * 0.005 / (this.model.maxZ - this.model.minZ)
      scaleValue = scaleValue == Infinity ? 0.1 : scaleValue
      scaleZ = Math.max(scaleZ, scaleValue)
    }
    this.scaledModelMatrix = Matrix4.translate(coordinate.x, coordinate.y, elevationPixel)
    this.scaledModelMatrix = Matrix4.scale(
      this.scale.x * buildingScale + scaleX,
      this.scale.y * buildingScale + scaleY,
      this.scale.z * buildingScale + scaleZ).concat(this.scaledModelMatrix)

    this.scaledModelMatrix = Matrix4.rotateZ(this.bearing).concat(this.scaledModelMatrix)
    this.scaledModelMatrix = Matrix4.rotateX(Building.buildingRotationX).concat(this.scaledModelMatrix)
    this.scaledMvpMatrix = this.scaledModelMatrix.concat(mapState.getViewProjectionMatrix())
  }

  updateScaledMatrix(mapState: MapState): void {
    let metersPerPixel = ProjectionMercator.getMetersPerPixelAtLatitude(MapConstants.metersPerPixelAtLatitude, mapState.getZoom())

    let buildingScale = 1.0 / metersPerPixel
    let position = ProjectionMercator.projectScale(this.location, mapState.getScale())
    let coordinate = GeometryUtils.pixelCoordinateToCameraCoordinate(new Vector2(position.x, position.y), mapState.cameraPosition)

    let elevationPixel = mapState.is3dMode() ? GeometryUtils.getElevationPixelFromMeter(this.getElevation(), mapState.cameraPosition) : 0.0

    let scaleX = 0.1
    let scaleY = 0.1
    let scaleZ = 0.015
    if (this.model.minX ) {
      let scaleValue = 100 * 0.05 / (this.model.maxX - this.model.minX)
      scaleValue = scaleValue == Infinity ? 0.1 : scaleValue
      scaleX = Math.max(scaleX, scaleValue)
      scaleValue = 100 * 0.05 / (this.model.maxY - this.model.minY)
      scaleValue = scaleValue == Infinity ? 0.1 : scaleValue
      scaleY = Math.max(scaleY, scaleValue)
      scaleValue = 100 * 0.005 / (this.model.maxZ - this.model.minZ)
      scaleValue = scaleValue == Infinity ? 0.1 : scaleValue
      scaleZ = Math.max(scaleZ, scaleValue)
    }

    this.scaledModelMatrix = Matrix4.translate(coordinate.x, coordinate.y, elevationPixel)
    this.scaledModelMatrix = Matrix4.scale(
      this.scale.x * buildingScale + scaleX,
      this.scale.y * buildingScale + scaleY,
      this.scale.z * buildingScale + scaleZ).concat(this.scaledModelMatrix)

    this.scaledModelMatrix = Matrix4.rotateZ(this.bearing).concat(this.scaledModelMatrix)
    this.scaledModelMatrix = Matrix4.rotateX(Building.buildingRotationX).concat(this.scaledModelMatrix)
    this.scaledMvpMatrix = this.scaledModelMatrix.concat(mapState.getViewProjectionMatrix())
  }
}

