import {Object3D} from "../building/Object3D"
import {MapState} from "./MapState"
import {Matrix4} from "../engine/Matrix"

export class SkyObject extends Object3D {

  private viewMatrix: Matrix4

  constructor() {
    super()
  }

  getViewMatrix(): Matrix4 {
    return this.viewMatrix
  }

  updateMatrices(mapState: MapState): void {
    this.viewMatrix = Matrix4.translate(0.0, 1 - mapState.getSkyRatio() * 2.0, -1.0)
    this.mvpMatrix = this.viewMatrix.concat(mapState.getProjectionMatrix())
  }

  destroy(): void {
  }
}
