import {Texture} from "../engine/Texture"
import {Images} from "../resources/Images"
import {UrlUtils} from "../util/UrlUtils"
import {MapConstants, SymbolConstants} from "../map/MapConstants"
import {MapSettings} from "../map/MapSettings"
import {RequestTask, Resource} from "../worker/RequestTask"
import {TaskManager} from "../worker/TaskManager"

export class SymbolResourceManager {

  private static instances: Map<string, SymbolResourceManager> = null

  private textures: Map<string, Texture>

  private gl: WebGLRenderingContext

  private taskManager: TaskManager

  private requestRender: () => void

  private constructor(gl: WebGLRenderingContext) {
    this.gl = gl
    this.textures = new Map<string, Texture>()
  }

  public static getInstance(gl: WebGLRenderingContext) {
    let id = gl["id"]
    if (this.instances == null) {
      this.instances = new Map<string, SymbolResourceManager>()
    }
    let instance = this.instances.get(id)
    if (!instance) {
      instance = new this(gl)
      this.instances.set(id, instance)
    }
    return instance
  }

  public static releaseInstance(gl: WebGLRenderingContext) {
    let id = gl["id"]
    if (this.instances == null) {
      return
    }
    let instance = this.instances.get(id)
    if (!instance) {
      return
    }
    instance.textures.forEach(texture => texture.destroy())
    this.instances.delete(id)
  }

  setRequestRender(requestRender: () => void): void {
    this.requestRender = requestRender
  }

  setTaskManager(taskManager: TaskManager): void {
    this.taskManager = taskManager
  }

  acquire(url : string, retain = true): Texture {
    let temp = url
    if (url == null) {
      console.error("Url should not be null")
      return null
    }

    if (!this.taskManager) {
      console.error("Not set TaskManager for SymbolResourceManager !")
      return null
    }

    if (!this.gl) {
      console.error("Please initialize SymbolResourceManager before using it")
    }

    let texture = this.textures.get(url)

    //cheat
    if (temp == "iconDefault") {
      url = UrlUtils.getIconRequestUrl(MapConstants.iconRequestUrl, "default", MapSettings.getAccessKey())
    }

    if (texture != null) {
      retain && texture.retain()
      return texture
    }
    else {
      texture = new Texture(this.gl)
      if (url.startsWith("http")) {
        let requestTask = new RequestTask(Resource.Texture)
        requestTask.url = url
        requestTask.completion = (response: any) => {
          if (response.data != null) {
            let callBack = () => {
              this.requestRender()
            }
            texture.loadImageFromArrayBuffer(response.data.data, response.data.width,
              response.data.height, true, callBack)
          }
        }
        this.taskManager && this.taskManager.postTask(requestTask)
      } else {
        texture.loadImage(!UrlUtils.isUrlRequest(url)  ? Images.get(url) : url,  () => {
          this.requestRender()
        })  
      }
      texture.retain()
      if (temp == "iconDefault") {
        this.textures.set(temp, texture)
      }
      else {
        this.textures.set(url, texture)
      }
      return texture
    }
  }

  release(url: string): void {
    if (url == null) {
      console.error("Url should not be null")
      return
    }
    if (!this.gl) {
      console.error("Please initialize SymbolResourceManager before using it")
    }
    let texture = this.textures.get(url)
    if (texture == null) {
      //console.error("Can not find any texture with id: " + url + " to decrease reference !")
      return
    }
    texture.release()
    if (texture.reference <= 0) {
      this.textures.delete(url)
    }
  }
}
