import {Layer, LayerType} from "../layer/Layer"
import {MapState} from "../map/MapState"
import {LayerRenderer} from "./LayerRenderer"
import {GeoJsonLayer} from "../layer/GeoJsonLayer"
import {ModelEnum, ResourceManager, ShaderEnum} from "../util/ResourceManager"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {Kind} from "../tile/Tile"
import {GeometryTile} from "../tile/GeometryTile"
import {MapUtils} from "../util/MapUtils"
import {Matrix4} from "../engine/Matrix"
import {Vector2} from "../engine/Vector2"
import {UniformInfo} from "../engine/Shader"
import {GeometryUtils} from "../util/GeometryUtils"
import {GeoJsonLayerData} from "./data/GeoJsonLayerData"
import {SymbolResourceManager} from "../annotation/SymbolResourceManager"
import {MapConstants, SymbolConstants} from "../map/MapConstants"

export class GeoJsonLayerRenderer extends LayerRenderer {
  constructor(mapState: MapState, layer: Layer) {
    super(mapState, layer)
  }

  render(): void {
    if (this.layer.getLayerType() != LayerType.GEOJSON) {
      return
    }
    let geoJsonLayer = this.layer as GeoJsonLayer
    if (geoJsonLayer.renderTiles.size == 0) {
      return
    }
    let currentZoom = this.mapState.getZoom()
    if (currentZoom < this.mapState.mapSettings.dataLayerMinZoom ||
        currentZoom > this.mapState.mapSettings.dataLayerMaxZoom) {
      return
    }
    let gl = this.layer.tileManager.mapView.gl
    let resourceManager = ResourceManager.getInstance(gl)
    let geoJsonShader = resourceManager.getShader(ShaderEnum.GEOJSON_SHADER)
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    let worldSize = ProjectionMercator.worldSize(this.mapState.getScale())
    let layerId = this.layer.getLayerId()
    let strokeScale = 1.0
    if (currentZoom > this.mapState.getMaxNativeZoom()) {
      strokeScale = Math.pow(2.0, Math.round(currentZoom) - this.mapState.getMaxNativeZoom())
    }
    let cameraPosition = this.mapState.cameraPosition
    let screenSize = this.mapState.getScreenSize()
    geoJsonLayer.renderTiles.forEach(renderTiles => {
      renderTiles.forEach(renderTile => {
        let tile = renderTile.getTile()
        let tileKind = tile.getKind()
        if (tileKind != Kind.VECTOR) {
          console.warn("Invalid tile kind")
          return
        }
        let geometryTile = tile as GeometryTile
        if (!geometryTile.hasData(layerId)) {
          return
        }

        let layerData = geometryTile.getLayerData(layerId, LayerType.GEOJSON)
        if (layerData.getType() != LayerType.GEOJSON) {
          console.warn("ERROR: layerData.getType() != LayerType.GEOJSON .............")
          return
        }
        geoJsonShader.bind()
        let geoJsonLayerData = layerData as GeoJsonLayerData

        // Calculate mvp matrix
        let tileCoordinate = geometryTile.getTileCoordinate()
        let tileScale = 1 << tileCoordinate.getZoom()
        let s = worldSize / tileScale
        let scale = s / MapUtils.getTileSize()
        let coordinate = GeometryUtils.pixelCoordinateToCameraCoordinate(new Vector2(tileCoordinate.getX() * s + s / 2,
          tileCoordinate.getY() * s + s / 2), cameraPosition)
        let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0.0)
        modelMatrix = Matrix4.scale(scale, scale, 1.0).concat(modelMatrix)
        let mvpMatrix = modelMatrix.concat(this.mapState.getViewProjectionMatrix())
        geoJsonShader.setUniforms([
          new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix)
        ])
        let partials = geoJsonLayerData.getPartials()
        let fillOutlinedPartials = geoJsonLayerData.getFillOutlinedPartials()
        let fillProperties = geoJsonLayerData.getFillProperties()
        let lineProperties = geoJsonLayerData.getLineProperties()
        let fillOutlinedProperties = geoJsonLayerData.getFillOutlinedProperties()
        let partialKeys = Array.from(partials.keys())
        let model = geoJsonLayerData.getModel()
        model.bind()
        geoJsonShader.setAttributes(model.attributeInfos)
        partialKeys.forEach(key => {
          if (lineProperties.has(key)) {
            let linePartials = partials.get(key)
            let strokeProperties = lineProperties.get(key)
            for (let i = 0; i < linePartials.length; ++i) {
              geoJsonShader.setUniforms([
                new UniformInfo("u_lineWidth", "1f",
                  strokeProperties.strokeWidth / strokeScale),
                new UniformInfo("u_color", "4fv",
                  strokeProperties.strokeColor.buffer())])
              let linePartial = linePartials[i]
              model.renderPartial(gl.TRIANGLES, linePartial.start, linePartial.length)
            }
          }
          else if (fillProperties.has(key)) {
            geoJsonShader.setUniforms([
              new UniformInfo("u_lineWidth", "1f", 0.0)
            ])
            let fillPartials = partials.get(key)
            let strokePartials = fillOutlinedPartials.get(key)
            for (let i = 0; i < fillPartials.length; ++i) {
              geoJsonShader.setUniforms([
                new UniformInfo("u_color", "4fv",
                  fillProperties.get(key).fillColor.buffer()),
              ])
              let fillPartial = fillPartials[i]
              model.renderPartial(gl.TRIANGLES, fillPartial.start, fillPartial.length)

              if (strokePartials && i < strokePartials.length) {
                let strokePartialArray = strokePartials[i]
                strokePartialArray.forEach(strokePartial => {
                  geoJsonShader.setUniforms([
                    new UniformInfo("u_color", "4fv",
                      fillOutlinedProperties.get(key).strokeColor.buffer())
                  ])
                  model.renderPartial(gl.LINES, strokePartial.start, strokePartial.length)
                })
              }
            }
            if (strokePartials) {
              for (let i = fillPartials.length; i < strokePartials.length; ++i) {
                let strokePartialArray = strokePartials[i]
                geoJsonShader.setUniforms([
                  new UniformInfo("u_color", "4fv",
                    fillOutlinedProperties.get(key).strokeColor.buffer())
                ])
                strokePartialArray.forEach(strokePartial => {
                  model.renderPartial(gl.LINES, strokePartial.start, strokePartial.length)
                })
              }
            }
          }
          else {
            console.warn("Invalid geojson key: " + key)
          }
        })

      })
    })

    // Render points
    let pointShader = resourceManager.getShader(ShaderEnum.SYMBOL_SHADER)
    let pointModel = resourceManager.getModel(ModelEnum.SYMBOL_MODEL)
    let symbolResourceManager = SymbolResourceManager.getInstance(gl)
    let pointTexture = symbolResourceManager.acquire(SymbolConstants.defaultSymbol)
    pointShader.bind()
    pointModel.bind()
    pointShader.setAttributes(pointModel.attributeInfos)
    pointShader.setUniforms([
      new UniformInfo("u_texture", "texture", pointTexture)
    ])
    geoJsonLayer.renderTiles.forEach(renderTiles => {
      renderTiles.forEach(renderTile => {
        let tile = renderTile.getTile()
        let tileKind = tile.getKind()
        if (tileKind != Kind.VECTOR) {
          console.warn("Invalid tile kind")
          return
        }
        let geometryTile = tile as GeometryTile
        if (!geometryTile.hasData(layerId)) {
          return
        }

        let layerData = geometryTile.getLayerData(layerId, LayerType.GEOJSON)
        if (layerData.getType() != LayerType.GEOJSON) {
          console.warn("ERROR: layerData.getType() != LayerType.GEOJSON .............")
          return
        }
        let geoJsonLayerData = layerData as GeoJsonLayerData
        geoJsonLayerData.getPointCoordinates().forEach(positions => {
          positions.forEach(position => {
            let coordinate = GeometryUtils.latLngToScreenCoordinate(position, 0, cameraPosition, screenSize)
            let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0)

            let anchor = MapConstants.pointGeoJsonAnchor
            let anchorX = anchor[0]
            let anchorY = anchor[1]

            let symbolWidth = MapConstants.pointGeoJsonWidth * MapUtils.pixelScale
            let symbolHeight = MapConstants.pointGeoJsonHeight * MapUtils.pixelScale
            let matrixTranslate = Matrix4.translate((0.5 - anchorX) * symbolWidth,
              (0.5 - anchorY) * symbolHeight,
              0)
            modelMatrix = matrixTranslate.concat(modelMatrix)
            let matrixScale = Matrix4.scale(symbolWidth / 2, symbolHeight / 2, 1)
            modelMatrix = matrixScale.concat(modelMatrix)
            let mvpMatrix = modelMatrix.concat(this.mapState.getOrthoProjectionMatrix())
            pointShader.setUniforms([
              new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix),
            ])
            pointModel.render(gl.TRIANGLES)
          })
        })
      })
    })
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, null)
    gl.bindBuffer(gl.ARRAY_BUFFER, null)
  }
}
