import {MapView} from "../map/MapView"
import {MapEvent} from "./MapEvent"
import {MapsEventListener} from "./MapsEventListener"
import {ILatLng, LatLng} from "./LatLng"

export namespace Data {

  export interface Geometry {

    getType(): string

    forEachLatLng(callback: (LatLng) => void): void
  }

  export class Point implements Geometry {

    private readonly _coordinate: LatLng

    constructor(coordinate: ILatLng) {
      if (coordinate == null) {
        throw new Error("Parameter coordinate can not be null")
      }
      this._coordinate = LatLng.convert(coordinate)
    }

    get(): LatLng {
      return this._coordinate
    }

    forEachLatLng(callback: (LatLng) => void): void {
      this._coordinate && callback(this._coordinate)
    }

    getType(): string {
      return "Point"
    }
  }

  export class MultiPoint implements Geometry {

    private readonly _coordinates: LatLng[]

    constructor(coordinates: ILatLng[]) {
      if (coordinates == null) {
        throw new Error("Parameter coordinates can not be null")
      }
      this._coordinates = coordinates.map(coordinate => LatLng.convert(coordinate))
    }

    forEachLatLng(callback: (LatLng) => void): void {
      this._coordinates && this._coordinates.forEach(coordinate => callback(coordinate))
    }

    getType(): string {
      return "MultiPoint";
    }

    getArray(): LatLng[] {
      return this._coordinates
    }

    getAt(n: number): LatLng {
      return this._coordinates[n]
    }

    getLength(): number {
      return this._coordinates.length
    }
  }

  export class LineString implements Geometry {

    private readonly _elements: LatLng[]

    constructor(elements: ILatLng[]) {
      if (elements == null) {
        throw new Error("Parameter elements can not be null")
      }
      this._elements = elements.map(element => LatLng.convert(element))
    }

    forEachLatLng(callback: (LatLng) => void): void {
      this._elements && this._elements.forEach(element => callback(element))
    }

    getType(): string {
      return "LineString"
    }
  }

  export class LinearRing {

    private readonly _elements: LatLng[]

    constructor(elements: ILatLng[]) {
      if (elements == null) {
        throw new Error("Parameter elements can not be null")
      }
      this._elements = elements.map(element => LatLng.convert(element))
    }

    get elements(): LatLng[] {
      return this._elements
    }
  }

  export class Polygon implements Geometry {

    private readonly _elements: LinearRing[]

    constructor(elements: LinearRing[]) {
      if (elements == null) {
        throw new Error("Parameter element can not be null")
      }
      this._elements = elements
    }

    forEachLatLng(callback: (LatLng) => void): void {
      this._elements.forEach(element => {
        element.elements.forEach(element => callback(element))
      })
    }

    getArray(): LinearRing[] {
      return this._elements
    }

    getAt(n: number): LinearRing {
      return this._elements[n]
    }

    getLength(): number {
      return this._elements.length
    }

    getType(): string {
      return "Polygon"
    }

  }

  export class MultiPolygon implements Geometry {

    private readonly _elements: Polygon[]

    constructor(elements: Data.Polygon[]) {
      if (elements == null) {
        throw new Error("Parameter elements can not be null")
      }
      this._elements = elements
    }

    forEachLatLng(callback: (LatLng) => void): void {
      this._elements.forEach(polygon => polygon.forEachLatLng(callback))
    }

    getAt(n: number): Data.Polygon {
      if (n < 0 || n >= this._elements.length) {
        throw new Error("Index out of size")
      }
      return this._elements[n]
    }

    getLength(): number {
      return this._elements.length
    }

    getType(): string {
      return "MultiPolygon"
    }

  }

  export class MultiLineString implements Geometry {

    private readonly _elements: LineString[]

    constructor(elements: Data.LineString[]) {
      if (elements == null) {
        throw new Error("Parameter elements can not be null")
      }
      this._elements = elements
    }

    forEachLatLng(callback: (LatLng) => void): void {
      this._elements.forEach(polygon => polygon.forEachLatLng(callback))
    }

    getAt(n: number): Data.LineString {
      if (n < 0 || n >= this._elements.length) {
        throw new Error("Index out of size")
      }
      return this._elements[n]
    }

    getLength(): number {
      return this._elements.length
    }

    getType(): string {
      return "MultiLineString"
    }
  }

  export class Feature {

    private readonly _id: number | string

    private readonly _geometry: Geometry

    private readonly _properties: Map<string, any>

    constructor(id: number | string, geometry: Geometry, properties: Map<string, any>) {
      this._id = id
      this._geometry = geometry
      this._properties = properties
    }

    getId(): number | string {
      return this._id
    }

    getGeometry(): Geometry {
      return this._geometry
    }

    getProperties(): Map<string, any> {
      return this._properties
    }

  }

  export type FeatureOptions = {

    id : number | string

    geometry: Data.Geometry

    properties?: Map<string, any>
  }

}

export class MapData {

  private _mapView: MapView

  constructor(mapView: MapView) {
    this._mapView = mapView
  }

  addGeoJson(geoJson: string): Data.Feature[] {
    return this._mapView.getGeoJsonManager().addGeoJson(geoJson)
  }

  add(feature: Data.Feature | Data.FeatureOptions): Data.Feature {
    return this._mapView.getGeoJsonManager().addFeature(feature)
  }

  remove(feature: Data.Feature): void  {
    return this._mapView.getGeoJsonManager().removeFeature(feature)
  }

  clear(): void {
    return this._mapView.getGeoJsonManager().removeAllFeatures()
  }

  addListener(event: string | MapEvent, func: Function): MapsEventListener {
    return this._mapView.addGeoJsonListener(event, func)
  }

  setMinZoom(minZoom: number): void {
    if (minZoom == null) {
      throw Error("Parameter can not be null")
    }
    this._mapView.setDataLayerMinZoom(minZoom)
  }

  setMaxZoom(maxZoom: number): void {
    if (maxZoom == null) {
      throw Error("Parameter can not be null")
    }
    this._mapView.setDataLayerMaxZoom(maxZoom)
  }

  getMinZoom(): number {
    return this._mapView.getDataLayerMinZoom()
  }

  getMaxZoom(): number {
    return this._mapView.getDataLayerMaxZoom()
  }
}