import {Vector2} from "../engine/Vector2"
import {Vector3} from "../engine/Vector3"
import {ModelGeometry} from "../building/data/ModelGeometry"

export class ObjModelLoader {
  static loadModel(source: string, useArrayBuffer: boolean = false): ModelGeometry[] {
    if (source.length == 0) {
      return null
    }
    let lines = source.split("\n")
      .map((line: string) => line.trim().toLowerCase().split(" ")
        .filter(e => e !== ""))

    /** data make this model */
    let positions: Vector3[] = [new Vector3()]
    let normals: Vector3[] = [new Vector3()]
    let texCoords: Vector2[] = [new Vector2()]

    let geometries: ModelGeometry[] = []
    let geometry: ModelGeometry = null
    let geometryData = []
    let highestHeight = -1
    let highestPosition = null

    let minX = Number.MAX_VALUE
    let minY = Number.MAX_VALUE
    let minZ = Number.MAX_VALUE
    let maxX = Number.MIN_VALUE
    let maxY = Number.MIN_VALUE
    let maxZ = Number.MIN_VALUE

    for (let line of lines) {
      let type = line[0]

      if (type == "v") {
        let x = +line[1] || 0
        let y = +line[2] || 0
        let z = +line[3] || 0
        let position = new Vector3(x, y, z)
        positions.push(position)
        if (y > highestHeight) {
          highestHeight = y
          highestPosition = position
        }
        minX = Math.min(minX, x)
        maxX = Math.max(maxX, x)

        // After rotate x axis 90 => y and z is exchanged
        minY = Math.min(minY, z)
        maxY = Math.max(maxY, z)
        minZ = Math.min(minZ, y)
        maxZ = Math.max(maxZ, y)
      }
      else if (type == "vn") {
        let x = +line[1] || 0
        let y = +line[2] || 0
        let z = +line[3] || 0
        normals.push(new Vector3(x, y, z))
      }
      else if (type == "vt") {
        let x = +line[1] || 0
        let y = +line[2] || 0

        texCoords.push(new Vector2(x, y))
      }
      else if (type == "f") {
        for (let i = 0; i < 3; i++) {
          let vertexInfo = line[i + 1].split("/")
          let position = positions[+vertexInfo[0] || 0]
          let texCoord = texCoords[+vertexInfo[1] || 0]
          let normal = normals[+vertexInfo[2] || 0]
          if (!geometry) {
            geometry = new ModelGeometry()
            geometries.push(geometry)
          }

          geometryData.push(position.x)
          geometryData.push(position.y)
          geometryData.push(position.z)

          geometryData.push(texCoord.x)
          geometryData.push(texCoord.y)

          geometryData.push(normal.x)
          geometryData.push(normal.y)
          geometryData.push(normal.z)

          geometry.numberOfVertices++
        }
      }
    }
    if (useArrayBuffer) {
      geometries[0].data = new Float32Array(geometryData).buffer
    }
    else {
      geometries[0].data = geometryData
    }
    geometries[0].highestPosition = highestPosition
    geometries[0].minX = minX
    geometries[0].minY = minY
    geometries[0].minZ = minZ

    geometries[0].maxX = maxX
    geometries[0].maxY = maxY
    geometries[0].maxZ = maxZ
    return geometries
  }
}
