import {ILatLng, LatLng} from "./LatLng"
import {SphericalUtil} from "../util/SphericalUtil"

export class Measure {

  private _path: LatLng[] = []

  constructor(path: ILatLng[]) {
    this._path = path.map(p => LatLng.convert(p))
  }

  setPath(path: ILatLng[]): void {
    this._path = path.map(p => LatLng.convert(p))
  }

  addPath(path: ILatLng[]): void {
    path.forEach(p => this._path.push(LatLng.convert(p)))
  }

  get length(): number {
    let length = 0
    for (let i = 0; i < this._path.length - 1; ++i) {
      length += SphericalUtil.computeDistanceBetween(this._path[i], this._path[i + 1])
    }
    return length
  }

  get perimeter(): number {
    if (this._path.length < 3) {
      return 0
    }
    let perimeter = this.length
    perimeter += SphericalUtil.computeDistanceBetween(this._path[0], this._path[this._path.length - 1])
    return perimeter
  }

  get area(): number {
    return SphericalUtil.computeArea(this._path)
  }

  get center(): LatLng {
    if (this._path.length == 0) {
      return null
    }
    let center = new LatLng(0, 0)
    for (let i = 0; i < this._path.length; ++i) {
      center.lat += this._path[i].lat
      center.lng += this._path[i].lng
    }
    center.lat /= this._path.length
    center.lng /= this._path.length
    return center
  }
}