/**
 * Refer from http://svn.webkit.org/repository/webkit/trunk/Source/WebCore/platform/graphics/UnitBezier.h
 */
export class UnitBezier {
  ax: number;

  bx: number;

  cx: number;

  ay: number;

  by: number;

  cy: number;

  constructor(p1x: number, p1y: number, p2x: number, p2y: number) {

    /** Calculate the polynomial coefficients, implicit first and last control points are (0,0) and (1,1). */
    this.cx = 3.0 * p1x
    this.bx = 3.0 * (p2x - p1x) - this.cx
    this.ax = 1.0 - this.cx - this.bx

    this.cy = 3.0 * p1y
    this.by = 3.0 * (p2y - p1y) - this.cy
    this.ay = 1.0 - this.cy - this.by
  }

  sampleCurveX(t: number): number {

    /** `ax t^3 + bx t^2 + cx t' expanded using Horner's rule. */
    return ((this.ax * t + this.bx) * t + this.cx) * t
  }

  sampleCurveY(t: number): number {
    return ((this.ay * t + this.by) * t + this.cy) * t
  }

  sampleCurveDerivativeX(t: number) {
    return (3.0 * this.ax * t + 2.0 * this.bx) * t + this.cx
  }

  /** Given an x value, find a parametric value it came from. */
  solveCurveX(x: number, epsilon: number): number {
    let t0: number
    let t1: number
    let t2: number
    let x2: number
    let d2: number
    let i: number

    /** First try a few iterations of Newton's method -- normally very fast. */
    for (t2 = x, i = 0; i < 8; ++i) {
      x2 = this.sampleCurveX(t2) - x
      if (Math.abs(x2) < epsilon)
        return t2
      d2 = this.sampleCurveDerivativeX(t2)
      if (Math.abs(d2) < 1e-6)
        break
      t2 = t2 - x2 / d2
    }

    /** Fall back to the bisection method for reliability. */
    t0 = 0.0
    t1 = 1.0
    t2 = x

    if (t2 < t0)
      return t0
    if (t2 > t1)
      return t1

    while (t0 < t1) {
      x2 = this.sampleCurveX(t2)
      if (Math.abs(x2 - x) < epsilon)
        return t2
      if (x > x2)
        t0 = t2
      else
        t1 = t2
      t2 = (t1 - t0) * .5 + t0
    }

    // Failure.
    return t2
  }

  solve(x: number, epsilon: number): number {
    return this.sampleCurveY(this.solveCurveX(x, epsilon))
  }
}
