import {AnnotationId} from "./Annotation"
import {Marker} from "../public/Marker"
import {Icon} from "../public/Icon"
import {MapUtils} from "../util/MapUtils"
import {SymbolResourceManager} from "./SymbolResourceManager"
import {MarkerLabel} from "../public/MarkerLabel"
import {MapView} from "../map/MapView"
import {LayerType} from "../layer/Layer"
import {AnnotationManager} from "./AnnotationManager"
import {SymbolLayer} from "../layer/SymbolLayer"
import {TextLabel} from "../text/TextLabel"
import {TextLabelFactory} from "../text/TextLabelFactory"
import {UrlUtils} from "../util/UrlUtils"
import {MapConstants} from "../map/MapConstants"

export class SymbolAnnotationData {

  private static readonly DEFAULT_SYMBOL_WIDTH = 32

  private static readonly DEFAULT_SYMBOL_HEIGHT = 32

  private readonly _id: AnnotationId

  private readonly _symbolAnnotation: Marker

  private _url: string

  private _width: number

  private _height: number

  // Rbush properties
  private _minX: number

  private _minY: number

  private _maxX: number

  private _maxY: number

  private _gl: WebGLRenderingContext

  constructor(gl: WebGLRenderingContext, annotationId: AnnotationId, symbolAnnotation: Marker) {
    this._gl = gl
    this._id = annotationId
    this._symbolAnnotation = symbolAnnotation
    this.updateBounds()
    this.updateIcon()
  }

  updateLayer(map: MapView): void {
    let layerId = AnnotationManager.pointLayerId + this._id
    let layer = map.getLayer(layerId)
    if (layer == null) {
      layer = map.createLayer(layerId, LayerType.SYMBOL, this._symbolAnnotation.getZIndex())
    }
    let symbolLayer = layer as SymbolLayer
    symbolLayer.anchor = this._symbolAnnotation.getAnchor()
    symbolLayer.position = this._symbolAnnotation.getPosition()
    symbolLayer.width = this._width ? this._width : -1
    symbolLayer.height = this._height ? this._height : -1
    symbolLayer.elevation = this._symbolAnnotation.getElevation()
    symbolLayer.labelAnchor = this._symbolAnnotation.getLabelAnchor()
    symbolLayer.rotation = this._symbolAnnotation.getRotation()
    symbolLayer.url = this._url

    let makerLabel = this._symbolAnnotation.getLabel()
    if (symbolLayer.textLabelData) {
      symbolLayer.textLabelData.destroy()
      symbolLayer.textLabelData = null
    }
    if (makerLabel) {
      symbolLayer.markerLabel =
        makerLabel instanceof MarkerLabel ? makerLabel : new MarkerLabel({text: makerLabel})
      symbolLayer.textLabelData =
        TextLabelFactory.getInstance(this._gl).createText(
          new TextLabel(symbolLayer.markerLabel.getText(), symbolLayer.markerLabel.getFontSize()),
          symbolLayer.markerLabel.getFontWeight(),
          symbolLayer.markerLabel.getMaxWidth(),
          symbolLayer.markerLabel.getLine())
    }
  }

  destroy(): void {
    SymbolResourceManager.getInstance(this._gl).release(this._url)
  }

  updateBounds(): void {
    let position = this._symbolAnnotation.getPosition()
    this._minX = position.lng
    this._minY = position.lat
    this._maxX = position.lng
    this._maxY = position.lat
  }

  updateAnnotationData(): void {
    let position = this._symbolAnnotation.getPosition()
    if (position.lat != this._minY || position.lng != this._minX) {
      this.updateBounds()
    }
    else {
      this.updateIcon()
    }
  }

  get id(): AnnotationId {
    return this._id
  }

  get symbolAnnotation(): Marker {
    return this._symbolAnnotation
  }

  get width(): number {
    return this._width
  }

  get height(): number {
    return this._height
  }

  get minX(): number {
    return this._minX
  }

  get minY(): number {
    return this._minY
  }

  get maxX(): number {
    return this._maxX
  }

  get maxY(): number {
    return this._maxY
  }

  private updateIcon(): void {
    this._width = null
    this._height = null
    let symbolResourceManager = SymbolResourceManager.getInstance(this._gl)
    let icon = this._symbolAnnotation.getIcon()
    let previousUrl = this._url
    if (icon instanceof Icon) {
      this._url = icon.getUrl()
      this._width = icon.getWidth() * MapUtils.pixelScale
      this._height = icon.getHeight() * MapUtils.pixelScale
    }
    else {
      this._url = icon
    }
    if (!UrlUtils.isUrlRequest(this._url) && (this._width == null || this._height == null)) {
      this._width = SymbolAnnotationData.DEFAULT_SYMBOL_WIDTH * MapUtils.pixelScale
      this._height = SymbolAnnotationData.DEFAULT_SYMBOL_HEIGHT * MapUtils.pixelScale
    }

    symbolResourceManager.acquire(this._url)
    if (previousUrl != null && previousUrl !== this._url) {
      symbolResourceManager.release(previousUrl)
    }
  }
}
