import {TileCoordinate} from "./TileCoordinate"
import {Texture} from "../engine/Texture"

export enum Kind {
  RASTER,
  ANNOTAION,
  VECTOR
}

export abstract class Tile {

  kind: Kind;

  renderable: boolean = false;

  tileCoordinate: TileCoordinate;

  gl: WebGLRenderingContext;

  protected constructor(kind: Kind, tileCoordinate: TileCoordinate, gl: WebGLRenderingContext) {
    this.kind = kind
    this.tileCoordinate = tileCoordinate
    this.gl = gl
  }

  abstract init(tileData: Blob, initCallBack: Function): void

  abstract getTexture(): Texture

  isRenderable(): boolean {
    return this.renderable
  }

  getKind(): Kind {
    return this.kind
  }

  getTileCoordinate(): TileCoordinate {
    return this.tileCoordinate
  }

  abstract destroy(): void
}
