import {ILatLng, LatLng} from "./LatLng"
import {CameraPosition} from "./CameraPosition"
import {Building} from "../building/Building"
import {Map4D} from "./Map"
import {MapUtils} from "../util/MapUtils"
import {MapObjectOptions} from "./options/MapObjectOptions"
import {MapObjectData} from "./MapObjectData"
// @ts-ignore
import * as cover from "@mapbox/tile-cover"
import {Vector3} from "../engine/Vector3"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {Vector2} from "../engine/Vector2"
import {Color} from "../engine/Color"
import {BuildingType} from "../building/BuildingType"
import {Point} from "./Point"
import {BuildingExtrudeUtils} from "../util/BuildingExtrudeUtils"

/*eslint camelcase: "off"*/

export class MapObject {

  private id: string

  private name: string

  private places: string[]

  private scale: number

  private bearing: number

  private location: LatLng

  private elevation: number

  private height: number

  private camera: CameraPosition

  private types: string[]

  private minZoom: number

  private maxZoom: number

  private startDate: Date

  private endDate: Date

  private obj: string | MapObjectData

  private texture: string | MapObjectData

  private coordinates: LatLng[] = []

  private draggable: boolean

  private map: Map4D

  onModelLoaded: Function

  private static readonly defaultMapObjectOptions = {
    name: "Unknown name",
    scale: 1,
    bearing: 0,
    elevation: 0,
    height: 1,
    obj: null,
    texture: null,
    coordinates: [],
    draggable: false
  }

  constructor(options?: MapObjectOptions) {
    if (options != null) {
      let mapObjectOptions: any = {}
      MapUtils.extendOptions(mapObjectOptions, MapObject.defaultMapObjectOptions, options)
      this.id = mapObjectOptions.id
      if (this.id == null) {
        throw Error("Id can not be null")
      }
      this.name = mapObjectOptions.name
      this.scale = mapObjectOptions.scale
      this.bearing = mapObjectOptions.bearing
      this.elevation = mapObjectOptions.elevation
      this.height = mapObjectOptions.height
      this.obj = mapObjectOptions.obj
      this.texture = mapObjectOptions.texture
      this.draggable = mapObjectOptions.draggable
      if (mapObjectOptions.location) {
        this.location = LatLng.convert(mapObjectOptions.location)
      }
      if (!this.obj) {
        if (mapObjectOptions.coordinates.length >= 2) {
          let centerLat = 0
          let centerLng = 0
          mapObjectOptions.coordinates.forEach(coordinate => {
            let latlng = LatLng.convert(coordinate)
            this.coordinates.push(latlng)

            centerLat += latlng.lat
            centerLng += latlng.lng
          })
          centerLat /= this.coordinates.length
          centerLng /= this.coordinates.length
          this.location = new LatLng(centerLat, centerLng)
          if (mapObjectOptions.location) {
            let location = LatLng.convert(mapObjectOptions.location)
            let translateVectorX = location.lng - this.location.lng
            let translateVectorY = location.lat - this.location.lat
            this.location = location
            let newCoordinates = []
            this.coordinates.forEach(coordinate => {
              newCoordinates.push(new LatLng(coordinate.lat + translateVectorY,
                coordinate.lng + translateVectorX))
            })
            this.coordinates = newCoordinates
          }
        }
        else {
          console.warn("Coordinates size must be greater than 2")
        }
      }
      this.onModelLoaded = null
    }
  }

  initWithBuilding(buildingObject: Building) {
    this.id = buildingObject.getId()
    this.name = buildingObject.getName()
    this.places = buildingObject.getPlaces()
    this.scale = buildingObject.getScale().x
    this.bearing = buildingObject.getBearing()
    this.location = buildingObject.getLocation()
    this.elevation = buildingObject.getElevation()
    this.height = buildingObject.getHeight()
    this.camera = buildingObject.getCamera()
    this.types = buildingObject.getTypes()
    this.minZoom = buildingObject.getMinZoom()
    this.maxZoom = buildingObject.getMaxZoom()
    this.startDate = new Date(buildingObject.getStartDate())
    this.endDate = new Date(buildingObject.getEndDate())
    let textureData =  buildingObject.getTextureData()
    this.texture = textureData ? {data: textureData} : buildingObject.getTextureName()
    let objData = buildingObject.getObjData()
    this.obj = objData ? {data: objData} : buildingObject.getObjName()
    this.coordinates = buildingObject.getCoordinates()
    this.draggable = buildingObject.isDraggable()
  }

  getId(): string {
    return this.id
  }

  getName(): string {
    return this.name
  }

  getPlaces(): string[] {
    return (this.places || []).slice()
  }

  getScale(): any {
    return this.scale
  }

  getBearing(): number {
    return this.bearing
  }

  getLocation(): LatLng{
    return this.location
  }

  getElevation(): number {
    return this.elevation
  }

  getHeight(): number {
    return this.height
  }

  getCamera(): CameraPosition {
    return this.camera == null ? null :
      new CameraPosition(
        this.camera.getTarget(),
        this.camera.getTilt(),
        this.camera.getBearing(),
        this.camera.getZoom())
  }

  getTypes(): string[] {
    return (this.types || []).slice()
  }

  getMaxZoom(): number{
    return this.maxZoom
  }

  getStartDate(): Date {
    return this.startDate != null ? new Date(this.startDate) : null
  }

  getEndDate(): Date {
    return new Date(this.endDate)
  }

  getTexture(): string | MapObjectData {
    if (this.texture == null) {
      return null
    }
    if (typeof this.texture === "string") {
      return this.texture
    }
    else {
      return {reuseId: this.texture.reuseId, data: this.texture.data}
    }
  }

  getObj(): string | MapObjectData {
    if (this.obj == null){
      return null
    }
    if (typeof this.obj === "string") {
      return this.obj
    }
    else {
      return {reuseId: this.obj.reuseId, data: this.obj.data}
    }
  }

  getCoordinates(): LatLng[] {
    return (this.coordinates || []).slice()
  }

  isDraggable(): boolean {
    return this.draggable
  }

  getMap(): Map4D {
    return this.map
  }

  setMap(map: Map4D): void {
    if (map && !this.obj && this.coordinates.length <= 2) {
      console.error("Cannot add object to the map without any model information")
      return
    }
    if (map && this.coordinates.length == 0 && !this.location) {
      console.error("Cannot add object to the map without knowing object's position")
      return
    }
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeMapObject(this.id)
    }
    this.map = map
    if (this.map != null) {
      this.map.mapView.addMapObject(this)
    }
  }

  setLocation(location: ILatLng): void {
    if (location == null) {
      console.warn("Parameter location can not be null")
      return
    }
    let newLocation = LatLng.convert(location)
    if (this.location != null && newLocation.equals(this.location)) {
      return
    }
    if (this.map == null) {
      this.location = new LatLng(newLocation.lat, newLocation.lng)
      return
    }
    this.map.mapView.getCustomBuildingManager().updateMapObjectLocation(this.id, newLocation)
    let building = this.map.mapView.getBuildingResourceManager().getBuilding(this.id)
    building && this.map.mapView.getBuildingIconSelectionManager().updateBuilding(building)
    this.map.mapView.dirty = true
  }

  setScale(scale: number): void {
    if (scale == null) {
      console.warn("Parameter scale can not be null")
      return
    }
    if (scale <= 0) {
      console.warn("Parameter scale can not be less than 0")
      return
    }
    if (scale == this.scale) {
      return
    }
    this.scale = scale
    if (this.map == null) {
      return
    }
    let building = this.map.mapView.getBuildingResourceManager().getBuilding(this.id)
    if (building) {
      let buildingType = building.getBuildingType()
      let isExtrude = buildingType == BuildingType.ExternalExtrude || buildingType == BuildingType.Extrude
      if (isExtrude) {
        building.setScale(new Vector3(scale, scale, scale * this.height))
      }
      else {
        building.setScale(new Vector3(scale, scale, scale))
        this.map.mapView.getBuildingResourceManager().updateBuildingHeight(building)
      }

      this.map.mapView.getCustomBuildingManager().updateMapObjectScale(this.id)
      this.map.mapView.getBuildingIconSelectionManager().updateBuilding(building)
      this.map.mapView.dirty = true
    }
  }

  setTexture(texture: string | MapObjectData): void {
    if (texture == null) {
      console.warn("Texture data could not be null")
      return
    }
    if (this.texture === texture) {
      return
    }

    this.texture = texture

    if (this.map == null) {
      return
    }

    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building == null) {
      return
    }
    if (building.getBuildingType() == BuildingType.ExternalExtrude || building.getBuildingType() == BuildingType.Extrude) {
      if (!(typeof texture === "string") || !Color.isValid(texture)) {
        console.warn("Extrude Building only support hex color")
        return
      }
    }

    if (typeof this.texture === "string") {
      if (Color.isValid(this.texture)) {
        buildingResourceManager.setColor(this.id, this.texture)
      }
      else {
        buildingResourceManager.setTextureName(this.id, this.texture)
      }
    }
    else {
      buildingResourceManager.setTextureData(this.id, this.texture)
    }
    buildingResourceManager.setBuildingLayerDirty()
    this.map.mapView.dirty = true
    this.map.mapView.getCustomBuildingManager().updateBuildingDataForTiles(this.id)
  }

  setObj(obj: string | MapObjectData): void {
    if (obj == null) {
      console.warn("Model data could not be null")
      return
    }
    if (this.obj === obj) {
      return
    }
    if (this.map == null) {
      this.obj = obj
      this.coordinates = []
      return
    }
    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building == null) {
      return
    }

    let map = this.map
    this.setMap(null)
    this.obj = obj
    this.coordinates = []
    if (building.getBuildingType() == BuildingType.ExternalExtrude || building.getBuildingType() == BuildingType.Extrude) {
      building.setBuildingType(BuildingType.Texture)
    }
    this.setMap(map)
    buildingResourceManager.setBuildingLayerDirty()
    this.map.mapView.dirty = true
  }

  setElevation(elevation: number): void {
    if (elevation == null) {
      console.warn("Parameter elevation can not be null")
      return
    }
    if (elevation == this.elevation) {
      return
    }
    this.elevation = elevation
    if (this.map == null) {
      return
    }
    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building) {
      building.setElevation(elevation)
      this.map.mapView.getBuildingIconSelectionManager().updateBuilding(building)
      buildingResourceManager.setBuildingLayerDirty()
      this.map.mapView.dirty = true
    }
    this.map.mapView.getCustomBuildingManager().updateBuildingDataForTiles(this.id)
  }

  setHeight(height: number): void {
    if (height == null) {
      console.warn("Parameter height can not be null")
      return
    }
    if (this.coordinates == null || this.coordinates.length == 0) {
      console.warn("Object with Obj model is unsupported this action")
      return
    }
    this.height = height
    if (this.map == null || this.obj) {
      return
    }
    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building) {
      building.setHeight(this.height)
      let scale = building.getScale().x
      building.setScale(new Vector3(scale, scale, scale * this.height))
      this.map.mapView.getBuildingIconSelectionManager().updateBuilding(building)
      buildingResourceManager.setBuildingLayerDirty()
      this.map.mapView.dirty = true
    }
    this.map.mapView.getCustomBuildingManager().updateBuildingDataForTiles(this.id)
  }

  setBearing(bearing: number): void {
    if (bearing == null) {
      console.warn("Parameter bearing can not be null")
      return
    }
    if (bearing == this.bearing) {
      return
    }
    this.bearing = bearing
    if (this.map == null) {
      return
    }
    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building) {
      building.setBearing(this.bearing)
      buildingResourceManager.setBuildingLayerDirty()
      this.map.mapView.dirty = true
    }
    this.map.mapView.getCustomBuildingManager().updateBuildingDataForTiles(this.id)
  }

  setName(name: string): void {
    if (name == null) {
      console.warn("Parameter name can not be null")
      return
    }
    if (this.name == name) {
      return
    }
    this.name = name
    if (this.map == null) {
      return
    }
    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building) {
      building.setName(this.name)
      buildingResourceManager.setBuildingLayerDirty()
      this.map.mapView.getBuildingIconSelectionManager().updateBuilding(building)
      this.map.mapView.dirty = true
    }
    this.map.mapView.getCustomBuildingManager().updateBuildingDataForTiles(this.id)
  }

  setCoordinates(coordinates: ILatLng[]): void {
    if (coordinates == null || coordinates.length <= 2) {
      console.warn("Coordinates size must be greater than 2")
      return
    }

    let centerLat = 0
    let centerLng = 0
    this.coordinates = []
    coordinates.forEach(coordinate => {
      let latLng = LatLng.convert(coordinate)
      this.coordinates.push(latLng)
      centerLat += latLng.lat
      centerLng += latLng.lng
    })
    centerLat /= this.coordinates.length
    centerLng /= this.coordinates.length
    this.location = new LatLng(centerLat, centerLng)

    let changeModelType = false
    if (this.obj) {
      this.obj = null
      changeModelType = true
    }
    if (this.map == null) {
      return
    }
    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building == null) {
      console.warn("Can not find this building to setCoordinates !")
      return
    }
    let buildingGeometry: Point[][] = []
    let polygon: Point[] = []
    let locationInMeters = ProjectionMercator.convertToMeterFromLatLng(this.location)
    coordinates.forEach(coordinate => {
      let latLng = LatLng.convert(coordinate)
      let pointInMeters = ProjectionMercator.convertToMeterFromLatLng(latLng)
      polygon.push(new Point(pointInMeters.x - locationInMeters.x,
        locationInMeters.y - pointInMeters.y))
    })
    buildingGeometry.push(polygon)
    let modelExtrude = BuildingExtrudeUtils.extrudePolygon(buildingGeometry, 1)
    buildingResourceManager.setCoordinates(this.id, this.coordinates, modelExtrude)

    if (!changeModelType) {
      this.map.mapView.getCustomBuildingManager().updateMapObjectCoordinates(this.id, this.coordinates)
      building && this.map.mapView.getBuildingIconSelectionManager().updateBuilding(building)
      this.map.mapView.dirty = true
    }
    else {
      let map = this.map
      this.setMap(null)
      if (building.getBuildingType() != BuildingType.ExternalExtrude && building.getBuildingType() != BuildingType.Extrude) {
        building.setBuildingType(BuildingType.ExternalExtrude)
      }
      this.setMap(map)
    }
  }

  setDraggable(draggable: boolean): void {
    if (draggable == null) {
      console.warn("Parameter draggable can not be null")
      return
    }
    if (this.draggable === draggable) {
      return
    }
    this.draggable = draggable
    if (this.map == null) {
      return
    }
    let buildingResourceManager = this.map.mapView.getBuildingResourceManager()
    let building = buildingResourceManager.getBuilding(this.id)
    if (building) {
      building.setDraggable(draggable)
    }
    this.map.mapView.getCustomBuildingManager().updateBuildingDataForTiles(this.id)
  }

  getTileCovers(zooms: number[], completion: Function): void {
    if (!completion) {
      return
    }
    if (!this.map) {
      console.warn("This Object was not added to Map or it is not referenced to Object in Map")
      completion([])
      return
    }
    let customBuildingManager = this.map.mapView.getCustomBuildingManager()
    let mapObjects = customBuildingManager.getMapObjects()
    let indexedMapObject = mapObjects.get(this.id)

    let getTileCovers = () => {
      let tileCovers = []
      for (let i = 0; i < zooms.length; ++i) {
        let coverTiles = cover.tiles(customBuildingManager.createFeatureData(indexedMapObject), {
          min_zoom: zooms[i],
          max_zoom: zooms[i]
        })
        coverTiles && coverTiles.forEach(coverTile => {
          tileCovers.push({x: coverTile[0], y: coverTile[1], z:  coverTile[2]})
        })
      }
      completion(tileCovers)
    }

    if (indexedMapObject.hasBounds) {
      getTileCovers()
      this.onModelLoaded = null
    }
    else {
      this.onModelLoaded = getTileCovers
    }
  }

  getTransformedCoordinates(): LatLng[] {
    console.warn(`This function is deprecated and will be removed in the future. 
    Please use method getBoundsCoordinates(completion: Function) instead.`)
    return this.getCoverCoordinates()
  }

  getCoverCoordinates(): LatLng[] {
    let positionInMeters = ProjectionMercator.convertToMeterFromLatLng(this.location)
    let coverCoordinates: LatLng[] = []
    let bearing = this.bearing * Math.PI / 180.0
    this.coordinates.forEach(coordinate => {
      let point = ProjectionMercator.convertToMeterFromLatLng(coordinate)
      point.x -= positionInMeters.x
      point.y -= positionInMeters.y
      if (bearing != 0) {
        let x = point.x
        let y = point.y
        point.x = (x * Math.cos(bearing) - y * Math.sin(bearing)) * this.scale
        point.y = (x * Math.sin(bearing) + y * Math.cos(bearing)) * this.scale
      }
      else {
        point.x = point.x * this.scale
        point.y = point.y * this.scale
      }
      let pointInLatLng = ProjectionMercator.convertToLatLngFromMeter(
        new Vector2(positionInMeters.x + point.x, positionInMeters.y + point.y))
      coverCoordinates.push(pointInLatLng)
    })
    return coverCoordinates
  }

  getBoundsCoordinates(completion: Function): void {
    if (!completion) {
      return
    }
    if (!this.map) {
      console.warn("This Object was not added to Map or it is not referenced to Object in Map")
      completion([])
      return null
    }
    let customBuildingManager = this.map.mapView.getCustomBuildingManager()
    let mapObjects = customBuildingManager.getMapObjects()
    let indexedMapObject = mapObjects.get(this.id)

    let getConvexHull = () => {
      let coverPoints = customBuildingManager.getBoundsCoordinates(indexedMapObject)
      completion(coverPoints)
    }

    if (indexedMapObject.hasBounds) {
      getConvexHull()
      this.onModelLoaded = null
    }
    else {
      this.onModelLoaded = getConvexHull
    }
  }
}
