import {Vector3} from "../../engine/Vector3"

export class ModelGeometry {

  /**
   * number of vertices
   * @type {number}
   */
  numberOfVertices = 0

  /**
   * data to render this geometry
   **/
  data: ArrayBuffer | number[]

  /**
   * indices of the geometries - if any
   */
  indices: ArrayBuffer | number[]

  /**
   * The highest position of the model
   */
  highestPosition: Vector3

  minX: number

  maxX: number

  minY: number

  maxY: number

  minZ: number

  maxZ: number

}
