import {Kind, Tile} from "./Tile"
import {Texture} from "../engine/Texture"
import {TileCoordinate} from "./TileCoordinate"
import {GeometryTileData} from "./data/GeometryTileData"
import {LayerType} from "../layer/Layer"
import {LayerData} from "../renderer/data/LayerData"
import {LineLayerData} from "../renderer/data/LineLayerData"
import {FillLayerData} from "../renderer/data/FillLayerData"
import {AnnotationManager} from "../annotation/AnnotationManager"
import {GeoJsonLayerData} from "../renderer/data/GeoJsonLayerData"

export abstract class GeometryTile extends Tile {

  tileData: GeometryTileData

  layerDatas: Map<string, LayerData>

  gl: WebGLRenderingContext

  reloadLayout: boolean

  protected constructor(kind: Kind, tileCoordinate: TileCoordinate, gl: WebGLRenderingContext) {
    super(kind, tileCoordinate, gl)
    this.gl = gl
    this.layerDatas = new Map<string, LayerData>()
    this.tileData = null
  }

  getTexture(): Texture {
    return null
  }

  init(tileData: Blob, initCallBack: Function): void {
  }

  setData(tileData: GeometryTileData): void {
    this.tileData = tileData

    // Only remove point layer data
    if (tileData != null) {
      tileData.getLayerIds().forEach(layerId => {
        if (layerId.startsWith(AnnotationManager.pointLayerId)) {
          this.removeLayerData(layerId)
        }
      })
    }
  }

  hasData(name: string): boolean {
    return this.tileData != null && this.tileData.getLayer(name) != null
  }

  removeLayerData(name: string): void {
    let layerData = this.layerDatas.get(name)
    if (layerData) {
      layerData.destroy()
      this.layerDatas.delete(name)
    }
  }

  getLayerData(name: string, layerType: LayerType, params: any = null): LayerData {
    if (!this.layerDatas.has(name)) {

      /** Create new layer data */
      let layer = this.tileData.getLayer(name)
      if (layer == null) {

        /** not reachable */
        console.error("getLayerData not reachable")
        return null
      }
      switch (layerType) {
      case LayerType.LINE: {
        let lineLayerData = new LineLayerData(layer, this.gl, params)
        this.layerDatas.set(name, lineLayerData)
        return this.layerDatas.get(name)
      }
      case LayerType.FILL: {
        let fillLayerData = new FillLayerData(layer, this.gl)
        this.layerDatas.set(name, fillLayerData)
        return this.layerDatas.get(name)
      }
      case LayerType.GEOJSON: {
        let geoJsonLayerData = new GeoJsonLayerData(layer, this.gl)
        this.layerDatas.set(name, geoJsonLayerData)
        return this.layerDatas.get(name)
      }
      default:

        /** Not reachable */
        console.error("getLayerData not reachable")
        return null
      }
    }
    return this.layerDatas.get(name)
  }

  getTileData(): GeometryTileData {
    return this.tileData
  }
}
