import {Vector2} from "../engine/Vector2"

export class Geometry2DUtils {
  static calculateDistanceBetweenPointToLineBy2Point(p: Vector2, lA: Vector2, lB: Vector2): number {
    return Math.sqrt(Geometry2DUtils.distToSegmentSquared(p, lA, lB))
  }

  static calculateDistanceBetweenPointToLine(p: Vector2, line: number[][]): number {
    return Math.sqrt(Geometry2DUtils.distToSegmentSquared(p, new Vector2(line[0][0], line[0][1]),
      new Vector2(line[1][0], line[1][1])))
  }

  private static distToSegmentSquared(p: Vector2, lA: Vector2, lB: Vector2): number {
    let l2 = Geometry2DUtils.dist2(lA, lB)
    if (l2 == 0) {
      return Geometry2DUtils.dist2(p, lA)
    }
    let t = ((p.x - lA.x) * (lB.x - lA.x) + (p.y - lA.y) * (lB.y - lA.y)) / l2
    t = Math.max(0, Math.min(1, t))
    return Geometry2DUtils.dist2(p, new Vector2(lA.x + t * (lB.x - lA.x), lA.y + t * (lB.y - lA.y)))
  }

  private static dist2(v: Vector2, w: Vector2): number {
    return Geometry2DUtils.sqr(v.x - w.x) + Geometry2DUtils.sqr(v.y - w.y)
  }

  private static sqr(x: number): number {
    return x * x
  }
}
