import {LatLng} from "../public/LatLng"
import {MathUtils} from "../engine/MathUtils"
import {MapConstants} from "../map/MapConstants"

/**
 * Refer from https://github.com/NotWoods/spherical-geometry-js
 */
export class SphericalUtil {

  private constructor() {
  }

  private static computeDistanceBetweenHelper(from: LatLng, to: LatLng): number {
    const radFromLat = MathUtils.toRadians(from.lat)
    const radFromLng = MathUtils.toRadians(from.lng)
    const radToLat = MathUtils.toRadians(to.lat)
    const radToLng = MathUtils.toRadians(to.lng)
    return (
      2 *
      Math.asin(
        Math.sqrt(
          Math.pow(Math.sin((radFromLat - radToLat) / 2), 2) +
          Math.cos(radFromLat) *
          Math.cos(radToLat) *
          Math.pow(Math.sin((radFromLng - radToLng) / 2), 2)
        )
      )
    )
  }

  private static sphericalExcess(a, b, c) {
    const polygon = [a, b, c, a]
    const distances = []
    let sumOfDistances = 0
    for (let i = 0; i < 3; i++) {
      distances[i] = SphericalUtil.computeDistanceBetweenHelper(polygon[i], polygon[i + 1])
      sumOfDistances += distances[i]
    }

    const semiPerimeter = sumOfDistances / 2
    let tan = Math.tan(semiPerimeter / 2)
    for (let i = 0; i < 3; i++) {
      tan *= Math.tan((semiPerimeter - distances[i]) / 2)
    }
    return 4 * Math.atan(Math.sqrt(Math.abs(tan)))
  }

  private static sphericalSign(a: LatLng, b: LatLng, c: LatLng) {
    const matrix = [a, b, c].map(point => {
      const lat = MathUtils.toRadians(point.lat)
      const lng = MathUtils.toRadians(point.lng)
      return [
        Math.cos(lat) * Math.cos(lng),
        Math.cos(lat) * Math.sin(lng),
        Math.sin(lat),
      ]
    })

    return 0 <
    matrix[0][0] * matrix[1][1] * matrix[2][2] +
    matrix[1][0] * matrix[2][1] * matrix[0][2] +
    matrix[2][0] * matrix[0][1] * matrix[1][2] -
    matrix[0][0] * matrix[2][1] * matrix[1][2] -
    matrix[1][0] * matrix[0][1] * matrix[2][2] -
    matrix[2][0] * matrix[1][1] * matrix[0][2]
      ? 1
      : -1
  }

  private static computeSphericalExcess(a: LatLng, b: LatLng, c: LatLng) {
    return SphericalUtil.sphericalExcess(a, b, c) * SphericalUtil.sphericalSign(a, b, c)
  }

  private static computeSignedArea(path: LatLng[]): number {
    if (path.length < 3) {
      return 0
    }
    let totalArea = 0
    for (let i = 1; i < path.length - 1; ++i) {
      totalArea += SphericalUtil.computeSphericalExcess(path[0], path[i], path[i + 1])

    }
    return totalArea * MapConstants.radiusEarthMeters * MapConstants.radiusEarthMeters
  }

  static computeDistanceBetween(from: LatLng, to: LatLng): number {
    return SphericalUtil.computeDistanceBetweenHelper(from, to) * MapConstants.radiusEarthMeters
  }

  static computeArea(path: LatLng[]): number {
    return Math.abs(SphericalUtil.computeSignedArea(path))
  }

}