import {Vector2} from "../engine/Vector2"
import {Particle} from "./Particle"
import {MapState} from "../map/MapState"
import {ParticlePool} from "./ParticlePool"

export abstract class ParticleSystem {

  /**
   * Time between particle spawns in seconds
   */
  protected _frequency: number

  /**
   *  Maximum number of particles to keep alive at a time. If this limit is reached,
   *  no more particles will spawn until some have died
   */
  protected _maxParticles: number

  /**
   * The min speed value of all particles.
   */
  protected _minSpeed: number

  /**
   * The max speed value of all particles.
   */
  protected _maxSpeed: number

  /**
   * The min scale value of all particles.
   */
  protected _minScale: number

  /**
   * The max scale value of all particles.
   */
  protected _maxScale: number

  /**
   * The min rotation value of all particles in degree.
   */
  protected _minRotation: number

  /**
   * The max rotation value of all particles in degree.
   */
  protected _maxRotation: number

  /**
   * The min rotation speed of all particles degrees/s
   */
  protected _minRotationSpeed: number

  /**
   * The min rotation speed of all particles degrees/s
   */
  protected _maxRotationSpeed: number

  /**
   * Acceleration to apply to particles
   */
  protected _acceleration: Vector2

  /**
   * The minimum lifetime for a particle, in seconds.
   */
  protected _minLifetime: number

  /**
   * The maximum lifetime for a particle, in seconds.
   */
  protected _maxLifetime: number

  /**
   * The life time function for a particle, if this one is set then _minLifetime and _maxLifetime will be ignored
   */
  protected _lifetimeFunc: (Particle) => boolean

  /**
   * Extra data for use in custom particles.
   */
  protected _extraData: any

  /**
   * The custom update position function for all particles
   */
  private _updateFunc: (Particle, number) => void

  /**
   * The current number of active particles.
   */
  protected _particleCount: number

  /**
   * The world position of the emitter's owner.
   */
  protected _emitterPosition: Vector2

  /**
   * A rectangle relative to spawnPos to spawn particles inside.
   */
  protected _spawnRect: {width: number, height: number}

  /**
   * The timer for when to spawn particles in seconds, where numbers less than 0 mean that particles should be spawned.
   */
  protected _spawnTimer: number

  /**
   * The particles that are active and on the display list. This is the first particle in a
   * linked list.
   */
  protected _activeParticlesFirst: Particle

  /**
   * The particles that are active and on the display list. This is the last particle in a
   * linked list.
   */
  protected _activeParticlesLast: Particle

  /**
   * The particles that are not currently being used will be stored in there.
   */
  protected _particlePool: ParticlePool

  protected constructor(particlePool: ParticlePool) {
    this._particlePool = particlePool
    this._acceleration = null
    this._frequency = 1
    this._maxParticles = 1000
    this._minSpeed = 0
    this._maxSpeed = 0
    this._minScale = 1
    this._maxScale = 1
    this._minLifetime = 0
    this._maxLifetime = 0
    this._minRotation = 0
    this._maxRotation = 0
    this._minRotationSpeed = 0
    this._maxRotationSpeed = 0
    this._lifetimeFunc = null
    this._extraData = null
    this._updateFunc = null
    this._emitterPosition = new Vector2(0, 0)
    this._activeParticlesFirst = null
    this._activeParticlesLast = null
    this._spawnRect = null
    this._spawnTimer = 0
    this._particleCount = 0
  }

  public init(config: any): void {

    // clean up any existing particles
    this.cleanup()
    if (config.frequency) {
      this._frequency = config.frequency
    }
    if (config.maxParticles) {
      this._maxParticles = config.maxParticles > 0 ? config.maxParticles : 1000
    }
    let acceleration = config.acceleration
    if(acceleration && (acceleration.x || acceleration.y)) {
      this._acceleration = new Vector2(acceleration.x, acceleration.y)
    }
    if (config.minSpeed) {
      this._minSpeed = config.minSpeed
    }
    if (config.maxSpeed) {
      this._maxSpeed = config.maxSpeed
    }
    if (config.minScale) {
      this._minScale = config.minScale
    }
    if (config.maxScale) {
      this._maxScale = config.maxScale
    }
    if (config.maxRotation) {
      this._maxRotation = config.maxRotation
    }
    if (config.minRotation) {
      this._minRotation = config.minRotation
    }
    if (config.minRotationSpeed) {
      this._minRotationSpeed = config.minRotationSpeed
    }
    if (config.maxRotationSpeed) {
      this._maxRotationSpeed = config.maxRotationSpeed
    }
    if (config.minLifetime) {
      this._minLifetime = config.minLifetime
    }
    if (config.maxLifetime) {
      this._maxLifetime = config.maxLifetime
    }
    if (config.extraData) {
      this._extraData = config.extraData
    }
    if (config.updateFunc) {
      this._updateFunc = config.updateFunc
    }
    if (config.lifetimeFunc) {
      this._lifetimeFunc = config.lifetimeFunc
    }
    if (config.spawnRect) {
      this._spawnRect = config.spawnRect
    }
    this._spawnTimer = 0
  }

  public update(delta: number) {

    // update existing particles
    let particle, next
    for (particle = this._activeParticlesFirst; particle; particle = next) {
      next = particle.next
      particle.update(delta)
    }

    // spawn new particles
    // decrease spawn timer
    this._spawnTimer -=  delta < 0 ? 0 : delta

    // while _spawnTimer < 0, we have particles to spawn
    while(this._spawnTimer <= 0) {

      // determine if we have hit the particle limit
      if (this._particleCount >= this._maxParticles) {
        this._spawnTimer += this._frequency
        continue
      }

      // determine the particle lifetime
      let lifetime
      if (this._lifetimeFunc) {

        // Lifetime will be ignored
        lifetime = 0
      }
      else {
        lifetime = Math.random() * (this._maxLifetime - this._minLifetime) + this._minLifetime
      }

      let emitPosX = this._emitterPosition.x,
        emitPosY = this._emitterPosition.y

      // create particle
      let p = this._particlePool.acquire(this)
      p.acceleration = this._acceleration ? new Vector2(this._acceleration.x, this._acceleration.y) : null
      p.scale = (this._maxScale - this._minScale) * Math.random() + this._minScale
      p.maxLife = lifetime
      p.lifetimeFunc = this._lifetimeFunc
      p.velocity = new Vector2(0, (this._maxSpeed - this._minSpeed) * Math.random() + this._minSpeed)
      p.rotation = (this._maxRotation - this._minRotation) * Math.random() + this._minRotation
      p.rotationSpeed = (this._maxRotationSpeed - this._minRotationSpeed) * Math.random() + this._minRotationSpeed
      p.extraData = ParticleSystem.deepClone(this._extraData)
      p.updateFunc = this._updateFunc
      if (this._spawnRect && this._spawnRect.width && this._spawnRect.height) {
        let x = Math.random() * this._spawnRect.width
        let y = Math.random() * this._spawnRect.height

        p.position = new Vector2(emitPosX + x - this._spawnRect.width / 2,
          emitPosY + y - this._spawnRect.height / 2)
      }
      else {
        p.position = new Vector2(emitPosX, emitPosY)
      }
      p.init()

      // add particle to list of active particles
      if (this._activeParticlesLast) {
        this._activeParticlesLast.next = p
        p.prev = this._activeParticlesLast
        this._activeParticlesLast = p
      }
      else {
        this._activeParticlesLast = this._activeParticlesFirst = p
      }
      ++this._particleCount

      // increase timer and continue on to any other particles that need to be created
      this._spawnTimer += this._frequency
    }
  }

  public recycle(particle: Particle) {
    if(particle.next)
      particle.next.prev = particle.prev
    if(particle.prev)
      particle.prev.next = particle.next
    if(particle == this._activeParticlesLast)
      this._activeParticlesLast = particle.prev
    if(particle == this._activeParticlesFirst)
      this._activeParticlesFirst = particle.next

    // add to pool
    this._particlePool.release(particle)

    // decrease count
    --this._particleCount
  }

  public cleanup(): void {
    let particle, next
    for (particle = this._activeParticlesFirst; particle; particle = next) {
      next = particle.next
      this.recycle(particle)
    }
    this._activeParticlesFirst = this._activeParticlesLast = null
    this._particleCount = 0
  }

  public destroy(): void {

    // puts all active particles in the pool
    this.cleanup()
    this._particlePool = null
    this._lifetimeFunc = null
    this._spawnRect = null
    this._extraData = null
    this._updateFunc = null
  }

  public abstract render(mapState: MapState): void

  get particleCount(): number {
    return this._particleCount
  }

  set emitterPosition(value: Vector2) {
    this._emitterPosition = value
  }

  set spawnRect(value: { width: number; height: number }) {
    this._spawnRect = value
  }

  set lifetimeFunc(value: (Particle) => boolean) {
    this._lifetimeFunc = value
  }

  //TODO: Support deep copy for object and array.
  private static deepClone(obj: any): any {
    let cloneObj = {}
    for (const att in obj) {
      cloneObj[att] = obj[att]
    }
    return cloneObj
  }
}