//TODO: Write generic code
import {MathUtils} from "../engine/MathUtils"
import {MapUtils} from "./MapUtils"

export class UrlUtils {
  static getTileUrl(mapUrlPattern: string, x: number, y:  number, zoom: number): string {
    let lastPos = 0
    let pos = 0
    let tileUrl = ""
    while(pos < mapUrlPattern.length) {
      if (mapUrlPattern[pos] == "{") {
        tileUrl += mapUrlPattern.substring(lastPos, pos)
        switch (mapUrlPattern[pos + 1]) {
        case "x":
          tileUrl += x.toString()
          break
        case "y":
          tileUrl += y.toString()
          break
        case "z":
          tileUrl += zoom.toString()
          break
        default:
          console.log("Invalid map url pattern: " + mapUrlPattern)
          break
        }
        pos += 3
        lastPos = pos
      }
      else {
        ++pos
      }
    }
    tileUrl += mapUrlPattern.substr(lastPos)
    return tileUrl
  }

  static getBuildingDataUrl(modelUrlPattern: string, id: string, key: string): string {
    let lastPos = 0
    let pos = 0
    let modelUrl = ""
    while(pos < modelUrlPattern.length) {
      if (modelUrlPattern[pos] == "{") {
        modelUrl += modelUrlPattern.substring(lastPos, pos)
        switch (modelUrlPattern[pos + 1]) {
        case "s":
          modelUrl += id
          break
        case "k":
          modelUrl += key
          break
        default:
          console.log("Invalid Model url pattern: " + modelUrlPattern)
          break
        }
        pos += 3
        lastPos = pos
      }
      else {
        ++pos
      }
    }
    modelUrl += modelUrlPattern.substr(lastPos)
    return modelUrl
  }

  static getIconRequestUrl(iconRequestUrlPattern: string, type: string, key: string): string {
    let lastPos = 0
    let pos = 0
    let iconUrl = ""
    while(pos < iconRequestUrlPattern.length) {
      if (iconRequestUrlPattern[pos] == "{") {
        iconUrl += iconRequestUrlPattern.substring(lastPos, pos)
        switch (iconRequestUrlPattern[pos + 1]) {
        case "t":
          iconUrl += type
          break
        case "k":
          iconUrl += key
          break
        case "s":
          iconUrl += MathUtils.clamp(MapUtils.resourceScale, 1, 3) + 'x'
          break
        default:
          console.log("Invalid Model url pattern: " + iconRequestUrlPattern)
          break
        }
        pos += 3
        lastPos = pos
      }
      else {
        ++pos
      }
    }
    iconUrl += iconRequestUrlPattern.substr(lastPos)
    return iconUrl
  }

  static getPOIImageUrl(poiUrlImagePattern: string, option: number, type: string, scale: string): string {
    let lastPos = 0
    let pos = 0
    let iconUrl = ""
    while(pos < poiUrlImagePattern.length) {
      if (poiUrlImagePattern[pos] == "{") {
        iconUrl += poiUrlImagePattern.substring(lastPos, pos)
        switch (poiUrlImagePattern[pos + 1]) {
        case "t":
          iconUrl += option.toString()
          break
        case "s":
          iconUrl += type
          break
        case "k":
          iconUrl += scale
          break
        default:
          console.log("Invalid POI image url pattern: " + poiUrlImagePattern)
          break
        }
        pos += 3
        lastPos = pos
      }
      else {
        ++pos
      }
    }
    iconUrl += poiUrlImagePattern.substr(lastPos)
    return iconUrl
  }

  static isUrlRequest(data: string): boolean {
    return (data != null) && (data.startsWith("http") || data.startsWith("./") || data.startsWith("/") || data.startsWith("blob:"))
  }
}
