import {LatLng, ILatLng} from "./LatLng"
import {Map4D} from "./Map"
import {Annotation} from "../annotation/Annotation"
import {MapUtils} from "../util/MapUtils"
import {PolylineOptions} from "./options/PolylineOptions"

export enum PolylineStyle {
  Solid = "solid",
  Dotted = "dotted"
}

export class Polyline extends Annotation {

  private path: LatLng[]

  private strokeWidth: number

  private strokeColor: string

  private strokeOpacity: number

  private visible: boolean

  private closed: boolean

  private draggable: boolean

  private style: string

  private static readonly defaultPolylineOptions = {
    strokeWidth: 0,
    strokeColor: "#0000ff",
    strokeOpacity: 1.0,
    visible: true,
    closed: false,
    draggable: false,
    zIndex: 0,
    elevation: 0,
    style: "solid",
    userInteractionEnabled: true
  }

  constructor(options: PolylineOptions) {
    super()
    let polylineOptions: any = {}
    MapUtils.extendOptions(polylineOptions, Polyline.defaultPolylineOptions, options)
    for (const option of polylineOptions) {
      if (option == null) {
        throw Error("Parameters can not be null")
      }
    }
    this.path = polylineOptions.path.map(p => LatLng.convert(p))
    this.strokeWidth = polylineOptions.strokeWidth
    this.strokeColor = polylineOptions.strokeColor
    this.strokeOpacity = polylineOptions.strokeOpacity
    this.visible = polylineOptions.visible
    this.closed = polylineOptions.closed
    this.draggable = polylineOptions.draggable
    this.elevation = polylineOptions.elevation
    this.zIndex = polylineOptions.zIndex
    if (polylineOptions.style != PolylineStyle.Dotted && polylineOptions.style != PolylineStyle.Solid) {
      console.warn("Wrong style options for Polyline. Only use 'dotted' or 'solid'.")
      polylineOptions.style = PolylineStyle.Solid
    }
    this.style = polylineOptions.style
    this.userInteractionEnabled = polylineOptions.userInteractionEnabled
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeAnnotation(this.id)
      this.id = -1
    }
    this.map = map
    if (this.map != null) {
      this.id = this.map.mapView.addAnnotation(this)
    }
  }

  setClosed(closed: boolean): void {
    if (closed == null) {
      console.warn("Parameter closed can not be null")
      return
    }
    if (this.closed == closed) {
      return
    }
    this.closed = closed
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id, true)
    }
  }

  setPath(path: ILatLng[]): void {
    if (path == null || path.length < 2) {
      console.warn("Parameter path size must be greater than 1")
      return
    }
    this.path = path.map(p => LatLng.convert(p))
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id, true)
    }
  }

  setStrokeWidth(strokeWidth: number): void {
    if (strokeWidth == null) {
      console.warn("Parameter strokeWidth can not be null")
      return
    }
    if (this.strokeWidth == strokeWidth) {
      return
    }
    this.strokeWidth = strokeWidth
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setStrokeColor(strokeColor: string): void {
    if (strokeColor == null) {
      console.warn("Parameter strokeColor can not be null")
      return
    }
    if (this.strokeColor == strokeColor) {
      return
    }
    this.strokeColor = strokeColor
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setStrokeOpacity(strokeOpacity: number): void {
    if (strokeOpacity == null) {
      console.warn("Parameter strokeOpacity can not be null")
      return
    }
    if (this.strokeOpacity == strokeOpacity) {
      return
    }
    this.strokeOpacity = strokeOpacity
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setVisible(visible: boolean): void {
    if (visible == null) {
      console.warn("Parameter visible can not be null")
      return
    }
    if (this.visible == visible) {
      return
    }
    this.visible = visible
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setStyle(style: string): void {
    if (style == null) {
      console.warn("Parameter style can not be null")
      return
    }
    if (style != PolylineStyle.Dotted && style != PolylineStyle.Solid) {
      console.warn("Wrong style options for Polyline. Only use 'dotted' or 'solid'.")
      style = PolylineStyle.Solid
    }
    if (this.style == style) {
      return
    }
    this.style = style
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  getPath(): LatLng[] {
    return (this.path || []).slice()
  }

  getStrokeWidth(): number {
    return this.strokeWidth
  }

  getStrokeColor(): string {
    return this.strokeColor
  }

  getStrokeOpacity(): number {
    return this.strokeOpacity
  }

  isVisible(): boolean {
    return this.visible
  }

  isClosed(): boolean {
    return this.closed
  }

  isDraggable(): boolean {
    return this.draggable
  }

  getStyle(): string {
    return this.style
  }

  setDraggable(draggable: boolean): void {
    if (draggable == null) {
      console.warn("Parameter draggable can not be null")
      return
    }
    this.draggable = draggable
  }

}
