import * as RBush from "rbush"
import {GeometryUtils} from "../util/GeometryUtils"
import {MapState} from "./MapState"
import {Vector2} from "../engine/Vector2"
import {LatLng} from "../public/LatLng"
import {MapConstants, SymbolConstants} from "./MapConstants"
import {MapUtils} from "../util/MapUtils"
import {SymbolResourceManager} from "../annotation/SymbolResourceManager"
import {TextLabelResourceManager} from "../text/TextLabelResourceManager"
import {MarkerLabel} from "../public/MarkerLabel"
import {MapSettings} from "./MapSettings"
import {CollisionUtils} from "../util/CollisionUtils"
import {Point} from "../public/Point"
import {VectorTileFeature} from "../tile/data/VectorTileData"
import {GeometryType} from "../tile/data/GeometryTileData"
import {Box} from "../util/Box"

type PlacementIndex = {

  id: string
  // Rbush properties
  minX: number
  minY: number
  maxX: number
  maxY: number
}

export class Placement {

  private collisionIndex: rbush.RBush<PlacementIndex>

  private readonly gl: WebGLRenderingContext

  private readonly mapSettings: MapSettings

  private readonly mapState: MapState

  private visibleFeatures: Set<string>

  private features: Map<string, {feature: VectorTileFeature, reference: number}>

  private selectedFeatureId: string

  constructor(gl: WebGLRenderingContext, mapState: MapState, mapSettings: MapSettings) {
    this.gl = gl
    this.collisionIndex = RBush<PlacementIndex>()
    this.visibleFeatures = new Set<string>()
    this.mapSettings = mapSettings
    this.mapState = mapState
    this.features = new Map<string, {feature: VectorTileFeature, reference: number}>()
    SymbolResourceManager.getInstance(gl).acquire(SymbolConstants.placeSelectedSymbol)
  }

  getFeatures(): Map<string, {feature: VectorTileFeature, reference: number}> {
    return this.features
  }

  setSelectedFeature(id: string): void {
    this.selectedFeatureId = id
    this.updatePlacement()
  }

  getSelectedFeature(): string {
    return this.selectedFeatureId
  }

  updatePlacement() {
    this.collisionIndex.clear()
    let visibleFeatures: VectorTileFeature[] = []
    let invisibleFeatures: VectorTileFeature[] = []
    let userFeatures: VectorTileFeature[] = []
    let selectedFeature: VectorTileFeature = null
    this.features.forEach(featureIterator => {
      let feature = featureIterator.feature
      if (feature == null) {
        return
      }
      let properties = feature.getProperties()
      let id = feature.getId()
      let selected = selectedFeature == null && id === this.selectedFeatureId
      properties.set("selected", selected)

      if (selected) {
        selectedFeature = feature
      }
      else if (properties.get("userFeature") === true) {
        userFeatures.push(feature)
        if (id == MapConstants.placeSelectedIdPrefix + this.selectedFeatureId) {
          properties.set("selected", true)
          if (selectedFeature != null) {
            selectedFeature.getProperties().set("selected", false)
          }
          selectedFeature = feature
        }
      }
      else if (this.visibleFeatures.has(id)) {
        visibleFeatures.push(feature)
      }
      else {
        invisibleFeatures.push(feature)
      }
    })
    this.visibleFeatures.clear()
    selectedFeature && (selectedFeature.getProperties().get("userFeature") === true ? this.placeUserFeatures([selectedFeature]) :
      this.placeFeatures([selectedFeature]))
    this.placeUserFeatures(userFeatures)
    this.placeFeatures(visibleFeatures)
    this.placeFeatures(invisibleFeatures)
  }

  queryFeature(touchPoint: Vector2): VectorTileFeature {
    let bBox = {
      minX: touchPoint.x,
      minY: touchPoint.y,
      maxX: touchPoint.x,
      maxY: touchPoint.y
    }
    let touchBox = new Box(
      new Point(touchPoint.x - MapUtils.touchBoxSize / 2, touchPoint.y - MapUtils.touchBoxSize / 2),
      new Point(touchPoint.x + MapUtils.touchBoxSize / 2, touchPoint.y + MapUtils.touchBoxSize / 2))

    let featureIndexs = this.collisionIndex.search(bBox)
    if (featureIndexs == null || featureIndexs.length <= 0) {
      return null
    }
    let features = featureIndexs.map(featureIndex => {
      return this.features.get(featureIndex.id).feature
    }).filter(feature => {

      // Currently, we only support query point feature
      return feature != null && feature.getType() == GeometryType.Point
    }).sort((a: VectorTileFeature, b: VectorTileFeature) => {
      let renderOrderA = a.getProperties().get("renderOrder") as number || -1
      let renderOrderB = b.getProperties().get("renderOrder") as number || -1
      return renderOrderB - renderOrderA
    })
    let cameraPosition = this.mapState.cameraPosition
    let screenSize = this.mapState.getScreenSize()
    for (let i = 0; i < features.length; ++i) {
      let feature = features[i]
      let properties = feature.getProperties()
      let pointCoordinate = LatLng.convert([properties.get("lng") as number, properties.get("lat") as number])
      let symbolWidth = MapConstants.placeWidth * MapUtils.pixelScale
      let symbolHeight = MapConstants.placeHeight * MapUtils.pixelScale
      let testCollision = CollisionUtils.boxIntersectsSymbol(touchBox,
        pointCoordinate, 0,
        symbolWidth,
        symbolHeight,
        MapConstants.placeAnchor, this.mapState)
      if (testCollision) {
        return feature
      }
      let coordinate = GeometryUtils.latLngToScreenCoordinate(pointCoordinate, 0, cameraPosition, screenSize)
      let markerLabel = new MarkerLabel({
        text: properties.get("name") as string,
        fontSize: MapConstants.placeFontSize,
        fontWeight: MapConstants.placeFontWeight,
        line: MapConstants.placeNumLine,
        maxWidth: MapConstants.placeMaxWidth
      })
      let textLabelData = TextLabelResourceManager.getInstance(this.gl).acquire(markerLabel)
      let padding = 1.0
      let textCoordinate =
        new Vector2(coordinate.x + (symbolWidth / 2.0) + (textLabelData.width / 2.0) + padding, coordinate.y - (symbolHeight / 2))
      testCollision = CollisionUtils.boxIntersectsSymbol(touchBox,
        GeometryUtils.screenCoordinateToLatLng(textCoordinate, cameraPosition, screenSize), 0,
        textLabelData.width,
        textLabelData.height,
        new Point(0.5, 0.5), this.mapState)
      TextLabelResourceManager.getInstance(this.gl).release(markerLabel)
      if (testCollision) {
        return feature
      }
    }
    return null
  }

  public addFeatures(vectorTileFeatures: VectorTileFeature[]): void {
    if (vectorTileFeatures == null || vectorTileFeatures.length == 0) {
      return
    }
    vectorTileFeatures.forEach(vectorTileFeature => {
      let featureId = vectorTileFeature.getId()
      let feature = this.features.get(featureId)
      if (feature != null) {
        feature.feature = vectorTileFeature
        ++feature.reference
      }
      else {
        this.features.set(featureId, {feature: vectorTileFeature, reference: 1})
      }
    })
    this.updatePlacement()
  }

  public removeFeatures(featureIds: string[]): void {
    if (featureIds == null || featureIds.length == 0) {
      return
    }
    featureIds.forEach(featureId => {
      let feature = this.features.get(featureId)
      if (feature == null) {
        !featureId.startsWith(MapConstants.placeSelectedIdPrefix) &&
        console.warn("Feature not found: " + featureId)
      }
      else {
        --feature.reference
        if (feature.reference <= 0) {
          this.features.delete(featureId)
        }
      }
    })
    this.updatePlacement()
  }

  private placeUserFeatures(vectorTileFeatures: VectorTileFeature[]) {
    let cameraPosition = this.mapState.cameraPosition
    let screenSize = this.mapState.getScreenSize()
    vectorTileFeatures.forEach(vectorTileFeature => {
      let properties = vectorTileFeature.getProperties()
      let isUserFeature = properties.get("userFeature") === true
      if (!isUserFeature) {
        return
      }
      properties.set("symbolVisible", true)
      let position = new LatLng(properties.get("lat") as number, properties.get("lng") as number)
      let selected = properties.get("selected") as boolean
      let symbolWidth = (selected === true ?
        MapConstants.placeSelectedWidth :
        MapConstants.placeWidth) * MapUtils.pixelScale
      let symbolHeight = (selected === true ?
        MapConstants.placeSelectedHeight :
        MapConstants.placeHeight) * MapUtils.pixelScale
      let coordinate = GeometryUtils.latLngToScreenCoordinate(position, 0, cameraPosition, screenSize)
      // The anchor is always in the middle of symbol
      let minX = coordinate.x - symbolWidth / 2.0
      let maxX = coordinate.x + symbolWidth / 2.0
      let minY = coordinate.y - symbolHeight
      let maxY = coordinate.y
      let symbolBox = {id: vectorTileFeature.getId(), minX: minX, maxX: maxX, minY: minY, maxY: maxY}
      this.collisionIndex.insert(symbolBox)
    })
    vectorTileFeatures.forEach(vectorTileFeature => {
      let properties = vectorTileFeature.getProperties()
      properties.set("textVisible", false)
      let placeName = properties.get("name") as string
      if (placeName == null) {

        // continue
        return
      }
      let markerLabel = new MarkerLabel({
        text: placeName,
        fontSize: MapConstants.placeFontSize,
        fontWeight: MapConstants.placeFontWeight,
        line: MapConstants.placeNumLine,
        maxWidth: MapConstants.placeMaxWidth
      })
      let textSize = markerLabel.getFontSize()
      let textLabelData = TextLabelResourceManager.getInstance(this.gl).acquire(markerLabel)
      let textLength = textLabelData.width
      TextLabelResourceManager.getInstance(this.gl).release(markerLabel)
      let selected = properties.get("selected") as boolean
      let symbolWidth = selected === true ?
        MapConstants.placeSelectedWidth :
        MapConstants.placeWidth * MapUtils.pixelScale
      let symbolHeight = selected === true ?
        MapConstants.placeSelectedHeight :
        MapConstants.placeHeight * MapUtils.pixelScale
      let position = new LatLng(properties.get("lat") as number, properties.get("lng") as number)
      let coordinate = GeometryUtils.latLngToScreenCoordinate(position, 0, cameraPosition, screenSize)
      let padding = 1.0
      let textCoordinate =
        new Vector2(coordinate.x + (symbolWidth / 2.0) + (textLength / 2.0) + padding, coordinate.y - (symbolHeight / 2))
      let minX = textCoordinate.x - textLength / 2.0
      let maxX = textCoordinate.x + textLength / 2.0
      let minY = textCoordinate.y - textSize / 2.0
      let maxY = textCoordinate.y + textSize / 2.0
      let collisionTest = this.collisionIndex.search({minX: minX, maxX: maxX, minY: minY, maxY: maxY})
      if (selected || !collisionTest || collisionTest.length == 0) {
        properties.set("textVisible", true)
        this.collisionIndex.insert({id: vectorTileFeature.getId(), minX: minX, maxX: maxX, minY: minY, maxY: maxY})
        return
      }
    })
  }

  private placeFeatures(vectorTileFeatures: VectorTileFeature[]) {
    let cameraPosition = this.mapState.cameraPosition
    let screenSize = this.mapState.getScreenSize()
    vectorTileFeatures.forEach(vectorTileFeature => {
      let properties = vectorTileFeature.getProperties()
      let isUserFeature = properties.get("userFeature") === true
      if (isUserFeature) {
        return
      }
      if (vectorTileFeature.getId() == this.selectedFeatureId) {

        // User feature will be rendered instead
        return
      }
      properties.set("textVisible", false)
      properties.set("symbolVisible", false)
      let types = (properties.get("types") as string || "").split(",")
      if (!this.isInFilterPlacesTypes(types)) {
        return
      }
      let position = new LatLng(properties.get("lat") as number, properties.get("lng") as number)
      let buf = 5 * MapUtils.pixelScale
      let selected = properties.get("selected") as boolean
      let symbolWidth = selected === true ?
        MapConstants.placeSelectedWidth :
        MapConstants.placeWidth * MapUtils.pixelScale
      let symbolHeight = selected === true ?
        MapConstants.placeSelectedHeight :
        MapConstants.placeHeight * MapUtils.pixelScale
      let coordinate = GeometryUtils.latLngToScreenCoordinate(position, 0, cameraPosition, screenSize)
      // The anchor is always in the middle of symbol
      let minX = coordinate.x - symbolWidth / 2.0 - buf
      let maxX = coordinate.x + symbolWidth / 2.0 + buf
      let minY = coordinate.y - symbolHeight - buf
      let maxY = coordinate.y + buf
      let collisionTest = this.collisionIndex.search({minX: minX, maxX: maxX, minY: minY, maxY: maxY})
      if (collisionTest && collisionTest.length != 0) {
        return
      }
      let id = vectorTileFeature.getId()
      let symbolBox = {id: id, minX: minX, maxX: maxX, minY: minY, maxY: maxY}
      properties.set("symbolVisible", true)
      this.visibleFeatures.add(id)
      let placeName = properties.get("name") as string
      if (placeName == null) {
        this.collisionIndex.insert(symbolBox)

        // continue
        return
      }
      let markerLabel = new MarkerLabel({
        text: placeName,
        fontSize: MapConstants.placeFontSize,
        fontWeight: MapConstants.placeFontWeight,
        line: MapConstants.placeNumLine,
        maxWidth: MapConstants.placeMaxWidth
      })
      let textSize = markerLabel.getFontSize()
      let textLabelData = TextLabelResourceManager.getInstance(this.gl).acquire(markerLabel)
      let textLength = textLabelData.width
      TextLabelResourceManager.getInstance(this.gl).release(markerLabel)
      let padding = 1.0
      let textCoordinate =
        new Vector2(coordinate.x + (symbolWidth / 2.0) + (textLength / 2.0) + padding, coordinate.y - (symbolHeight / 2))
      minX = textCoordinate.x - textLength / 2.0 - buf
      maxX = textCoordinate.x + textLength / 2.0 + buf
      minY = textCoordinate.y - textSize / 2.0 - buf
      maxY = textCoordinate.y + textSize / 2.0 + buf
      collisionTest = this.collisionIndex.search({minX: minX, maxX: maxX, minY: minY, maxY: maxY})
      if (!collisionTest || collisionTest.length == 0) {
        properties.set("textVisible", true)
        this.collisionIndex.insert(symbolBox)
        this.collisionIndex.insert({id: id, minX: minX, maxX: maxX, minY: minY, maxY: maxY})
        return
      }
      this.collisionIndex.insert(symbolBox)
      properties.set("textVisible", false)
    })
  }

  private isInFilterPlacesTypes(placeTypes: string[]): boolean {
    let filterPlaceTypes = this.mapSettings.filterPlaceTypes
    if (filterPlaceTypes == null || filterPlaceTypes.length == null || filterPlaceTypes.length < 1) {
      return true
    }
    if (placeTypes == null || placeTypes.length <= 0) {
      return false
    }
    return filterPlaceTypes.some(placeType => placeTypes.indexOf(placeType) !== -1)
  }
}
