import {LatLng, ILatLng} from "./LatLng"
import {MapView} from "../map/MapView"
import {LatLngBounds, ILatLngBounds} from "./LatLngBounds"
import {MapsEventListener} from "./MapsEventListener"
import {Point, IPoint} from "./Point"
import {CameraPosition, ICameraPosition} from "./CameraPosition"
import {Marker} from "./Marker"
import {MapUtils} from "../util/MapUtils"
import {MapEvent} from "./MapEvent"
import {Weather} from "./Weather"
import {MapOptions} from "./options/MapOptions"
import {AnimationOptions} from "./options/AnimationOptions"
import {PaddingOptions} from "./options/PaddingOptions"
import {SwitchMode} from "./SwitchMode"
import {TimeEffect} from "./TimeEffect"
import {MapObject} from "./MapObject"
import {EventOptions} from "./options/EventOptions"
import {MapData} from "./MapData"
import {MapSettings} from "../map/MapSettings"

export class Map4D {

  private readonly _mapView: MapView

  private readonly _data: MapData

  constructor(container: HTMLElement, options?: MapOptions) {
    if (options.accessKey) {
      MapSettings.setAccessKey(options.accessKey)
    }
    this._mapView = new MapView(container, options)
    this._data = new MapData(this._mapView)
  }

  get mapView() : MapView {
    return this._mapView
  }

  get data(): MapData {
    return this._data
  }

  enable3dMode(enabled: boolean): void {
    this.mapView.enable3dMode(enabled)
  }

  getBounds(paddingOptions?: PaddingOptions): LatLngBounds {
    return this.mapView.getBounds(paddingOptions)
  }

  setTileUrl(mapUrl: string, is3dMode: boolean = false): void {
    this.mapView.setTileUrl(mapUrl, is3dMode)
  }

  getTileUrl(is3dMode: boolean = false): string {
    return this.mapView.getTileUrl(is3dMode)
  }

  setMinZoom(minZoom: number): void {
    this.mapView.setMinZoom(minZoom)
  }

  setMaxZoom(maxZoom: number): void {
    this.mapView.setMaxZoom(maxZoom)
  }

  getCamera(): CameraPosition {
    let cameraPosition = this.mapView.getCamera()
    return new CameraPosition(cameraPosition.getTarget(), cameraPosition.getTilt(),
      cameraPosition.getBearing(), cameraPosition.getZoom())
  }

  /**
   * @deprecated since version 1.0.2
   */
  setCamera(position: ICameraPosition): void {
    console.warn(`This function is deprecated and will be removed in the future. 
    Please use method moveCamera(position:ICameraPosition, animationOptions: AnimationOptions)`)
    this.moveCamera(position)
  }

  setWeather(weather: Weather): void {
    this.mapView.setWeather(weather)
  }

  getWeather(): Weather {
    return this.mapView.getWeather()
  }

  panBy(offset: IPoint, animationOptions?: AnimationOptions): void {
    let offsetPoint = Point.convert(offset)
    let offsetPixel = MapUtils.pointToPixel(offsetPoint)
    this.mapView.panBy(offsetPixel, animationOptions)
  }

  panTo(latLng: ILatLng, animationOptions?: AnimationOptions): void {
    this.mapView.panTo(LatLng.convert(latLng), animationOptions)
  }

  /**
   * @deprecated since version 1.0.2
   */
  flyTo(latLng: ILatLng, zoom: number, animationOptions?: AnimationOptions): void {
    console.warn(`This function is deprecated and will be removed in the future. 
    Please use method moveCamera(position:ICameraPosition, animationOptions: AnimationOptions)`)
    let position = new CameraPosition()
    position.setTarget(latLng)
    position.setZoom(zoom)
    this.moveCamera(position, animationOptions)
  }

  moveCamera(position: ICameraPosition, animationOptions?: AnimationOptions): void {
    this.mapView.moveCamera(position, animationOptions)
  }

  fitBounds(bounds: ILatLngBounds, padding?: PaddingOptions, animationOptions?: AnimationOptions): void {
    this._mapView.fitBounds(LatLngBounds.convert(bounds), padding, animationOptions)
  }

  getCameraWithBounds(bounds: ILatLngBounds, padding?: PaddingOptions): CameraPosition {
    return this._mapView.getCameraPositionForLatLngBounds(LatLngBounds.convert(bounds), padding)
  }

  setSwitchMode(mode: SwitchMode): void {
    this.mapView.setSwitchMode(mode)
  }

  addListener(event: string | MapEvent, func: Function, options: EventOptions = {}): MapsEventListener {
    return this.mapView.addListener(event, func, options)
  }

  setTime(date: Date): void {
    this.setDate(date)
    console.warn("This function is deprecated and will be removed in the future. " +
      "Please use setDate(date: Date) method")
  }

  setDate(date: Date): void {
    this.mapView.setDate(date)
  }

  /**
   * @deprecated since version 1.0.2
   */
  setInfoWindow(infoWindow: (marker: Marker) => string | Node): void {
    console.warn("This function is deprecated and will be removed in the future. " +
      "Please use setInfoWindow(infoWindow: string | Node) method of Marker class instead")
  }

  /**
   * @deprecated since version 1.0.2
   */
  setInfoContents(infoContents: (marker: Marker) => string | Node): void {
    console.warn("This function is deprecated and will be removed in the future. " +
      "Please use setInfoContents(infoContents: string | Node) method of Marker class instead")
  }

  /**
   * @deprecated since version 1.0.2
   */
  flyToMyLocation(animate: boolean = true): void {
    console.warn("This function is deprecated and will be removed in the future. " +
      "Please use moveToMyLocation(animate: boolean) method")
    this.mapView.moveToMyLocation(animate)
  }

  moveToMyLocation(animate: boolean = true): void {
    this.mapView.moveToMyLocation(animate)
  }

  is3dMode(): boolean {
    return this.mapView.mapState.is3dMode()
  }

  is3DMode(): boolean {
    console.warn("This function is deprecated and will be removed in the future. " +
      "Please use is3dMode() method")
    return this.mapView.mapState.is3dMode()
  }

  getMinZoom(): number {
    return this.mapView.mapSettings.getMinZoom()
  }

  getPreferMinZoom(): number {
    return this.mapView.mapSettings.getPreferMinZoom()
  }

  getMaxZoom(): number {
    return this.mapView.mapSettings.getMaxZoom()
  }

  setMaxNativeZoom(maxNativeZoom: number): void {
    if (maxNativeZoom == null) {
      throw Error("Parameter can not be null")
    }
    return this.mapView.setMaxNativeZoom(maxNativeZoom)
  }

  getMaxNativeZoom(): number {
    return this.mapView.getMaxNativeZoom()
  }

  setTimeEffect(timeEffect : TimeEffect): void {
    this.mapView.setTimeEffect(timeEffect)
  }

  getTimeEffect(): TimeEffect {
    return this.mapView.getTimeEffect()
  }

  setShadowEffect(enabled: boolean): void {
    this.mapView.setShadowEffect(enabled)
  }

  setWaterEffect(enabled: boolean): void {
    this.mapView.setWaterEffect(enabled)
  }

  /**
   * @deprecated since version 1.0.2
   */
  showMapObject(isShow: boolean): void {
    console.warn("This function is deprecated and will be removed in the future. " +
      "Please use setObjectsEnabled(enabled: boolean) method instead")
    this.mapView.setObjectsEnabled(isShow)
  }

  /**
   * @deprecated since version 1.0.2
   */
  isShowMapObject(): boolean {
    console.warn("This function is deprecated and will be removed in the future. " +
      "Please use isObjectsEnabled() method instead")
    return this.mapView.isObjectsEnabled()
  }

  setObjectsEnabled(enabled: boolean): void  {
    this.mapView.setObjectsEnabled(enabled)
  }

  isObjectsEnabled(): boolean {
    return this.mapView.isObjectsEnabled()
  }

  setPlacesEnabled(enabled: boolean): void {
    this.mapView.setPlacesEnabled(enabled)
  }

  isPlacesEnabled(): boolean {
    return this.mapView.isPlacesEnabled()
  }

  getRestrictionBounds(): LatLngBounds {
    return this.mapView.getRestrictionBounds()
  }

  setRestrictionBounds(bounds: ILatLngBounds): void {
    this.mapView.setRestrictionBounds(bounds)
  }

  setFilterPlaces(placeTypes: string[]): void {
    this.mapView.setFilterPlaces(placeTypes)
  }

  getFilterPlaces(): string[] {
    return this.mapView.getFilterPlaces()
  }

  setFilterObjects(objectTypes: string[]): void {
    this.mapView.setFilterObjects(objectTypes)
  }

  getFilterObjects(): string[] {
    return this.mapView.getFilterObjects()
  }

  setSelectedObjects(ids: string[]): void {
    this.mapView.setSelectedObjects(ids)
  }

  getSelectedObjects(): string[] {
    return this.mapView.getSelectedObjects()
  }

  setSelectedPlace(id: string): void {
    this.mapView.setSelectedPlace(id)
  }

  getSelectedPlace(): string {
    return this.mapView.getSelectedPlace()
  }

  setHiddenObject(id: string): void {
    this.mapView.setHiddenObject(id)
  }

  setUnhiddenObject(id: string): void {
    this.mapView.setUnhiddenObject(id)
  }

  getMapObjectById(id: string): MapObject {
    let building = this.mapView.getBuildingResourceManager().getBuilding(id)
    if (building) {
      let object = new MapObject()
      object.initWithBuilding(building)
      return object
    }
    else {
      let indexedMapObject = this.mapView.getCustomBuildingManager().getMapObjects().get(id)
      return indexedMapObject && indexedMapObject.mapObject
    }
  }
}
