declare var envApiPrefix: string
declare var envDebug: boolean
declare var sdkVersion: string

export class Env {
  static get apiPrefix() {
    return envApiPrefix
  }

  static get tileEnv() {
    if (this.apiPrefix == "api") {
      return ""
    } else if (this.apiPrefix == "api-dev") {
      return "-dev"
    } else {
      return "-test"
    }
  }

  static get isDebug() {
    return envDebug
  }

  static get isTileDebug() {
    return this.isDebug && false
  }

  static get sdkVersion() {
    return sdkVersion
  }
}
