import {Kind, Tile} from "./Tile"
import {TileCoordinate} from "./TileCoordinate"
import {Texture} from "../engine/Texture"
import {RasterLayerData} from "../renderer/data/RasterLayerData"
import {BuildingJson} from "../building/BuildingJson"
import {PlaceJson} from "../map/PlaceJson"
import {VectorTile} from "./VectorTile"

export class RasterTile extends Tile {

  private initialized: boolean

  private rasterLayerData: RasterLayerData

  private buildings: BuildingJson[] = []

  private places: PlaceJson[] = []

  private overlayPlaces: PlaceJson[] = []

  private vectorTile: VectorTile

  private placesVectorTile: VectorTile

  private overlayTiles: RasterTile[] = []

  private _isOverlayTile: boolean

  private _isOverlayBuilding: boolean

  private _overlayBuildingLoaded: boolean

  constructor(tileCoordinate: TileCoordinate, gl: WebGLRenderingContext) {
    super(Kind.RASTER, tileCoordinate, gl)
    this.initialized = false
    this._isOverlayTile = false
    this._isOverlayBuilding = false
    this._overlayBuildingLoaded = false
  }

  init(tileData: any, initCallBack: Function): void {
    if (!tileData) {
      initCallBack()
      return
    }
    let texture = new Texture(this.gl)
    let data = tileData.rasterTile ? tileData.rasterTile.data : tileData.data.data
    let width = tileData.rasterTile ? tileData.rasterTile.width : tileData.data.width
    let height = tileData.rasterTile ? tileData.rasterTile.height : tileData.data.height
    texture.loadImageFromArrayBuffer(data,
      width, height, false, () => {
        this.renderable = true
        initCallBack()
      })
    this.rasterLayerData = new RasterLayerData(texture)
    this.initialized = true
  }

  isInitialized(): boolean {
    return this.initialized
  }

  addPlaces(places: PlaceJson[]): void {
    this.places = places
  }

  getPlaces(): PlaceJson[] {
    return this.places
  }

  addOverlayPlaces(overlayPlaces: PlaceJson[]): void {
    this.overlayPlaces = overlayPlaces
  }

  getOverlayPlaces(): PlaceJson[] {
    return this.overlayPlaces
  }

  getTexture(): Texture {
    if (this.rasterLayerData) {
      return this.rasterLayerData.texture
    }
    return null
  }

  addBuildings(buildings: BuildingJson[]): void {
    this.buildings = buildings
  }

  getBuildings(): BuildingJson[] {
    return this.buildings
  }

  setVectorTile(vectorTile: VectorTile): void {
    this.vectorTile = vectorTile
  }

  getVectorTile(): VectorTile {
    return this.vectorTile
  }

  setPlacesVectorTile(placesVectorTile: VectorTile): void {
    this.placesVectorTile = placesVectorTile
  }

  getPlacesVectorTile(): VectorTile {
    return this.placesVectorTile
  }

  setOverlayTile(overlayTile: RasterTile): void {
    this.overlayTiles.push(overlayTile)
    this._isOverlayTile = true
  }

  getOverlayTiles(): RasterTile[] {
    return this.overlayTiles
  }

  isOverlayTile(): boolean {
    return this._isOverlayTile
  }

  setOverlayBuilding(): void {
    this._isOverlayBuilding = true
    this._overlayBuildingLoaded = false
  }

  isOverlayBuilding(): boolean {
    return this._isOverlayBuilding
  }

  setOverlayBuildingLoaded(): void {
    this._overlayBuildingLoaded = true
  }

  overlayBuildingLoaded(): boolean {
    return this._overlayBuildingLoaded
  }

  destroy(): void {
    this.rasterLayerData && this.rasterLayerData.destroy()
    this.overlayTiles.forEach(overlayTile => {
      overlayTile.destroy()
    })
  }
}
