import {MapState} from "../map/MapState"
import {SkyObject} from "../map/SkyObject"
import {ModelEnum, ResourceManager, ShaderEnum} from "../util/ResourceManager"
import {UniformInfo} from "../engine/Shader"
import {TimeEffectManager} from "../effect/TimeEffectManager"

export class SkyRenderer {

  private gl: WebGLRenderingContext

  private timeEffectManager: TimeEffectManager

  constructor(gl: WebGLRenderingContext, timeEffectManager: TimeEffectManager) {
    this.gl = gl
    this.timeEffectManager = timeEffectManager
  }

  render(skyObject: SkyObject, mapState: MapState): void {
    if (skyObject == null || skyObject.getMvpMatrix() == null) {
      console.warn("Can not render because skyObject is null !")
      return
    }
    this.gl.enable(this.gl.DEPTH_TEST)

    let resourceManager = ResourceManager.getInstance(this.gl)
    let shader = resourceManager.getShader(ShaderEnum.SKY_SHARDER)
    let model = resourceManager.getModel(ModelEnum.SKY_MODEL)

    shader.bind()

    //transform from ratio to NDC value
    let lowerLimit = 1 - mapState.getSkyRatio() * 2.0

    let gradientHeight = mapState.screenHeight

    let upperLimit = lowerLimit + gradientHeight / mapState.screenHeight / 2.0

    let skyColor = this.timeEffectManager.getSkyColor()
    let fogColor = this.timeEffectManager.getFogColor()

    shader.setUniforms([
      new UniformInfo("u_lowerLimit", "1f", lowerLimit),
      new UniformInfo("u_upperLimit", "1f", upperLimit),
      new UniformInfo("u_viewMatrix", "matrix", skyObject.getViewMatrix()),
      new UniformInfo("u_mvpMatrix", "matrix", skyObject.getMvpMatrix()),
      new UniformInfo("u_skyColor", "3fv", skyColor.rgbBuffer()),
      new UniformInfo("u_fogColor", "3fv", fogColor.rgbBuffer())
    ])

    model.bind()
    shader.setAttributes(model.attributeInfos)
    model.render(this.gl.TRIANGLES)
    model.unbind()

    this.gl.disable(this.gl.DEPTH_TEST)
  }
}
