import {Vector2} from "../engine/Vector2"
import {ILatLng, LatLng} from "./LatLng"
import {Map4D} from "./Map"
import {Point, IPoint} from "./Point"
import {MapUtils} from "../util/MapUtils"

/**
 * A projection is used to translate between on screen location and geographic coordinates on
 * the surface of the Earth (LatLng). Screen location is in screen pixels (not display pixels)
 * with respect to the top left corner of the map (and not necessarily of the whole screen).
 */

export class Projection {
  private map: Map4D

  constructor(map: Map4D) {
    this.map = map
  }

  /**
   * Returns a screen location that corresponds to a geographical coordinate (LatLng).
   * @param latLng
   */
  fromLatLngToScreen(latLng: ILatLng, elevation: number = 0): Point {
    let screenCoordinate = this.map.mapView.latLngToScreenCoordinate(LatLng.convert(latLng), elevation)
    let screenPoint =  MapUtils.pixelToPoint(new Point(screenCoordinate.x, screenCoordinate.y))
    return screenPoint
  }

  /**
   * Returns the geographic location that corresponds to a screen location.
   * @param screenCoordinate
   */
  fromScreenToLatLng(screenCoordinate: IPoint, elevation: number = 0): LatLng {
    let screenPoint = Point.convert(screenCoordinate)
    let screenPixel = MapUtils.pointToPixel(screenPoint)
    return this.map.mapView.screenCoordinateToLatLng(new Vector2(screenPixel.x, screenPixel.y), elevation)
  }
}
