import {Annotation, AnnotationId} from "./Annotation"
import {MapView} from "../map/MapView"
import {LayerType} from "../layer/Layer"
import {MapConstants} from "../map/MapConstants"
import {LineLayer} from "../layer/LineLayer"
import {ShapeAnnotationData} from "./ShapeAnnotationData"
import {Polyline} from "../public/Polyline"

export class PolylineAnnotationData extends ShapeAnnotationData   {

  private readonly polylineAnnotation: Polyline

  constructor(annotationId: AnnotationId, polylineAnnotation: Polyline) {
    super(annotationId)
    this.polylineAnnotation = polylineAnnotation
  }

  getAnnotation(): Annotation {
    return this.polylineAnnotation
  }

  updateLayer(map: MapView): void {
    let layerId = this.getLayerId()
    let layer = map.getLayer(layerId)
    if (layer == null) {
      layer = map.createLayer(layerId, LayerType.LINE, this.polylineAnnotation.getZIndex())
    }
    let lineLayer = layer as LineLayer
    lineLayer.setStrokeColor(this.polylineAnnotation.getStrokeColor())
    lineLayer.setStrokeOpacity(this.polylineAnnotation.getStrokeOpacity())
    lineLayer.setStrokeWidth(this.polylineAnnotation.getStrokeWidth())
    lineLayer.setElevation(this.polylineAnnotation.getElevation())
    lineLayer.setVisible(this.polylineAnnotation.isVisible())
    lineLayer.setStyle(this.polylineAnnotation.getStyle())
  }

  getFeatures(): any {
    let features: string
    let coordinatesArray = []
    let path = this.polylineAnnotation.getPath()
    path.forEach(point => {
      coordinatesArray.push([point.lng, point.lat])
    })
    if (this.polylineAnnotation.isClosed() && coordinatesArray.length != 0) {
      coordinatesArray.push(coordinatesArray[0])
    }
    let lineFeature = {
      "type": "FeatureCollection",
      "features": [
        {
          "type": "Feature",
          "properties": {},
          "geometry": {
            "type": "LineString",
            "coordinates":
            coordinatesArray
          }
        }
      ]
    }
    features = JSON.stringify(lineFeature, null, 2)
    return JSON.parse(features)
  }
}
