import Point from "@mapbox/point-geometry"
import {MapConstants} from "../../map/MapConstants"

export type GeometryCoordinate = Point

export class GeometryCoordinates {

  geometryCoordinates: GeometryCoordinate[][] = []

  constructor(geometryCoordinates: GeometryCoordinate[][]) {
    this.geometryCoordinates = geometryCoordinates
  }
}

export class GeometryCollection extends GeometryCoordinates {
  constructor(geometryCoordinates: GeometryCoordinate[][]) {
    super(geometryCoordinates)
    this.normalizeGeometryCoordinates()
  }

  normalizeGeometryCoordinates(): void {
    let coordinates: GeometryCoordinate[][] = []
    for (let i = 0; i < this.geometryCoordinates.length; ++i) {
      let coordinate = []
      for (let j = 0; j < this.geometryCoordinates[i].length; ++j) {
        coordinate.push([this.geometryCoordinates[i][j][0] / MapConstants.tileExtend - 0.5,
          0.5 - this.geometryCoordinates[i][j][1] / MapConstants.tileExtend])
      }
      coordinates.push(coordinate)
    }
    this.geometryCoordinates = coordinates
  }
}
