import {ParticleSystemQuad} from "./ParticleSystemQuad"
import {ResourceManager, ShaderEnum, TextureEnum} from "../util/ResourceManager"
import {Vector2} from "../engine/Vector2"
import {ParticleConstants} from "../map/MapConstants"
import {ParticlePool} from "./ParticlePool"

export class ParticleRain extends ParticleSystemQuad {

  constructor(gl: WebGLRenderingContext, particlePool: ParticlePool) {
    let resourceManager = ResourceManager.getInstance(gl)
    let rainShader = resourceManager.getShader(ShaderEnum.PARTICLE_SHADER)
    let rainTexture = resourceManager.getTexture(TextureEnum.RAIN_TEXTURE)
    super(gl, rainShader, rainTexture, particlePool)
  }

  init(): void {
    super.init({ "frequency": ParticleConstants.rainFrequency,
      "maxParticles": 1000,
      "acceleration": (new Vector2(0, 1000)).rotateBy(ParticleConstants.rainParticleRotation),
      "minSpeed": 0,
      "maxSpeed": 1000,
      "minScale": 0.35,
      "maxScale": 0.35,
      "maxRotation": ParticleConstants.rainParticleRotation,
      "minRotation": ParticleConstants.rainParticleRotation
    })
  }

}