import {CameraPosition} from "../public/CameraPosition"
import {Matrix4} from "../engine/Matrix"
import {MapConstants} from "./MapConstants"
import {UnwrappedTileCoordinate} from "../tile/UnwrappedTileCoordinate"
import {ProjectionMercator} from "./ProjectionMercator"
import {Vector2} from "../engine/Vector2"
import {Vector3} from "../engine/Vector3"
import {LatLng} from "../public/LatLng"
import {GeometryUtils} from "../util/GeometryUtils"
import {MapSettings} from "./MapSettings"
import {MathUtils} from "../engine/MathUtils"
import {SwitchMode} from "../public/SwitchMode"
import {CameraUtils} from "../util/CameraUtils"

export class MapState {

  cameraPosition: CameraPosition

  scale: number

  // Canvas dimensions
  screenWidth: number

  screenHeight: number

  // Matrix
  private projectionMatrix: Matrix4

  private orthoProjectionMatrix: Matrix4

  private viewProjectionMatrix: Matrix4

  willChange: boolean

  zoomIn: boolean

  zoomOut: boolean

  panning: boolean

  flyingTo: boolean

  movingTo: boolean

  tilting: boolean

  bearing: boolean

  gesture: boolean

  startedZoom: boolean

  private force2d = false

  private distanceToSky: number

  private skyRatio: number

  private constraining;

  mapSettings: MapSettings

  cameraPositionVector: Vector3

  constructor(mapSettings: MapSettings) {
    this.scale = 1
    this.willChange = false
    this.zoomIn = false
    this.zoomOut = false
    this.panning = false
    this.flyingTo = false
    this.tilting = false
    this.bearing = false
    this.gesture = false
    this.constraining = false

    this.mapSettings = mapSettings
    this.cameraPosition = new CameraPosition([0, 0], 0, 0, 0)
  }

  setScreenSize(width: number, height: number): void {
    this.screenWidth = width
    this.screenHeight = height
    this.constraintCameraWithRestrictionBounds()
    this.recalculateMatrices()
  }

  /**
   * ok for test
   * @param tileCoordinate
   */
  matrixFor(unwrappedTileCoordinate: UnwrappedTileCoordinate): Matrix4 {
    let tileCoordinate = unwrappedTileCoordinate.getTileCoordinate()
    let tileScale = 1 << tileCoordinate.getZoom()
    let worldSize = ProjectionMercator.worldSize(this.scale)
    let s = worldSize / tileScale

    // Get tile position
    let unwrappedTileCoord = unwrappedTileCoordinate.toVec2()
    let x = unwrappedTileCoord.x * s + s / 2
    let y = unwrappedTileCoord.y * s + s / 2
    let coordinate = GeometryUtils.pixelCoordinateToCameraCoordinate(new Vector2(x, y), this.cameraPosition)
    let matrix = Matrix4.translate(coordinate.x, coordinate.y, 0)
    matrix = Matrix4.scale(s / 2, s / 2, 1).concat(matrix)
    return matrix
  }

  /**
   * ok for test
   */
  recalculateMatrices(): void {
    this.projectionMatrix = CameraUtils.calculateProjectionMatrix(this.screenWidth, this.screenHeight)
    this.orthoProjectionMatrix = CameraUtils.calculateOrthoProjectionMatrix(this.screenWidth, this.screenHeight)
    let viewMatrix = CameraUtils.calculateViewMatrix(this.cameraPosition, this.screenHeight)
    this.viewProjectionMatrix = viewMatrix.concat(this.projectionMatrix)

    let tempVector = Matrix4.rotateX(this.cameraPosition.getTilt())
      .transform(new Vector3(0, 0, 1), 0)
    this.cameraPositionVector = Matrix4.rotateZ(this.cameraPosition.getBearing()).transform(tempVector, 0)
  }

  constraintCameraWithRestrictionBounds(): void {
    if (this.mapSettings.getRestrictionBounds() == null || this.constraining
      || this.getScreenSize().x == 0 || this.getScreenSize().y == 0) {
      return
    }
    this.constraining = true

    let restrictionBounds = this.mapSettings.getRestrictionBounds()
    let scale = this.getScale()
    let scaleX, scaleY
    let x2 = null
    let y2 = null
    const screenSize = this.getScreenSize()

    let bottomLeftPoint = ProjectionMercator.projectScale(restrictionBounds.getSouthwest(), scale)
    let topRightPoint = ProjectionMercator.projectScale(restrictionBounds.getNortheast(), scale)
    let minX = bottomLeftPoint.x
    let minY = topRightPoint.y
    let maxX = topRightPoint.x
    let maxY = bottomLeftPoint.y

    scaleX = maxX - minX < screenSize.x ? screenSize.x / (maxX - minX) : 0
    scaleY = maxY - minY < screenSize.y ? screenSize.y / (maxY - minY) : 0

    const pointTarget = ProjectionMercator.projectScale(this.getTarget(), scale)

    // how much the map should scale to fit the screen into given restriction bounds
    const targetScale = Math.min(scaleX, scaleY)

    if (targetScale) {
      this.setTarget(ProjectionMercator.unproject(
        new Vector2(scaleX ? (maxX + minX) / 2 : pointTarget.x, scaleY ? (maxY + minY) / 2 : pointTarget.y), scale))
      this.setZoom(Math.floor(this.getZoom() + Math.log2(targetScale)))
      this.constraining = false
      return
    }

    if (maxX - minX < screenSize.x) {
      pointTarget.x = (maxX + minX) / 2
    }
    else {
      const w2 = screenSize.x / 2
      if (pointTarget.x - w2 < minX) x2 = minX + w2
      if (pointTarget.x + w2 > maxX) x2 = maxX - w2
    }

    if (maxY - minY < screenSize.y) {
      pointTarget.y = (maxY + minY) / 2
    }
    else {
      const h2 = screenSize.y / 2
      if (pointTarget.y - h2 < minY) y2 = minY + h2
      if (pointTarget.y + h2 > maxY) y2 = maxY - h2
    }

    // restrict pan the map if the screen goes off the range
    this.setTarget(ProjectionMercator.unproject(new Vector2(
      x2 != null ? x2 : pointTarget.x,
      y2 != null ? y2 : pointTarget.y
    ), scale))
    this.constraining = false
  }

  setTarget(target: LatLng): void {
    this.cameraPosition.setTarget(target)
    this.constraintCameraWithRestrictionBounds()
    this.recalculateMatrices()
  }

  setTilt(degrees: number): void {
    this.cameraPosition.setTilt(degrees)
    this.constraintCameraWithRestrictionBounds()
    this.recalculateMatrices()
  }

  setBearing(degrees: number): void {
    this.cameraPosition.setBearing(degrees)
    this.constraintCameraWithRestrictionBounds()
    this.recalculateMatrices()
  }

  setZoom(zoom: number): void {
    this.cameraPosition.setZoom(zoom)
    this.scale = Math.pow(2, zoom)
    this.constraintCameraWithRestrictionBounds()
    this.recalculateMatrices()
  }

  enable3dMode(enabled: boolean): boolean {
    if (enabled == this.mapSettings.is3dMode()) {
      return false
    }
    if (!enabled) {
      let tilt = MathUtils.clamp(this.getTilt(), this.getMinTilt(), MapConstants.maxTilt2D)
      this.setTilt(tilt)
    }
    return this.mapSettings.enable3dMode(enabled)
  }

  getProjectionMatrix(): Matrix4 {
    return this.projectionMatrix
  }

  getViewProjectionMatrix(): Matrix4 {
    return this.viewProjectionMatrix
  }

  getOrthoProjectionMatrix(): Matrix4 {
    return this.orthoProjectionMatrix
  }

  getScreenSize(): Vector2 {
    return new Vector2(this.screenWidth, this.screenHeight)
  }

  getTilt(): number {
    return this.cameraPosition.getTilt()
  }

  getMinTilt(): number {
    return this.mapSettings.getMinTilt()
  }

  getMaxTilt(): number {
    return this.mapSettings.getMaxTilt()
  }

  getDistanceToSky(): number {
    return this.distanceToSky
  }

  setDistanceToSky(distance: number): void {
    this.distanceToSky = distance
  }

  getBearing(): number {
    return this.cameraPosition.getBearing()
  }

  getScale(): number {
    return this.scale
  }

  getZoom(): number {
    return this.cameraPosition.getZoom()
  }

  getTarget(): LatLng {
    return this.cameraPosition.getTarget()
  }

  isGesture(): boolean {
    return this.gesture
  }

  isZoomIn(): boolean {
    return this.zoomIn
  }

  isZoomOut(): boolean {
    return this.zoomOut
  }

  isTilting(): boolean {
    return this.tilting
  }

  isBearing(): boolean {
    return this.bearing
  }

  isPanning(): boolean {
    return this.panning
  }

  isFlyingTo(): boolean {
    return this.flyingTo
  }

  isMoving(): boolean {
    return this.movingTo
  }

  isStill() {
    return !(this.isBearing() ||
      this.isFlyingTo() ||
      this.isMoving() ||
      this.isPanning() ||
      this.isTilting() ||
      this.isZoomIn() ||
      this.isZoomOut()
    )
  }

  dirty(): boolean {
    return this.isFlyingTo() || this.isPanning() || this.isZoomIn() ||
      this.isZoomOut() || this.isMoving() || this.isBearing()
  }

  getCamera(): CameraPosition {
    return this.cameraPosition
  }

  isWillChange(): boolean {
    return this.willChange
  }

  setCamera(cameraPosition: CameraPosition): void {
    this.cameraPosition = cameraPosition
  }

  getMaxNativeZoom(): number {
    return this.mapSettings.getMaxNativeZoom()
  }

  is3dMode(): boolean {
    return this.mapSettings.is3dMode()
  }

  setSwitchMode(mode: SwitchMode): void {
    this.mapSettings.setSwitchMode(mode)
  }

  handleSwitchMode(): void {
    if (this.force2d) {
      return
    }

    let mode = this.mapSettings.getSwitchMode()
    switch (mode) {
    case SwitchMode.Auto3DTo2D:
      if (this.getZoom() < MapConstants.defaultMinZoom3d) {
        this.enable3dMode(false)
      }
      break
    case SwitchMode.Auto2DTo3D:
      this.enable3dMode(this.getZoom() >= MapConstants.defaultMinZoom3d )
      break
    case SwitchMode.Auto:
      this.enable3dMode(this.getZoom() >= MapConstants.defaultMinZoom3d )
      break
    default:
      break
    }
  }

  setForce2d(force: boolean) {
    this.force2d = force
  }

  getForce2d() {
    return this.force2d
  }

  getSkyRatio(): number {
    return this.skyRatio
  }

  setSkyRatio(value: number) {
    this.skyRatio = value
  }
}

