// @ts-ignore
import * as MBPoint from "@mapbox/point-geometry"

export class Point {

  x: number

  y: number

  constructor(x: number, y: number) {
    if (x == null || y == null) {
      throw Error("Parameters can not be null")
    }
    this.x = x
    this.y = y
  }

  static convertPointToMBPoint(point: Point): MBPoint {
    return new MBPoint(point.x, point.y)
  }

  equals(other: Point): boolean {
    return this.x == other.x && this.y == other.y
  }

  toString(){
    return "(" + String(this.x) + "," + String(this.y) + ")"
  }

  static convert(input: IPoint): Point {
    if (input instanceof Point) {
      return input
    }
    if (Array.isArray(input) && (input.length === 2)) {
      return new Point(Number(input[0]), Number(input[1]))
    }
    if (!Array.isArray(input) && typeof input === "object" && input !== null && "x" in input && "y" in input) {
      return new Point(
        Number(input.x),
        Number(input.y))
    }
    throw new Error("`IPoint` argument must be specified as a Point instance, an object " +
                            "{x: <x>, y: <y>}, or an array of [<x>, <y>]")
  }
}

export type IPoint  = Point | {x: number, y: number} | [number, number]
