import {AnnotationId} from "./Annotation"
import {MapView} from "../map/MapView"
import {Marker} from "../public/Marker"
import {DOMUtils} from "../util/DOMUtils"
import {MapUtils} from "../util/MapUtils"
import {MapConstants} from "../map/MapConstants"

let classNameContainer = "mf-iconview-marker-container"
let defaultId = "mf-iconview-marker-"

export class IconViewMarkerManager{

  private _iconViewMarkers: Map<AnnotationId, Marker>

  private _iconViewsHTML: Map<AnnotationId, HTMLElement>

  private _mapView: MapView

  constructor(mapView: MapView) {
    this._mapView = mapView
    this._iconViewMarkers = new Map<AnnotationId, Marker>()
    this._iconViewsHTML = new Map<AnnotationId, HTMLElement>()
  }

  update(): void {
    this._iconViewMarkers.forEach((marker) =>{
      this.updateIconView(marker.getId())
    })
  }

  updateIconView(annotationId: AnnotationId): void {
    if (!this._iconViewMarkers.get(annotationId)){
      return
    }
    let marker = this._iconViewMarkers.get(annotationId)
    let position = this._mapView.latLngToScreenCoordinate(marker.getPosition(), marker.getElevation())
    position.x /= MapUtils.pixelScale
    position.y /= MapUtils.pixelScale
    let iconView = this._iconViewsHTML.get(annotationId)
    if (!iconView) {
      iconView = DOMUtils.create("div",
        `${classNameContainer}`, this._mapView.getMapContainer())
      iconView.style.pointerEvents = "none"
    }
    iconView.id = defaultId + annotationId
    iconView.innerHTML = `${marker.getIconView()}`
    this._iconViewsHTML.set(annotationId, iconView)
    let infoClass = document.getElementById(`${iconView.id}`) as HTMLElement
    let style = iconView.style

    let width = (infoClass.clientWidth % 2) ? infoClass.clientWidth + 1 : infoClass.clientWidth
    let height = (infoClass.clientHeight % 2) ? infoClass.clientHeight + 1 : infoClass.clientHeight
    let left = position.x + (0.5 - marker.getAnchor().x) * width
    let top = position.y + (0.5 - marker.getAnchor().y) * height
    style.top = top.toString() + "px"
    style.left = left.toString() + "px"
    style.zIndex = marker.getZIndex().toString()
    if(marker.isVisible()) {
      style.visibility = "visible"
    }
    else {
      style.visibility = "hidden"
    }

    // Check position visibility of icon view
    position.y = (this._mapView.mapState.getZoom() > MapConstants.defaultMaxNativeZoom)
      ? position.y : position.y + 24
    let positionLatlng = this._mapView.screenCoordinateToLatLng(position)
    if (iconView.offsetLeft > this._mapView.mapState.getScreenSize().x || iconView.offsetLeft < -iconView.offsetWidth
        || iconView.offsetTop > this._mapView.mapState.getScreenSize().y
        || iconView.offsetTop < -iconView.offsetHeight
        || positionLatlng.lng == Infinity || positionLatlng.lat == Infinity) {
      style.visibility = "hidden"
    }
  }

  add(annotationId: AnnotationId, marker: Marker): void {
    this._iconViewMarkers.set(annotationId, marker)
    this.updateIconView(annotationId)
  }

  getIconViewMarkers(): Map<AnnotationId, Marker>{
    return this._iconViewMarkers
  }

  getIconViewsHTML(): Map<AnnotationId, HTMLElement> {
    return this._iconViewsHTML
  }

  remove(annotationId: AnnotationId): void {
    this.removeContainer(annotationId)
    this._iconViewsHTML.delete(annotationId)
    this._iconViewMarkers.delete(annotationId)
  }

  removeContainer(annotationId: AnnotationId): void {
    if (this._iconViewsHTML.get(annotationId)) {
      let iconView = this._iconViewsHTML.get(annotationId)
      DOMUtils.remove(iconView)
      iconView = null
    }
  }

  removeAllContainers(): void {
    this._iconViewsHTML.forEach((iconView) =>{
      DOMUtils.remove(iconView)
      iconView = null
    })
    this._iconViewsHTML.clear()
  }

  destroy(): void{
    this.removeAllContainers()
    this._iconViewMarkers.clear()
  }
}
