import {LatLng} from "../public/LatLng"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {MapUtils} from "../util/MapUtils"
import {MapConstants} from "../map/MapConstants"

export class TileCoordinate {
  x: number

  y: number

  zoom: number

  mode3D: boolean

  /**
   * Constructor
   * @param x
   * @param y
   * @param zoom
   */
  constructor(x: number,
    y: number,
    zoom: number,
    mode3D: boolean = false) {
    this.x = x
    this.y = y
    this.zoom = zoom
    this.mode3D = mode3D && this.getZoom() >= MapConstants.defaultMinZoom3d
  }

  /**
   *
   * @param latLng
   * @param zoom
   */
  static fromLatLng(latLng: LatLng,
    zoom: number, mode3D: boolean): TileCoordinate {
    let tile = ProjectionMercator.projectScale(latLng, Math.pow(2, zoom)).multiply(
      1 / MapUtils.getTileSize())

    return new TileCoordinate(Math.floor(tile.x), Math.floor(tile.y), zoom, mode3D)
  }

  parent(): TileCoordinate {
    return new TileCoordinate(this.x / 2, this.y / 2, (this.zoom - 1), this.mode3D)
  }

  children(): TileCoordinate[] {
    let childZ = this.zoom + 1
    let childX = this.x * 2
    let childY = this.y * 2
    return [
      new TileCoordinate(childX, childY, childZ, this.mode3D),
      new TileCoordinate(childX, childY + 1, childZ, this.mode3D),
      new TileCoordinate(childX + 1, childY, childZ, this.mode3D),
      new TileCoordinate(childX + 1, childY + 1, childZ, this.mode3D)
    ]
  }

  set3DMode(mode3D: boolean): void {
    this.mode3D = mode3D && this.getZoom() >= MapConstants.defaultMinZoom3d
  }

  /**
   * Return x
   */
  getX() {
    return this.x
  }

  /**
   * Return y
   */
  getY() {
    return this.y
  }

  /**
   * Return zoom
   */
  getZoom() {
    return this.zoom
  }

  is3DMode(): boolean {
    return this.mode3D
  }

  equal(rhs: TileCoordinate): boolean {
    if (rhs == null) {
      return false
    }
    return (this.x == rhs.x && this.y == rhs.y && this.zoom == rhs.zoom && this.mode3D == rhs.mode3D)
  }

  notEqual(rhs: TileCoordinate): boolean {
    if (rhs == null) {
      return false
    }
    return (this.x != rhs.x || this.y != rhs.y || this.zoom != rhs.zoom || this.mode3D != rhs.mode3D)
  }

  lessThan(rhs: TileCoordinate): boolean {
    if (rhs == null) {
      return false
    }
    if (this.x == rhs.x) {
      if (this.y == rhs.y) {
        if (this.zoom == rhs.zoom) {
          return this.mode3D < rhs.mode3D
        }
        return this.zoom < rhs.zoom
      }
      return this.y < rhs.y
    }
    return this.x < rhs.x
  }

  isChildOf(parent: TileCoordinate): boolean {
    if (this.mode3D != parent.mode3D || parent == null) {
      return false
    }
    return parent.zoom == 0 ||
            (parent.zoom < this.zoom &&
            parent.x == (this.x >> (this.zoom - parent.zoom)) &&
            parent.y == (this.y >> (this.zoom - parent.zoom)))
  }

  toString(): string {
    return this.getZoom() + "/" + this.getX() + "/" + this.getY() + "/" + (this.mode3D ? "1" : "0")
  }
}
