import {Layer, LayerType} from "../layer/Layer"
import {Vector2} from "../engine/Vector2"
import {LineLayerData} from "./data/LineLayerData"
import {ResourceManager, ShaderEnum, TextureEnum} from "../util/ResourceManager"
import {UniformInfo} from "../engine/Shader"
import {MapUtils} from "../util/MapUtils"
import {LineLayer} from "../layer/LineLayer"
import {MapState} from "../map/MapState"
import {Matrix4} from "../engine/Matrix"
import {Kind} from "../tile/Tile"
import {GeometryUtils} from "../util/GeometryUtils"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {LayerRenderer} from "./LayerRenderer"
import {GeometryTile} from "../tile/GeometryTile"
import {PolyConstants} from "../map/MapConstants"
import {PolylineStyle} from "../public/Polyline"

/**
 * Class LineLayerRenderer
 */
export class LineLayerRenderer extends LayerRenderer {
  constructor(mapState: MapState, layer: Layer) {
    super(mapState, layer)
  }

  render(): void {
    if (this.layer.getLayerType() != LayerType.LINE) {
      return
    }
    let lineLayer = this.layer as LineLayer
    if (lineLayer.renderTiles.size == 0) {
      return
    }
    if (!lineLayer.isVisible()) {
      return
    }
    let isDotted = lineLayer.getStyle() == PolylineStyle.Dotted ? true : false
    let gl = this.layer.tileManager.mapView.gl
    let shader = ResourceManager.getInstance(gl).getShader(ShaderEnum.POLYLINE_SHADER)
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    lineLayer.getElevation() > 0 && gl.enable(gl.DEPTH_TEST)
    shader.bind()

    let worldSize = ProjectionMercator.worldSize(this.mapState.getScale())
    let layerId = this.layer.getLayerId()

    // Scale line width when zoom > maxNativeZoom
    let strokeScale = 1.0
    if (this.mapState.getZoom() > this.mapState.getMaxNativeZoom()) {
      strokeScale = Math.pow(2.0, Math.round(this.mapState.getZoom()) - this.mapState.getMaxNativeZoom())
    }
    let lineWidth = lineLayer.getStrokeWidth()

    let renderTiles = lineLayer.renderTiles
    renderTiles.forEach(renderTile => {
      let tile = renderTile.getTile()
      let tileKind = tile.getKind()
      if (tileKind != Kind.ANNOTAION && tileKind != Kind.VECTOR) {
        console.warn("Invalid tile kind")
        return
      }
      let geometryTile = tile as GeometryTile
      if (!geometryTile.hasData(layerId)) {
        return
      }

      let layerData = geometryTile.getLayerData(layerId, LayerType.LINE, lineWidth)
      if (layerData.getType() != LayerType.LINE) {
        console.warn("ERROR: layerData.getType() != LayerType.LINE .............")
        return
      }
      let lineLayerData = layerData as LineLayerData

      // Calculate mvp matrix
      let tileCoordinate = geometryTile.getTileCoordinate()
      let tileScale = 1 << tileCoordinate.getZoom()
      let s = worldSize / tileScale
      let scale = s / MapUtils.getTileSize()
      let coordinate = GeometryUtils.pixelCoordinateToCameraCoordinate(new Vector2(tileCoordinate.getX() * s + s / 2,
        tileCoordinate.getY() * s + s / 2), this.mapState.cameraPosition)
      let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y,
        this.mapState.is3dMode() ? GeometryUtils.getElevationPixelFromMeter(lineLayer.getElevation(), this.mapState.cameraPosition) : 0.0)
      modelMatrix = Matrix4.scale(scale, scale, 1.0).concat(modelMatrix)

      let lineLayerDataProperties = lineLayerData.getLineLayerDataProperties()
      let model = lineLayerData.getModel()
      model.bind()
      shader.setAttributes(model.attributeInfos)
      shader.setUniforms([
        new UniformInfo("u_mvpMatrix", "matrix", modelMatrix.concat(this.mapState.getViewProjectionMatrix()))
      ])
      let linePartials = lineLayerData.getLinePartials()
      let textureScale
      if (isDotted) {
        textureScale = strokeScale / lineWidth * (PolyConstants.polylineTextureHeight / PolyConstants.polylineTextureWidth)
        let polylineTexture = ResourceManager.getInstance(gl).getTexture(TextureEnum.POLYLINE_TEXTURE)
        shader.setUniforms([
          new UniformInfo("u_texture", "texture", polylineTexture)
        ])
      }
      for (let i = 0; i < linePartials.length; ++i) {
        let lineLayerDataProperty = lineLayerDataProperties[i]
        shader.setUniforms([
          new UniformInfo("u_lineWidth", "1f",
            tileKind == Kind.ANNOTAION ?
              lineWidth / strokeScale :
              lineLayerDataProperty.strokeWidth / strokeScale),
          new UniformInfo("u_color", "4fv",
            tileKind == Kind.ANNOTAION ?
              lineLayer.getStrokeColor().buffer() :
              lineLayerDataProperty.strokeColor.buffer()),
          new UniformInfo("u_isDotted", "1i", isDotted)
        ])
        if (isDotted) {
          shader.setUniforms([
            new UniformInfo("u_textureScale", "1f", textureScale)
          ])
        }
        let linePartial = linePartials[i]
        model.renderPartial(gl.TRIANGLES, linePartial.start, linePartial.length)
      }
    })
    gl.disable(gl.DEPTH_TEST)
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, null)
    gl.bindBuffer(gl.ARRAY_BUFFER, null)
    shader.disableAttributes()
  }
}
