/**
 * Fling animation tracker which is referred from
 * https://cs.chromium.org/chromium/src/remoting/client/ui/fling_tracker.h?dr=C&g=0
 */
import {MathUtils} from "../engine/MathUtils"
import {MapConstants} from "../map/MapConstants"
import {CameraAnimationObserver} from "./CameraAnimationObserver"

export class FlingTracker {
  
  cameraAnimationObserver: CameraAnimationObserver
  
  timeConstant: number;

  initialSpeedRate: number;

  flingDuration: number;

  velocityRatioX: number;

  velocityRatioY: number;

  previousPositionX: number;

  previousPositionY: number;

  flingStart: number;

  dx: number = 0;

  dy: number = 0;

  static secToMs = 0.001;

  /**
   * Stop flinging if the speed drops below this. This is a small speed to make
   * sure the animation stops smoothly.
   */
  static minTrackSpeed = 0.01;

  /**
   * The minimum displacement needed to trigger the fling animation. This is to
   * prevent unintentional fling with low velocity.
   */
  static minDisplacement = 20.0;

  constructor(timeConstant: number, cameraAnimationObserver: CameraAnimationObserver) {
    this.timeConstant = timeConstant
    this.initialSpeedRate = 0
    this.velocityRatioX = 0
    this.velocityRatioY = 0
    this.cameraAnimationObserver = cameraAnimationObserver
  }

  getDisplacement(timeConstant: number, initialSpeedRate: number, timeElapsed: number): number {

    /**
     * x = T * v0 * (1 - e^(-t / T))
     * This comes from a solution to the linear drag equation F=-kv
     */
    let expFactor = -Math.expm1(-timeElapsed / timeConstant)
    return timeConstant * initialSpeedRate * expFactor
  }

  /**
   * Returns the time needed for the object to get to the stable state where the
   * speed drops below kMinTrackSpeed.
   * @param timeConstant
   * @param initialSpeedRate
   * @return
   */
  getDuration(timeConstant: number, initialSpeedRate: number): number {

    /**
     * t = -T * ln(kMinTrackSpeed / v0)
     * Solution of v(t) = kMinTrackSpeed
     */
    return -timeConstant * Math.log(FlingTracker.minTrackSpeed / initialSpeedRate)
  }

  startFling(velocityX: number, velocityY: number): void {

    /** Contrain velocity */
    velocityX = MathUtils.clamp(velocityX, MapConstants.flingMinVelocity, MapConstants.flingMaxVelocity)
    velocityY = MathUtils.clamp(velocityY, MapConstants.flingMinVelocity, MapConstants.flingMaxVelocity)

    /** Convert to pixel/ms */
    velocityX *= FlingTracker.secToMs
    velocityY *= FlingTracker.secToMs

    this.initialSpeedRate = Math.sqrt(velocityX * velocityX + velocityY * velocityY)
    if (this.initialSpeedRate < FlingTracker.minTrackSpeed) {
      this.stopFling()
      this.cameraAnimationObserver.notifyCameraDidChange()
      return
    }

    this.flingDuration = this.getDuration(this.timeConstant, this.initialSpeedRate)

    let finalDisplacement =
      this.getDisplacement(this.timeConstant, this.initialSpeedRate, this.flingDuration)

    if (finalDisplacement < FlingTracker.minDisplacement) {
      this.stopFling()
      this.cameraAnimationObserver.notifyCameraDidChange()
      return
    }

    this.velocityRatioX = velocityX / this.initialSpeedRate
    this.velocityRatioY = velocityY / this.initialSpeedRate

    this.previousPositionX = 0
    this.previousPositionY = 0
    this.flingStart = Date.now()
  }

  stopFling(): void {
    this.initialSpeedRate = 0
  }

  isFlingInProgress(): boolean {
    return this.initialSpeedRate > 0
  }

  trackMovement(): boolean {
    if (!this.isFlingInProgress()) {
      return false
    }

    let timeElapsedMs = Date.now() - this.flingStart
    if (timeElapsedMs > this.flingDuration) {
      this.stopFling()
      this.cameraAnimationObserver.notifyCameraDidChange()
      return false
    }

    let displacement = this.getDisplacement(this.timeConstant, this.initialSpeedRate, timeElapsedMs)

    let positionX = displacement * this.velocityRatioX
    let positionY = displacement * this.velocityRatioY

    this.dx = positionX - this.previousPositionX
    this.dy = positionY - this.previousPositionY

    this.previousPositionX = positionX
    this.previousPositionY = positionY
    return true
  }
}
