import {Annotation, AnnotationId} from "./Annotation"
import {MapView} from "../map/MapView"
import {LayerType} from "../layer/Layer"
import {MapConstants} from "../map/MapConstants"
import {ShapeAnnotationData} from "./ShapeAnnotationData"
import {FillLayer} from "../layer/FillLayer"
import {Polygon} from "../public/Polygon"

export class PolygonAnnotationData extends ShapeAnnotationData   {

  private readonly polygonAnnotation: Polygon

  constructor(annotationId: AnnotationId, polygonAnnotation: Polygon) {
    super(annotationId)
    this.polygonAnnotation = polygonAnnotation
  }

  getAnnotation(): Annotation {
    return this.polygonAnnotation
  }

  updateLayer(map: MapView): void {
    let layerId = this.getLayerId()
    let layer = map.getLayer(layerId)
    if (layer == null) {
      layer = map.createLayer(layerId, LayerType.FILL, this.polygonAnnotation.getZIndex())
    }
    let fillLayer = layer as FillLayer
    fillLayer.setFillColor(this.polygonAnnotation.getFillColor())
    fillLayer.setFillOpacity(this.polygonAnnotation.getFillOpacity())
    fillLayer.setStrokeColor(this.polygonAnnotation.getStrokeColor())
    fillLayer.setStrokeWidth(this.polygonAnnotation.getStrokeWidth())
    fillLayer.setElevation(this.polygonAnnotation.getElevation())
    fillLayer.setVisible(this.polygonAnnotation.isVisible())
  }

  getFeatures(): any {
    let features: string
    let coordinates = []
    let paths = this.polygonAnnotation.getPaths()
    paths.forEach(path => {
      let pathCoordinates = []
      path.forEach(point => pathCoordinates.push([point.lng, point.lat]))
      coordinates.push(pathCoordinates)
    })
    let polygonFeature = {
      "type": "FeatureCollection",
      "features": [
        {
          "type": "Feature",
          "properties": {},
          "geometry": {
            "type": "Polygon",
            "coordinates":
            coordinates
          }
        },
        {
          "type": "Feature",
          "properties": {},
          "geometry": {
            "type": "MultiLineString",
            "coordinates":
            coordinates
          }
        }
      ]
    }
    features = JSON.stringify(polygonFeature, null, 2)
    return JSON.parse(features)
  }
}
