import {RequestTask, Resource, ResponseTask} from "./RequestTask"
import {WorkerHelper} from "./WorkerHelper"
import {TaskWorker} from "./TaskWorker"

export class TaskManager {

  private maxConcurrentRequest = 5

  private maxWorker = 3

  private workers: TaskWorker[] = []

  private readonly pendingTasks: RequestTask[][] = []

  private readonly activedTasks: RequestTask[] = []

  constructor() {
    for (let i = 0; i < this.maxWorker; ++i) {
      let worker = WorkerHelper.createWorker((data: any) => {
        this.onCompletionTask(data)
      })
      this.workers.push(worker)
    }

    for (let i = 0; i < Resource.MAX; i++) {
      this.pendingTasks.push([])
    }
  }

  postTask(task: RequestTask) {
    this.pendingTasks[task.type].unshift(task)
    this.executeTasks()
  }

  private executeTasks() {
    if (this.activedTasks.length > this.maxConcurrentRequest){
      return
    }
    for (let worker of this.workers) {
      if (worker.canExecuteTask() && this.hasTask()) {
        let task = this.getTask()
        this.activedTasks.push(task)
        task.worker = worker
        worker.executeTask(task)
      }
    }
  }

  private hasTask(): boolean {
    for (let i = 0; i < Resource.MAX; i++) {
      if (this.pendingTasks[i].length > 0) {
        return true
      }
    }
    return false
  }

  private getTask(): RequestTask {

    for (let i = 0; i < Resource.MAX; i++) {
      if (this.pendingTasks[i].length > 0) {
        return this.pendingTasks[i].pop()
      }
    }

    return null
  }

  cancelTask(task: RequestTask) {
    if (!task) {
      return
    }
    let endTask = this.removeTask(task.id)
    if (endTask != null) {
      if (endTask.worker) {
        endTask.worker.cancel(endTask)
      }
      if (endTask.completion) {
        endTask.completion(new ResponseTask(task.id))
      }
    }
  }

  private onCompletionTask(data: ResponseTask){
    let index = this.activedTasks.findIndex(t => t.id == data.id)
    if (index < 0) {
      this.executeTasks()
      return
    }

    let originTask = this.activedTasks[index]
    this.activedTasks.splice(index, 1)

    if (originTask.completion) {
      originTask.completion(data)
    }

    this.executeTasks()
  }

  private removeTask(id: string) {

    for (let i = 0; i < Resource.MAX; i++) {
      let index = this.pendingTasks[i].findIndex(t => t.id == id)
      if (index >= 0){
        let originTask = this.pendingTasks[i][index]
        this.pendingTasks[i].splice(index, 1)
        return originTask
      }
    }

    let index = this.activedTasks.findIndex(t => t.id == id)
    if (index >= 0){
      let originTask = this.activedTasks[index]
      this.activedTasks.splice(index, 1)
      return originTask
    }
    return null
  }

}
