import {Layer, LayerType} from "../layer/Layer"
import {ModelEnum, ResourceManager, ShaderEnum} from "../util/ResourceManager"
import {UniformInfo} from "../engine/Shader"
import {MapState} from "../map/MapState"
import {Matrix4} from "../engine/Matrix"
import {GeometryUtils} from "../util/GeometryUtils"
import {LayerRenderer} from "./LayerRenderer"
import {CircleLayer} from "../layer/CircleLayer"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {PolyConstants} from "../map/MapConstants"

export class CircleLayerRenderer extends LayerRenderer {
  constructor(mapState: MapState, layer: Layer) {
    super(mapState, layer)
  }

  render(): void {
    if (this.layer.getLayerType() != LayerType.CIRCLE) {
      console.log("Invalid layer type")
      return
    }
    let circleLayer = this.layer as CircleLayer
    if (circleLayer.renderTiles.size == 0) {
      return
    }
    if (!circleLayer.visible) {
      return
    }

    let gl = this.layer.tileManager.mapView.gl
    let resourceManager = ResourceManager.getInstance(gl)
    let shader = resourceManager.getShader(ShaderEnum.CIRCLE_SHADER)
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    circleLayer.elevation > 0 && gl.enable(gl.DEPTH_TEST)
    shader.bind()
    let circleModel = resourceManager.getModel(ModelEnum.CIRCLE_MODEL)
    let cameraTarget = this.mapState.cameraPosition.getTarget()

    let meterPerPixel =
      ProjectionMercator.getMetersPerPixelAtLatitude(cameraTarget.lat, this.mapState.getZoom())
    let adjacentRadius = (circleLayer.radius / meterPerPixel)
    let hypotenuseRadius = adjacentRadius / Math.cos(Math.PI / PolyConstants.numTrianglesPerCircle)
    let adjacentStrokeWidth = circleLayer.strokeWidth
    let coordinate =
      GeometryUtils.pixelCoordinateToCameraCoordinate(
        ProjectionMercator.projectScale(circleLayer.center, this.mapState.scale), this.mapState.cameraPosition)
    let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y,
      this.mapState.is3dMode() ? GeometryUtils.getElevationPixelFromMeter(circleLayer.elevation, this.mapState.cameraPosition) : 0.0)

    shader.setUniforms([
      new UniformInfo("u_color", "4fv", circleLayer.fillColor.buffer()),
      new UniformInfo("u_radius", "1f", hypotenuseRadius + circleLayer.hypotenuseStrokeWidth),
      new UniformInfo("u_outerRadius", "1f", adjacentRadius + adjacentStrokeWidth),
      new UniformInfo("u_innerRadius", "1f",  adjacentRadius),
      new UniformInfo("u_strokeColor", "4fv", circleLayer.strokeColor.buffer()),
      new UniformInfo("u_mvpMatrix", "matrix", modelMatrix.concat(this.mapState.getViewProjectionMatrix()))
    ])
    circleModel.bind()
    shader.setAttributes(circleModel.attributeInfos)
    circleModel.render(gl.TRIANGLES)
    circleModel.unbind()
    gl.disable(gl.DEPTH_TEST)
  }
}
