import {AnnotationManager} from "../annotation/AnnotationManager"
import {GeometryTile} from "./GeometryTile"
import {TileCoordinate} from "./TileCoordinate"
import {Kind} from "./Tile"

export class AnnotationTile extends GeometryTile {
  annotationManager: AnnotationManager

  constructor(tileCoordinate: TileCoordinate, annotationManager: AnnotationManager, gl: WebGLRenderingContext) {
    super(Kind.ANNOTAION, tileCoordinate, gl)
    this.annotationManager = annotationManager
    this.annotationManager.addTile(this)
  }

  destroy(): void {
    this.layerDatas.forEach(layerData => layerData.destroy())
    this.annotationManager.removeTile(this)
  }
}
