import {Building} from "./Building"
import {Texture} from "../engine/Texture"
import {Model} from "../engine/Model"
import {BuildingType} from "./BuildingType"
import {UrlUtils} from "../util/UrlUtils"
import {MapConstants, MathConstants} from "../map/MapConstants"
import {MapSettings} from "../map/MapSettings"
import {TaskManager} from "../worker/TaskManager"
import {RequestTask, Resource} from "../worker/RequestTask"
import {ObjModelLoader} from "../util/ObjModelLoader"
import {ResourceManager, TextureEnum} from "../util/ResourceManager"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {MapState} from "../map/MapState"
import {MouseEventHandler} from "../controls/MouseEventHandler"
import {Color} from "../engine/Color"
import {MapObjectData} from "../public/MapObjectData"
import {LatLng} from "../public/LatLng"
import {ExtrudeData} from "./ExtrudeData"

export class BuildingResourceManager {

  gl: WebGLRenderingContext

  buildings: Map<string, Building>

  private invalidatedBuildings: Map<string, Building>

  models: Map<string, Model>

  modelBuildingsMap: Map<string, Building[]>

  textures: Map<string, Texture>

  hiddenBuildings: Map<string, boolean>

  private buildingTaskManager: TaskManager

  private modelRequestsMap: Map<string, RequestTask>

  private textureRequestsMap: Map<string, RequestTask>

  private mouseEventHandler: MouseEventHandler

  private readonly requestRender: () => void

  private mapState: MapState

  setBuildingLayerDirty: Function

  constructor(gl: WebGLRenderingContext, buildingTaskManager: TaskManager,
    mapState: MapState,
    mouseEventHandler: MouseEventHandler,
    requestRender: () => void) {
    this.gl = gl
    this.mapState = mapState
    this.mouseEventHandler = mouseEventHandler
    this.buildings = new Map<string, Building>()
    this.hiddenBuildings = new Map<string, boolean>()
    this.invalidatedBuildings = new Map<string, Building>()
    this.models = new Map<string, Model>()
    this.textures = new Map<string, Texture>()
    this.buildingTaskManager = buildingTaskManager
    this.modelRequestsMap = new Map<string, RequestTask>()
    this.textureRequestsMap = new Map<string, RequestTask>()
    this.setBuildingLayerDirty = () => {}
    this.requestRender = requestRender
    this.modelBuildingsMap = new Map<string, Building[]>()
  }

  hasBuilding(id: string): boolean {
    return this.buildings.has(id)
  }

  getBuilding(id: string): Building {
    if (this.buildings.has(id)) {
      return this.buildings.get(id)
    }
    return null
  }

  isHiddenBuilding(id: string): boolean {
    let hidden = this.hiddenBuildings.get(id)
    return hidden ? true : false
  }

  //use for add extrude building
  addBuildingWithData(id: string, building: Building, data: any[], indices: number[]): void {
    let buildingType = building.getBuildingType()
    if (buildingType != BuildingType.Extrude && buildingType != BuildingType.ExternalExtrude) {
      return
    }
    if (this.buildings.has(id)) {
      return
    }
    let invalidatedBuilding = this.invalidatedBuildings.get(id)
    if (invalidatedBuilding) {
      this.buildings.set(id, invalidatedBuilding)
      this.invalidatedBuildings.delete(id)
      this.setBuildingLayerDirty()
      building.destroy()
      return
    }
    let buildingModel = new Model(this.gl)
    let array = [].concat.apply([], data)
    buildingModel.init3dObject(array, indices)

    // Building texture
    let textureName = building.getTextureName()
    let texture = this.textures.get(textureName)
    if (texture != null) {
      texture.retain()
      building.setTexture(texture)
    }
    else if (textureName != null) {
      if (Color.isValid(textureName)) {
        building.setTexture(this.loadTextureFromColor(textureName))
      }
      else {
        let isCustomBuilding = building.getTypes() != null
          && building.getTypes().indexOf(MapConstants.customBuildingType) != -1
        building.setTexture(this.requestTexture(textureName, building.getTextureData(), isCustomBuilding))
      }
    }

    building.setModel(buildingModel)
    building.setHighestPosition(building.getLocation())
    building.notifyModelDownloaded()
    building.retain()
    this.buildings.set(id, building)
    this.setBuildingLayerDirty()
  }

  addBuilding(id: string, building: Building): void {
    let buildingType = building.getBuildingType()
    if (buildingType == BuildingType.Extrude || buildingType == BuildingType.ExternalExtrude) {
      return
    }
    if (this.buildings.has(id)) {
      return
    }
    let invalidatedBuilding = this.invalidatedBuildings.get(id)
    if (invalidatedBuilding) {
      this.buildings.set(id, invalidatedBuilding)
      this.invalidatedBuildings.delete(id)
      this.setBuildingLayerDirty()
      building.destroy()
      return
    }
    building.retain()

    // Building model
    let objName = building.getObjName()
    let model = this.models.get(objName)
    if (model != null) {
      model.retain()
      building.setModel(model)
      let modelBuilding = this.modelBuildingsMap.get(objName)
      if (modelBuilding) {
        modelBuilding.push(building)
      }
      else {
        building.highestModelPosition = model.getHighestModelPosition()
        this.updateBuildingHeight(building)
      }
    }
    else {

      // Check building have object data file or not ?
      let objData = building.getObjData()
      if (objData != null) {

        // Model request
        let buildingModel = new Model(this.gl)
        buildingModel.retain()
        this.models.set(objName, buildingModel)
        building.setModel(buildingModel)
        let data = ObjModelLoader.loadModel(objData) as any
        if (data != null && data.length > 0) {
          let highestModelPosition = data[0].highestPosition
          buildingModel.setHighestModelPosition(highestModelPosition)
          buildingModel.minX = data[0].minX
          buildingModel.minY = data[0].minY
          buildingModel.minZ = data[0].minZ

          buildingModel.maxX = data[0].maxX
          buildingModel.maxY = data[0].maxY
          buildingModel.maxZ = data[0].maxZ

          building.highestModelPosition = highestModelPosition

          this.updateBuildingHeight(building)
          buildingModel.init3dObject(data[0].data)
          building.updateScaledMatrix(this.mapState)
          this.requestRender()
        }
      }
      else if (objName != null) {

        // If building do not have object data file then check to request server
        this.modelBuildingsMap.set(objName, [building])
        let isCustomBuilding = building.getTypes() != null
          && building.getTypes().indexOf(MapConstants.customBuildingType) != -1
        building.setModel(this.requestModel(objName, isCustomBuilding))
      }
    }

    // Building texture
    let textureName = building.getTextureName()
    let texture = this.textures.get(textureName)
    if (texture != null) {
      texture.retain()
      building.setTexture(texture)
    }
    else if (textureName != null) {
      if (Color.isValid(textureName)) {
        building.setTexture(this.loadTextureFromColor(textureName))
      }
      else {
        let isCustomBuilding = building.getTypes() != null
          && building.getTypes().indexOf(MapConstants.customBuildingType) != -1
        building.setTexture(this.requestTexture(textureName, building.getTextureData(), isCustomBuilding))
      }
    }

    this.buildings.set(id, building)

    this.setBuildingLayerDirty()
  }

  setHiddenObject(id: string): void {
    this.hiddenBuildings.set(id, true)
  }

  setUnhiddenObject(id: string): void {
    let hidden = this.hiddenBuildings.get(id)
    if (hidden) {
      this.hiddenBuildings.delete(id)
    }
    else {
      console.warn("No hidden builing with id : " + id + " to unhide !")
    }
  }

  removeAllResources(): void {
    this.invalidatedBuildings.forEach(building => building.destroy())
    this.buildings.forEach(building => building.destroy())
    this.models.forEach(model => model.destroy())
    this.textures.forEach(texture => texture.destroy())
    this.buildings.clear()
    this.invalidatedBuildings.clear()
    this.models.clear()
    this.textures.clear()
    this.modelRequestsMap.forEach(value => {
      this.buildingTaskManager.cancelTask(value)
    })
    this.modelRequestsMap.clear()
    this.textureRequestsMap.forEach(value => {
      this.buildingTaskManager.cancelTask(value)
    })
    this.textureRequestsMap.clear()
  }

  increaseBuildingReference(id: string): void {
    let building = this.buildings.get(id)
    if (building == null) {
      console.warn("Can not find any building with id: " + id + " to increase reference !")
      return
    }
    building.retain()
  }

  decreaseBuildingReference(id: string): void {
    let building = this.buildings.get(id)
    if (building == null) {

      // May be we disabled showing map objects on the map
      return
    }
    if (building.reference == 1 && this.mouseEventHandler.isHandling) {
      this.invalidatedBuildings.set(building.getId(), building)
      this.buildings.delete(id)
    }
    else {
      building.release()
      if (!building.reference) {
        let buildingType = building.getBuildingType()
        if (buildingType != BuildingType.Extrude && buildingType != BuildingType.ExternalExtrude) {
          // Decrease model reference
          let objName = building.getObjName()
          this.decreaseModelReference(objName)
        }
        // Decrease texture reference
        let textureName = building.getTextureName()
        this.decreaseTextureReference(textureName)

        this.buildings.delete(id)
      }
    }
    this.setBuildingLayerDirty()
  }

  decreaseTextureReference(textureName: string) {
    if (textureName == null) {
      return
    }
    let texture = this.textures.get(textureName)
    if (texture == null) {
      console.error("Can not find texture with id: " + textureName + " for building !")
      return
    }
    texture.release()
    if (!texture.reference) {
      let textureRequest = this.textureRequestsMap.get(textureName)
      if (textureRequest) {
        this.buildingTaskManager.cancelTask(textureRequest)
        this.textureRequestsMap.delete(textureName)
      }
      this.textures.delete(textureName)
    }
  }

  decreaseModelReference(objName: string) {
    let model = this.models.get(objName)
    if (model == null) {
      console.error("Can not find model with id: " + objName + " for building !")
      return
    }
    model.release()
    if (!model.reference) {
      let modelRequest = this.modelRequestsMap.get(objName)
      if (modelRequest != null) {
        this.buildingTaskManager.cancelTask(modelRequest)
        this.modelRequestsMap.delete(objName)
      }
      this.models.delete(objName)
    }
  }

  removeBuildings(filterFunc: (building: Building) => boolean) {
    Array.from(this.buildings.values())
      .filter(building => filterFunc != null ? filterFunc(building) : true)
      .forEach(building => {
        this.deleteBuilding(building.getId())
      })
  }

  removeHiddenBuildings(): void {
    this.hiddenBuildings.clear()
  }

  update(): void {
    if (!this.mouseEventHandler.isHandling && this.invalidatedBuildings.size != 0) {
      this.invalidatedBuildings.forEach(building => {
        building.release()
        if (!building.reference) {
          let buildingType = building.getBuildingType()
          if (buildingType != BuildingType.Extrude && buildingType != BuildingType.ExternalExtrude) {
            // Decrease model reference
            let objName = building.getObjName()
            this.decreaseModelReference(objName)
          }

          // Decrease texture reference
          let textureName = building.getTextureName()
          this.decreaseTextureReference(textureName)
        }
        else {
          console.warn("The reference must be 0 now...")
        }
      })
      this.invalidatedBuildings.clear()
    }
  }

  private deleteBuilding(id: string) {
    let building = this.buildings.get(id)
    if (building == null) {

      //  We disabled showing objects on the map
      return
    }
    let buildingType = building.getBuildingType()
    if (buildingType != BuildingType.Extrude && buildingType != BuildingType.ExternalExtrude) {
      // Decrease model reference
      let objName = building.getObjName()
      this.decreaseModelReference(objName)
    }

    // Decrease texture reference
    let textureName = building.getTextureName()
    this.decreaseTextureReference(textureName)

    building.destroy()
    this.buildings.delete(id)
    this.setBuildingLayerDirty()
  }

  private requestModel(objName: string, isCustomBuilding: boolean): Model {
    if (objName == null) {
      console.error("You do not set Url for Model. Please set first !")
      return null
    }
    // Model request
    let buildingModel = new Model(this.gl)
    buildingModel.retain()
    this.models.set(objName, buildingModel)

    // If building do not have object data file then check to request server
    let buildingModelRequestUrl = null

    if (UrlUtils.isUrlRequest(objName)) {
      buildingModelRequestUrl = objName
    }
    else {
      buildingModelRequestUrl =
        UrlUtils.getBuildingDataUrl(MapConstants.baseModelRequestUrl, objName, MapSettings.getAccessKey())
    }

    let requestTask = new RequestTask(isCustomBuilding ? Resource.CustomModel : Resource.Model)
    requestTask.url = buildingModelRequestUrl
    requestTask.completion = (response: any) => {
      if (response.data != null) {
        let highestModelPosition = response.data[0].highestPosition
        buildingModel.init3dObjectFormArrayBuffer(response.data[0].data)
        buildingModel.setHighestModelPosition(highestModelPosition)
        buildingModel.minX = response.data[0].minX
        buildingModel.minY = response.data[0].minY
        buildingModel.minZ = response.data[0].minZ

        buildingModel.maxX = response.data[0].maxX
        buildingModel.maxY = response.data[0].maxY
        buildingModel.maxZ = response.data[0].maxZ
        let modelBuildings = this.modelBuildingsMap.get(objName)
        if (modelBuildings) {
          modelBuildings.forEach(b => {
            b.highestModelPosition = highestModelPosition
            this.updateBuildingHeight(b)
            b.updateScaledMatrix(this.mapState)
          })
          this.modelBuildingsMap.delete(objName)
        }
        this.modelRequestsMap.delete(objName)
        this.requestRender()
      }
    }
    this.modelRequestsMap.set(objName, requestTask)
    this.buildingTaskManager.postTask(requestTask)

    return buildingModel
  }

  private loadTextureFromColor(color: string): Texture {
    if (color == null) {
      return
    }
    let mapObjectTexture = new Texture(this.gl)
    mapObjectTexture.retain()
    this.textures.set(color, mapObjectTexture)
    mapObjectTexture.loadColor(Color.fromHex(color))
    return mapObjectTexture
  }

  private requestTexture(textureName: string, textureData?: string, isCustomBuilding: boolean = false): Texture {

    let mapObjectTexture = new Texture(this.gl)
    mapObjectTexture.retain()
    this.textures.set(textureName, mapObjectTexture)

    // Check building have texture data file or not ?
    if (textureData) {
      mapObjectTexture.loadImage(textureData, this.requestRender)
    }
    else {

      // If building do not have texture data file then check to request server
      let buildingTextureRequestUrl = null
      if (textureName == null) {
        mapObjectTexture = ResourceManager.getInstance(this.gl).getTexture(TextureEnum.WHITE_TEXTURE)
        return mapObjectTexture
      }
      if (UrlUtils.isUrlRequest(textureName)) {
        buildingTextureRequestUrl = textureName
      }
      else {
        buildingTextureRequestUrl =
            UrlUtils.getBuildingDataUrl(MapConstants.baseTextureRequestUrl, textureName, MapSettings.getAccessKey())
      }

      let requestTask = new RequestTask(isCustomBuilding ? Resource.CustomTexture : Resource.Texture)
      requestTask.url = buildingTextureRequestUrl
      requestTask.completion = (response: any) => {
        if (response.data != null) {
          let callBack = () => {
            this.textureRequestsMap.delete(textureName)
            this.requestRender()
          }
          mapObjectTexture.loadImageFromArrayBuffer(response.data.data, response.data.width,
            response.data.height, true, callBack)
        }
      }
      this.textureRequestsMap.set(textureName, requestTask)
      this.buildingTaskManager.postTask(requestTask)
    }
    return mapObjectTexture
  }

  public updateBuildingHeight(building: Building): void {
    let highestModelPosition = building.highestModelPosition
    if (!highestModelPosition) {
      return
    }
    let location = building.getLocation()
    let bearing = building.getBearing() * MathConstants.deg2Rad
    let scale = building.getScale()
    let locationInMeters = ProjectionMercator.convertToMeterFromLatLng(location)
    locationInMeters.y -= Math.cos(bearing) * highestModelPosition.z * scale.y
      - Math.sin(bearing) * highestModelPosition.x * scale.x
    locationInMeters.x += Math.cos(bearing) * highestModelPosition.x * scale.x
      + Math.sin(bearing) * highestModelPosition.z * scale.y

    building.setHighestPosition(ProjectionMercator.convertToLatLngFromMeter(locationInMeters))
    building.setHeight(highestModelPosition.y * scale.z)
    building.notifyModelDownloaded()
  }

  setColor(id: string, color: string): void {
    let building = this.buildings.get(id)
    if (building == null) {
      return
    }
    this.decreaseTextureReference(building.getTextureName())
    let texture = this.textures.get(color)
    if (texture != null) {
      texture.retain()
      building.setTexture(texture)
    }
    else {
      building.setTexture(this.loadTextureFromColor(color))
    }
    building.setTextureName(color)
    building.setTextureData(null)
  }

  setTextureName(id: string, textureName: string): void {
    let building = this.buildings.get(id)
    if (building == null) {
      return
    }
    this.decreaseTextureReference(building.getTextureName())
    let texture = this.textures.get(textureName)
    if (texture != null) {
      texture.retain()
      building.setTexture(texture)
    }
    else {
      building.setTexture(this.requestTexture(textureName, null, true))
    }
    building.setTextureName(textureName)
    building.setTextureData(null)
  }

  setTextureData(id: string, textureData: MapObjectData): void {
    let building = this.buildings.get(id)
    if (building == null) {
      return
    }
    this.decreaseTextureReference(building.getTextureName())
    let texture = this.textures.get(textureData.reuseId)
    if (texture != null) {
      texture.retain()
      building.setTexture(texture)
      building.setTextureName(textureData.reuseId)
    }
    else {
      let textureName = textureData.reuseId || building.getId()
      building.setTexture(this.requestTexture(textureName, textureData.data, true))
      building.setTextureName(textureName)
    }
  }

  setCoordinates(id: string, coordinates: LatLng[], data: ExtrudeData): void {
    let building = this.buildings.get(id)
    if (building == null) {
      return
    }
    let buildingModel = building.getModel()
    if (buildingModel) {
      buildingModel.destroy()
    }
    buildingModel = new Model(this.gl)
    let array = [].concat.apply([], data.vertices)
    buildingModel.init3dObject(array, data.indices)
    building.setModel(buildingModel)
    building.setCoordinates(coordinates)
  }
}
