import geojsonvt from "geojson-vt"
import {Annotation, AnnotationId} from "./Annotation"
import {MapView} from "../map/MapView"
import {TileCoordinate} from "../tile/TileCoordinate"
import {AnnotationTileData} from "../tile/data/AnnotationTileData"
import {MapConstants} from "../map/MapConstants"
import {GeometryCollection} from "../tile/data/GeometryCollection"
import {AnnotationManager} from "./AnnotationManager"

export abstract class ShapeAnnotationData {

  private geoJsonIndexer: geojsonvt

  private readonly id: AnnotationId

  private readonly layerId: string

  abstract updateLayer(map: MapView): void

  abstract getAnnotation(): Annotation

  abstract getFeatures(): any

  protected constructor(annotationId: AnnotationId) {
    this.id = annotationId
    this.layerId = AnnotationManager.annotationLayerId + annotationId.toString()
  }

  reloadLayout(): void {
    this.geoJsonIndexer = null
  }

  updateTileData(tileCoordinate: TileCoordinate, annotationTileData: AnnotationTileData, maxNativeZoom: number): void {
    if (!this.geoJsonIndexer) {
      let options = {
        maxZoom: maxNativeZoom,
        buffer: 0,
        extent: MapConstants.tileExtend,
        tolerance: 4,
      }
      this.geoJsonIndexer = geojsonvt(this.getFeatures(), options)
    }
    let tile = this.geoJsonIndexer.getTile(tileCoordinate.getZoom(), tileCoordinate.getX(), tileCoordinate.getY())
    if (tile == null || tile.features.length === 0) {
      return
    }
    let layer = annotationTileData.addLayer(this.layerId)
    tile.features.forEach(feature => {
      let geometryType = feature.type
      let geometries = new GeometryCollection(feature.geometry)
      layer.addFeature(this.id, geometryType, geometries)
    })
  }

  getLayerId(): string {
    return this.layerId
  }

  clearIndex(): void {
    this.geoJsonIndexer = null
  }
}

