import {Building} from "./Building"
import {MapState} from "../map/MapState"
import {Vector2} from "../engine/Vector2"
import {CollisionUtils} from "../util/CollisionUtils"
import {Point} from "../public/Point"
import {InfoWindow} from "../annotation/InfoWindow"
import {DOMUtils} from "../util/DOMUtils"
import {MapView} from "../map/MapView"
import {GeometryUtils} from "../util/GeometryUtils"
import {Matrix4} from "../engine/Matrix"
import {UniformInfo} from "../engine/Shader"
import {ModelEnum, ResourceManager, ShaderEnum} from "../util/ResourceManager"
import {MapUtils} from "../util/MapUtils"
import {Texture} from "../engine/Texture"
import {SymbolResourceManager} from "../annotation/SymbolResourceManager"
import {LatLng} from "../public/LatLng"
import {SymbolConstants} from "../map/MapConstants"
import {Box} from "../util/Box"

class SymbolSelectionData {

  private _position: LatLng

  private _elevation: number

  private _buildingInformation: string

  constructor(position: LatLng, elevation: number, buildingInformation: string) {
    this._position = position
    this._elevation = elevation
    this._buildingInformation = buildingInformation
  }

  get position(): LatLng {
    return this._position
  }

  get elevation(): number {
    return this._elevation
  }

  get buildingInformation(): string {
    return this._buildingInformation
  }

  set position(value: LatLng) {
    this._position = value
  }

  set elevation(value: number) {
    this._elevation = value
  }

  set buildingInformation(value: string) {
    this._buildingInformation = value
  }
}

export class BuildingIconSelectionManager {

  private iconSelectionWidth: number

  private iconSelectionHeight: number

  private iconSelelectionWindowAnchor: Point

  private iconSelectionAnchor: Point

  private iconSelectionTexture: Texture

  private selectionBuildingIdIconMap: Map<string, SymbolSelectionData>

  private selectedSymbolInfoWindow: InfoWindow

  private infoWindowContainer: HTMLElement

  private infoContentsContainer: HTMLElement

  private selectedSymbol: SymbolSelectionData

  private mapView: MapView

  private  gl: WebGLRenderingContext

  constructor(gl: WebGLRenderingContext, mapView: MapView) {
    this.selectionBuildingIdIconMap = new Map<string, SymbolSelectionData>()
    this.mapView = mapView
    this.gl = gl
    this.iconSelectionWidth = 16 * MapUtils.pixelScale
    this.iconSelectionHeight = 20 * MapUtils.pixelScale
    this.iconSelectionTexture = SymbolResourceManager.getInstance(this.gl).acquire(SymbolConstants.selectionSymbol)
    this.iconSelectionAnchor = new Point(0.5, 1.0)
    this.iconSelelectionWindowAnchor = new Point(0.5, 0)
  }

  addBuilding(building: Building): void {
    let selectionIconSymbol = new SymbolSelectionData(building.getLocation(),
      building.getElevation() + building.getHeight(),
      building.getName())
    this.selectionBuildingIdIconMap.set(building.getId(), selectionIconSymbol)
  }

  updateBuilding(building: Building): void {
    let selectionIconSymbol = this.selectionBuildingIdIconMap.get(building.getId())
    if (selectionIconSymbol) {
      selectionIconSymbol.position = building.getLocation()
      selectionIconSymbol.buildingInformation = building.getName()
      selectionIconSymbol.elevation = building.getElevation() + building.getHeight()
    }
  }

  removeBuilding(buildingId: string): void {
    if (!this.selectionBuildingIdIconMap.delete(buildingId)) {
      console.warn("selection symbol does not exist- buildingId: " + buildingId)
    }
  }

  onHover(p: Vector2): boolean {
    let touchBox = new Box(
      new Point(p.x - MapUtils.touchBoxSize / 2, p.y - MapUtils.touchBoxSize / 2),
      new Point(p.x + MapUtils.touchBoxSize / 2, p.y + MapUtils.touchBoxSize / 2))

    let hitSelectionSymbol = Array.from(this.selectionBuildingIdIconMap.values()).some(symbolSelectionData => {
      if (CollisionUtils.boxIntersectsSymbol(touchBox, symbolSelectionData.position,
        symbolSelectionData.elevation, this.iconSelectionWidth, this.iconSelectionHeight,
        this.iconSelectionAnchor, this.mapView.mapState)) {
        this.showSelectionSymbolInfoWindow(symbolSelectionData)
        return true
      }
      return false
    })
    if (!hitSelectionSymbol) {

      // deselect showing info window
      this.deselectInfoWindow()
    }
    return hitSelectionSymbol
  }

  deselectInfoWindow(): void {
    this.selectedSymbol = null
    if (this.infoContentsContainer) {
      this.infoWindowContainer.style.visibility = "hidden"
    }
  }

  render(mapState: MapState): void {
    let resourceManager = ResourceManager.getInstance(this.gl)
    let shader = resourceManager.getShader(ShaderEnum.SYMBOL_SHADER)
    let model = resourceManager.getModel(ModelEnum.SYMBOL_MODEL)
    shader.bind()
    model.bind()
    shader.setAttributes(model.attributeInfos)

    let cameraPosition = mapState.cameraPosition
    let screenSize = mapState.getScreenSize()
    Array.from(this.selectionBuildingIdIconMap.values()).forEach(symbolSelectionData => {
      let elevation = this.mapView.mapState.is3dMode() ? symbolSelectionData.elevation : 0.0
      let coordinate =
        GeometryUtils.latLngToScreenCoordinate(symbolSelectionData.position, elevation, cameraPosition, screenSize)
      let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0)
      let matrixTranslate = Matrix4.translate((0.5 - this.iconSelectionAnchor.x) * this.iconSelectionWidth,
        (0.5 - this.iconSelectionAnchor.y) * this.iconSelectionHeight,
        0)
      modelMatrix = matrixTranslate.concat(modelMatrix)
      let matrixScale = Matrix4.scale(this.iconSelectionWidth / 2,
        this.iconSelectionHeight / 2,
        1)
      modelMatrix = matrixScale.concat(modelMatrix)
      let mvpMatrix = modelMatrix.concat(mapState.getOrthoProjectionMatrix())
      shader.setUniforms([
        new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix),
        new UniformInfo("u_texture", "texture", this.iconSelectionTexture)
      ])
      model.render(this.gl.TRIANGLES)

    })
    model.unbind()
    this.updateInfoWindowPosition()
  }

  destroy(): void {
    this.selectionBuildingIdIconMap.clear()
    this.selectionBuildingIdIconMap = null
    this.mapView = null
    this.selectedSymbolInfoWindow = null
    this.infoWindowContainer = null
    this.infoContentsContainer = null
    this.selectedSymbol = null
    this.iconSelectionTexture = null
    SymbolResourceManager.getInstance(this.gl).release(SymbolConstants.selectionSymbol)
  }

  private updateInfoWindowPosition(): void {
    if (!this.selectedSymbol) {
      return
    }
    let position =
      this.selectedSymbolInfoWindow.getPosition(this.selectedSymbol.position, this.selectedSymbol.elevation,
        this.iconSelectionWidth, this.iconSelectionHeight, this.iconSelectionAnchor, this.mapView.mapState)
    let left = position.x
    let top = position.y
    let style = this.infoWindowContainer.style
    style.top = top.toString() + "px"
    style.left = left.toString() + "px"
    if (style.visibility == "hidden") {
      style.visibility = "visible"
    }
  }

  private showSelectionSymbolInfoWindow(symbol: SymbolSelectionData): void {
    this.selectedSymbol = symbol
    let content = this.getInfoWindowHTMLElement(symbol.buildingInformation)
    this.selectedSymbolInfoWindow = new InfoWindow(content, this.iconSelelectionWindowAnchor)
    this.updateInfoWindowPosition()
  }

  private getInfoWindowHTMLElement(buildingInformation: string): HTMLElement {
    if (!this.infoContentsContainer) {
      if (!this.infoContentsContainer) {
        this.infoWindowContainer = DOMUtils.create("div",
          "mf-info-window-container",
          this.mapView.getMapContainer())
      }
      this.infoContentsContainer
        = DOMUtils.create("div", "mf-info-window-content mf-scrollbar", this.infoWindowContainer)
    }
    this.infoContentsContainer.innerHTML = `<p> ${buildingInformation} </p>`
    return this.infoContentsContainer
  }
}
