import {TileManager} from "../tile/TileManager"
import {LayerOverlay, LayerOverlayId} from "../public/LayerOverlay"

export class LayerOverlayManager {

  private idCounter: LayerOverlayId = 0

  private tileManager: TileManager

  constructor(tileManage: TileManager) {
    this.tileManager = tileManage
  }

  addLayerOverlay(layerOverlay: LayerOverlay): number {
    let tileAreaId = this.idCounter++
    this.tileManager.addLayerOverlay(tileAreaId, layerOverlay)
    return tileAreaId
  }

  removeLayerOverlay(overlayId: LayerOverlayId): void {
    this.tileManager.removeLayerOverlay(overlayId)
  }
}
