import {Vector2} from "../engine/Vector2"
import {Vector3} from "../engine/Vector3"
import {LatLng} from "../public/LatLng"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {TileCoordinate} from "../tile/TileCoordinate"
import {LatLngBounds} from "../public/LatLngBounds"
import {MapConstants, MathConstants} from "../map/MapConstants"
import {MapUtils} from "./MapUtils"
import {CameraPosition} from "../public/CameraPosition"
import {CameraUtils} from "./CameraUtils"

export class GeometryUtils {

  constructor() {}

  private static lat(z: number, y:number): number {
    let n = Math.PI - 2.0 * Math.PI * y / Math.pow(2.0, z)
    return MathConstants.rad2Deg * Math.atan(0.5 * (Math.exp(n) - Math.exp(-n)))
  }

  private static lon(z: number, x: number): number {
    return x / Math.pow(2.0, z) * MapConstants.maxDegrees - MapConstants.maxLongitude
  }

  static screenCoordinateToNDC(screenCoordinate: Vector2, screenSize: Vector2): Vector2 {
    let x = 2 * screenCoordinate.x / screenSize.x - 1
    let y = 1 - 2 * screenCoordinate.y / screenSize.y
    return new Vector2(x, y)
  }

  static NDCCoordinateToScreen(ndcCoordinate: Vector2, screenSize: Vector2): Vector2 {
    let x = screenSize.x * (1 + ndcCoordinate.x) / 2
    let y = screenSize.y * (1 - ndcCoordinate.y) / 2
    return new Vector2(x, y)
  }

  static screenCoordinateToLatLng(screenCoordinate: Vector2, cameraPosition: CameraPosition,
    screenSize: Vector2, elevation: number = 0): LatLng {
    return this.screenCoordinateToLatLngZoom(screenCoordinate, cameraPosition, screenSize, cameraPosition.getZoom(), elevation)
  }

  /****
   * screenCoordinateToLatLngZoom
   * @param screenCoordinate
   * @param cameraPosition
   * @param screenSize
   * @param zoom
   * @param elevation
   */
  static screenCoordinateToLatLngZoom(
    screenCoordinate: Vector2,
    cameraPosition: CameraPosition,
    screenSize: Vector2,
    zoom: number,
    elevation: number = 0): LatLng {

    if (cameraPosition.getTilt() > MapConstants.maxTiltIntersectWithScreen) {
      let latLngTopLeft = this.screenCoordinateToLatLng(new Vector2(0, 0),
        new CameraPosition(cameraPosition.getTarget(), MapConstants.maxTiltIntersectWithScreen, cameraPosition.getBearing(), cameraPosition.getZoom()), screenSize)
      let ratio = this.latLngToScreenCoordinate(latLngTopLeft, 0, cameraPosition, screenSize).y / screenSize.y
      let skyHeight = ratio * screenSize.y

      if (screenCoordinate.y < skyHeight) {
        return new LatLng(1 / 0, 1 / 0)
      }
    }

    //TODO: Fix Me! precision floating-number
    if (zoom < 0) {
      zoom = cameraPosition.getZoom()
    }
    let elevationPixel = GeometryUtils.getElevationPixelFromMeter(elevation, cameraPosition)
      * Math.sin(MathConstants.deg2Rad * -cameraPosition.getTilt())

    let inverseViewProjectionMatrix = CameraUtils.calculateInverseViewProjectionMatrix(cameraPosition, screenSize.x, screenSize.y)
    let scale = Math.pow(2, zoom)

    // Convert to NDC coorinate
    let ndcCoordinate = GeometryUtils.screenCoordinateToNDC(
      new Vector2(screenCoordinate.x, screenCoordinate.y - elevationPixel), screenSize)
    let pos1W = inverseViewProjectionMatrix.transform(new Vector3(ndcCoordinate.x, ndcCoordinate.y, -1), 1)
    let pos2W = inverseViewProjectionMatrix.transform(new Vector3(ndcCoordinate.x, ndcCoordinate.y, 1), 1)
    let dz = pos2W.z - pos1W.z
    let xm = pos1W.x - (pos2W.x - pos1W.x) * pos1W.z / dz
    let ym = pos1W.y - (pos2W.y - pos1W.y) * pos1W.z / dz

    // Get camera target position
    let target = cameraPosition.getTarget()
    let worldScale = Math.pow(2, zoom)
    let targetInPixel = ProjectionMercator.projectScale(target, worldScale)
    return ProjectionMercator.unproject(new Vector2(xm + targetInPixel.x, targetInPixel.y - ym), scale)
  }

  static pixelCoordinateToCameraCoordinate(pixelCoordinate: Vector2, cameraPosition: CameraPosition): Vector2 {
    return GeometryUtils.pixelCoordinateToCameraCoordinateZoom(pixelCoordinate, cameraPosition, cameraPosition.getZoom())
  }

  static pixelCoordinateToCameraCoordinateZoom(pixelCoordinate: Vector2, cameraPosition: CameraPosition, zoom: number): Vector2 {
    let scale = Math.pow(2, zoom)
    let worldSize = ProjectionMercator.worldSize(scale)
    let targetInPixel = ProjectionMercator.projectScale(cameraPosition.getTarget(), scale)

    /** Convert camera position to center coordinate */
    targetInPixel.x = targetInPixel.x - worldSize / 2
    targetInPixel.y = worldSize / 2 - targetInPixel.y

    let point = pixelCoordinate

    /** Convert to center coordinate */
    point.x = point.x - worldSize / 2
    point.y = worldSize / 2 - point.y

    /** Convert to camera coordinate */
    point.x -= targetInPixel.x
    point.y -= targetInPixel.y
    return point
  }

  static latLngToScreenCoordinate(latLng: LatLng, elevation: number, cameraPosition: CameraPosition, screenSize: Vector2): Vector2 {
    return this.latLngToScreenCoordinateZoom(latLng, elevation, cameraPosition, screenSize, cameraPosition.getZoom())
  }

  static latLngToScreenCoordinateZoom(latLng: LatLng, elevation: number, cameraPosition: CameraPosition,
    screenSize: Vector2, zoom: number): Vector2 {
    if (zoom < 0){
      zoom = cameraPosition.getZoom()
    }
    let scale = Math.pow(2, zoom)
    let pixelCoordinate = ProjectionMercator.projectScale(latLng, scale)
    let coordinate = this.pixelCoordinateToCameraCoordinateZoom(pixelCoordinate, cameraPosition, zoom)
    let elevationPixel = GeometryUtils.getElevationPixelFromMeter(elevation, cameraPosition)
    let pointCoordinate = new Vector3(coordinate.x, coordinate.y, elevationPixel)

    let viewProjectionMatrix = CameraUtils.calculateViewProjectionMatrix(cameraPosition, screenSize.x, screenSize.y)
    let point = viewProjectionMatrix.transform(pointCoordinate, 1.0)

    // Convert NDC to Screen Coordinate
    let x =  (point.x + 1.0) * screenSize.x / 2
    let y = screenSize.y - (point.y + 1.0) * screenSize.y / 2
    return new Vector2(x, y)
  }

  static tileCoordinateToLatLngBounds(tileCoordinate: TileCoordinate): LatLngBounds {
    let x = tileCoordinate.getX()
    let y = tileCoordinate.getY()
    let z = tileCoordinate.getZoom()

    let southwest = new LatLng(GeometryUtils.lat(z, y + 1), GeometryUtils.lon(z, x))
    let northeast = new LatLng(GeometryUtils.lat(z, y), GeometryUtils.lon(z, x + 1))
    return new LatLngBounds(southwest, northeast)
  }

  static pixelCoordinateToVectorTileCoordinate(pixelCoordinate: Vector2,
    tileCoordinateX: number,
    tileCoordinateY: number, tileScale: number) {
    let tileSize = MapUtils.getTileSize() * tileScale
    let vectorTileCoordinateX = pixelCoordinate.x - tileCoordinateX * tileSize
    let vectorTileCoordinateY = pixelCoordinate.y - tileCoordinateY * tileSize
    vectorTileCoordinateX = vectorTileCoordinateX / tileSize - 0.5
    vectorTileCoordinateY = 0.5 - vectorTileCoordinateY / tileSize
    return new Vector2(vectorTileCoordinateX, vectorTileCoordinateY)
  }

  static getElevationPixelFromMeter(meter: number, cameraPosition: CameraPosition): number {
    meter = Math.max(meter, 0.0)
    return GeometryUtils.getPixelFromMeter(meter, cameraPosition.getTarget().lat, cameraPosition.getZoom())
  }

  static getPixelFromMeter(meter: number, lat: number, zoom: number): number {
    let metersPerPixel = ProjectionMercator.getMetersPerPixelAtLatitude(
      lat, zoom)
    return (meter / metersPerPixel)
  }
}
