import {Particle} from "./Particle"
import {ParticleSystem} from "./ParticleSystem"

export class ParticlePool {

  /**
   * This is the first particle in a linked list pool.
   */
  private _poolFirst: Particle

  public acquire(emitter: ParticleSystem): Particle {
    let particle
    if (this._poolFirst) {

      // Get particle from the pool
      particle = this._poolFirst
      this._poolFirst = this._poolFirst.next
      particle.next = null

      // Change the emitter
      particle.emitter = emitter
    }
    else {

      // Create a new one
      particle = new Particle(emitter)
    }
    return particle
  }

  release(particle: Particle): void {
    particle.prev = null
    particle.next = this._poolFirst
    this._poolFirst = particle
  }

  destroy(): void {

    // wipe the pool clean
    let next
    for (let particle = this._poolFirst; particle; particle = next) {
      next = particle.next
      particle.destroy()
    }
    this._poolFirst = null
  }
}