import {MapObject} from "../public/MapObject"
import {BuildingResourceManager} from "./BuildingResourceManager"
import * as RBush from "rbush"
import * as cover from "@mapbox/tile-cover"
import {MapState} from "../map/MapState"
import {TileCoordinate} from "../tile/TileCoordinate"
import {GeometryUtils} from "../util/GeometryUtils"
import {TileManager} from "../tile/TileManager"
import {RasterTile} from "../tile/RasterTile"
import {ModelJson} from "./ModelJson"
import {BuildingJson} from "./BuildingJson"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {Vector2} from "../engine/Vector2"
import {LatLngBounds} from "../public/LatLngBounds"
import {MapConstants} from "../map/MapConstants"
import {TaskManager} from "../worker/TaskManager"
import {RequestTask, Resource} from "../worker/RequestTask"
import {UrlUtils} from "../util/UrlUtils"
import {MapSettings} from "../map/MapSettings"
import {ObjModelLoader} from "../util/ObjModelLoader"
import {BoundingBox} from "./BoundingBox"
import {ModelGeometry} from "./data/ModelGeometry"
import {LatLng} from "../public/LatLng"
import {Point} from "../public/Point"
import {ConvexHull} from "../util/ConvexHull"

/*eslint camelcase: "off"*/

class IndexedMapObject {

  private _mapObject: MapObject

  private _boundingBox: BoundingBox

  private _coverPoints: LatLng[] = null

  private _points: Point[] = null

  private _hasBounds: boolean = false

  constructor(mapObject: MapObject) {
    this._mapObject = mapObject
    this.updateBounds()
    this._hasBounds = false
  }

  updateBoundsFromLatLngBounds(bounds: LatLngBounds) {
    let southwest = bounds.getSouthwest()
    let northeast = bounds.getNortheast()
    if (!this._boundingBox) {
      let scale = this._mapObject.getScale()
      if (scale > 0) {
        let mapScale = this.mapObject.getMap().mapView.mapState.getScale()
        let southwestInMeters = ProjectionMercator.projectScale(southwest, mapScale)
        let northeastInMeters = ProjectionMercator.projectScale(northeast, mapScale)
        let locationInMeters = ProjectionMercator.projectScale(this.mapObject.getLocation(), mapScale)

        let distanceNorthEast =
          new Vector2(northeastInMeters.x - locationInMeters.x, locationInMeters.y - northeastInMeters.y)
        let distanceSouthWest =
          new Vector2(locationInMeters.x - southwestInMeters.x, southwestInMeters.y - locationInMeters.y)

        let northeastInMetersAfterScale = new Vector2(locationInMeters.x + scale * distanceNorthEast.x,
          locationInMeters.y - scale * distanceNorthEast.y)
        let southwestInMetersAfterScale = new Vector2(locationInMeters.x - scale * distanceSouthWest.x,
          locationInMeters.y + scale * distanceSouthWest.y)

        northeast = ProjectionMercator.unproject(northeastInMetersAfterScale, mapScale)
        southwest = ProjectionMercator.unproject(southwestInMetersAfterScale, mapScale)
      }
    }
    this._minX = southwest.lng
    this._minY = southwest.lat
    this._maxX = northeast.lng
    this._maxY = northeast.lat
    this._hasBounds = true
  }

  updateCoverPoints(points: Point[]) {
    if (points != null && points.length != 0) {
      /** Sort array points increase by y and x **/
      points.sort((a: Point, b: Point) => {
        if (a.y == b.y) {
          return a.x - b.x
        }
        return a.y - b.y
      })

      /** Delete duplicate points **/
      points = points.filter((value, index, array) => index == 0 ||
        (value.x != array[index - 1].x || value.y != array[index - 1].y))

      this._points = points
    }
    if (this._points == null || this._points.length ==0) {
      return
    }
    let bearing = this._mapObject.getBearing()
    let scale = this._mapObject.getScale()
    let positionInMeters = ProjectionMercator.convertToMeterFromLatLng(this._mapObject.getLocation())
    let coverPoints: LatLng[] = []

    let _points: Point[] = this._points.map(point => new Point(point.x, point.y))
    for (let i = 0; i < _points.length; ++i) {
      if (bearing != 0) {
        let x = _points[i].x
        let y = _points[i].y
        _points[i].x = (x * Math.cos(bearing * Math.PI / 180.0)
          + y * Math.sin(bearing * Math.PI / 180.0)) * scale
        _points[i].y = (x * Math.sin(bearing * Math.PI / 180.0)
          - y * Math.cos(bearing * Math.PI / 180.0)) * scale
      }
      else {
        _points[i].x = _points[i].x * scale
        _points[i].y = -_points[i].y * scale
      }
      let pointInLatLng = ProjectionMercator.convertToLatLngFromMeter(
        new Vector2(positionInMeters.x + _points[i].x, positionInMeters.y + _points[i].y))
      coverPoints.push(pointInLatLng)
    }
    this._coverPoints = coverPoints
  }

  updateBounds(objectBounds?: BoundingBox): void {
    this._boundingBox = objectBounds
    let minX = 0
    let minY = 0
    let maxX = 0
    let maxY = 0
    if (this._boundingBox) {
      let scale = this.mapObject.getScale()
      minX = this._boundingBox.minX * scale
      minY = this._boundingBox.minY * scale
      maxX = this._boundingBox.maxX * scale
      maxY = this._boundingBox.maxY * scale
      let bearing = -this.mapObject.getBearing()
      if (bearing != 0) {
        let northeastInMeters = new Vector2(maxX, minY)
        let southwestInMeters = new Vector2(minX, maxY)
        let southeastInMeters = new Vector2(maxX, maxY)
        let northwestInMeters = new Vector2(minX, minY)
        southwestInMeters = new Vector2(southwestInMeters.x * Math.cos(bearing * Math.PI / 180.0)
          - southwestInMeters.y * Math.sin(bearing * Math.PI / 180.0),
        southwestInMeters.x * Math.sin(bearing * Math.PI / 180.0)
          + southwestInMeters.y * Math.cos(bearing * Math.PI / 180.0))

        northeastInMeters = new Vector2(northeastInMeters.x * Math.cos(bearing * Math.PI / 180.0)
          - northeastInMeters.y * Math.sin(bearing * Math.PI / 180.0),
        northeastInMeters.x * Math.sin(bearing * Math.PI / 180.0)
          + northeastInMeters.y * Math.cos(bearing * Math.PI / 180.0))

        southeastInMeters = new Vector2(southeastInMeters.x * Math.cos(bearing * Math.PI / 180.0)
          - southeastInMeters.y * Math.sin(bearing * Math.PI / 180.0),
        southeastInMeters.x * Math.sin(bearing * Math.PI / 180.0)
          + southeastInMeters.y * Math.cos(bearing * Math.PI / 180.0))

        northwestInMeters = new Vector2(northwestInMeters.x * Math.cos(bearing * Math.PI / 180.0)
          - northwestInMeters.y * Math.sin(bearing * Math.PI / 180.0),
        northwestInMeters.x * Math.sin(bearing * Math.PI / 180.0)
          + northwestInMeters.y * Math.cos(bearing * Math.PI / 180.0))

        minX = Math.min(southwestInMeters.x, northeastInMeters.x, southeastInMeters.x, northwestInMeters.x)
        maxX = Math.max(southwestInMeters.x, northeastInMeters.x, southeastInMeters.x, northwestInMeters.x)
        minY = Math.min(southwestInMeters.y, northeastInMeters.y, southeastInMeters.y, northwestInMeters.y)
        maxY = Math.max(southwestInMeters.y, northeastInMeters.y, southeastInMeters.y, northwestInMeters.y)
      }
    }
    let position = this._mapObject.getLocation()
    let positionInMeters = ProjectionMercator.convertToMeterFromLatLng(position)
    let southwestInMeters = new Vector2(positionInMeters.x + minX, positionInMeters.y - maxY)
    let northeastInMeters = new Vector2(positionInMeters.x + maxX, positionInMeters.y - minY)
    let bounds = new LatLngBounds(ProjectionMercator.convertToLatLngFromMeter(southwestInMeters),
      ProjectionMercator.convertToLatLngFromMeter(northeastInMeters))
    this.updateBoundsFromLatLngBounds(bounds)
  }

  // Rbush properties
  private _minX: number

  private _minY: number

  private _maxX: number

  private _maxY: number

  get mapObject(): MapObject {
    return this._mapObject
  }

  get minX(): number {
    return this._minX
  }

  get minY(): number {
    return this._minY
  }

  get maxX(): number {
    return this._maxX
  }

  get maxY(): number {
    return this._maxY
  }

  get hasBounds(): boolean {
    return this._hasBounds
  }

  get objectBounds(): BoundingBox {
    return this._boundingBox
  }

  get coverPoints(): LatLng[] {
    return this._coverPoints
  }
}

export class CustomBuildingManager {

  private mapObjects: Map<string, IndexedMapObject>

  private readonly mapObjectTree: rbush.RBush<IndexedMapObject>

  private buildingResourceManager: BuildingResourceManager

  private tileManager: TileManager

  private mapState: MapState

  private modelTaskManager: TaskManager

  private modelRequestsMap: Map<string, RequestTask>

  private readonly requestRender: () => void

  constructor(buildingResourceManager: BuildingResourceManager, mapState: MapState,
    modelTaskManager: TaskManager, requestRender: () => void) {
    this.buildingResourceManager = buildingResourceManager
    this.mapState = mapState
    this.modelTaskManager = modelTaskManager
    this.mapObjectTree = RBush<IndexedMapObject>()
    this.mapObjects = new Map<string, IndexedMapObject>()
    this.modelRequestsMap = new Map<string, RequestTask>()
    this.requestRender = requestRender

  }

  setTileManager(tileManager: TileManager) {
    this.tileManager = tileManager
  }

  addMapObject(mapObject: MapObject): void {
    if (this.mapObjects.get(mapObject.getId())) {
      console.warn("Can not add 2 map objects have the same id")
      return
    }
    let indexedMapObject = new IndexedMapObject(mapObject)
    this.mapObjects.set(mapObject.getId(), indexedMapObject)

    let obj = mapObject.getObj()

    if (obj) {
      if (typeof obj === "string") {
        this.requestModel(indexedMapObject)
      }
      else {
        let data = ObjModelLoader.loadModel(obj.data) as any
        if (data != null && data.length > 0) {

          this.onDataModelAvailable(data[0], mapObject)

          this.mapObjectTree.insert(indexedMapObject)
          this.requestRender()
        }
        else {
          console.warn("Invalid object data for custom building id: " + mapObject.getId())
        }
      }
    }
    else {
      let coordinates = mapObject.getCoordinates()
      let bounds = new LatLngBounds()
      coordinates.forEach(coordinate => {
        bounds.extend(coordinate)
      })
      indexedMapObject.updateBoundsFromLatLngBounds(bounds)
      this.tileCover(indexedMapObject,
        (rasterTile: RasterTile) => {
          if (rasterTile.isInitialized()) {
            let buildingJson = this.getJsonForMapObject(mapObject)
            this.tileManager.addBuildings([buildingJson])
            rasterTile.getBuildings().push(buildingJson)
          }
        },
        (rasterTile: RasterTile) => {
          let buildingJson = this.getJsonForMapObject(mapObject)
          rasterTile.getBuildings().push(buildingJson)
        })
      this.mapObjectTree.insert(indexedMapObject)
      this.requestRender()
    }
  }

  getMapObjects(): Map<string, IndexedMapObject> {
    return this.mapObjects
  }

  removeMapObject(id: string): void {
    let indexedMapObject = this.mapObjects.get(id)
    if (!indexedMapObject) {
      console.warn("Can not remove non exist map object- id: " + id)
      return
    }
    if (indexedMapObject.hasBounds) {
      this.tileCover(indexedMapObject,
        (rasterTile: RasterTile) => {
          if (rasterTile.isInitialized()) {

            // Remove building
            rasterTile.addBuildings(rasterTile.getBuildings().filter(building => {
              return building.id != id
            }))
            this.buildingResourceManager.decreaseBuildingReference(id)
          }
        },
        (rasterTile: RasterTile) => {
          rasterTile.addBuildings(rasterTile.getBuildings().filter(building => {
            return building.id != id
          }))
        })
    }
    else {

      // In the case, model is downloading. Stop requesting model
      let objName = indexedMapObject.mapObject.getObj()
      if (typeof objName !== "string") {
        console.error("objName type must be string")
        return
      }
      let requestTask = this.modelRequestsMap.get(objName)
      if (requestTask) {
        this.modelTaskManager.cancelTask(requestTask)
      }
      else {
        console.error("Request task null")
      }
      this.modelRequestsMap.delete(objName)
    }
    this.mapObjectTree.remove(indexedMapObject)
    this.mapObjects.delete(id)
  }

  getJsonForMapObject(mapObject: MapObject): BuildingJson {
    let coordinates = mapObject.getCoordinates()
    return {
      id: mapObject.getId(),
      name: mapObject.getName(),
      location: mapObject.getLocation(),
      scale: mapObject.getScale(),
      bearing: mapObject.getBearing(),
      elevation: mapObject.getElevation(),
      draggable: mapObject.isDraggable(),
      types: [MapConstants.customBuildingType],
      model: {
        type: coordinates.length > 0 ? "ExternalExtrude" : "External",
        obj: mapObject.getObj(),
        texture: mapObject.getTexture(),
        coordinates: coordinates,
        height: mapObject.getHeight()
      } as ModelJson
    } as BuildingJson
  }

  getObjectsForTile(tileCoordinate: TileCoordinate): MapObject[] {
    let bounds = GeometryUtils.tileCoordinateToLatLngBounds(tileCoordinate)
    let bBox = {
      minX: bounds.getSouthwest().lng,
      minY: bounds.getSouthwest().lat,
      maxX: bounds.getNortheast().lng,
      maxY: bounds.getNortheast().lat
    }
    let x = tileCoordinate.getX()
    let y = tileCoordinate.getY()
    let z = tileCoordinate.getZoom()
    let indexedMapObjects = this.mapObjectTree.search(bBox)
    if (indexedMapObjects && indexedMapObjects.length != 0) {
      return indexedMapObjects.filter(indexedMapObject => {
        let coverTiles = cover.tiles(this.createFeatureData(indexedMapObject), {
          min_zoom: z,
          max_zoom: z
        })
        if (coverTiles && coverTiles.length > 0) {
          for (let i = 0; i < coverTiles.length; ++i) {
            let coverTile = coverTiles[i]
            if (coverTile[0] == x && coverTile[1] == y) {
              return true
            }
          }
        }
        return false
      }).map(indexedMapObject => {
        return indexedMapObject.mapObject
      })
    }
    return []
  }

  destroy(): void {
    this.modelRequestsMap.forEach(value => {
      this.modelTaskManager.cancelTask(value)
    })
    this.modelRequestsMap.clear()
    this.mapObjects.clear()
    this.mapObjectTree.clear()
    this.mapObjects = null
    this.buildingResourceManager = null
    this.tileManager = null
    this.mapState = null
    this.modelRequestsMap = null
    this.modelTaskManager = null
  }

  private requestModel(indexedMapObject: IndexedMapObject): void {
    let objName = indexedMapObject.mapObject.getObj()
    if (typeof objName !== "string") {
      console.error("Obj name type must be string")
      return
    }
    if (objName == null) {
      console.error("You do not set Url for Model. Please set it first!")
      return
    }
    let buildingModelRequestUrl = UrlUtils.isUrlRequest(objName) ? objName :
      UrlUtils.getBuildingDataUrl(MapConstants.baseModelRequestUrl, objName, MapSettings.getAccessKey())

    let requestTask = new RequestTask(Resource.CustomModel)
    requestTask.url = buildingModelRequestUrl
    requestTask.completion = (response: any) => {
      if (response.data != null) {
        this.onDataModelAvailable(response.data[0], indexedMapObject.mapObject)
        if (indexedMapObject.mapObject.onModelLoaded) {
          indexedMapObject.mapObject.onModelLoaded()
        }
        // Only insert to the tree after loading model successful
        this.mapObjectTree.insert(indexedMapObject)
        this.requestRender()
      }
      else {
        console.warn("Fail to load model for custom building: " + objName)
      }

      // @ts-ignore
      this.modelRequestsMap.delete(objName)
    }
    this.modelRequestsMap.set(objName, requestTask)
    this.modelTaskManager.postTask(requestTask)
  }

  private onDataModelAvailable(dataModel: ModelGeometry, mapObject: MapObject): void {
    if (dataModel.minX != 0 || dataModel.minY != 0
      || dataModel.maxX != 0 || dataModel.maxY != 0 ) {
      let indexedMapObject = this.mapObjects.get(mapObject.getId())
      indexedMapObject.updateBounds(new BoundingBox(dataModel.minX, dataModel.minY, dataModel.maxX, dataModel.maxY))
      let data = (dataModel.data instanceof ArrayBuffer) ? new Float32Array(dataModel.data) : dataModel.data
      let points: Point[] = []
      let stride = 8
      for (let i = 0; i < (data instanceof Float32Array ? data.byteLength / 4 : data.length) - stride; i += stride) {
        let point = new Point(data[i], data[i + 2])
        points.push(point)
      }
      indexedMapObject.updateCoverPoints(points)
      this.tileCover(indexedMapObject,
        (rasterTile: RasterTile) => {
          if (rasterTile.isInitialized()) {
            let buildingJson = this.getJsonForMapObject(mapObject)
            this.tileManager.addBuildings([buildingJson])
            rasterTile.getBuildings().push(buildingJson)
          }
        },
        (rasterTile: RasterTile) => {
          let buildingJson = this.getJsonForMapObject(mapObject)
          rasterTile.getBuildings().push(buildingJson)
        })
    }
  }

  private updateBounds(id: string, indexedMapObject: IndexedMapObject, oldLocation: LatLng, newCoordinates: LatLng[]): void {
    if (indexedMapObject.hasBounds) {

      let deleteCount = 0

      // tile cover for the old location
      this.tileCover(indexedMapObject,
        (rasterTile: RasterTile) => {
          if (rasterTile.isInitialized()) {
            rasterTile.addBuildings(rasterTile.getBuildings().filter(building => {
              return building.id != indexedMapObject.mapObject.getId()
            }))
            ++deleteCount
          }
        },
        (rasterTile: RasterTile) => {
          rasterTile.addBuildings(rasterTile.getBuildings().filter(building => {
            return building.id != indexedMapObject.mapObject.getId()
          }))
        })

      this.mapObjectTree.remove(indexedMapObject)

      let coordinates = indexedMapObject.mapObject.getCoordinates()
      if (coordinates.length > 0) {
        if (oldLocation != null) {
          let newLocation = indexedMapObject.mapObject.getLocation()
          let translateVectorX = newLocation.lng - oldLocation.lng
          let translateVectorY = newLocation.lat - oldLocation.lat
          let updatedCoordinates = []
          coordinates.forEach(coordinate => {
            updatedCoordinates.push(new LatLng(coordinate.lat + translateVectorY,
              coordinate.lng + translateVectorX))
          })

          // @ts-ignore
          indexedMapObject.mapObject.coordinates = updatedCoordinates
          coordinates = updatedCoordinates
        }
        if (newCoordinates != null) {

          // @ts-ignore
          indexedMapObject.mapObject.coordinates = newCoordinates
          coordinates = newCoordinates
        }
        let bounds = new LatLngBounds()
        coordinates.forEach(coordinate => {
          bounds.extend(coordinate)
        })
        indexedMapObject.updateBoundsFromLatLngBounds(bounds)
      }
      else {
        indexedMapObject.updateCoverPoints(null)
      }

      this.mapObjectTree.insert(indexedMapObject)

      // tile cover for the new location
      this.tileCover(indexedMapObject,
        (rasterTile: RasterTile) => {
          if (rasterTile.isInitialized()) {
            let buildingJson = this.getJsonForMapObject(indexedMapObject.mapObject)
            this.tileManager.addBuildings([buildingJson])
            rasterTile.getBuildings().push(buildingJson)
          }
        },
        (rasterTile: RasterTile) => {
          let buildingJson = this.getJsonForMapObject(indexedMapObject.mapObject)
          rasterTile.getBuildings().push(buildingJson)
        })

      for (let i = 0; i < deleteCount; ++i) {
        this.buildingResourceManager.decreaseBuildingReference(id)
      }
      this.buildingResourceManager.setBuildingLayerDirty()
    }
  }

  updateMapObjectCoordinates(id: string, newCoordinates: LatLng[]) {
    let indexedMapObject = this.mapObjects.get(id)
    if (indexedMapObject == null) {
      console.warn("Can not set position non-exist map object- id: " + id)
      return
    }
    this.updateBounds(id, indexedMapObject, null, newCoordinates)
  }

  updateMapObjectLocation(id: string, newLocation: LatLng): void {
    let indexedMapObject = this.mapObjects.get(id)
    if (indexedMapObject == null) {
      console.warn("Can not set position non-exist map object- id: " + id)
      return
    }
    let oldLocation = indexedMapObject.mapObject.getLocation()

    // @ts-ignore
    indexedMapObject.mapObject.location = newLocation

    this.updateBounds(id, indexedMapObject, oldLocation, null)
  }

  updateMapObjectScale(id: string): void {
    let indexedMapObject = this.mapObjects.get(id)
    if (indexedMapObject == null) {
      console.warn("Can not set scale non-exist map object- id: " + id)
      return
    }
    this.updateBounds(id, indexedMapObject, null, null)
  }

  updateBuildingDataForTiles(id: string): void {
    let indexedMapObject = this.mapObjects.get(id)
    if (!indexedMapObject) {
      console.warn("indexedMapObject for id: " + id)
      return
    }
    if (indexedMapObject.hasBounds) {
      this.tileCover(indexedMapObject,
        (rasterTile: RasterTile) => {
          let index = rasterTile.getBuildings().findIndex(building => building.id == id)
          if (index != -1) {
            rasterTile.getBuildings()[index] = this.getJsonForMapObject(indexedMapObject.mapObject)
          }
        },
        (rasterTile: RasterTile) => {
          let index = rasterTile.getBuildings().findIndex(building => building.id == id)
          if (index != -1) {
            rasterTile.getBuildings()[index] = this.getJsonForMapObject(indexedMapObject.mapObject)
          }
        })
    }

  }

  private tileCover(indexedMapObject: IndexedMapObject,
    onTileCallback: (RasterTile) => void, onCacheTileCallback: (RasterTile) => void ) {
    for (let zoom = MapConstants.defaultMinZoom3d; zoom <= this.mapState.getMaxNativeZoom(); ++zoom) {
      let coverTiles = cover.tiles(this.createFeatureData(indexedMapObject), {
        min_zoom: zoom,
        max_zoom: zoom
      })
      let tiles = this.tileManager.getTiles()
      let tileCache = this.tileManager.getTileCache()
      coverTiles && coverTiles.forEach(coverTile => {
        let tileCoordinate = new TileCoordinate(coverTile[0], coverTile[1], coverTile[2], true).toString()
        let tile = tiles.get(tileCoordinate)
        if (tile) {
          let rasterTile = tile.tile as RasterTile
          onTileCallback(rasterTile)
        }
        let cacheTile = tileCache.get(tileCoordinate)
        if (cacheTile) {
          let rasterTile = cacheTile as RasterTile
          onCacheTileCallback(rasterTile)
        }
      })
    }
  }

  public createFeatureData(indexedMapObject: IndexedMapObject) {
    let coordinates = []
    let featureData
    let coverPoint = indexedMapObject.coverPoints
    if (!coverPoint) {
      let coverCoordinates = indexedMapObject.mapObject.getCoverCoordinates()
      coverCoordinates.forEach(coordinate => {
        coordinates.push([coordinate.lng, coordinate.lat])
      })
      coordinates.push([coverCoordinates[0].lng, coverCoordinates[0].lat])
      featureData = {
        "type": "Polygon",
        "properties": {},
        "coordinates": [coordinates]
      }
    }
    else {
      for (let i = 0; i < coverPoint.length; ++i) {
        coordinates.push([coverPoint[i].lng, coverPoint[i].lat])
      }
      featureData = {
        "type": "MultiPoint",
        "properties": {},
        "coordinates": coordinates
      }
    }
    return featureData
  }

  public getBoundsCoordinates(indexedMapObject: IndexedMapObject): LatLng[] {
    let coverPoints: LatLng[] = []
    let coverPoint = indexedMapObject.coverPoints
    if (!coverPoint) {
      let coverCoordinates = indexedMapObject.mapObject.getCoverCoordinates()
      coverCoordinates.forEach(coordinate => {
        coverPoints.push(coordinate)
      })
    }
    else {
      for (let i = 0; i < coverPoint.length; ++i) {
        coverPoints.push(coverPoint[i])
      }
      coverPoints = ConvexHull.makeHull(coverPoints)
    }
    return coverPoints
  }
}
