import {UnwrappedTileCoordinate} from "../tile/UnwrappedTileCoordinate"
import {Tile} from "../tile/Tile"

export class RenderTile {
  unwrappedTileCoordinate: UnwrappedTileCoordinate;

  tile: Tile;

  constructor(unwrappedTileCoordinate: UnwrappedTileCoordinate, tile: Tile) {
    this.unwrappedTileCoordinate = unwrappedTileCoordinate
    this.tile = tile
  }

  clone() {
    return new RenderTile(this.unwrappedTileCoordinate, this.tile)
  }

  getTile(): Tile {
    return this.tile
  }

  getUnwrappedTileCoordinate(): UnwrappedTileCoordinate {
    return this.unwrappedTileCoordinate
  }
}
