import {Layer, LayerType} from "./Layer"
import {MapState} from "../map/MapState"
import {BuildingResourceManager} from "../building/BuildingResourceManager"
import {TileManager} from "../tile/TileManager"
import {BuildingGroup} from "../building/BuildingGroup"
import {Building} from "../building/Building"
import {BuildingType} from "../building/BuildingType"
import {LightingManager} from "../lighting/LightingManager"
import {MapObject} from "../public/MapObject"
import {EffectManager} from "../effect/EffectManager"
import {TimeEffectManager} from "../effect/TimeEffectManager"
import {CustomBuildingManager} from "../building/CustomBuildingManager"

export class BuildingLayer extends Layer {

  textureBuildingGroups: Map<string, BuildingGroup>

  extrudeBuildings: Building[] = []

  buildingResourceManager: BuildingResourceManager

  lightManager: LightingManager

  dirty: boolean = true

  private _effectManager: EffectManager

  private _timeEffectManager: TimeEffectManager

  // TODO: Move to MapSettings
  private _filterObjectTypes: string[]

  private _selectedObjectMap: Map<string, boolean>

  constructor(layerId: string, order: number, tileManager: TileManager) {
    super(layerId, order, LayerType.BUILDING, tileManager)
    this.textureBuildingGroups = new Map<string, BuildingGroup>()
    this._selectedObjectMap = new Map<string, boolean>()
  }

  update(mapState: MapState): void {
    if (!mapState.is3dMode()) {
      return
    }
    this.buildingResourceManager.update()
    if (!this.dirty) {
      return
    }
    this.dirty = false

    this.textureBuildingGroups.clear()
    this.extrudeBuildings = []

    this.buildingResourceManager.buildings.forEach(buildingObject => {
      if (!this.isInFilterObjectTypes(buildingObject.getTypes())) {

        // continue
        return
      }
      buildingObject.updateMatrices(mapState)

      buildingObject.setSelected(this._selectedObjectMap.has(buildingObject.getId()))
      let objName = buildingObject.getObjName()
      let buildingType = buildingObject.getBuildingType()
      if (buildingType == BuildingType.Extrude || buildingType == BuildingType.ExternalExtrude) {
        this.extrudeBuildings.push(buildingObject)
      }
      else {
        let textureBuildingGroup = this.textureBuildingGroups.get(objName)
        if (textureBuildingGroup == null) {
          let buildingGroup = new BuildingGroup(objName)
          buildingGroup.addBuilding(buildingObject)
          this.textureBuildingGroups.set(objName, buildingGroup)
        }
        else {
          textureBuildingGroup.addBuilding(buildingObject)
        }
      }
    })
    this.lightManager.update(mapState.getCamera().getTarget())
  }

  setDirty(): void {
    this.dirty = true
  }

  getBuildingResourceManager(): BuildingResourceManager {
    return this.buildingResourceManager
  }

  getCustomBuildingManager(): CustomBuildingManager {
    return this.tileManager.getCustomBuildingManager()
  }

  setBuildingResourceManager(buildingResourceManage: BuildingResourceManager): void {
    this.buildingResourceManager = buildingResourceManage
    this.buildingResourceManager.setBuildingLayerDirty = () => {
      this.setDirty()
    }
  }

  setLightingManager(lightingManager: LightingManager) {
    this.lightManager = lightingManager
  }

  getBuilding(selectionCode: number): MapObject {
    if (selectionCode < 0) {
      return null
    }
    if (this.textureBuildingGroups.size == 0
      && this.extrudeBuildings.length == 0) {
      return null
    }

    let findBuilding: Building = null

    if (this.textureBuildingGroups.size != 0) {
      this.textureBuildingGroups.forEach(buildingGroup => {
        buildingGroup.buildings.forEach(building => {
          if (building.getSelectionCode() == selectionCode){
            findBuilding = building
            return
          }
        })
      })
    }

    if (findBuilding == null && this.extrudeBuildings.length != 0) {
      this.extrudeBuildings.forEach(building => {
        if (building.getSelectionCode() == selectionCode) {
          findBuilding = building
          return
        }
      })
    }
    if (findBuilding != null){
      let object = new MapObject()
      object.initWithBuilding(findBuilding)
      return object
    }
    else {
      return null
    }
  }

  get effectManager(): EffectManager {
    return this._effectManager
  }

  set effectManager(value: EffectManager) {
    this._effectManager = value
  }

  get timeEffectManager(): TimeEffectManager {
    return this._timeEffectManager
  }

  set timeEffectManager(timeEffectManager: TimeEffectManager) {
    this._timeEffectManager = timeEffectManager
  }

  clear(): void {
    this.textureBuildingGroups.clear()
    this.extrudeBuildings = []
  }

  set filterObjectTypes(objectTypes: string[]) {
    this._filterObjectTypes = objectTypes
    this.dirty = true
  }

  get filterObjectTypes(): string[] {
    return this._filterObjectTypes
  }

  getSelectedObjectIds(): string[] {
    return Array.from(this._selectedObjectMap.keys())
  }

  setSelectedObjectIds(ids: string[]): void {
    this._selectedObjectMap.clear()
    if (!ids) {
      return
    }
    ids.forEach(id => this._selectedObjectMap.set(id, true))
    this.dirty = true
  }

  private isInFilterObjectTypes(buildingPlaceTypes: string[]): boolean {
    if (!this._filterObjectTypes || !this._filterObjectTypes.length || this._filterObjectTypes.length < 1) {
      return true
    }
    if (!buildingPlaceTypes) {
      return false
    }
    return this._filterObjectTypes.some(placeType => buildingPlaceTypes.indexOf(placeType) !== -1)
  }
}
