/**
 * attribute information to store
 * data for webgl attribute
 */
export class AttributeInfo {

  /**
   * create an attribute information
   * @param name
   * @param size
   * @param offset
   * @param stride
   */
  constructor(public name: string,
              public size: number,
              public offset: number = 0,
              public stride: number = 0) {
  }
}

/**
 * storage for uniform information
 */
export class UniformInfo {

  /**
   *
   * @param name
   * @param type
   * @param value
   */
  constructor(public name: string,
              public type: string,
              public value: any) {

  }
}

/**
 * the shader class - for managing shader
 */
export class Shader {

  /**
   * the program to user
   */
  program: WebGLProgram;

  fragmentShader: WebGLShader;

  vertexShader: WebGLShader;

  /**
   * current texture unit
   * @type {number}
   */
  textureUnit = 0;

  /**
   * dictionary
   * @type {{}}
   */
  uniforms: {[key: string]: WebGLUniformLocation } = {};

  /**
   * dictionary that stores all attributes
   * @type {{}}
   */
  attributes: {[key: string]: number} = {};

  /**
   * create a shader with sources
   * @param gl
   * @param vertexSource
   * @param fragmentSource
   */
  constructor(

    /// web gl object
    private gl: WebGLRenderingContext,
    vertexSource: string,
    fragmentSource: string) {

    /// compile shaders
    this.program = gl.createProgram()

    /// attach shaders
    this.fragmentShader = this.loadShader(gl.FRAGMENT_SHADER, fragmentSource)
    this.vertexShader = this.loadShader(gl.VERTEX_SHADER, vertexSource)
    gl.attachShader(this.program, this.vertexShader)
    gl.attachShader(this.program, this.fragmentShader)

    /// link the shader
    gl.linkProgram(this.program)

    /// check for link errors
    if (!gl.getProgramParameter(this.program, gl.LINK_STATUS)) {
      console.log("Error linking shader program")
      console.log(gl.getProgramInfoLog(this.program))
    }

    this.loadAttributes()
    this.loadUniforms()

  }

  /**
   * load all attributes of this shader
   */
  loadAttributes() {
    let gl = this.gl

    /// get shader attributes
    let attributeCount = gl.getProgramParameter(this.program, gl.ACTIVE_ATTRIBUTES)

    /// get all uniform info
    for (let i = 0; i < attributeCount; ++i) {
      let attributeInfo = gl.getActiveAttrib(this.program, i)
      let name = attributeInfo.name
      this.attributes[name] = gl.getAttribLocation(this.program, name)
    }
  }

  /**
   * load all uniforms of this shader
   */
  loadUniforms() {
    let gl = this.gl

    /// get shader uniforms
    let uniformCount = gl.getProgramParameter(this.program, gl.ACTIVE_UNIFORMS)

    /// get all uniform info
    for (let i = 0; i < uniformCount; ++i) {
      let uniformInfo = gl.getActiveUniform(this.program, i)
      let name = uniformInfo.name
      this.uniforms[name] = gl.getUniformLocation(this.program, name)
    }
  }

  /**
   * load a shader with source
   * @param type type of the shader | either VERTEX_SHADER or FRAGMENT_SHADER
   * @param source
   * @returns {WebGLShader}
   */
  loadShader(type: number, source: string): WebGLShader {
    let gl = this.gl

    /// create the shader
    let shader = gl.createShader(type)

    /// attach shader source
    gl.shaderSource(shader, source)

    /// compile shader
    gl.compileShader(shader)

    /// if compile error then log the message
    if (!gl.getShaderParameter(shader, gl.COMPILE_STATUS)) {
      console.log("Error loading shader")
      console.log(source)
      console.log(gl.getShaderInfoLog(shader))
    }

    return shader
  }

  /**
   * Bind the shader to use
   */
  bind() {

    /// use program
    this.gl.useProgram(this.program)
    this.resetTextureUnit()
  }

  /**
   * Reset texture unit
   */
  resetTextureUnit() {
    this.textureUnit = 0
  }

  /**
   * Set attributes
   * @param attributeInfos
   */
  setAttributes(attributeInfos: AttributeInfo[]) {

    let gl = this.gl

    /// set attributes
    for (let attributeInfo of attributeInfos) {

      /// get the index of attribute
      let index = this.attributes[attributeInfo.name]
      if (index != null) {
        gl.enableVertexAttribArray(index)
        gl.vertexAttribPointer(
          index,
          attributeInfo.size,
          gl.FLOAT,
          false,
          4 * attributeInfo.stride,
          4 * attributeInfo.offset
        )
      }
    }
  }

  /**
   * set uniforms
   * @param uniformInfos
   */
  setUniforms(uniformInfos: UniformInfo[]) {
    let gl = this.gl

    /// set uniforms
    for (let uniformInfo of uniformInfos) {
      let index = this.uniforms[uniformInfo.name]

      /// set the uniform
      if (index !== null) {
        if (uniformInfo.type == "matrix" && uniformInfo.value) {
          gl.uniformMatrix4fv(index, false, uniformInfo.value.buffer())
        }
        else if (uniformInfo.type == "texture" && uniformInfo.value) {
          gl.activeTexture(gl.TEXTURE0 + this.textureUnit)
          gl.bindTexture(gl.TEXTURE_2D, uniformInfo.value.textureId)
          gl.uniform1i(index, this.textureUnit)
          this.textureUnit += 1
        }
        else if (uniformInfo.type == "textureCube" && uniformInfo.value) {
          gl.activeTexture(gl.TEXTURE0 + this.textureUnit)
          gl.bindTexture(gl.TEXTURE_CUBE_MAP, uniformInfo.value.textureId)
          gl.uniform1i(index, this.textureUnit)
          this.textureUnit += 1
        }
        else if (uniformInfo.type == "4fv" && uniformInfo.value) {
          gl.uniform4fv(index, uniformInfo.value)
        }
        else if (uniformInfo.type == "3fv" && uniformInfo.value) {
          gl.uniform3fv(index, uniformInfo.value)
        }
        else if (uniformInfo.type == "3f" && uniformInfo.value) {
          gl.uniform3f(index, uniformInfo.value.x, uniformInfo.value.y, uniformInfo.value.z)
        }
        else if (uniformInfo.type == "2fv" && uniformInfo.value) {
          gl.uniform2fv(index, uniformInfo.value)
        }
        else if (uniformInfo.type == "1f") {
          gl.uniform1f(index, uniformInfo.value)
        }
        else if (uniformInfo.type == "1i") {
          gl.uniform1i(index, uniformInfo.value)
        }
        else {
          console.error("Invalid uniform info type: " + uniformInfo.type)
        }
      }

    }
  }

  /**
   * disable all attributes
   */
  disableAttributes() {
    for (const attribute in this.attributes) {
      this.gl.disableVertexAttribArray(this.attributes[attribute])
    }
  }

  /**
   * destroy this shader to release memory
   */
  destroy() {
    this.uniforms = {}
    this.attributes = {}
    this.gl.deleteProgram(this.program)
    this.gl.deleteShader(this.vertexShader)
    this.gl.deleteShader(this.fragmentShader)
  }
}
