import * as RBush from "rbush"
import {Annotation, AnnotationId} from "./Annotation"
import {LayerType} from "../layer/Layer"
import {MapView} from "../map/MapView"
import {AnnotationTile} from "../tile/AnnotationTile"
import {TileCoordinate} from "../tile/TileCoordinate"
import {AnnotationTileData, AnnotationTileFeature} from "../tile/data/AnnotationTileData"
import {RenderTile} from "../renderer/RenderTile"
import {UnwrappedTileCoordinate} from "../tile/UnwrappedTileCoordinate"
import {LineLayer} from "../layer/LineLayer"
import {ShapeAnnotationData} from "./ShapeAnnotationData"
import {FillLayer} from "../layer/FillLayer"
import {CircleAnnotationData} from "./CircleAnnotationData"
import {CircleLayer} from "../layer/CircleLayer"
import {GeometryUtils} from "../util/GeometryUtils"
import {Vector2} from "../engine/Vector2"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {GeometryTileData, GeometryType} from "../tile/data/GeometryTileData"
import {MapUtils} from "../util/MapUtils"
import {CollisionUtils} from "../util/CollisionUtils"
import {Circle} from "../public/Circle"
import {Marker} from "../public/Marker"
import {Polygon} from "../public/Polygon"
import {PolygonAnnotationData} from "./PolygonAnnotationData"
import {Polyline} from "../public/Polyline"
import {PolylineAnnotationData} from "./PolylineAnnotationData"
import {Point} from "../public/Point"
import {SymbolAnnotationData} from "./SymbolAnnotationData"
import {SymbolLayer} from "../layer/SymbolLayer"
import {InfoWindowManager} from "./InfoWindowManager"
import {TileManager} from "../tile/TileManager"
import {IconViewMarkerManager} from "./IconViewMarkerManager"
import {MapObject} from "../public/MapObject"
import {LatLng} from "../public/LatLng"
import {Box} from "../util/Box"
import {MapConstants} from "../map/MapConstants"

export class AnnotationManager {

  private dirty: boolean

  private idCounter: number = 0

  private shapeAnnotations: Map<AnnotationId, ShapeAnnotationData>

  private symbolAnnotations: Map<AnnotationId, SymbolAnnotationData>

  private tiles: Map<string, AnnotationTile>

  private circleAnnotations: Map<AnnotationId, CircleAnnotationData>

  private circleTree: rbush.RBush<CircleAnnotationData>

  private symbolTree: rbush.RBush<SymbolAnnotationData>

  private readonly infoWindowManager: InfoWindowManager

  private map: MapView

  private tileManager: TileManager

  // The name of the annotation layer and point layer is very importance
  // Please don't change it because it may cause unexpected bug related to zIndex
  static annotationLayerId = "1.maplink.annotation.layer."

  static pointLayerId = "3.maplink.annotation.points."

  private _iconViewMarkerManager: IconViewMarkerManager

  private _symbolVisibleIds = []

  constructor(map: MapView, tileManager: TileManager) {
    this.map = map
    this.tileManager = tileManager
    this.circleTree = RBush<CircleAnnotationData>()
    this.symbolTree = RBush<SymbolAnnotationData>()
    this.tiles = new Map<string, AnnotationTile>()
    this.circleAnnotations = new Map<AnnotationId, CircleAnnotationData>()
    this.symbolAnnotations = new Map<AnnotationId, SymbolAnnotationData>()
    this.shapeAnnotations = new Map<AnnotationId, ShapeAnnotationData>()
    this._iconViewMarkerManager = new IconViewMarkerManager(this.map)
    this.infoWindowManager = new InfoWindowManager(this.map, this.symbolAnnotations)
    this.dirty = false
  }

  update(): void {
    this._iconViewMarkerManager.update()
    if (this.dirty) {
      this._symbolVisibleIds = []
      this.tiles.forEach(tile => {
        let annotationTileData = this.getTileData(tile.getTileCoordinate())

        /** TODO: Refactor */
        this.updateRenderTile(annotationTileData, tile)
        tile.setData(annotationTileData)
      })
      this.dirty = false
    }
    this.infoWindowManager.render(this._symbolVisibleIds)
  }

  addTile(tile: AnnotationTile): void {
    let tileCoordinate = tile.getTileCoordinate()
    let annotationTileData = this.getTileData(tileCoordinate)
    /** TODO: Refactor */
    this.updateRenderTile(annotationTileData, tile)
    tile.setData(annotationTileData)
    this.tiles.set(tileCoordinate.toString(), tile)
  }

  removeTile(tile: AnnotationTile): void {
    let geometryTileData = tile.getTileData()
    if (geometryTileData != null) {
      let annotationTileData = geometryTileData as AnnotationTileData
      let layerIds = annotationTileData.getLayerIds()
      let tileCoordinateKey = tile.getTileCoordinate().toString()
      layerIds.forEach(layerId => {
        let layer = this.map.getLayer(layerId)
        if (layer != null) {
          let layerType = layer.getLayerType()
          if (layerType == LayerType.LINE) {
            let layerLineLayer = layer as LineLayer
            layerLineLayer.removeRenderTile(tileCoordinateKey)
          }
          else if (layerType == LayerType.FILL) {
            let fillLayer = layer as FillLayer
            fillLayer.removeRenderTile(tileCoordinateKey)
          }
          else if (layerType == LayerType.CIRCLE) {
            let circleLayer = layer as CircleLayer
            circleLayer.removeRenderTile(tileCoordinateKey)
          }
          else if (layerType == LayerType.SYMBOL) {
            let symbolLayer = layer as SymbolLayer
            let index = this._symbolVisibleIds.findIndex(element => {
              return AnnotationManager.pointLayerId + element == layerId
            })
            if (index != -1) {
              this._symbolVisibleIds.splice(index, 1)
            }
            symbolLayer.removeRenderTile(tileCoordinateKey)
          }
        }
      })
    }
    this.tiles.delete(tile.getTileCoordinate().toString())
  }

  private getTileData(tileCoordinate: TileCoordinate): AnnotationTileData {
    if (this.symbolAnnotations.size == 0 && this.shapeAnnotations.size == 0 && this.circleAnnotations.size == 0) {
      return null
    }
    let annotationTileData = new AnnotationTileData()
    this.shapeAnnotations.forEach(shape => {
      shape.updateTileData(tileCoordinate, annotationTileData, this.map.mapState.getMaxNativeZoom())
    })
    let bounds = GeometryUtils.tileCoordinateToLatLngBounds(tileCoordinate)
    let bBox = {
      minX: bounds.getSouthwest().lng,
      minY: bounds.getSouthwest().lat,
      maxX: bounds.getNortheast().lng,
      maxY: bounds.getNortheast().lat
    }
    let circles = this.circleTree.search(bBox)
    circles.forEach(circle => annotationTileData.addLayer(circle.layerId))

    let symbols = this.symbolTree.search(bBox)
    symbols.forEach(symbol => {
      if (!symbol.symbolAnnotation.isVisible()) {

        // continue
        return
      }
      let layerId = AnnotationManager.pointLayerId + symbol.id
      annotationTileData.addLayer(layerId)
      this._symbolVisibleIds.push(symbol.id)
      symbol.updateLayer(this.map)
      if (this.map.getLayer(layerId) == null) {
        console.error("Symbol layer id should not be null: " + layerId)
      }
    })
    return annotationTileData
  }

  addAnnotation(annotation: Annotation) {
    this.dirty = true
    let annotationId: AnnotationId = this.idCounter++
    if (this.idCounter < 0) {
      this.idCounter = 0
    }
    this.addAnnotationInternal(annotationId, annotation)
    return annotationId
  }

  private addAnnotationInternal(annotationId: AnnotationId, annotation: Annotation) {
    if (annotation instanceof Marker) {
      let marker = annotation as Marker
      if (!marker.getIconView()) {
        let symbolAnnotationData = new SymbolAnnotationData(this.map.gl, annotationId, marker)
        this.symbolTree.insert(symbolAnnotationData)
        this.symbolAnnotations.set(annotationId, symbolAnnotationData)
        let layerId = AnnotationManager.pointLayerId + annotationId
        this.map.createLayer(layerId, LayerType.SYMBOL, MapConstants.buildingLayerOrder + marker.getZIndex())
      }
      else {
        this._iconViewMarkerManager.add(annotationId, marker)
        let layerId = AnnotationManager.pointLayerId + annotationId
        this.map.createLayer(layerId, LayerType.SYMBOL, MapConstants.buildingLayerOrder + marker.getZIndex())
      }
    }
    else if (annotation instanceof Polyline) {
      let polylineAnnotationData = new PolylineAnnotationData(annotationId, annotation as Polyline)
      polylineAnnotationData.updateLayer(this.map)
      this.shapeAnnotations.set(annotationId, polylineAnnotationData)
    }
    else if (annotation instanceof Polygon) {
      let polygonAnnotationData = new PolygonAnnotationData(annotationId, annotation as Polygon)
      polygonAnnotationData.updateLayer(this.map)
      this.shapeAnnotations.set(annotationId, polygonAnnotationData)
    }
    else if (annotation instanceof Circle) {
      let circleAnnotationData = new CircleAnnotationData(annotationId, annotation as Circle)
      circleAnnotationData.updateLayer(this.map)
      this.circleAnnotations.set(annotationId, circleAnnotationData)
      this.circleTree.insert(circleAnnotationData)
    }
  }

  hideInfoWindow(annotationId: AnnotationId): void {
    this.infoWindowManager.hideInfoWindow(annotationId)
  }

  isInfoWindowShown(annotationId: AnnotationId): boolean {
    return this.infoWindowManager.isInfoWindowShown(annotationId)
  }

  showInfoWindow(annotationId: AnnotationId): void {
    this.infoWindowManager.showInfoWindow(annotationId)
  }

  updateInfoContents(annotationId: AnnotationId): void {
    this.infoWindowManager.updateInfoContents(annotationId)
  }

  updateAnnotationZIndex(annotationId: AnnotationId, oldZIndex: number, newZIndex: number): void {
    let symbolAnnotationData = this.symbolAnnotations.get(annotationId)
    //TODO: Refactor if has time
    if (symbolAnnotationData) {
      let layerId = AnnotationManager.pointLayerId + symbolAnnotationData.id
      let symbolLayer = this.map.getLayer(layerId)
      if (!symbolLayer) {
        console.warn("Symbol layer should not be null")
        return
      }
      this.map.mapRenderer.removeLayer(layerId, oldZIndex)
      symbolLayer.setOrder(newZIndex)
      this.map.mapRenderer.addLayer(symbolLayer)
    }
    else if (this.shapeAnnotations.has(annotationId)) {
      let layerId = AnnotationManager.annotationLayerId + annotationId.toString()
      let shapeLayer = this.map.getLayer(layerId)
      if (!shapeLayer) {
        console.warn("Shape layer should not be null")
        return
      }
      this.map.mapRenderer.removeLayer(layerId, oldZIndex)
      shapeLayer.setOrder(newZIndex)
      this.map.mapRenderer.addLayer(shapeLayer)
    }
    else if (this.circleAnnotations.has(annotationId)) {
      let layerId = AnnotationManager.annotationLayerId + annotationId.toString()
      let circleLayer = this.map.getLayer(layerId)
      if (!circleLayer) {
        console.warn("Circle layer should not be null")
        return
      }
      this.map.mapRenderer.removeLayer(layerId, oldZIndex)
      circleLayer.setOrder(newZIndex)
      this.map.mapRenderer.addLayer(circleLayer)
    }
  }

  updateAnnotation(annotationId: AnnotationId, reloadLayout: boolean): void {
    let symbolAnnotationData = this.symbolAnnotations.get(annotationId)
    let iconViewMarker = this._iconViewMarkerManager.getIconViewMarkers().get(annotationId)
    if (iconViewMarker){
      this._iconViewMarkerManager.updateIconView(annotationId)
    }
    if (symbolAnnotationData) {
      if (reloadLayout) {
        this.symbolTree.remove(symbolAnnotationData)
        symbolAnnotationData.updateAnnotationData()
        this.symbolTree.insert(symbolAnnotationData)
      }
      if (!symbolAnnotationData.symbolAnnotation.isVisible() || reloadLayout) {
        let layer = this.map.getLayer(AnnotationManager.pointLayerId + symbolAnnotationData.id)
        if (layer) {
          let symbolLayer = layer as SymbolLayer
          symbolLayer.clearRenderTiles()
        }
      }
      this.dirty = true
    }
    else if (this.shapeAnnotations.has(annotationId)) {
      let shapeAnnotationData = this.shapeAnnotations.get(annotationId)
      if (reloadLayout) {
        let layerName = AnnotationManager.annotationLayerId + annotationId.toString()
        shapeAnnotationData.reloadLayout()
        this.tiles.forEach(tile => tile.removeLayerData(layerName))
        this.dirty = true
      }
      shapeAnnotationData.updateLayer(this.map)
    }
    else if (this.circleAnnotations.has(annotationId)) {
      let circleAnnotationData = this.circleAnnotations.get(annotationId)
      circleAnnotationData.updateLayer(this.map)

      // Re-index circle in the case we update circle's center or radius
      if (reloadLayout) {
        circleAnnotationData.updateBounds()
        this.circleTree.remove(circleAnnotationData)
        this.circleTree.insert(circleAnnotationData)
        this.dirty = true
      }
    }
  }

  removeAnnotation(annotationId: AnnotationId): void {
    this.dirty = true
    let symbolAnnotationData = this.symbolAnnotations.get(annotationId)
    let iconViewMarker = this._iconViewMarkerManager.getIconViewMarkers().get(annotationId)
    if (symbolAnnotationData || iconViewMarker) {
      let symbolLayerOrder = MapConstants.buildingLayerOrder
      if (!iconViewMarker) {
        symbolLayerOrder += symbolAnnotationData.symbolAnnotation.getZIndex()
        this.symbolTree.remove(symbolAnnotationData)
        this.symbolAnnotations.delete(annotationId)
        symbolAnnotationData.destroy()
      }
      else {
        symbolLayerOrder += iconViewMarker.getZIndex()
        this._iconViewMarkerManager.remove(annotationId)
      }

      let layerId = AnnotationManager.pointLayerId + annotationId
      let layer = this.map.getLayer(layerId)
      if (layer) {
        let symbolLayer = layer as SymbolLayer
        symbolLayer.textLabelData && symbolLayer.textLabelData.destroy()
      }
      // Remove layer
      this.map.removeLayer(layerId, symbolLayerOrder)
    }
    else if (this.shapeAnnotations.has(annotationId)) {
      let shapeLayerOrder = this.shapeAnnotations.get(annotationId).getAnnotation().getZIndex()
      this.shapeAnnotations.delete(annotationId)

      //Remove layer
      this.map.removeLayer(AnnotationManager.annotationLayerId + annotationId.toString(), shapeLayerOrder)
    }
    else if (this.circleAnnotations.has(annotationId)) {
      let circleLayerOrder = this.circleAnnotations.get(annotationId).getAnnotation().getZIndex()
      this.circleTree.remove(this.circleAnnotations.get(annotationId))
      this.circleAnnotations.delete(annotationId)

      // Remove layer
      this.map.removeLayer(AnnotationManager.annotationLayerId + annotationId.toString(), circleLayerOrder)
    }
  }

  updateRenderTile(annotationTileData: AnnotationTileData, tile: AnnotationTile) {
    if (annotationTileData != null) {
      let tileCoordinate = tile.getTileCoordinate()
      let tileCoordinateKey = tileCoordinate.toString()
      let layerIds = annotationTileData.getLayerIds()
      layerIds.forEach(layerId => {
        let layer = this.map.getLayer(layerId)
        if (layer != null) {
          let layerType = layer.getLayerType()
          let renderTile = new RenderTile(
            UnwrappedTileCoordinate.createWithXYZoom(
              tileCoordinate.getX(),
              tileCoordinate.getY(),
              tileCoordinate.getZoom()),
            tile)
          if (layerType == LayerType.LINE) {
            let layerLineLayer = layer as LineLayer
            layerLineLayer.addRenderTile(tileCoordinateKey, renderTile)
          }
          else if (layerType == LayerType.FILL) {
            let fillLayer = layer as FillLayer
            fillLayer.addRenderTile(tileCoordinateKey, renderTile)
          }
          else if (layerType == LayerType.CIRCLE) {
            let circleLayer = layer as CircleLayer
            circleLayer.addRenderTile(tileCoordinateKey, renderTile)
          }
          else if (layerType == LayerType.SYMBOL) {
            let symbolLayer = layer as SymbolLayer
            symbolLayer.addRenderTile(tileCoordinateKey, renderTile)
          }
        }
      })
    }
  }

  queryMapObjectAndAnnotation(touchPoint: Vector2): Annotation | MapObject {
    let annotations = this.queryAnnotations(touchPoint)
    let mapState = this.map.mapState
    let touchPointLatLng = GeometryUtils.screenCoordinateToLatLng(touchPoint, mapState.cameraPosition, mapState.getScreenSize())
    let geometryTileData = null
    let minBoxVectorTileCoordinate = null
    let maxBoxVectorTileCoordinate = null
    let touchBox = new Box(
      new Point(touchPoint.x - MapUtils.touchBoxSize / 2, touchPoint.y - MapUtils.touchBoxSize / 2),
      new Point(touchPoint.x + MapUtils.touchBoxSize / 2, touchPoint.y + MapUtils.touchBoxSize / 2))

    if (touchPointLatLng.valid()) {
      let tileCoordinate =
        TileCoordinate.fromLatLng(touchPointLatLng, mapState.getZoom(), mapState.is3dMode())
      let tileScale = 1.0
      let maxNativeZoom = mapState.getMaxNativeZoom()
      if (mapState.getZoom() > maxNativeZoom) {
        tileCoordinate = TileCoordinate.fromLatLng(touchPointLatLng,
          mapState.getMaxNativeZoom(), mapState.is3dMode())
        tileScale = Math.pow(2.0, Math.round(mapState.getZoom()) - maxNativeZoom)
      }

      if (this.tiles.has(tileCoordinate.toString())) {
        let annotationTile = this.tiles.get(tileCoordinate.toString())
        geometryTileData = annotationTile.getTileData()
        if (geometryTileData != null && geometryTileData.getLayerIds().length != 0) {
          let scale = mapState.scale
          let tileCoordinateX = tileCoordinate.getX()
          let tileCoordinateY = tileCoordinate.getY()
          let minBoxLatLng = GeometryUtils.screenCoordinateToLatLng(new Vector2(touchBox.min.x, touchBox.min.y),
            mapState.cameraPosition, mapState.getScreenSize())
          let maxBoxLatLng = GeometryUtils.screenCoordinateToLatLng(new Vector2(touchBox.max.x, touchBox.max.y),
            mapState.cameraPosition, mapState.getScreenSize())
          minBoxVectorTileCoordinate =
            GeometryUtils.pixelCoordinateToVectorTileCoordinate(
              ProjectionMercator.projectScale(
                minBoxLatLng,
                scale),
              tileCoordinateX, tileCoordinateY, tileScale)
          maxBoxVectorTileCoordinate =
            GeometryUtils.pixelCoordinateToVectorTileCoordinate(
              ProjectionMercator.projectScale(
                maxBoxLatLng,
                scale),
              tileCoordinateX, tileCoordinateY, tileScale)
        }
      }
    }
    annotations.frontBuildingAnnotations.sort((a, b) => {
      let firstLayerId =
        a instanceof Marker ? AnnotationManager.pointLayerId + a.getId() :
          AnnotationManager.annotationLayerId + a.getId()
      let secondLayerId =
        b instanceof Marker ? AnnotationManager.pointLayerId + b.getId() :
          AnnotationManager.annotationLayerId + b.getId()
      if (a.getZIndex() == b.getZIndex()) {
        if (firstLayerId > secondLayerId) {
          return -1
        }
        else if (secondLayerId > firstLayerId) {
          return 1
        }
      }
      else if (a.getZIndex() > b.getZIndex()) {
        return -1
      }
      else if (b.getZIndex() > a.getZIndex()) {
        return 1
      }
      return 0
    })
    let clickedAnnotation = this.checkCollisionAnnotations(touchBox,
      minBoxVectorTileCoordinate,
      maxBoxVectorTileCoordinate,
      touchPointLatLng,
      annotations.frontBuildingAnnotations,
      geometryTileData)
    if (clickedAnnotation) {
      return clickedAnnotation
    }

    let iconViewMarker = this._iconViewMarkerManager.getIconViewMarkers()
    let markersClicked: Marker[] = []

    iconViewMarker.forEach((marker) =>{
      if (marker.isUserInteractionEnabled() && this.checkCollisionIconViewMarker(touchBox, marker)) {
        markersClicked.push(marker)
      }
    })
    if (markersClicked.length != 0) {
      if (markersClicked.length > 1) {
        let it = 0
        let maxZIndex = markersClicked[0].getZIndex()
        for (let i = 1; i < markersClicked.length; ++i) {
          if (markersClicked[i].getZIndex() > maxZIndex) {
            maxZIndex = markersClicked[i].getZIndex()
            it = i
          }
        }
        return markersClicked[it]
      }
      else {
        return markersClicked[0]
      }
    }

    if (mapState.is3dMode()) {
      let mapObject = this.map.queryBuildingInfo(touchBox)
      if (mapObject != null) {
        return mapObject
      }
    }

    annotations.belowBuildingAnnotations.sort((a, b) => {
      let firstLayerId =
        a instanceof Marker ? AnnotationManager.pointLayerId + a.getId() :
          AnnotationManager.annotationLayerId + a.getId()
      let secondLayerId =
        b instanceof Marker ? AnnotationManager.pointLayerId + b.getId() :
          AnnotationManager.annotationLayerId + b.getId()
      if (a.getZIndex() == b.getZIndex()) {
        if (firstLayerId > secondLayerId) {
          return -1
        }
        else if (secondLayerId > firstLayerId) {
          return 1
        }
      }
      else if (a.getZIndex() > b.getZIndex()) {
        return -1
      }
      else if (b.getZIndex() > a.getZIndex()) {
        return 1
      }
      return 0
    })
    return this.checkCollisionAnnotations(touchBox,
      minBoxVectorTileCoordinate,
      maxBoxVectorTileCoordinate,
      touchPointLatLng,
      annotations.belowBuildingAnnotations,
      geometryTileData)
  }

  private queryAnnotations(touchPoint: Vector2):
    {frontBuildingAnnotations: Annotation[], belowBuildingAnnotations: Annotation[]} {

    // Query markers
    let frontBuildingAnnotations: Annotation[] = []
    let belowBuildingAnnotations: Annotation[] = []
    if (touchPoint == null) {
      return {frontBuildingAnnotations: frontBuildingAnnotations, belowBuildingAnnotations: belowBuildingAnnotations}
    }
    let visibleTiles = this.tileManager.getVisibleTiles()
    visibleTiles.forEach(visibleTile => {
      let tile = visibleTile.getTileCoordinate()
      let bounds = GeometryUtils.tileCoordinateToLatLngBounds(tile)
      let bBox = {
        minX: bounds.getSouthwest().lng,
        minY: bounds.getSouthwest().lat,
        maxX: bounds.getNortheast().lng,
        maxY: bounds.getNortheast().lat
      }
      let symbols = this.symbolTree.search(bBox)
      symbols.forEach(symbol => {
        let marker = symbol.symbolAnnotation
        if (marker.isVisible()) {
          if (marker.getZIndex() > MapConstants.buildingLayerOrder) {
            frontBuildingAnnotations.push(marker)
          }
          else {
            belowBuildingAnnotations.push(marker)
          }
        }
      })
    })

    let mapState = this.map.mapState
    let clickedLatLng = GeometryUtils.screenCoordinateToLatLng(touchPoint, mapState.cameraPosition, mapState.getScreenSize())
    if (clickedLatLng.valid()) {
      let maxNativeZoom = mapState.getMaxNativeZoom()
      let currentZoom = mapState.getZoom()
      let tileCoordinate =
        TileCoordinate.fromLatLng(clickedLatLng, currentZoom > maxNativeZoom ? maxNativeZoom : currentZoom,
          mapState.is3dMode())
      if (this.tiles.has(tileCoordinate.toString())) {

        // Query polygon/ polyline annotations
        let annotationTile = this.tiles.get(tileCoordinate.toString())
        let geometryTileData = annotationTile.getTileData()
        if (geometryTileData != null && geometryTileData.getLayerIds().length != 0) {
          let layerIds = geometryTileData.getLayerIds()
          let layerIdsSize = geometryTileData.getLayerIds().length
          for (let i = 0; i < layerIdsSize; ++i) {
            let layerId = layerIds[i]
            let geometryLayer = geometryTileData.getLayer(layerId)
            if (geometryLayer == null) {
              continue
            }
            for (let j = 0; j < geometryLayer.featureCount(); ++j) {
              let feature = geometryLayer.getFeature(j)
              if (!(feature instanceof AnnotationTileFeature) || feature.getType() == GeometryType.Point) {
                continue
              }
              let annotationTileFeature = feature as AnnotationTileFeature
              let annotationId = annotationTileFeature.id
              let shapeAnnotation = this.shapeAnnotations.get(annotationId).getAnnotation()
              if (shapeAnnotation && shapeAnnotation.getZIndex() > MapConstants.buildingLayerOrder) {
                frontBuildingAnnotations.push(shapeAnnotation)
              }
              else if (shapeAnnotation) {
                belowBuildingAnnotations.push(shapeAnnotation)
              }
            }
          }
        }

        // Query circle
        let bounds = GeometryUtils.tileCoordinateToLatLngBounds(tileCoordinate)
        let bBox = {
          minX: bounds.getSouthwest().lng,
          minY: bounds.getSouthwest().lat,
          maxX: bounds.getNortheast().lng,
          maxY: bounds.getNortheast().lat
        }
        let circles = this.circleTree.search(bBox)
        for (let i = 0; i < circles.length; ++i) {
          let circleAnnotation = circles[i].getAnnotation()
          if (circleAnnotation.isVisible()) {
            if (circleAnnotation.getZIndex() > MapConstants.buildingLayerOrder) {
              frontBuildingAnnotations.push(circleAnnotation)
            }
            else {
              belowBuildingAnnotations.push(circleAnnotation)
            }
          }
        }
      }
    }

    return {frontBuildingAnnotations: frontBuildingAnnotations.filter(f => f.isUserInteractionEnabled()),
      belowBuildingAnnotations: belowBuildingAnnotations.filter(b => b.isUserInteractionEnabled())}
  }

  public clearShapeIndexer(): void {
    this.shapeAnnotations.forEach(shapeAnnotation => {
      shapeAnnotation.clearIndex()
    })
  }

  private checkCollisionAnnotations(touchBox: Box,
    minBoxVectorTileCoordinate: Point,
    maxBoxVectorTileCoordinate: Point,
    touchPointLatLng: LatLng,
    annotations: Annotation[],
    geometryTileData: GeometryTileData): Annotation {

    let tileSize = MapUtils.getTileSize()
    let mapState = this.map.mapState
    for (let i = 0; i < annotations.length; ++i) {
      if (annotations[i] instanceof Marker) {
        let symbolAnnotation = annotations[i] as Marker
        if (symbolAnnotation.getId() == null) {

          // My location dot
          continue
        }
        let symbolAnnotationData = this.symbolAnnotations.get(symbolAnnotation.getId())
        if (symbolAnnotationData == null) {
          continue
        }
        if (CollisionUtils.boxIntersectsSymbol(touchBox, symbolAnnotation.getPosition(),
          symbolAnnotation.getElevation(), symbolAnnotationData.width, symbolAnnotationData.height,
          symbolAnnotation.getAnchor(),
          mapState)) {

          // Clicked on marker
          return annotations[i]
        }
      }
      if (touchPointLatLng.valid()) {
        if (annotations[i] instanceof Polyline) {
          if (minBoxVectorTileCoordinate == null || maxBoxVectorTileCoordinate == null || geometryTileData == null) {
            continue
          }
          let touchBoxPolygon = [
            new Point(minBoxVectorTileCoordinate.x, maxBoxVectorTileCoordinate.y),
            new Point(maxBoxVectorTileCoordinate.x, maxBoxVectorTileCoordinate.y),
            new Point(maxBoxVectorTileCoordinate.x, minBoxVectorTileCoordinate.y),
            new Point(minBoxVectorTileCoordinate.x, minBoxVectorTileCoordinate.y)
          ]
          let layerId = AnnotationManager.annotationLayerId + annotations[i].getId()
          let geometryLayer =
            geometryTileData.getLayer(layerId)
          if (geometryLayer == null) {
            continue
          }
          for (let j = 0; j < geometryLayer.featureCount(); ++j) {
            let feature = geometryLayer.getFeature(j)
            let annotationTileFeature = feature as AnnotationTileFeature
            let geometries = annotationTileFeature.getGeometries().geometryCoordinates
            let layer = this.map.getLayer(layerId)
            if (layer == null) {
              continue
            }
            let lineLayer = layer as LineLayer
            let haftLineWidth = lineLayer.getStrokeWidth() / 2.0
            if (mapState.getZoom() > mapState.getMaxNativeZoom()) {
              haftLineWidth /= Math.pow(2.0, Math.round(mapState.getZoom()) - mapState.getMaxNativeZoom())
            }
            let lines = []
            geometries.forEach(geometry => {
              let line = []
              geometry.forEach(point => line.push(new Point(point[0], point[1])))
              lines.push(line)
            })

            let testHit = CollisionUtils.polygonIntersectsBufferedMultiLine(touchBoxPolygon, lines, haftLineWidth / tileSize)
            if (testHit) {
              return annotations[i]
            }
          }
        }
        else if (annotations[i] instanceof Polygon) {
          if (minBoxVectorTileCoordinate == null || maxBoxVectorTileCoordinate == null || geometryTileData == null) {
            continue
          }
          let touchBoxPolygon = [
            new Point(minBoxVectorTileCoordinate.x, maxBoxVectorTileCoordinate.y),
            new Point(maxBoxVectorTileCoordinate.x, maxBoxVectorTileCoordinate.y),
            new Point(maxBoxVectorTileCoordinate.x, minBoxVectorTileCoordinate.y),
            new Point(minBoxVectorTileCoordinate.x, minBoxVectorTileCoordinate.y)
          ]
          let layerId = AnnotationManager.annotationLayerId + annotations[i].getId()
          let geometryLayer =
            geometryTileData.getLayer(layerId)
          if (geometryLayer == null) {
            continue
          }
          for (let j = 0; j < geometryLayer.featureCount(); ++j) {
            let feature = geometryLayer.getFeature(j)
            let annotationTileFeature = feature as AnnotationTileFeature
            let geometries = annotationTileFeature.getGeometries().geometryCoordinates
            let hitPolygon = false
            for (let k = 0; k < geometries.length; ++k) {
              let ring = geometries[k]
              let polygon = []
              ring.forEach(point => polygon.push(new Point(point[0], point[1])))
              if (k == 0) {

                // Extrude
                hitPolygon = CollisionUtils.polygonIntersectsPolygon(touchBoxPolygon, polygon)
                if (!hitPolygon) {
                  break
                }
              }
              else {

                // Holes
                let testHitHole = CollisionUtils.polygonIntersectsPolygon(touchBoxPolygon, polygon)
                if (testHitHole) {
                  if (!CollisionUtils.polygonIntersectsBufferedMultiLine(touchBoxPolygon, [polygon], 0)) {
                    hitPolygon = false
                    break
                  }
                }
              }
            }
            if (hitPolygon) {
              return annotations[i]
            }
          }
        }
        else if (annotations[i] instanceof Circle) {
          let circleAnnotation = annotations[i] as Circle
          let circleCenter = circleAnnotation.getCenter()
          let circleRadius = circleAnnotation.getRadius()
          let scale = mapState.getScale()
          let metersPerPixel =
            ProjectionMercator.getMetersPerPixelAtLatitude(
              mapState.getCamera().getTarget().lat, mapState.getZoom())
          let circleRadiusInPixel = circleRadius / metersPerPixel
          let circleCenterInMapPixelCoordinate = ProjectionMercator.projectScale(circleCenter, scale)

          let minBoxInLatLng = GeometryUtils.screenCoordinateToLatLng(new Vector2(touchBox.min.x, touchBox.min.y),
            mapState.cameraPosition, mapState.getScreenSize())
          let maxBoxInLatLng = GeometryUtils.screenCoordinateToLatLng(new Vector2(touchBox.max.x, touchBox.max.y),
            mapState.cameraPosition, mapState.getScreenSize())
          let minBox = ProjectionMercator.projectScale(minBoxInLatLng, scale)
          let maxBox = ProjectionMercator.projectScale(maxBoxInLatLng, scale)

          let testHit =
            CollisionUtils.boxIntersectsCircle(minBox, maxBox,
            new Point(circleCenterInMapPixelCoordinate.x,
              circleCenterInMapPixelCoordinate.y),
            circleRadiusInPixel)
          if (testHit) {
            return annotations[i]
          }
        }
      }
    }
    return null
  }

  private checkCollisionIconViewMarker(touchBox: Box, marker: Marker) {
    let position = this.map.latLngToScreenCoordinate(marker.getPosition(), marker.getElevation())
    position.x /= MapUtils.pixelScale
    position.y /= MapUtils.pixelScale
    let iconView = this._iconViewMarkerManager.getIconViewsHTML().get(marker.getId())
    let infoClass = document.getElementById(`${iconView.id}`) as HTMLElement
    let width = (infoClass.clientWidth % 2) ? infoClass.clientWidth + 1 : infoClass.clientWidth
    let height = (infoClass.clientHeight % 2) ? infoClass.clientHeight + 1 : infoClass.clientHeight
    let left = position.x + (0.5 - marker.getAnchor().x) * width
    let top = position.y + (0.5 - marker.getAnchor().y) * height
    let box = new Box(
      new Point(left, top),
      new Point(left + width, top + height)
    )
    return CollisionUtils.boxIntersectsBox(touchBox, box)
  }
}
