import {Vector3} from "../engine/Vector3"
import {Vector2} from "../engine/Vector2"
import {MapConstants, PolyConstants} from "../map/MapConstants"
import earcut from "earcut"
import {MathUtils} from "../engine/MathUtils"

export enum LineCapType {
  Butt,
  Square,
  Round
}

export enum LineJoinType {
  Miter,
  Bevel,
  Round
}

export class CircleBuilderContext {

  addVertex: Function

  indices: number[] = []

  constructor(addVertex: Function = (coord: Vector2) => {}) {
    this.addVertex = addVertex
  }
}

export function buildCircle(circleBuilderContext: CircleBuilderContext): void {
  let numberOfSides = PolyConstants.numTrianglesPerCircle
  let doublePi = 2.0 * Math.PI
  let startIndex = 0
  circleBuilderContext.addVertex(new Vector2(0, 0))
  for (let i = 0; i < numberOfSides; ++i) {
    circleBuilderContext.addVertex((new Vector2(Math.cos(i * doublePi / numberOfSides),
      Math.sin(i * doublePi / numberOfSides))).normalize())
    if (i == 0) {
      continue
    }
    circleBuilderContext.indices.push(startIndex)
    circleBuilderContext.indices.push(startIndex + i)
    circleBuilderContext.indices.push(startIndex + i + 1)
  }
  circleBuilderContext.indices.push(startIndex)
  circleBuilderContext.indices.push(numberOfSides)
  circleBuilderContext.indices.push(startIndex + 1)
}

export class PolygonBuilderContext {

  addVertex: Function

  indices: number[] = []

  constructor(addVertex: Function = (coord: Vector2) => {}) {
    this.addVertex = addVertex
  }

  clear(): void {
    this.indices = []
  }
}

export function buildPolygon(paths: Vector2[][], polygonBuilderContext: PolygonBuilderContext) {
  let vertices = []
  let holes= []
  paths.forEach((path, index) => {
    if (index != 0) {
      holes.push(vertices.length / 2)
    }
    path.forEach(point => {
      vertices.push((point.x + 0.5) * MapConstants.tileExtend)
      vertices.push((0.5 - point.y) * MapConstants.tileExtend)
      polygonBuilderContext.addVertex(point)
    })
  })
  polygonBuilderContext.indices = earcut(vertices, holes)
}

export class PolylineBuilderContext {
  addVertex: Function

  lineCapType: LineCapType

  lineJoinType: LineJoinType

  closedPolyline: boolean

  miterLimit: number

  indices: number[] = []

  numVertices: number

  constructor(addVertex: Function = (coord: Vector3, extrude: Vector3) => {},
    lineCapType: LineCapType = LineCapType.Butt,
    lineJoinType: LineJoinType = LineJoinType.Bevel,
    closedPolyline: boolean = false,
    miterLimit: number = 3.0) {
    this.addVertex = addVertex
    this.lineCapType = lineCapType
    this.lineJoinType = lineJoinType
    this.closedPolyline = closedPolyline
    this.miterLimit = miterLimit
    this.numVertices = 0
  }

  buildPolyLine(points: Vector2[],  onLoad: Function = null, lineWidth: number = null): void {
    this.buildPolyLineSegment(points, onLoad, lineWidth)
  }

  clear(): void {
    this.indices = []
    this.numVertices = 0
  }

  private buildPolyLineSegment(points: Vector2[], onLoad: Function, lineWidth: number = null): void {

    // Cumulative distance along the polyline.
    let distance = 0

    let startIndex = 0
    let endIndex = this.closedPolyline ? points.length + 2 : points.length
    let originPointsSize = points.length
    let pointsSize = endIndex - startIndex

    if (pointsSize < 2) {
      return
    }

    let normalPrevious: Vector2
    let normalNext: Vector2
    let miter: Vector2
    let coordCurr = points[startIndex]
    let coordNext = points[startIndex + 1]

    normalNext = new Vector2(coordNext.y - coordCurr.y, coordCurr.x - coordNext.x).normalize()
    if (!this.closedPolyline) {
      this.addCap(coordCurr, normalNext, true)
    }

    /** Add the 2 first points */
    this.addPolyLineVertex(coordCurr, normalNext, new Vector2(0.0, 1.0))
    this.addPolyLineVertex(coordCurr, normalNext.multiply(-1), new Vector2(0.0, 0.0))

    /** Process intermediate points */
    for (let i = 1; i < pointsSize - 1; ++i) {
      let nextIndex = (i + startIndex + 1) % originPointsSize
      distance += MathUtils.distance(coordCurr, coordNext)
      coordCurr = coordNext
      coordNext = points[nextIndex]
      if (coordCurr == coordNext) {
        continue
      }
      normalPrevious = normalNext
      normalNext = new Vector2(coordNext.y - coordCurr.y, coordCurr.x - coordNext.x).normalize()

      /** Compute miter vector */
      miter = normalPrevious.add(normalNext)
      let scale = 1
      if (miter.x == 0 && miter.y == 0) {
        miter = new Vector2(normalPrevious.y - normalNext.y, normalNext.x - normalPrevious.x)
      }
      else {
        scale = 2 / miter.dot(miter)
      }
      miter = miter.multiply(scale)
      let bevel = this.lineJoinType == LineJoinType.Bevel
      if (miter.length > this.miterLimit) {

        /** If the miter length is greater than miter limit then it should be bevel joint */
        bevel = true
        miter = miter.multiply(this.miterLimit / miter.length)
      }

      let v = distance

      if (this.lineJoinType == LineJoinType.Miter && !bevel) {

        /** Miter */
        this.addPolyLineVertex(coordCurr, miter, new Vector2(v, 1.0))
        this.addPolyLineVertex(coordCurr, miter.multiply(-1), new Vector2(v, 0.0))
        this.addIndices()
      }
      else {
        let numOfTrianglesOnJoin =
          (bevel || this.lineJoinType == LineJoinType.Bevel) ? 1 : PolyConstants.numTrianglesOnRoundJoin

        /** Bevel or round */
        let isRightTurn = (normalNext.x * normalPrevious.y - normalNext.y * normalPrevious.x) > 0
        if (isRightTurn) {
          this.addPolyLineVertex(coordCurr, miter, new Vector2(v, 1.0))
          this.addPolyLineVertex(coordCurr, normalPrevious.multiply(-1), new Vector2(v, 0.0))
          this.addIndices()

          this.addFan(coordCurr, normalPrevious.multiply(-1), normalNext.multiply(-1), miter, new Vector2(v, 0.0), new Vector2(v, 0.0), new Vector2(v, 1.0), numOfTrianglesOnJoin)

          this.addPolyLineVertex(coordCurr, miter, new Vector2(v, 1.0))
          this.addPolyLineVertex(coordCurr, normalNext.multiply(-1), new Vector2(v, 0.0))
        }
        else {
          this.addPolyLineVertex(coordCurr, normalPrevious, new Vector2(v, 1.0))
          this.addPolyLineVertex(coordCurr, miter.multiply(-1), new Vector2(v, 0.0))
          this.addIndices()

          this.addFan(coordCurr, normalPrevious, normalNext, miter.multiply(-1), new Vector2(v, 1.0), new Vector2(v, 1.0), new Vector2(v, 0.0), numOfTrianglesOnJoin)

          this.addPolyLineVertex(coordCurr, normalNext, new Vector2(v, 1.0))
          this.addPolyLineVertex(coordCurr, miter.multiply(-1), new Vector2(v, 0.0))
        }
      }
    }

    distance += MathUtils.distance(coordCurr, coordNext)
    if (!lineWidth) {
      lineWidth = 1.0
      console.error("lineWidth value pass into buildPolyLineSegment is invalid !")
    }
    let textureWidth = lineWidth * PolyConstants.polylineTextureWidth / PolyConstants.polylineTextureHeight
    distance = Math.floor(distance / textureWidth) * textureWidth

    /** Add the last 2 points */
    this.addPolyLineVertex(coordNext, normalNext, new Vector2(distance, 1.0))
    this.addPolyLineVertex(coordNext, normalNext.multiply(-1), new Vector2(distance, 0.0))
    this.addIndices()
    if (!this.closedPolyline) {
      this.addCap(coordNext, normalNext, false)
    }
    onLoad && onLoad()
  }

  private addCap(coord: Vector2, normal: Vector2, isBeginning: boolean): void {
    let v = isBeginning ? 0.0 : 1.0
    switch (this.lineCapType) {
    case LineCapType.Butt:
      break
    case LineCapType.Square: {
      let tangent = new Vector2(-normal.y, normal.x)
      this.addPolyLineVertex(coord, normal.add(tangent), new Vector2(v, 0.0))
      this.addPolyLineVertex(coord, normal.multiply(-1).add(tangent), new Vector2(v, 0.0))
      if (!isBeginning) {

        // At the beginning of a line we can't form triangles with previous vertices
        this.addIndices()
      }
    }
      break
    case LineCapType.Round: {
      let nA = normal
      let nB = normal.multiply(-1)
      let nC = new Vector2(0, 0)
      let uvA = new Vector2(v, 1.0)
      let uvB = new Vector2(v, 0.0)
      let uvC = new Vector2(v, 0.5)
      if (isBeginning) {

        /** Flip the direction of the fan */
        nA = nA.multiply(-1)
        nB = nB.multiply(-1)
        uvA.y = 0.0
        uvB.y = 1.0
      }
      this.addFan(coord, nA, nB, nC, uvA, uvB, uvC, PolyConstants.numTrianglesOnRoundCap)
    }
      break
    default:
      console.log("Invalid line cap type")
      break
    }
  }

  private addPolyLineVertex(coord: Vector2, extrude: Vector2, uv: Vector2): void {
    ++this.numVertices
    this.addVertex(coord, extrude, uv)
  }

  private addIndices(): void {
    this.indices.push(this.numVertices - 4)
    this.indices.push(this.numVertices - 2)
    this.indices.push(this.numVertices - 3)

    this.indices.push(this.numVertices - 3)
    this.indices.push(this.numVertices - 2)
    this.indices.push(this.numVertices - 1)
  }

  private addFan(center: Vector2, normalA: Vector2, normalB: Vector2, normalC: Vector2,
    uvA: Vector2, uvB: Vector2, uvC: Vector2, numTriangles: number) {
    let cross = normalA.x * normalB.y - normalA.y * normalB.x
    let angle = Math.atan2(cross, normalA.dot(normalB))
    let startIndex = this.numVertices

    /** Add center vertex */
    this.addPolyLineVertex(center, normalC, uvC)

    /** Add vertex for point A */
    this.addPolyLineVertex(center, normalA, uvA)

    /** Add radial vertices */
    let radial = normalA
    for (let i = 0; i < numTriangles; i++) {
      let frac = (i + 1) / numTriangles
      radial = normalA.rotateByRadian(angle * frac)
      let uv = new Vector2(0.0, 0.0)
      uv = uvA.multiply(1.0 - frac).add(uvB.multiply(frac))
      this.addPolyLineVertex(center, radial, uv)

      /** Add indices */
      this.indices.push(startIndex)
      this.indices.push(startIndex + i + (angle > 0 ? 1 : 2))
      this.indices.push(startIndex + i + (angle > 0 ? 2 : 1))
    }
  }
}
