import {Marker} from "./Marker"
import {LatLng} from "./LatLng"
import {MarkerClusterOptions} from "./options/MarkerClusterOptions"
import {MapUtils} from "../util/MapUtils"
import {Map4D} from "./Map"
import {LatLngBounds} from "./LatLngBounds"
import {PaddingOptions} from "./options/PaddingOptions"
import {MapsEventListener} from "./MapsEventListener"
import {MapConstants} from "../map/MapConstants"
import * as GeoJSON from "geojson"
import Supercluster, {PointFeature, ClusterProperties, AnyProps} from "supercluster"
import {Feature, Point} from "geojson"
import {MarkerLabel} from "./MarkerLabel"
import {Icon} from "./Icon"

export class MarkerClusterer {

  private readonly markers: Marker[]

  private clusters: Marker[] = []

  private markerClusterMap: Map<number, number>

  private events: MapsEventListener[] = []

  private zoomOnClick: boolean

  private superCluster: Supercluster

  private map: Map4D

  private static readonly defaultMarkerClusterOptions: MarkerClusterOptions = {
    minZoom: 0,
    maxZoom: 22,
    radius: 100,
    zoomOnClick: true
  }

  private static readonly defaultPadding: PaddingOptions = {
    top: 10,
    bottom: 10,
    left: 10,
    right: 10
  }

  constructor(markers = [], options?: MarkerClusterOptions) {
    if (markers == null || markers.length == 0) {
      throw Error("Markers list can not be null or empty")
    }
    this.markers = markers
    this.markerClusterMap = new Map<number, number>()
    let markerClusterOptions: any = {}
    MapUtils.extendOptions(markerClusterOptions, MarkerClusterer.defaultMarkerClusterOptions, options)
    for (const option of markerClusterOptions) {
      if (option == null) {
        throw Error("Parameters can not be null")
      }
    }

    markerClusterOptions.radius = markerClusterOptions.radius * MapUtils.pixelScale
    this.zoomOnClick = markerClusterOptions.zoomOnClick
    this.superCluster = new Supercluster(markerClusterOptions)
    let jsonData = this.getFeatures()
    this.superCluster.load(jsonData)
  }

  setMap(map: Map4D): void {
    if (this.map === map ) {
      return
    }
    if (this.map) {
      this.events.forEach(event => event.remove())
      this.events = []
      this.markers.forEach(marker => marker.setMap(null))
      this.removeMarkerClusters()
    }
    this.map = map
    if (this.map) {
      this.updateCluster(this.map.getCamera().getZoom())

      let markerClickEvent = this.map.addListener("markerClick", (args) => {
        if (args.xa.type != "mouseup" || !this.zoomOnClick) {
          return
        }
        let cluster = args.marker
        let markerCluster = this.markerClusterMap.get(cluster.getId())
        if (markerCluster == null) {
          return
        }
        let leaves = this.superCluster.getLeaves(markerCluster, MapConstants.clusterMaxPointCount)
        let bounds = new LatLngBounds()
        for (let positions of leaves) {
          bounds.extend([positions.geometry.coordinates[0], positions.geometry.coordinates[1]])
        }
        let cameraPosition = this.map.mapView.getCameraPositionForLatLngBounds(
          bounds, {top: 0, left: 0, right: 0, bottom: 0})
        this.map.fitBounds(bounds, MarkerClusterer.defaultPadding, {animate: true})
        this.updateCluster(cameraPosition.getZoom())
      })
      let idleEvent = this.map.addListener("idle", () => {
        let mapState = this.map.mapView.mapState

        // These states SHOULD NOT BE be reset before calling idle events otherwise this function will not work properly
        if (mapState.isZoomIn() || mapState.isZoomOut() || mapState.isFlyingTo()) {
          this.updateCluster(this.map.getCamera().getZoom())
        }
      })
      this.events.push(markerClickEvent)
      this.events.push(idleEvent)
    }
  }

  setZoomOnClick(zoomOnClick: boolean): void {
    if (zoomOnClick == null) {
      console.warn("Parameter zoomOnClick can not be null")
      return
    }
    this.zoomOnClick = zoomOnClick
  }

  private updateCluster(zoom : number): void {
    this.removeMarkerClusters()
    let bounds = this.map.mapView.getBounds()
    if (bounds == null) {
      return
    }
    let bbox : GeoJSON.BBox= [
      bounds.getSouthwest().lng,
      bounds.getSouthwest().lat,
      bounds.getNortheast().lng,
      bounds.getNortheast().lat
    ]
    let markerClusters = this.superCluster.getClusters(bbox, zoom)
    this.update(markerClusters)
  }

  private removeMarkerClusters():void {
    this.clusters.forEach(cluster => cluster.setMap(null))
    this.clusters = []
  }

  private update(markerClusters: GeoJSON.Feature<GeoJSON.Point, ClusterProperties & AnyProps> |
  GeoJSON.Feature<GeoJSON.Point, AnyProps>[]): void {

    for (let i in markerClusters) {
      if (markerClusters[i].id != null) {

        let markers = this.superCluster.getLeaves(markerClusters[i].id, MapConstants.clusterMaxPointCount)
        let size = (markers.length.toString().length - 1) * 15
        let width = 50 + size
        let height = 50 + size
        let icon = new Icon(width, height, "markerClusterIcon")

        // Maker cluster
        let cluster = new Marker({
          position: LatLng.convert(markerClusters[i].geometry.coordinates),
          icon: icon,
          label: new MarkerLabel({
            text: markers.length.toString(),
            fontSize: 13,
            color: "#000000",
            haloColor: "#b22525"
          })

        })
        cluster.setMap(this.map)
        this.clusters.push(cluster)
        this.markerClusterMap.set(cluster.getId(), markerClusters[i].id)

        // Remove markers on a cluster
        markers.forEach(marker => this.markers[marker.properties.index].setMap(null))
      }
      else {

        // Marker
        let marker = this.markers[markerClusters[i].properties.index]
        marker.setMap(this.map)
      }
    }
  }

  private getFeatures(): Array<PointFeature<Point>> {
    let lineFeature = []
    this.markers.forEach((marker, index) => {
      let features = {
        "type": "Feature",
        "properties": {index: index},
        "geometry": {
          "type": "Point",
          "coordinates": []
        }
      }
      features.geometry.coordinates.push(marker.getPosition().lng)
      features.geometry.coordinates.push(marker.getPosition().lat)
      lineFeature.push(features)
    })
    return lineFeature
  }
}
