import {MapView} from "./MapView"
import {MapsEventListener} from "../public/MapsEventListener"
import {Point} from "../public/Point"
import {Vector2} from "../engine/Vector2"
import {Polyline} from "../public/Polyline"
import {Polygon} from "../public/Polygon"
import {Circle} from "../public/Circle"
import {CameraPosition} from "../public/CameraPosition"
import {MapUtils} from "../util/MapUtils"
import {MapEvent} from "../public/MapEvent"
import {EventOptions} from "../public/options/EventOptions"
import {Annotation} from "../annotation/Annotation"
import {MapObject} from "../public/MapObject"
import {Marker} from "../public/Marker"
import {GeometryType} from "../tile/data/GeometryTileData"
import {Place} from "../public/Place"

class Event {

  private readonly _id: number

  private readonly _eventCallback: Function

  private _eventOptions: EventOptions

  constructor(_id: number, _eventOptions: EventOptions, _eventCallback: Function) {
    this._id = _id
    this._eventOptions = _eventOptions
    this._eventCallback = _eventCallback
  }

  get id(): number {
    return this._id
  }

  get eventCallBack(): Function {
    return this._eventCallback
  }

  get eventOptions(): EventOptions {
    return this._eventOptions
  }

  set eventOptions(value: EventOptions) {
    this._eventOptions = value
  }
}

export class MapsEventManager {

  private eventIdCounter = 0

  private readonly events: Map<MapEvent, Event[]>

  private readonly geoJsonEvents: Map<MapEvent, Event[]>

  private mapView: MapView

  private static readonly defaultEventOptions = {
    marker: false,

    polygon: false,

    polyline: false,

    circle: false,

    object: false,

    location: false,

    place: false
  }

  constructor(mapView: MapView) {
    this.events = new Map<MapEvent, Event[]>()
    this.geoJsonEvents = new Map<MapEvent, Event[]>()
    this.mapView = mapView
  }

  private static convertEventFromName(eventName: string): MapEvent {
    switch (eventName) {
    case "cameraWillChange":
      return MapEvent.cameraWillChange
    case "cameraChanging":
      return MapEvent.cameraChanging
    case "idle":
      return MapEvent.idle
    case "click":
      return MapEvent.click
    case "dblClick":
      return MapEvent.dblClick
    case "drag":
      return MapEvent.drag
    case "dragEnd":
      return MapEvent.dragEnd
    case "dragStart":
      return MapEvent.dragStart
    case "mouseMove":
      return MapEvent.mouseMove
    case "mouseOut":
      return MapEvent.mouseOut
    case "mouseOver":
      return MapEvent.mouseOver
    case "rightClick":
      return MapEvent.rightClick
    case "modeChanged":
      return MapEvent.modeChanged
    case "longClick":
      return MapEvent.longClick
    case "hover":
      return MapEvent.hover
    case "tilesLoaded":
      return MapEvent.tilesLoaded
    case "boundsChanged":
      return MapEvent.boundsChanged
    default:
      throw new Error("Event not supported: " + eventName)
    }
  }

  addGeoJsonListener(event: string | MapEvent, func: Function): MapsEventListener {
    let mapEvent = (typeof event === "string") ? MapsEventManager.convertEventFromName(event) : event
    let eventId = this.eventIdCounter++
    let eventObject = new Event(eventId, {}, func)
    if (this.geoJsonEvents.has(mapEvent)) {
      this.geoJsonEvents.get(mapEvent).push(eventObject)
    }
    else {
      this.geoJsonEvents.set(mapEvent, [eventObject])
    }
    let geoJsonEvents = this.geoJsonEvents
    return new (class MapsEventListenerResult implements MapsEventListener {
      remove(): void {
        if (geoJsonEvents.has(mapEvent)) {
          let mapEvents = geoJsonEvents.get(mapEvent)
          let removeIndex = mapEvents.findIndex(mapEvent => mapEvent.id == eventId)
          if (removeIndex >= 0) {
            mapEvents.splice(removeIndex, 1)
          }
          if (mapEvents.length === 0) {
            geoJsonEvents.delete(mapEvent)
          }
        }
      }

      updateEventOptions(options: EventOptions): void {
      }
    })()
  }

  addEvent(event: string | MapEvent, func: Function, options: EventOptions): MapsEventListener {
    let mapEvent = (typeof event === "string") ? MapsEventManager.convertEventFromName(event) : event
    let optionDefault: EventOptions = {}
    MapUtils.extendOptions(optionDefault, MapsEventManager.defaultEventOptions, options)

    if (!optionDefault.marker && !optionDefault.polygon && !optionDefault.polyline
      && !optionDefault.object && !optionDefault.circle && !optionDefault.location
      && !optionDefault.place) {
      optionDefault.location = true
    }

    let eventId = this.eventIdCounter++
    let eventObject = new Event(eventId, optionDefault, func)
    if (this.events.has(mapEvent)) {
      this.events.get(mapEvent).push(eventObject)
    }
    else {
      this.events.set(mapEvent, [eventObject])
    }
    let events = this.events
    return new (class MapsEventListenerResult implements MapsEventListener {
      remove(): void {
        if (events.has(mapEvent)) {
          let mapEvents = events.get(mapEvent)
          let removeIndex = mapEvents.findIndex(mapEvent => mapEvent.id == eventId)
          if (removeIndex >= 0) {
            mapEvents.splice(removeIndex, 1)
          }
          if (mapEvents.length === 0) {
            events.delete(mapEvent)
          }
        }
      }

      updateEventOptions(options: EventOptions): void {
        let optionDefault: EventOptions = {}
        MapUtils.extendOptions(optionDefault, MapsEventManager.defaultEventOptions, options)
        if (!optionDefault.marker && !optionDefault.polygon && !optionDefault.polyline
          && !optionDefault.object && !optionDefault.circle && !optionDefault.location
          && !optionDefault.place) {
          optionDefault.location = true
        }
        eventObject.eventOptions = optionDefault
      }
    })()
  }

  onHoverEvent(touchPoint: Vector2, mouseEvent: MouseEvent): boolean {

    if (this.mapView.getBuildingSelectionIconManager().onHover(touchPoint)) {
      this.mapView.hideInfoWindow()
      return
    }
    return this.onEvent(touchPoint, mouseEvent, MapEvent.hover)
  }

  onClickEvent(touchPoint: Vector2, mouseEvent: MouseEvent): boolean {
    return this.onEvent(touchPoint, mouseEvent, MapEvent.click)
  }

  onLongClickEvent(touchPoint: Vector2, mouseEvent: MouseEvent): boolean {
    return this.onEvent(touchPoint, mouseEvent, MapEvent.longClick)
  }

  onRightClickEvent(touchPoint: Vector2, mouseEvent: MouseEvent): boolean {
    return this.onEvent(touchPoint, mouseEvent, MapEvent.rightClick)
  }

  onDoubleClickEvent(touchPoint: Vector2, mouseEvent: MouseEvent): boolean {
    return this.onEvent(touchPoint, mouseEvent, MapEvent.dblClick)
  }

  onDragEvent(event: MapEvent, mouseEvent: MouseEvent, draggable: Annotation | MapObject): void {

    if (event != MapEvent.dragStart && event != MapEvent.drag && event != MapEvent.dragEnd) {
      console.error("Invalid event")
      return
    }

    let events = this.events.get(event) || []
    if (events.length == 0) {
      return
    }
    let callbackObject = {}
    if (draggable instanceof MapObject) {
      events = events.filter(event => event.eventOptions.object)
      callbackObject["object"] = draggable as MapObject
    }
    else if (draggable instanceof Marker) {
      events = events.filter(event => event.eventOptions.marker)
      callbackObject["marker"] = draggable as Marker
    }
    else if (draggable instanceof Polyline) {
      events = events.filter(event => event.eventOptions.polyline)
      callbackObject["polyline"] = draggable as Polyline
    }
    else if (draggable instanceof Polygon) {
      events = events.filter(event => event.eventOptions.polygon)
      callbackObject["polygon"] = draggable as Polygon
    }
    else if (draggable instanceof Circle) {
      events = events.filter(event => event.eventOptions.circle)
      callbackObject["circle"] = draggable as Circle
    }
    if (events.length == 0) {
      return
    }
    let mousePosition = this.mapView.getCurrentMousePosition(mouseEvent)
    let pixel = new Point(mousePosition.x, mousePosition.y)
    let point = MapUtils.pixelToPoint(pixel)
    callbackObject["location"] = this.mapView.screenCoordinateToLatLng(mousePosition)
    callbackObject["point"] = point
    callbackObject["pixel"] = pixel
    callbackObject["xa"] = mouseEvent
    events.forEach(event => event.eventCallBack(callbackObject))
  }

  private queryMapObjectAndAnnotation(events: Event[], touchPoint: Vector2, mouseEvent: MouseEvent, mapEvent: MapEvent) {
    let mousePosition = this.mapView.getCurrentMousePosition(mouseEvent)
    let location = this.mapView.screenCoordinateToLatLng(mousePosition)
    let pixel = new Point(mousePosition.x, mousePosition.y)
    let point = MapUtils.pixelToPoint(pixel)
    let clickedItem = this.mapView.queryMapObjectAndAnnotation(touchPoint)
    if (clickedItem instanceof Marker) {
      let markerEvents = (events || []).filter(event => event.eventOptions.marker)
      let clickedMarker = clickedItem as Marker
      this.mapView.showInfoWindow(clickedMarker.getId())
      if (markerEvents && markerEvents.length != 0 && clickedMarker.getMap()) {
        markerEvents
          .forEach(markerEvent =>
            markerEvent.eventCallBack({
              marker: clickedMarker,
              point: point,
              pixel: pixel,
              xa: mouseEvent,
              location: location
            }))
      }
    }
    else if (clickedItem instanceof Polyline) {
      let polylineEvents = (events || []).filter(event => event.eventOptions.polyline)
      if (polylineEvents && polylineEvents.length != 0) {
        polylineEvents
          .forEach(polylineEvent =>
            polylineEvent.eventCallBack({
              polyline: clickedItem as Polyline,
              point: point,
              pixel: pixel,
              xa: mouseEvent,
              location: location
            }))
      }
    }
    else if (clickedItem instanceof Polygon) {
      let polygonEvents = (events || []).filter(event => event.eventOptions.polygon)
      if (polygonEvents && polygonEvents.length != 0) {
        polygonEvents
          .forEach(polygonEvent =>
            polygonEvent.eventCallBack({
              polygon: clickedItem as Polygon,
              point: point,
              pixel: pixel,
              xa: mouseEvent,
              location: location
            }))
      }
    }
    else if (clickedItem instanceof Circle) {
      let circleEvents = (events || []).filter(event => event.eventOptions.circle)
      if (circleEvents && circleEvents.length != 0) {
        circleEvents
          .forEach(circleEvent =>
            circleEvent.eventCallBack({
              circle: clickedItem as Circle,
              point: point,
              pixel: pixel,
              xa: mouseEvent,
              location: location
            }))
      }
    }
    else if (clickedItem instanceof MapObject) {
      let buildingEvents = (events || []).filter(event => event.eventOptions.object)
      if (buildingEvents && buildingEvents.length != 0) {
        buildingEvents
          .forEach(buildingEvent =>
            buildingEvent.eventCallBack({
              object: clickedItem as MapObject,
              point: point,
              pixel: pixel,
              xa: mouseEvent,
              location: location
            }))
      }
    }
    return clickedItem != null
  }

  private queryPlace(events: Event[], touchPoint: Vector2, mouseEvent: MouseEvent, mapEvent: MapEvent,
    userFeatureOnly: boolean) {
    if (!this.mapView.mapSettings.placesEnabled && !userFeatureOnly) {
      return false
    }
    let mousePosition = this.mapView.getCurrentMousePosition(mouseEvent)
    let location = this.mapView.screenCoordinateToLatLng(mousePosition)
    let pixel = new Point(mousePosition.x, mousePosition.y)
    let point = MapUtils.pixelToPoint(pixel)

    // Query place
    let feature = this.mapView.queryFeature(touchPoint)
    if (feature != null && feature.getType() == GeometryType.Point &&

        // !(userFeatureOnly XOR this.mapView.getFeatureManager().hasFeature(feature.getId()))
        !((userFeatureOnly && !this.mapView.getFeatureManager().hasFeature(feature.getId())) || (!userFeatureOnly && this.mapView.getFeatureManager().hasFeature(feature.getId())))) {
      let placeEvents = (events || []).filter(event => event.eventOptions.place)
      let properties = feature.getProperties()
      let place = new Place({
        id: feature.getId(), name: properties.get("name") as string,
        icon: properties.get("icon") as string,
        location: [properties.get("lng") as number, properties.get("lat") as number]
      })
      placeEvents && placeEvents
        .forEach(placeEvent =>
          placeEvent.eventCallBack({
            place: place,
            point: point,
            pixel: pixel,
            xa: mouseEvent,
            location: location
          }))
      return true
    }
    return false
  }

  private queryGeoJsonFeature(events: Event[], touchPoint: Vector2, mouseEvent: MouseEvent, mapEvent: MapEvent) {
    let mousePosition = this.mapView.getCurrentMousePosition(mouseEvent)
    let location = this.mapView.screenCoordinateToLatLng(mousePosition)
    let pixel = new Point(mousePosition.x, mousePosition.y)
    let point = MapUtils.pixelToPoint(pixel)

    // Query geojson feature
    let geoJsonEvents = this.geoJsonEvents.get(mapEvent)
    if (geoJsonEvents && geoJsonEvents.length > 0) {
      let feature = this.mapView.getGeoJsonManager().queryFeature(touchPoint)
      if (feature) {
        geoJsonEvents.forEach(event =>
          event.eventCallBack({
            feature: feature,
            point: point,
            pixel: pixel,
            xa: MouseEvent,
            location: location
          }))
        return true
      }
    }
    return false
  }

  private onEvent(touchPoint: Vector2, mouseEvent: MouseEvent, mapEvent: MapEvent): boolean {
    let events = this.events.get(mapEvent)
    if (mapEvent == MapEvent.click) {
      this.mapView.hideInfoWindow()
    }
    else if (events == null || events.length == 0) {
      return false
    }

    return this.mapView.mapState.is3dMode() ?

      // Query order in 3D: place user feature -> map object and annotation -> geojson
      (this.queryPlace(events, touchPoint, mouseEvent, mapEvent, true) ||
            this.queryMapObjectAndAnnotation(events, touchPoint, mouseEvent, mapEvent) ||
            this.queryGeoJsonFeature(events, touchPoint, mouseEvent, mapEvent)) :

      // Query order in 2D: place user feature -> map object and annotation -> geojson -> sdk POI feature
      (this.queryPlace(events, touchPoint, mouseEvent, mapEvent, true)) ||
            (this.queryMapObjectAndAnnotation(events, touchPoint, mouseEvent, mapEvent) ||
            this.queryGeoJsonFeature(events, touchPoint, mouseEvent, mapEvent)) ||
            this.queryPlace(events, touchPoint, mouseEvent, mapEvent, false)
  }

  notify(mapEvent: MapEvent, mouseEvent?: MouseEvent, gesture?: boolean) {
    let events = this.events.get(mapEvent)
    if (!events || !Array.isArray(events) || events.length === 0) {
      return
    }
    switch (mapEvent) {
    case MapEvent.hover:
    case MapEvent.click:
    case MapEvent.rightClick:
    case MapEvent.dblClick:
    case MapEvent.longClick:
    case MapEvent.dragStart:
    case MapEvent.drag:
    case MapEvent.dragEnd:
    case MapEvent.mouseMove:
    case MapEvent.mouseOut:
    case MapEvent.mouseOver: {
      let locationEvents = (events || []).filter(event => event.eventOptions.location)
      if (locationEvents.length == 0) {
        return
      }
      let mousePosition = this.mapView.getCurrentMousePosition(mouseEvent)
      let pixel = new Point(mousePosition.x, mousePosition.y)
      let point = MapUtils.pixelToPoint(pixel)
      locationEvents.forEach(event => event.eventCallBack({
        location: this.mapView.screenCoordinateToLatLng(mousePosition),
        point: point,
        pixel: pixel,
        xa: mouseEvent
      }))
      break
    }
    case MapEvent.cameraWillChange: {
      let cameraPosition = this.mapView.getCamera()
      events.filter(event => event.eventOptions.location).forEach(event => event.eventCallBack(
        {camera: new CameraPosition(cameraPosition.getTarget(), cameraPosition.getTilt(),
          cameraPosition.getBearing(), cameraPosition.getZoom()), gesture: gesture}))
      break
    }
    case MapEvent.cameraChanging:
    case MapEvent.idle: {
      let cameraPosition = this.mapView.getCamera()
      events.filter(event => event.eventOptions.location).forEach(event => event.eventCallBack(
        {camera: new CameraPosition(cameraPosition.getTarget(), cameraPosition.getTilt(),
          cameraPosition.getBearing(), cameraPosition.getZoom())}))
      break
    }
    case MapEvent.modeChanged:
      events.filter(event => event.eventOptions.location)
        .forEach(event => event.eventCallBack({is3DMode: this.mapView.mapSettings.is3dMode()}))
      break
    case MapEvent.tilesLoaded:
      events.filter(event => event.eventOptions.location)
        .forEach(event => event.eventCallBack())
      break
    case MapEvent.boundsChanged:
      events.filter(event => event.eventOptions.location)
        .forEach(event => event.eventCallBack())
      break
    default:
      events.filter(event => event.eventOptions.location).forEach(event => event.eventCallBack())
      break
    }
  }

  destroy(): void {
    this.events.clear()
  }

}
