import {LayerRenderer} from "./LayerRenderer"
import {MapState} from "../map/MapState"
import {Layer, LayerType} from "../layer/Layer"
import {BuildingLayer} from "../layer/BuildingLayer"
import {ResourceManager, ShaderEnum, TextureEnum} from "../util/ResourceManager"
import {Shader, UniformInfo} from "../engine/Shader"
import {Color} from "../engine/Color"
import {Vector3} from "../engine/Vector3"
import {MapConstants} from "../map/MapConstants"
import {Building} from "../building/Building"

enum BuildingRenderType {
  Building = 0,
  Selection,
  Shadow
}

export class BuildingLayerRenderer extends LayerRenderer {

  private readonly gl: WebGLRenderingContext

  private fogStartUniformValue: number

  private fogRangeUniformValue: number

  constructor(gl: WebGLRenderingContext, mapState: MapState, layer: Layer) {
    super(mapState, layer)
    this.gl = gl
    this.fogStartUniformValue = Number.POSITIVE_INFINITY
    this.fogRangeUniformValue = Number.POSITIVE_INFINITY
  }

  render(): void {
    this.renderInternal(BuildingRenderType.Building)
  }

  renderSelection(): void {
    this.renderInternal(BuildingRenderType.Selection)
  }

  renderShadowMapping(): void {
    this.renderInternal(BuildingRenderType.Shadow)
  }

  private renderOutlining(outliningObjects: Building[]): void {
    if (outliningObjects && outliningObjects.length != 0) {
      let shader = ResourceManager.getInstance(this.gl).getShader(ShaderEnum.BUILDING_OUTLINING)
      shader.bind()
      this.gl.enable(this.gl.STENCIL_TEST)

      //TODO: Considering to turn it on again
      //this.gl.enable(this.gl.CULL_FACE)
      this.gl.stencilFunc(this.gl.NOTEQUAL, 1, 0xFF)
      this.gl.stencilMask(0x00)
      outliningObjects.forEach(outliningObject => {
        let model = outliningObject.getModel()
        model.bind()
        shader.setAttributes(model.attributeInfos)
        shader.setUniforms([
          new UniformInfo("u_modelMatrix", "matrix", outliningObject.getScaledModelMatrix()),
          new UniformInfo("u_mvpMatrix", "matrix", outliningObject.getScaledMvpMatrix()),
        ])
        model.render(this.gl.TRIANGLES)
        model.unbind()
      })
      this.gl.disable(this.gl.STENCIL_TEST)

      //TODO: Considering to turn it on again
      //this.gl.disable(this.gl.CULL_FACE)
      shader.disableAttributes()
    }
  }

  private renderInternal(renderType: BuildingRenderType): void {
    if (!this.mapState.is3dMode()) {
      return
    }
    if (this.layer.getLayerType() != LayerType.BUILDING) {
      console.warn("This layer is not building !")
      return
    }
    let buildingLayer = this.layer as BuildingLayer
    if (buildingLayer.textureBuildingGroups.size == 0 &&
      buildingLayer.extrudeBuildings.length == 0) {
      return
    }
    let resourceManager = ResourceManager.getInstance(this.gl)

    this.gl.enable(this.gl.DEPTH_TEST)

    //TODO: Considering to turn it on again
    //this.gl.enable(this.gl.CULL_FACE)

    let buildingShader = null
    this.fogStartUniformValue = Number.POSITIVE_INFINITY
    this.fogRangeUniformValue = Number.POSITIVE_INFINITY
    if (renderType === BuildingRenderType.Building) {
      if (this.mapState.getTilt() > MapConstants.maxTiltIntersectWithScreen) {

        //TODO: Fix hard code
        let maxNativeZoom = MapConstants.defaultMaxNativeZoom
        let extra = 256.0
        let fogStart = this.mapState.getScreenSize().y * 1.5
        let fogScale = Math.pow(2, Math.max(this.mapState.getZoom() - maxNativeZoom, 0.0))
        this.fogStartUniformValue = fogStart * fogScale + extra
        //fix bug on iPhone 8+: fog causes building is hidden
        //this.fogRangeUniformValue = this.mapState.getDistanceToSky() * fogScale - fogStart
      }
      else {
        //Fix bug on iOS, texture was black
        this.fogStartUniformValue = 99999999999
        this.fogRangeUniformValue = 99999999999
      }

      if (buildingLayer.effectManager.enableShadowEffect) {
        buildingShader= resourceManager.getShader(ShaderEnum.BUILDING_WITH_SHADOW_SHADER)
      }
      else {
        buildingShader = resourceManager.getShader(ShaderEnum.BUILDING_SHADER)
      }

      buildingShader.bind()
      let sunLight = buildingLayer.lightManager.sunLight
      let fogColor = buildingLayer.timeEffectManager.getFogColor()
      buildingShader.setUniforms([
        new UniformInfo("u_light_position", "3f", sunLight.lightPosition),
        new UniformInfo("u_light_color", "3fv", sunLight.lightColor.rgbBuffer()),
        new UniformInfo("u_fogStart", "1f", this.fogStartUniformValue),
        new UniformInfo("u_fogRange", "1f", this.fogRangeUniformValue),
        new UniformInfo("u_fogColor", "3fv", fogColor.rgbBuffer())
      ])
    }
    else if (renderType === BuildingRenderType.Selection) {
      buildingShader = resourceManager.getShader(ShaderEnum.BUILDING_SELECTION_SHADER)
      buildingShader.bind()
      this.resetSelectionCode()
    }
    else if (renderType === BuildingRenderType.Shadow) {
      buildingLayer.effectManager.shadowMap.bind()
      buildingShader = resourceManager.getShader(ShaderEnum.SHADOW_MAPPING_SHADER)
      buildingShader.bind()
    }

    let outliningObjects: Building[] = []

    // Render buildings which has texture
    this.renderTextureBuildings(renderType, buildingLayer, buildingShader, outliningObjects)

    // Render extrude buildings
    this.renderExtrudeBuildings(renderType, buildingLayer, buildingShader, outliningObjects)

    this.gl.disable(this.gl.DEPTH_TEST)

    //TODO: Considering to turn it on again
    //this.gl.disable(this.gl.CULL_FACE)

    if (renderType == BuildingRenderType.Shadow) {
      buildingLayer.effectManager.shadowMap.unbind()
    }
    buildingShader.disableAttributes()
    this.renderOutlining(outliningObjects)
  }

  private renderTextureBuildings(renderType: BuildingRenderType, buildingLayer: BuildingLayer,
    buildingShader: Shader, outliningObjects: Building[]) {
    if (buildingLayer.textureBuildingGroups.size != 0) {
      let whiteTexture = ResourceManager.getInstance(this.gl).getTexture(TextureEnum.WHITE_TEXTURE)
      let buildingResourceManager = buildingLayer.getBuildingResourceManager()
      let customBuildingManager = buildingLayer.getCustomBuildingManager()
      buildingLayer.textureBuildingGroups.forEach(buildingGroup => {
        let model = buildingGroup.getModel()
        if (model == null || model.attributeInfos.length == 0) {
          return
        }
        buildingGroup.buildings.forEach(building => {
          /** If Building is not custom building, will not render building in hidden list**/
          if (buildingResourceManager.isHiddenBuilding(building.getId())
            && customBuildingManager.getMapObjects().get(building.getId()) == null) {
            return
          }
          if (renderType === BuildingRenderType.Building) {
            if (building.isSelected()) {
              this.gl.enable(this.gl.STENCIL_TEST)
              this.gl.stencilFunc(this.gl.ALWAYS, 1, 0xFF)
              this.gl.stencilMask(0xFF)
              outliningObjects.push(building)
            }
            let texture = building.getTexture()
            if (texture == null) {
              texture = whiteTexture
            }
            let effectManager = buildingLayer.effectManager
            if (effectManager.enableShadowEffect) {
              let shadowMap = effectManager.shadowMap
              let shadowMvpMatrix = building.getModelMatrix().concat(shadowMap.getLightViewProjectionMatrix())
                .concat(MapConstants.biasMatrix)
              buildingShader.setUniforms([
                new UniformInfo("u_shadowMap", "texture", shadowMap.getShadowMapTexture()),
                new UniformInfo("u_shadowMvpMatrix", "matrix", shadowMvpMatrix)
              ])
            }

            buildingShader.setUniforms([
              new UniformInfo("u_camera_direction", "3f", this.mapState.cameraPositionVector),
              new UniformInfo("u_modelMatrix", "matrix", building.getModelMatrix()),
              new UniformInfo("u_mvpMatrix", "matrix", building.getMvpMatrix()),
              new UniformInfo("u_texture", "texture", texture)
            ])
          }
          else if (renderType === BuildingRenderType.Selection) {
            let code = this.getSelectionCode()
            building.setSelectionCode(code)
            let color = Color.fromInt(code)
            buildingShader.setUniforms([
              new UniformInfo("u_color", "3f", new Vector3(color.red, color.green, color.blue)),
              new UniformInfo("u_mvpMatrix", "matrix", building.getMvpMatrix()),
            ])
          }
          else if (renderType === BuildingRenderType.Shadow) {
            let effectManager = buildingLayer.effectManager
            let shadowMap = effectManager.shadowMap
            let shadowMvpMatrix = building.getModelMatrix().concat(shadowMap.getLightViewProjectionMatrix())
            buildingShader.setUniforms([
              new UniformInfo("u_shadowMvpMatrix", "matrix", shadowMvpMatrix),
            ])
          }
          model.bind()
          buildingShader.setAttributes(model.attributeInfos)
          model.render(this.gl.TRIANGLES)
          model.unbind()
          buildingShader.resetTextureUnit()
          if (renderType === BuildingRenderType.Building && building.isSelected()) {
            this.gl.disable(this.gl.STENCIL_TEST)
          }
        })
      })
    }
  }

  private renderExtrudeBuildings(renderType: BuildingRenderType, buildingLayer: BuildingLayer,
    buildingShader: Shader, outliningObjects: Building[]) {
    if (buildingLayer.extrudeBuildings.length != 0) {
      let whiteTexture = ResourceManager.getInstance(this.gl).getTexture(TextureEnum.WHITE_TEXTURE)
      let buildingResourceManager = buildingLayer.getBuildingResourceManager()
      let customBuildingManager = buildingLayer.getCustomBuildingManager()
      if (renderType === BuildingRenderType.Building) {
        let effectManager = buildingLayer.effectManager
        if (effectManager.enableShadowEffect) {
          let shadowMap = effectManager.shadowMap
          buildingShader.setUniforms([
            new UniformInfo("u_shadowMap", "texture", shadowMap.getShadowMapTexture()),
          ])
        }
      }
      buildingLayer.extrudeBuildings.forEach(extrudeBuilding => {
        /** If Building is not custom building, will not render building in hidden list**/
        if (buildingResourceManager.isHiddenBuilding(extrudeBuilding.getId())
            && customBuildingManager.getMapObjects().get(extrudeBuilding.getId()) == null) {
          return
        }

        if (renderType === BuildingRenderType.Shadow) {
          return
        }
        let model = extrudeBuilding.getModel()
        if (model == null) {

          // continue
          return
        }
        if (renderType === BuildingRenderType.Building) {
          if (extrudeBuilding.isSelected()) {
            this.gl.enable(this.gl.STENCIL_TEST)
            this.gl.stencilFunc(this.gl.ALWAYS, 1, 0xFF)
            this.gl.stencilMask(0xFF)
            outliningObjects.push(extrudeBuilding)
          }

          let texture = extrudeBuilding.getTexture()
          if (texture == null) {
            texture = whiteTexture
          }
          let effectManager = buildingLayer.effectManager
          if (effectManager.enableShadowEffect) {
            let shadowMap = effectManager.shadowMap
            let shadowMvpMatrix = extrudeBuilding.getModelMatrix().concat(shadowMap.getLightViewProjectionMatrix())
              .concat(MapConstants.biasMatrix)
            buildingShader.setUniforms([
              new UniformInfo("u_shadowMvpMatrix", "matrix", shadowMvpMatrix)
            ])
          }
          buildingShader.setUniforms([
            new UniformInfo("u_texture", "texture", texture),
            new UniformInfo("u_camera_direction", "3f", this.mapState.cameraPositionVector),
            new UniformInfo("u_modelMatrix", "matrix", extrudeBuilding.getModelMatrix()),
            new UniformInfo("u_mvpMatrix", "matrix", extrudeBuilding.getMvpMatrix())
          ])
        }
        else if (renderType === BuildingRenderType.Selection) {
          let code = this.getSelectionCode()
          extrudeBuilding.setSelectionCode(code)
          let color = Color.fromInt(code)
          buildingShader.setUniforms([
            new UniformInfo("u_color", "3f", new Vector3(color.red, color.green, color.blue)),
            new UniformInfo("u_mvpMatrix", "matrix", extrudeBuilding.getMvpMatrix()),
          ])
        }
        else if (renderType === BuildingRenderType.Shadow) {
          let effectManager = buildingLayer.effectManager
          let shadowMap = effectManager.shadowMap
          let shadowMvpMatrix =  extrudeBuilding.getModelMatrix().concat(shadowMap.getLightViewProjectionMatrix())
          buildingShader.setUniforms([
            new UniformInfo("u_shadowMvpMatrix", "matrix", shadowMvpMatrix),
          ])
        }
        model.bind()
        buildingShader.setAttributes(model.attributeInfos)
        model.render(this.gl.TRIANGLES)
        model.unbind()
        buildingShader.resetTextureUnit()
        if (renderType === BuildingRenderType.Building && extrudeBuilding.isSelected()) {
          this.gl.disable(this.gl.STENCIL_TEST)
        }
      })
    }
  }
}
