import {RequestTask} from "./RequestTask"

export class TaskWorker {
  private maxTask = 3

  private worker: Worker;

  private numberOfRunningTask = 0

  constructor(worker: Worker) {
    this.worker = worker
  }

  finishedTask() {
    this.numberOfRunningTask = Math.max(this.numberOfRunningTask - 1, 0)
  }

  canExecuteTask() {
    return this.numberOfRunningTask < this.maxTask
  }

  executeTask(task: RequestTask) {
    if (!this.canExecuteTask()) {
      return
    }
    let data = {
      execute: true,
      payload: JSON.stringify(task)
    }
    this.worker.postMessage(data)
    this.numberOfRunningTask += 1
  }

  cancel(task: RequestTask) {
    let data = {
      execute: false,
      payload: JSON.stringify(task)
    }
    this.worker.postMessage(data)
    this.numberOfRunningTask = Math.max(this.numberOfRunningTask - 1, 0)
  }
}