import {LatLngBounds} from "../public/LatLngBounds"
import {TileCoordinate} from "./TileCoordinate"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {MathUtils} from "../engine/MathUtils"

export class TileRange {

  private minX: number;

  private maxX: number;

  private minY: number;

  private maxY: number;

  private minZoom: number;

  private maxZoom: number;

  static fromLatLngBounds(bounds: LatLngBounds, minZoom: number, maxZoom: number): TileRange {
    let swProj = ProjectionMercator.projectZoom(bounds.getSouthwest(), maxZoom)
    let neProj = ProjectionMercator.projectZoom(bounds.getNortheast(), maxZoom)
    let maxTile = Math.pow(2.0, maxZoom)
    let minX = Math.floor(swProj.x)
    let maxX = Math.floor(neProj.x)
    let minY = MathUtils.clamp(Math.floor(neProj.y), 0.0, maxTile)
    let maxY = MathUtils.clamp(Math.floor(swProj.y), 0.0, maxTile)
    return new TileRange(minX, maxX, minY, maxY, minZoom, maxZoom)
  }

  private constructor(minX: number, maxX: number, minY: number, maxY: number, minZoom: number, maxZoom: number) {
    this.minX = minX
    this.maxX = maxX
    this.minY = minY
    this.maxY = maxY
    this.minZoom = minZoom
    this.maxZoom = maxZoom
  }

  contains(tileCoordinate: TileCoordinate): boolean {
    let x = tileCoordinate.getX()
    let y = tileCoordinate.getY()
    let zoom = tileCoordinate.getZoom()
    if (zoom <= this.maxZoom && zoom >= this.minZoom) {
      if (zoom == 0) {
        return true
      }
      let dz = (this.maxZoom - zoom)
      let x0 = this.minX >> dz
      let x1 = this.maxX >> dz
      let y0 = this.minY >> dz
      let y1 = this.maxY >> dz

      /** For wrapped bounds */
      return (this.minX > this.maxX ?
        x >= x0 || x <= x1 :
        x <= x1 && x >= x0) &&
        y <= y1 &&
        y >= y0
    }
    return false
  }

}
