import * as SunCalc from "suncalc"
import {DirectionalLight} from "./DirectionalLight"
import {Vector3} from "../engine/Vector3"
import {LatLng} from "../public/LatLng"
import {MathUtils} from "../engine/MathUtils"
import {TimeEffectManager} from "../effect/TimeEffectManager"
import {TimeEffect} from "../public/TimeEffect"
import {Color} from "../engine/Color"

export class LightingManager {

  private readonly _sunLight: DirectionalLight

  private _lastUpdateTime: number = 0

  private _timeEffectManager: TimeEffectManager

  private _onLightPositionChanged: () => void

  private _oldTimeEffect: TimeEffect

  constructor(timeEffectManager: TimeEffectManager) {
    this._sunLight = new DirectionalLight(Color.fromHex("FFFFFF"), new Vector3(0.0, 0.0, 1.0))
    this._timeEffectManager = timeEffectManager
    this._oldTimeEffect = this._timeEffectManager.getTimeEffect()
  }

  update(target: LatLng): void {
    let nowMs = new Date().getTime()
    let timeElapsedMs = nowMs - this._lastUpdateTime
    if (timeElapsedMs < 60000 && this._oldTimeEffect == this._timeEffectManager.getTimeEffect() ) {
      return
    }
    this._lastUpdateTime = nowMs
    this._oldTimeEffect = this._timeEffectManager.getTimeEffect()

    // Update sun position
    let sunPosition = SunCalc.getPosition(this._timeEffectManager.getTime(), target.lat, target.lng)
    let azimuth = -1 * sunPosition.azimuth
    let altitude =  MathUtils.clamp(Math.abs(sunPosition.altitude), Math.PI / 6, Math.PI / 2)

    this._sunLight.lightColor = this._timeEffectManager.getBuildingColor()
    this._sunLight.lightPosition =  new Vector3(Math.cos(altitude) * Math.sin(azimuth),
      Math.cos(altitude) * Math.cos(azimuth),
      Math.sin(altitude))
    this._onLightPositionChanged && this._onLightPositionChanged()
  }

  setOnLightPositionChanged(onLightPositionChanged: () => void ) {
    this._onLightPositionChanged = onLightPositionChanged
  }

  get sunLight(): DirectionalLight {
    return this._sunLight
  }

}