import {DirectionalLight} from "../lighting/DirectionalLight"
import {FrameBuffer} from "../engine/Framebuffer"
import {Matrix4} from "../engine/Matrix"
import {Vector3} from "../engine/Vector3"
import {MapUtils} from "../util/MapUtils"
import {CameraConstants, MapConstants} from "../map/MapConstants"
import {MapState} from "../map/MapState"
import {Texture} from "../engine/Texture"
import {Color} from "../engine/Color"

export class ShadowMap {

  private _gl: WebGLRenderingContext

  private _width: number

  private _height: number

  private _frameBuffer: FrameBuffer

  private _directionalLight: DirectionalLight

  private _mapState: MapState

  private _lightViewProjectionMatrix: Matrix4

  private _lightProjectionMatrix: Matrix4

  constructor(gl: WebGLRenderingContext, directionalLight: DirectionalLight, mapState: MapState) {
    this._gl = gl
    this._directionalLight = directionalLight
    this._mapState = mapState
    this._lightProjectionMatrix = Matrix4.ortho(-10.0, 10.0, -10.0, 10.0, -10.0, 20.0)
    this.resize(mapState.screenWidth, mapState.screenHeight)
  }

  bind(): void {
    this._frameBuffer.bind()
    this._gl.viewport(0, 0, this._width, this._height)
    this._gl.clearColor(1.0, 1.0, 1.0, 1.0)
    this._gl.clear(this._gl.COLOR_BUFFER_BIT | this._gl.DEPTH_BUFFER_BIT)
    let backgroundColor = Color.fromHex(this._mapState.is3dMode() ? MapConstants.mapBackgroundColor3D :
      MapConstants.mapBackgroundColor2D)
    this._gl.clearColor(backgroundColor.red, backgroundColor.green, backgroundColor.blue, 1.0)
  }

  unbind(): void {
    this._frameBuffer.unbind()
    this._gl.viewport(0, 0, this._mapState.screenWidth, this._mapState.screenHeight)
  }

  clear() {
    this.bind()
    this.unbind()
  }

  getLightViewProjectionMatrix(): Matrix4 {
    return this._lightViewProjectionMatrix
  }

  getShadowMapTexture(): Texture {
    return this._frameBuffer.colorTexture
  }

  resize(width: number, height: number): void {
    this._width = width * 1.5
    this._height = height * 1.5
    this._frameBuffer && this._frameBuffer.destroy()
    this._frameBuffer = new FrameBuffer(this._gl, this._width, this._height)
    this.updateMatrices()
    this.clear()
  }

  updateMatrices(): void {
    let screenTileSize = MapUtils.getTileSize()
    let scale = (CameraConstants.height * Math.tan(CameraConstants.fov / 2)
      * screenTileSize / this._mapState.screenHeight) / (screenTileSize / 2)
    let lightView =
      Matrix4.lookAt(this._directionalLight.lightPosition, new Vector3(0, 0, 0), 1,
        new Vector3(0.0, 1.0, 0.0))
    lightView = Matrix4.scale(scale, scale, scale).concat(lightView)
    this._lightViewProjectionMatrix = lightView.concat(this._lightProjectionMatrix)
  }

  get width(): number {
    return this._width
  }

  get height(): number {
    return this._height
  }

  renderDebug(): void {
    this._frameBuffer.drawDebug()
  }

  destroy(): void {
    this._frameBuffer.destroy()
    this._frameBuffer = null
    this._directionalLight = null
    this._gl = null
    this._mapState = null
    this._lightViewProjectionMatrix = null
    this._lightProjectionMatrix = null
  }

}