import {Layer, LayerType} from "./Layer"
import {TileManager} from "../tile/TileManager"
import {MapState} from "../map/MapState"
import {RenderTile} from "../renderer/RenderTile"

export class GeoJsonLayer extends Layer {

  renderTiles: Map<string, [RenderTile]>

  constructor(layerId: string, order: number, tileManager: TileManager) {
    super(layerId, order, LayerType.GEOJSON, tileManager)

    this.renderTiles = new Map<string, [RenderTile]>()
  }

  update(mapState: MapState): void {

  }

  addRenderTile(tileCoordinate: string, renderTile: RenderTile) {
    if (!this.renderTiles.has(tileCoordinate)) {
      this.renderTiles.set(tileCoordinate, [renderTile])
    }
    else {
      this.renderTiles.get(tileCoordinate).push(renderTile)
    }
  }

  removeRenderTile(tileCoordinate: string) {
    this.renderTiles.delete(tileCoordinate)
  }
}
