import {MapUtils} from "../util/MapUtils"
import {MathUtils} from "../engine/MathUtils"
export class Images {

  static markerDefault = "data:image/png;base64,"

  static markerDefault2x = "data:image/png;base64,"

  static myPosition = "data:image/png;base64,"

  static myPosition2x = "data:image/png;base64,"

  static logoIcon ="data:image/png;base64,"

  static rainTexture = "data:image/png;base64,"

  static snowTexture = "data:image/png;base64,"

  static selectionIcon = "data:image/png;base64,"

  static selectionIcon2x = "data:image/png;base64,"
  
  static waterColor = "data:image/jpeg;base64,"

  static waterDisplacement = "data:image/jpeg;base64,"

  static markerClusterIcon = "data:image/png;base64,"

  static placeSelectedIcon = "data:image/png;base64,"

  static images = { "markerDefault" : [Images.markerDefault, Images.markerDefault2x, Images.markerDefault2x],
    "placeSelectedIcon": [Images.placeSelectedIcon, Images.placeSelectedIcon, Images.placeSelectedIcon],
    "myPosition"   : [Images.myPosition,   Images.myPosition2x, Images.myPosition2x],
    "logoIcon"     : [Images.logoIcon, Images.logoIcon, Images.logoIcon],
    "rainTexture"  : [Images.rainTexture, Images.rainTexture, Images.rainTexture],
    "snowTexture"  : [Images.snowTexture, Images.snowTexture, Images.snowTexture],
    "selectionIcon"  : [Images.selectionIcon, Images.selectionIcon2x, Images.selectionIcon2x],
    "markerClusterIcon": [Images.markerClusterIcon, Images.markerClusterIcon, Images.markerClusterIcon]
  }

  static get(name: string): string {
    let images = this.images[name] || [null, null, null]
    let index = MathUtils.clamp(MapUtils.resourceScale, 1, 3)
    return images[index - 1]
  }
}
