import {Layer, LayerType} from "./Layer"
import {TileManager} from "../tile/TileManager"
import {MapState} from "../map/MapState"
import {RenderTile} from "../renderer/RenderTile"
import {LatLng} from "../public/LatLng"
import {Point} from "../public/Point"
import {TextLabelData} from "../text/TextLabelData"
import {MarkerLabel} from "../public/MarkerLabel"

export class SymbolLayer extends Layer {

  private _renderTiles: Map<string, RenderTile>

  private _position: LatLng

  private _anchor: Point

  private _width: number

  private _height: number

  private _elevation: number

  private _labelAnchor: Point

  private _markerLabel: MarkerLabel

  private _url: string

  private _rotation: number

  //TODO: Remove
  private _textLabelData: TextLabelData

  constructor(layerId: string, order: number, tileManager: TileManager) {
    super(layerId, order, LayerType.SYMBOL, tileManager)

    this._renderTiles = new Map<string, RenderTile>()
  }

  update(mapState: MapState): void {
  }

  addRenderTile(tileCoordinate: string, renderTile: RenderTile) {
    if (!this._renderTiles.has(tileCoordinate)) {
      this._renderTiles.set(tileCoordinate, renderTile)
    }
  }

  removeRenderTile(tileCoordinate: string) {
    this._renderTiles.delete(tileCoordinate)
  }

  clearRenderTiles(): void {
    this._renderTiles.clear()
  }

  get renderTiles(): Map<string, RenderTile> {
    return this._renderTiles
  }

  set renderTiles(value: Map<string, RenderTile>) {
    this._renderTiles = value
  }

  set url(value: string) {
    this._url = value
  }

  set width(value: number) {
    this._width = value
  }

  set markerLabel(value: MarkerLabel) {
    this._markerLabel = value
  }

  set height(value: number) {
    this._height = value
  }

  set position(value: LatLng) {
    this._position = value
  }

  set anchor(value: Point) {
    this._anchor = value
  }

  set elevation(value: number) {
    this._elevation = value
  }

  set labelAnchor(value: Point) {
    this._labelAnchor = value
  }

  set textLabelData(value: TextLabelData) {
    this._textLabelData = value
  }

  set rotation(value: number) {
    this._rotation = value
  }

  get url(): string {
    return this._url
  }

  get width(): number {
    return this._width
  }

  get height(): number {
    return this._height
  }

  get markerLabel(): MarkerLabel {
    return this._markerLabel
  }

  get position(): LatLng {
    return this._position
  }

  get anchor(): Point {
    return this._anchor
  }

  get elevation(): number {
    return this._elevation
  }

  get labelAnchor(): Point {
    return this._labelAnchor
  }

  get textLabelData(): TextLabelData {
    return this._textLabelData
  }

  get rotation(): number {
    return this._rotation
  }
}
