import {TileArea, TileAreaId} from "./../public/TileArea"
import {TileManager} from "../tile/TileManager"

export class TileAreaManager {

  private idCounter: number = 0

  private tileAreas: Map<TileAreaId, TileArea>

  private tileManager: TileManager

  constructor(tileManage: TileManager) {
    this.tileManager = tileManage
    this.tileAreas = new Map<TileAreaId, TileArea>()
  }

  addTileArea(tileArea: TileArea): TileAreaId {
    let tileAreaId = this.idCounter++
    this.tileAreas.set(tileAreaId, tileArea)
    this.tileManager.addTileArea(tileAreaId, tileArea)
    return tileAreaId
  }

  updateTileArea(tileAreaId: TileAreaId, tileArea: TileArea): void {
    if (!this.tileAreas.has(tileAreaId)) {
      return
    }
    this.tileAreas.delete(tileAreaId)
    this.tileAreas.set(tileAreaId, tileArea)
    this.tileManager.updateTileArea(tileAreaId, tileArea)
  }

  removeTileArea(tileAreaId: TileAreaId): void {
    this.tileAreas.delete(tileAreaId)
    this.tileManager.removeTileArea(tileAreaId)
  }

}
