// @ts-ignore
import * as decode from "image-decode"
import {RequestTask, Resource, ResponseTask} from "./RequestTask"
import {Thread} from "../util/Thread"
import {TaskWorker} from "./TaskWorker"
import {ObjModelLoader} from "../util/ObjModelLoader"

declare function postMessage(message: any, transfer?: Transferable[]): void

class ExecuteTask {
  request: XMLHttpRequest

  public id: string

  constructor(id: string) {
    this.id = id
  }

  abort() {
    if (this.request != null) {
      this.request.abort()
    }
  }
}

export class WorkerHelper {
  static scriptUrl = ""

  private static executeTasks: ExecuteTask[] = []

  static createWorker(onMessage: Function) {
    let url = this.workerUrl
    let worker = new Worker(url)
    let taskWorker = new TaskWorker(worker)
    worker.onmessage = e => {
      let task = e.data as ResponseTask
      taskWorker.finishedTask()
      onMessage(task)
    }

    return taskWorker
  }

  private static get workerUrl() {
    let urls = []
    if (WorkerHelper.scriptUrl) {
      urls = [WorkerHelper.scriptUrl]
    }
    else {
      let scripts = document.getElementsByTagName("script")
      for (let index = 0; index < scripts.length; ++index) {
        let script = scripts[index]
        if (script["src"].indexOf("map4d.vn") > -1 && script["src"].indexOf("/api/js") > -1) {
          urls.push(script["src"])
        }
      }
    }
    let body = `importScripts(${urls.map(url => `"${url}"`).join(",")});`
    let blob = new Blob([body], { type: "application/javascript" })
    return window.URL.createObjectURL(blob)
  }

  static setupWorkerThread() {
    self.onmessage = (e) => {
      let data = e.data
      if (data.execute) {
        this.execute(JSON.parse(data.payload) as RequestTask)
      }
      else {
        this.cancel(JSON.parse(data.payload) as RequestTask)
      }
    }
  }

  static getTexture(task: RequestTask, executeTask: ExecuteTask) {
    let xmlRequest = new XMLHttpRequest()
    xmlRequest.onreadystatechange = function(){
      if (this.readyState == XMLHttpRequest.DONE) {
        let responseTask = new ResponseTask(task.id)
        if (xmlRequest.status == 200) {
          responseTask.success = true
          let textureImage = decode(this.response)
          if (!textureImage) {
            console.error("There is an error while parsing the texture image")
            responseTask.success = false
            postMessage(responseTask)
            return
          }
          textureImage.data = textureImage.data.buffer
          responseTask.data = textureImage
          postMessage(responseTask, [responseTask.data.data])
        }
        else {
          responseTask.success = false
          postMessage(responseTask)
        }
      }
    }
    xmlRequest.open("GET", task.url)
    this.setHeaderInfo(xmlRequest)
    xmlRequest.responseType = "arraybuffer"
    xmlRequest.send()
    executeTask.request = xmlRequest
  }

  static getModel(task: RequestTask, executeTask: ExecuteTask) {
    let xmlRequest = new XMLHttpRequest()
    xmlRequest.onreadystatechange = function(){
      if (this.readyState == XMLHttpRequest.DONE) {
        let responseTask = new ResponseTask(task.id)
        if (xmlRequest.status == 200) {
          responseTask.success = true
          let data = ObjModelLoader.loadModel(this.responseText, true)
          responseTask.data = data
          if (data != null && data.length > 0) {
            postMessage(responseTask, [responseTask.data[0].data])
          }
          else {
            responseTask.success = false
            postMessage(responseTask)
          }
        }
        else {
          responseTask.success = false
          postMessage(responseTask)
        }
      }
    }
    xmlRequest.open("GET", task.url)
    this.setHeaderInfo(xmlRequest)
    xmlRequest.send()
    executeTask.request = xmlRequest
  }

  static getRasterTile(task: RequestTask, executeTask: ExecuteTask) {
    let xmlRequest = new XMLHttpRequest()
    xmlRequest.onreadystatechange = () => {
      if (xmlRequest.readyState == XMLHttpRequest.DONE) {
        let responseTask = new ResponseTask(task.id)
        if (xmlRequest.status == 200) {
          responseTask.success = true
          let responseData = JSON.parse(xmlRequest.responseText)
          let rasterTile = (responseData.result || {}).rasterTile
          if (rasterTile != null && !rasterTile.startsWith("data:image")) {
            rasterTile = "data:image;base64," + rasterTile
          }
          let rasterTileImage = decode(rasterTile)
          if (!rasterTileImage) {
            console.error("There is an error while parsing the texture image")
            responseTask.success = false
            postMessage(responseTask)
            return
          }
          rasterTileImage.data = rasterTileImage.data.buffer
          responseData.result.rasterTile = rasterTileImage
          responseTask.data = responseData
          postMessage(responseTask, [responseTask.data.result.rasterTile.data])
        }
        else {
          responseTask.success = false
          postMessage(responseTask)
        }
      }

    }

    xmlRequest.open(task.method, task.url, true)

    if (task.params != null) {
      xmlRequest.setRequestHeader("Content-Type", "application/json")
    }
    xmlRequest.setRequestHeader("Accept", "application/json")
    this.setHeaderInfo(xmlRequest)
    xmlRequest.send(task.params)
    executeTask.request = xmlRequest
  }

  static getTileData(task: RequestTask, executeTask: ExecuteTask) {
    let xmlRequest = new XMLHttpRequest()
    xmlRequest.onreadystatechange = () => {
      if (xmlRequest.readyState == XMLHttpRequest.DONE) {
        let responseTask = new ResponseTask(task.id)
        if (xmlRequest.status == 200) {
          responseTask.success = true
          let responseData = JSON.parse(xmlRequest.responseText)
          responseTask.data = responseData
          postMessage(responseTask)
        }
        else {
          responseTask.success = false
          postMessage(responseTask)
        }
      }

    }
    xmlRequest.open(task.method, task.url, true)

    if (task.params != null) {
      xmlRequest.setRequestHeader("Content-Type", "application/json")
    }
    xmlRequest.setRequestHeader("Accept", "application/json")
    //this.setHeaderInfo(xmlRequest)
    xmlRequest.send(task.params)
    executeTask.request = xmlRequest
  }

  static setHeaderInfo(request: XMLHttpRequest){
    //request.setRequestHeader("sdk-name", "map4d-map")
    //request.setRequestHeader("sdk-platform", "web")
    //request.setRequestHeader("sdk-version", Env.sdkVersion)
    //request.setRequestHeader("signature", "__SIGNATURE__")
  }

  static getJson(task: RequestTask, executeTask: ExecuteTask) {
    let xmlRequest = new XMLHttpRequest()
    xmlRequest.onreadystatechange = () => {
      if (xmlRequest.readyState == XMLHttpRequest.DONE) {
        let responseTask = new ResponseTask(task.id)
        if (xmlRequest.status == 200) {
          responseTask.success = true
          let responseData = JSON.parse(xmlRequest.responseText)
          responseTask.data = responseData
          postMessage(responseTask)
        }
        else {
          responseTask.success = false
          postMessage(responseTask)
        }
      }
    }

    xmlRequest.open(task.method, task.url, true)

    if (task.params != null) {
      xmlRequest.setRequestHeader("Content-Type", "application/json")
    }
    xmlRequest.setRequestHeader("Accept", "application/json")
    this.setHeaderInfo(xmlRequest)
    xmlRequest.send(task.params)
    executeTask.request = xmlRequest
  }

  static execute(task: RequestTask) {
    let executeTask = new ExecuteTask(task.id)
    this.executeTasks.push(executeTask)

    switch (task.type) {
    case Resource.Texture:
    case Resource.CustomTexture:
    case Resource.TileOverlay:
      this.getTexture(task, executeTask)
      break
    case Resource.RasterTile:
      this.getRasterTile(task, executeTask)
      break
    case Resource.TileData:
      this.getTileData(task, executeTask)
      break
    case Resource.Model:
    case Resource.CustomModel:
      this.getModel(task, executeTask)
      break
    case Resource.Json:
      this.getJson(task, executeTask)
      break
    }
  }

  static cancel(task: RequestTask) {
    let endTask = this.removeTask(task.id)
    if (endTask != null) {
      endTask.abort()
    }
  }

  private static removeTask(id: string) {
    let index = this.executeTasks.findIndex(t => t.id == id)
    if (index >=0){
      let task = this.executeTasks[index]
      this.executeTasks.splice(index, 1)
      return task
    }
    return null
  }
}

if (Thread.isWorker) {
  WorkerHelper.setupWorkerThread()
}

if (!Thread.isWorker) {
  document.currentScript.onload = (e: any) => {
    let path1 = e.path != null && e.path.length > 0 ? e.path[0].src : null
    let path2 = e.currentTarget != null ? e.currentTarget.src : null
    let path3 = e.target != null ? e.target.src : null
    WorkerHelper.scriptUrl = path1 || path2 || path3
    console.log(WorkerHelper.scriptUrl)
    if (window["__INIT_MAP__"]) {
      window["__INIT_MAP__"]()
    }
  }
}
