import {GeometryTile} from "./GeometryTile"
import {TileCoordinate} from "./TileCoordinate"
import {Kind} from "./Tile"

export class VectorTile extends GeometryTile {

  constructor(tileCoordinate: TileCoordinate, gl: WebGLRenderingContext) {
    super(Kind.VECTOR, tileCoordinate, gl)
  }

  destroy(): void {
    this.layerDatas.forEach(layerData => layerData.destroy())
  }
}