import {LatLng, ILatLng} from "./LatLng"
import {Map4D} from "./Map"
import {Annotation} from "../annotation/Annotation"
import {MapUtils} from "../util/MapUtils"
import {PolygonOptions} from "./options/PolygonOptions"

export class Polygon extends Annotation {

  private paths: LatLng[][]

  private fillColor: string

  private fillOpacity: number

  private visible: boolean

  private strokeColor: string

  private strokeWidth: number

  private draggable: boolean

  private static readonly defaultPolygonOptions = {
    fillColor: "#0000ff",
    fillOpacity: 1.0,
    visible: true,
    strokeColor: "#ff0000",
    strokeWidth: 1,
    draggable: false,
    zIndex: 0,
    elevation: 0,
    userInteractionEnabled: true
  }

  constructor(options: PolygonOptions) {
    super()
    let optionDefault: any = {}
    MapUtils.extendOptions(optionDefault, Polygon.defaultPolygonOptions, options)
    for (const option of optionDefault) {
      if (option == null) {
        throw Error("Parameters can not be null")
      }
    }
    this.paths = optionDefault.paths.map(path => path.map(point => LatLng.convert(point)))
    this.fillColor = optionDefault.fillColor
    this.fillOpacity = optionDefault.fillOpacity
    this.strokeColor = optionDefault.strokeColor
    this.strokeWidth = optionDefault.strokeWidth
    this.visible = optionDefault.visible
    this.draggable = optionDefault.draggable
    this.zIndex = optionDefault.zIndex
    this.elevation = optionDefault.elevation
    this.userInteractionEnabled = optionDefault.userInteractionEnabled
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeAnnotation(this.id)
      this.id = -1
    }
    this.map = map
    if (this.map != null) {
      this.id = this.map.mapView.addAnnotation(this)
    }
  }

  setPaths(paths: ILatLng[][]): void {
    if (paths == null) {
      console.warn("Parameter paths can not be null")
      return
    }
    this.paths = paths.map(path => path.map(point => LatLng.convert(point)))
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id, true)
    }
  }

  setFillColor(fillColor: string): void {
    if (fillColor == null) {
      console.warn("Parameter fillColor can not be null")
      return
    }
    if (this.fillColor == fillColor) {
      return
    }
    this.fillColor = fillColor
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setFillOpacity(fillOpacity: number): void {
    if (fillOpacity == null) {
      console.warn("Parameter fillOpacity can not be null")
      return
    }
    if (this.fillOpacity == fillOpacity) {
      return
    }
    this.fillOpacity = fillOpacity
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setStrokeColor(strokeColor: string): void {
    if (this.strokeColor == strokeColor) {
      return
    }
    this.strokeColor = strokeColor
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setStrokeWidth(strokeWidth: number): void {
    if (this.strokeWidth == strokeWidth) {
      return
    }
    this.strokeWidth = strokeWidth
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setVisible(visible: boolean): void {
    if (visible == null) {
      console.warn("Parameter visible can not be null")
      return
    }
    if (this.visible === visible) {
      return
    }
    this.visible = visible
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  getPaths(): LatLng[][] {
    return (this.paths || []).slice()
  }

  getFillColor(): string {
    return this.fillColor
  }

  getFillOpacity(): number {
    return this.fillOpacity
  }
  
  getStrokeColor(): string {
    return this.strokeColor
  }

  getStrokeWidth(): number {
    return this.strokeWidth
  }

  isVisible(): boolean {
    return this.visible
  }

  setDraggable(draggable: boolean): void {
    if (draggable == null) {
      console.warn("Parameter draggable can not be null")
      return
    }
    this.draggable = draggable
  }

  isDraggable(): boolean {
    return this.draggable
  }

}
