import {Layer, LayerType} from "../layer/Layer"
import {Vector2} from "../engine/Vector2"
import {ResourceManager, ShaderEnum} from "../util/ResourceManager"
import {UniformInfo} from "../engine/Shader"
import {MapUtils} from "../util/MapUtils"
import {MapState} from "../map/MapState"
import {Matrix4} from "../engine/Matrix"
import {Kind} from "../tile/Tile"
import {GeometryUtils} from "../util/GeometryUtils"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {LayerRenderer} from "./LayerRenderer"
import {FillLayer} from "../layer/FillLayer"
import {FillLayerData} from "./data/FillLayerData"
import {GeometryTile} from "../tile/GeometryTile"

export class FillLayerRenderer extends LayerRenderer {
  constructor(mapState: MapState, layer: Layer) {
    super(mapState, layer)
  }

  render(): void {
    if (this.layer.getLayerType() != LayerType.FILL) {
      console.log("Invalid layer type")
      return
    }
    let fillLayer = this.layer as FillLayer
    if (fillLayer.renderTiles.size == 0) {
      return
    }
    if (!fillLayer.visible) {
      return
    }

    let gl = this.layer.tileManager.mapView.gl
    let shader = ResourceManager.getInstance(gl).getShader(ShaderEnum.FILL_SHADER)
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    shader.bind()
    fillLayer.elevation > 0 && gl.enable(gl.DEPTH_TEST)
    let worldSize = ProjectionMercator.worldSize(this.mapState.getScale())
    let layerId = this.layer.getLayerId()
    fillLayer.renderTiles.forEach(renderTile => {
      let tile = renderTile.getTile()
      let tileKind = tile.getKind()
      if (tileKind != Kind.ANNOTAION && tileKind != Kind.VECTOR) {
        console.warn("Invalid tile kind")
        return
      }
      let geometryTile = tile as GeometryTile
      if (!geometryTile.hasData(layerId)) {
        return
      }
      let layerData = geometryTile.getLayerData(layerId, LayerType.FILL)
      if (layerData.getType() != LayerType.FILL) {
        console.warn("ERROR: layerData.getType() != LayerType.FILL .............")
        return
      }
      let fillLayerData = layerData as FillLayerData

      // Calculate mvp matrix
      let tileCoordinate = geometryTile.getTileCoordinate()
      let tileScale = 1 << tileCoordinate.getZoom()
      let s = worldSize / tileScale
      let scale = s / MapUtils.getTileSize()
      let coordinate = GeometryUtils.pixelCoordinateToCameraCoordinate(new Vector2(tileCoordinate.getX() * s + s / 2,
        tileCoordinate.getY() * s + s / 2), this.mapState.cameraPosition)
      let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y,
        this.mapState.is3dMode() ? GeometryUtils.getElevationPixelFromMeter(fillLayer.elevation, this.mapState.cameraPosition) : 0.0)
      modelMatrix = Matrix4.scale(scale, scale, 1.0).concat(modelMatrix)
      let mvpMatrix =  modelMatrix.concat(this.mapState.getViewProjectionMatrix())
      shader.setUniforms([
        new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix)
      ])
      let fillPartials = fillLayerData.getFillPartials()
      let linePartials = fillLayerData.getLinePartials()
      let model = fillLayerData.getModel()
      let fillProperties = fillLayerData.getFillProperties()
      let strokeProperties = fillLayerData.getStrokeProperties()
      let fillPartialKeys = Array.from(fillPartials.keys())
      model.bind()
      shader.setAttributes(model.attributeInfos)
      fillPartialKeys.forEach(key => {
        let partials = fillPartials.get(key)
        let strokePartials = linePartials.get(key)
        for (let i = 0; i < partials.length; ++i) {
          shader.setUniforms([
            new UniformInfo("u_color", "4fv",
              tileKind == Kind.ANNOTAION ?
                fillLayer.fillColor.buffer() :
                fillProperties.get(key).fillColor.buffer()),
          ])
          let fillPartial = partials[i]
          model.renderPartial(gl.TRIANGLES, fillPartial.start, fillPartial.length)
          if ((tileKind == Kind.ANNOTAION && fillLayer.strokeWidth > 0) ||
            (tileKind != Kind.ANNOTAION)) {
            if (strokePartials && i < strokePartials.length) {
              let strokePartialArray = strokePartials[i]
              strokePartialArray.forEach(strokePartial => {
                shader.setUniforms([
                  new UniformInfo("u_color", "4fv",
                    tileKind == Kind.ANNOTAION ?
                      fillLayer.strokeColor.buffer() :
                      strokeProperties.get(key).strokeColor.buffer())
                ])
                model.renderPartial(gl.LINES, strokePartial.start, strokePartial.length)
              })
            }
          }
        }
        if (strokePartials) {
          for (let i = partials.length; i < strokePartials.length; ++i) {
            let strokePartialArray = strokePartials[i]
            shader.setUniforms([
              new UniformInfo("u_color", "4fv",
                tileKind == Kind.ANNOTAION ?
                  fillLayer.strokeColor.buffer() :
                  strokeProperties.get(key).strokeColor.buffer())
            ])
            strokePartialArray.forEach(strokePartial => {
              model.renderPartial(gl.LINES, strokePartial.start, strokePartial.length)
            })
          }
        }
      })
    })
    gl.disable(gl.DEPTH_TEST)
    gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, null)
    gl.bindBuffer(gl.ARRAY_BUFFER, null)
  }
}
