import {LayerOverlay, OverlayType} from "./LayerOverlay"
import {MapConstants} from "../map/MapConstants"
import {MapUtils} from "../util/MapUtils"
import {POIOverlayOptions} from "./options/POIOverlayOptions"
import {Map4D} from "./Map"

export class POIOverlay extends LayerOverlay {

  private static readonly defaultPOIOverlay = {

    getUrl: (x, y, z) => {
      return null
    },

    minZoom: MapConstants.defaultMinZoom,

    maxZoom: MapConstants.defaultMaxZoom

  }

  constructor(options: POIOverlayOptions) {
    super()
    let optionDefault: any = {}
    MapUtils.extendOptions(optionDefault, POIOverlay.defaultPOIOverlay, options)
    this._getUrl = optionDefault.getUrl
    this.minZoom = optionDefault.minZoom
    this.maxZoom = optionDefault.maxZoom
    this.type = OverlayType.POI
  }

  getUrl(x: number, y: number, z: number): string {
    return this._getUrl(x, y, z)
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeLayerOverlay(this.id)
      this.map.mapView.setPOIOverlay(false)
      this.id = -1
    }
    this.map = map
    if (this.map != null) {
      this.id = this.map.mapView.addLayerOverlay(this)
      this.map.mapView.setPOIOverlay(true)
    }
  }
}
