import {Layer} from "../layer/Layer"
import {ModelEnum, ResourceManager, ShaderEnum, TextureEnum} from "../util/ResourceManager"
import {MapState} from "../map/MapState"
import {UniformInfo} from "../engine/Shader"
import {LayerRenderer} from "./LayerRenderer"
import {RasterLayer} from "../layer/RasterLayer"
import {MapConstants} from "../map/MapConstants"
import {Matrix4} from "../engine/Matrix"
import {GeometryUtils} from "../util/GeometryUtils"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {Vector2} from "../engine/Vector2"
import {Kind} from "../tile/Tile"
import {Color} from "../engine/Color"
import {Env} from "../Env"
import {CameraPosition} from "../public/CameraPosition"
import {CameraUtils} from "../util/CameraUtils"
import {RasterTile} from "../tile/RasterTile"

/**
 * Class RasterLayerRenderer
 */
export class RasterLayerRenderer extends LayerRenderer {

  time = 0

  constructor(mapState: MapState, layer: Layer) {
    super(mapState, layer)
  }

  render(): void {
    let gl = this.layer.tileManager.mapView.gl
    let resourceManager = ResourceManager.getInstance(gl)
    let useFogShader = this.mapState.is3dMode()
      && this.mapState.getTilt() > MapConstants.maxTiltIntersectWithScreen
    let model = resourceManager.getModel(ModelEnum.RASTER_MODEL)
    let shader = null
    let rasterLayer = this.layer as RasterLayer

    let effectManager = rasterLayer.effectManager
    let renderShadow = this.mapState.is3dMode() && effectManager.enableShadowEffect

    // Bind the buffer
    model.bind()
    if (useFogShader) {
      shader = renderShadow ? resourceManager.getShader(ShaderEnum.RASTER_3D_WITH_SHADOW_SHADER)
        : resourceManager.getShader(ShaderEnum.RASTER_FOG_SHADER)
      shader.bind()
      shader.setAttributes(model.attributeInfos)
      let maxNativeZoom = MapConstants.defaultMaxNativeZoom

      let fogColor = rasterLayer.timeEffectManager.getFogColor()
      let fogScale = Math.max(this.mapState.getZoom() - maxNativeZoom, 0) * 6.0
      let sunLight = rasterLayer.lightManager.sunLight
      shader.setUniforms([
        new UniformInfo("u_light_color", "3fv", sunLight.lightColor.rgbBuffer()),
        new UniformInfo("u_light_position", "3f", sunLight.lightPosition),
        new UniformInfo("u_fogStart", "1f", 22.0 + fogScale),
        new UniformInfo("u_fogRange", "1f", 10.0 + fogScale),
        new UniformInfo("u_fogColor", "3fv", fogColor.rgbBuffer())
      ])
    }
    else {
      shader = renderShadow ? resourceManager.getShader(ShaderEnum.RASTER_WITH_SHADOW_SHADER)
        : resourceManager.getShader(ShaderEnum.RASTER_SHADER)
      shader.bind()
      shader.setAttributes(model.attributeInfos)
    }

    // Render background and mix fog color before render all tiles if map in 3D Mode
    if (this.mapState.is3dMode() && this.mapState.getTilt() > MapConstants.maxTiltIntersectWithScreen) {
      let backgroundColor = Color.fromHex(MapConstants.mapBackgroundColor3D)
      let texture = resourceManager.getTexture(TextureEnum.WHITE_TEXTURE)
      texture.loadColor(backgroundColor)
      let cameraPosition = new CameraPosition(this.mapState.cameraPosition.target,
        this.mapState.cameraPosition.tilt,
        0,
        this.mapState.cameraPosition.zoom)

      let screenSize = this.mapState.getScreenSize()
      let projectionMatrix = CameraUtils.calculateViewProjectionMatrix(cameraPosition, screenSize.x, screenSize.y)

      let position = ProjectionMercator.projectScale(this.mapState.getTarget(), this.mapState.scale)
      let coordinate = GeometryUtils.pixelCoordinateToCameraCoordinate(
        new Vector2(position.x, position.y), this.mapState.cameraPosition)

      let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0)

      let scaleMatrix = Matrix4.scale(MapConstants.mapBackgroundScaleRatio, MapConstants.mapBackgroundScaleRatio, 1.0)
      modelMatrix = modelMatrix.concat(scaleMatrix)
      let mvpMatrix = modelMatrix.concat(projectionMatrix)
      shader.setUniforms([
        new UniformInfo("u_texture", "texture", texture),
        new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix)
      ])
      model.render(gl.TRIANGLES)
      shader.resetTextureUnit()
    }

    // Render all tiles
    let displacement = resourceManager.getTexture(TextureEnum.WATER_DISPLACEMENT)
    let water = resourceManager.getTexture(TextureEnum.WATER_COLOR)
    let enableWater = rasterLayer.effectManager.enableWaterEffect && this.mapState.is3dMode() ? 1 : 0
    let sunLight = rasterLayer.lightManager.sunLight
    this.time += 0.0008

    shader.setUniforms([
      new UniformInfo("u_light_color", "3fv", sunLight.lightColor.rgbBuffer()),
      new UniformInfo("u_light_position", "3f", sunLight.lightPosition),
      new UniformInfo("u_time", "1f", this.time),
      new UniformInfo("u_enableWater", "1i", enableWater),
    ])

    let renderTiles = this.getRenderTiles()
    renderTiles.forEach(renderTile => {
      let tile = renderTile.getTile()
      if (tile.getKind() != Kind.RASTER) {
        console.log("Tile is not raster tile")
        return
      }
      let texture = tile.getTexture()

      if (texture == null) {
        return
      }

      let projectionMatrix = this.mapState.getViewProjectionMatrix()
      let modelMatrix = this.mapState.matrixFor(renderTile.getUnwrappedTileCoordinate())
      let mvpMatrix = modelMatrix.concat(projectionMatrix)

      if (renderShadow) {
        let shadowMap = effectManager.shadowMap
        let shadowMvpMatrix =
          modelMatrix.concat(shadowMap.getLightViewProjectionMatrix()).concat(MapConstants.biasMatrix)
        shader.setUniforms([
          new UniformInfo("u_shadowMap", "texture", shadowMap.getShadowMapTexture()),
          new UniformInfo("u_shadowMvpMatrix", "matrix", shadowMvpMatrix)
        ])
      }

      shader.setUniforms([
        new UniformInfo("u_waterDisplacement", "texture", displacement),
        new UniformInfo("u_waterColor", "texture", water),
        new UniformInfo("u_texture", "texture", texture),
        new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix)
      ])

      model.render(gl.TRIANGLES)
      shader.resetTextureUnit()
    })

    // Render overlay raster tile    
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    renderTiles.forEach(renderTile => {
      let tile = renderTile.getTile()
      if (tile.getKind() != Kind.RASTER) {
        console.log("Tile is not raster tile")
        return
      }
      let rasterTile = tile as RasterTile
      let overlayTiles = rasterTile.getOverlayTiles()
      if (overlayTiles.length == 0) {
        return
      }

      let projectionMatrix = this.mapState.getViewProjectionMatrix()
      let modelMatrix = this.mapState.matrixFor(renderTile.getUnwrappedTileCoordinate())
      let mvpMatrix = modelMatrix.concat(projectionMatrix)

      shader.setUniforms([
        new UniformInfo("u_waterDisplacement", "texture", displacement),
        new UniformInfo("u_waterColor", "texture", water),
        new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix)
      ])

      overlayTiles.forEach(overlayTiles => {
        let texture = overlayTiles.getTexture()
        if (texture == null) {
          return
        }

        shader.setUniforms([
          new UniformInfo("u_texture", "texture", texture),
        ])
        model.render(gl.TRIANGLES)
      })
      shader.resetTextureUnit()
    })
    gl.disable(gl.BLEND)
    // Render debug tiles
    if (Env.isTileDebug) {
      shader = resourceManager.getShader(ShaderEnum.TILE_DEBUG_SHADER)
      shader.bind()
      shader.setAttributes(model.attributeInfos)
      renderTiles.forEach(renderTile => {
        let tile = renderTile.getTile()
        if (tile.getKind() != Kind.RASTER) {
          console.log("Tile is not raster tile")
          return
        }

        let projectionMatrix = this.mapState.getViewProjectionMatrix()
        let modelMatrix = this.mapState.matrixFor(renderTile.getUnwrappedTileCoordinate())
        let mvpMatrix = modelMatrix.concat(projectionMatrix)
        shader.setUniforms([
          new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix)
        ])
        model.render(gl.LINE_STRIP)
      })
    }

    model.unbind()
    shader.disableAttributes()
    // Render sky
    if (this.mapState.is3dMode() && this.mapState.getTilt() > MapConstants.maxTiltIntersectWithScreen) {
      rasterLayer.skyRenderer.render(rasterLayer.skyObject, this.mapState)
    }
  }
}
