import {Layer} from "../layer/Layer"
import {MapState} from "../map/MapState"
import {RenderTile} from "./RenderTile"
import {UnwrappedTileCoordinate} from "../tile/UnwrappedTileCoordinate"

export abstract class LayerRenderer {
  
  static MIN_SELECTION_CODE = 1

  static MAX_SELECTION_CODE = 0xFFFFFF
  
  layer: Layer

  mapState: MapState
  
  selectionCode: number

  protected constructor(mapState: MapState, layer: Layer) {
    this.mapState = mapState
    this.layer = layer
    this.selectionCode = 0
  }

  abstract render(): void
  
  renderSelection(): void {

  }

  renderShadowMapping(): void {

  }

  getRenderTiles(): RenderTile[] {
    return this.layer.tileManager.getRenderTiles()
  }

  getVisibleTiles(): UnwrappedTileCoordinate[] {
    return this.layer.tileManager.getVisibleTiles()
  }
  
  getSelectionCode(): number {
    this.selectionCode++
    if (this.selectionCode < LayerRenderer.MIN_SELECTION_CODE || this.selectionCode > LayerRenderer.MAX_SELECTION_CODE){
      this.selectionCode = LayerRenderer.MIN_SELECTION_CODE
    }
    return this.selectionCode
  }
  
  resetSelectionCode(): void {
    this.selectionCode = 0
  }

}

