import {Layer, LayerType} from "./Layer"
import {Color} from "../engine/Color"
import {TileManager} from "../tile/TileManager"
import {MapState} from "../map/MapState"
import {RenderTile} from "../renderer/RenderTile"
import {MapUtils} from "../util/MapUtils"
import {PolylineStyle} from "../public/Polyline"

export class LineLayer extends Layer {

  private _strokeWidth: number

  private _strokeColor: Color

  private _strokeOpacity:  number

  private _elevation: number

  private _visible: boolean

  private _style: string

  renderTiles: Map<string, RenderTile>

  constructor(layerId: string, order: number, tileManager: TileManager) {
    super(layerId, order, LayerType.LINE, tileManager)
    this._strokeWidth = 5.0
    this._strokeColor = new Color(0, 0, 0, 1)
    this._strokeOpacity = 1.0
    this._visible = true
    this._elevation = 0
    this._style = PolylineStyle.Solid
    this.renderTiles = new Map<string, RenderTile>()
  }

  update(mapState: MapState): void {

  }

  setStrokeWidth(strokeWidth: number): void {
    this._strokeWidth = strokeWidth * MapUtils.pixelScale
  }

  setStrokeColor(strokeColor?: string): void {
    if (strokeColor !== null) {
      this._strokeColor = Color.fromHex(strokeColor)
      this._strokeColor.alpha = this._strokeOpacity
    }
    else {
      this._strokeColor = new Color(255, 0, 0, this._strokeOpacity)
    }
  }

  setStrokeOpacity(strokeOpacity: number): void {
    this._strokeOpacity =  strokeOpacity
    this._strokeColor.alpha = this._strokeOpacity
  }

  setElevation(elevation: number) {
    this._elevation = elevation
  }

  setStyle(style: string) {
    this._style = style
  }

  setVisible(visible: boolean): void {
    this._visible = visible
  }

  getStrokeWidth(): number {
    return this._strokeWidth
  }

  getStrokeColor(): Color {
    return this._strokeColor
  }

  getStrokeOpacity(): number {
    return this._strokeOpacity
  }

  getElevation(): number {
    return this._elevation
  }

  getStyle(): string {
    return this._style
  }

  isVisible(): boolean {
    return this._visible
  }

  addRenderTile(tileCoordinate: string, renderTile: RenderTile) {
    if (!this.renderTiles.has(tileCoordinate)) {
      this.renderTiles.set(tileCoordinate, renderTile)
    }
  }

  removeRenderTile(tileCoordinate: string) {
    this.renderTiles.delete(tileCoordinate)
  }

}
