import {GeometryCollection} from "./GeometryCollection"

export enum GeometryType {
  Unknown = 0,
  Point = 1,
  LineString = 2,
  Polygon = 3
}

export class GeometryTileFeature {
  type: GeometryType

  geometries: GeometryCollection

  properties: Map<string, string | boolean | number> = new Map<string, string | boolean | number>()

  constructor(type: GeometryType, geometries: GeometryCollection, properties: Map<string, string | boolean | number>) {
    this.type = type
    this.geometries = geometries
    this.properties = properties
  }

  getType(): GeometryType {
    return this.type
  }

  getGeometries(): GeometryCollection {
    return this.geometries
  }

  getProperties():  Map<string, string | boolean | number> {
    return this.properties
  }
}

export class GeometryTileLayer {
  name: string

  features: GeometryTileFeature[] = []

  constructor(name: string) {
    this.name = name
  }

  featureCount(): number {
    return this.features.length
  }

  getFeature(index: number): GeometryTileFeature {
    return this.features[index]
  }

  getName(): string {
    return this.name
  }
}

export abstract class GeometryTileData {
  abstract getLayer(name: string): GeometryTileLayer

  abstract getLayerIds(): string[]
}
