import {LatLng, ILatLng} from "./LatLng"

export class CameraPosition {

  target: LatLng

  tilt: number

  bearing: number

  zoom: number

  /**
   * Constructor
   * @param target
   * @param tilt
   * @param bearing
   * @param zoom
   */
  constructor(target: ILatLng = null,
    tilt: number = null,
    bearing: number = null,
    zoom: number = null) {
    this.target = target == null ? null : LatLng.convert(target)
    this.tilt = tilt
    this.bearing = bearing
    this.zoom = zoom
  }

  setTarget(target: ILatLng): void {
    this.target = LatLng.convert(target)
  }

  setTilt(degrees: number): void {
    this.tilt = degrees
  }

  setBearing(degrees: number): void {
    this.bearing = degrees

    //TODO: ML-322 need to fix another way
    if (Math.abs(Math.abs(this.bearing) % 180 - 90) < 0.1) {
      this.bearing += 0.2
    }
  }

  setZoom(zoom: number): void {
    this.zoom = zoom
  }

  getTarget(): LatLng {
    return this.target
  }

  getTilt(): number {
    return this.tilt
  }

  getBearing(): number {
    return this.bearing
  }

  getZoom(): number {
    return this.zoom
  }

  static convert(input: ICameraPosition): CameraPosition {
    if (input instanceof CameraPosition) {
      return input
    }
    return new CameraPosition(input.target, input.tilt, input.bearing, input.zoom)
  }
}

export type ICameraPosition  = CameraPosition | {target?: ILatLng, tilt?: number, bearing?: number, zoom?: number}
