import {MapView} from "../map/MapView"
import {MouseState} from "./MouseEventHandler"
import {Vector2} from "../engine/Vector2"
import {MapUtils} from "../util/MapUtils"
import {MapConstants} from "../map/MapConstants"
import {Point} from "../public/Point"
import {MapObject} from "../public/MapObject"
import {Marker} from "../public/Marker"
import {Annotation} from "../annotation/Annotation"
import {Polyline} from "../public/Polyline"
import {LatLng} from "../public/LatLng"
import {Polygon} from "../public/Polygon"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {Circle} from "../public/Circle"

export class DragPanHandler {

  private _mapView: MapView

  private _draggableObjectId: string

  private _draggableAnnotation: Annotation

  private _targetStart: LatLng

  constructor(mapView: MapView) {
    this._mapView = mapView
    this._targetStart = null
  }

  onDragStart(touchPosition: Vector2) {
    this._draggableObjectId = null
    this._draggableAnnotation = null
    this._targetStart = this._mapView.screenCoordinateToLatLng(touchPosition)
    if (!this._targetStart.valid()) {
      this._targetStart = null
      return
    }
    let clickedItem = this._mapView.queryMapObjectAndAnnotation(touchPosition)
    if (clickedItem == null) {
      return
    }
    if (((clickedItem instanceof Marker) && (clickedItem as Marker).isDraggable()) ||
        ((clickedItem instanceof Polyline) && (clickedItem as Polyline).isDraggable()) ||
        ((clickedItem instanceof Polygon) && (clickedItem as Polygon).isDraggable()) ||
        ((clickedItem instanceof Circle) && (clickedItem as Circle).isDraggable())) {
      this._draggableAnnotation = clickedItem
      return
    }
    if ((clickedItem instanceof MapObject) && (clickedItem as MapObject).isDraggable()) {
      this._draggableObjectId = (clickedItem as MapObject).getId()
      return
    }
  }

  onDrag(data: MouseState) {
    let targetLatLng = this._mapView.screenCoordinateToLatLng(new Vector2(data.x, data.y), 0)
    if (!targetLatLng.valid()){
      return
    }
    if (this._draggableObjectId)  {
      let deltaLocation = targetLatLng.subtract(this._targetStart)
      let building = this._mapView.getBuildingResourceManager().getBuilding(this._draggableObjectId)
      if (building == null) {
        this._targetStart = targetLatLng
        return
      }
      let currentLocation = building.getLocation()
      let newLocation = currentLocation.add(deltaLocation)
      this._mapView.getCustomBuildingManager().updateMapObjectLocation(this._draggableObjectId, newLocation)
      this._mapView.getBuildingIconSelectionManager().updateBuilding(building)
      this._targetStart = targetLatLng
      this._mapView.dirty = true
    }
    else if (this._draggableAnnotation) {
      if (this._draggableAnnotation instanceof Marker) {
        let marker = this._draggableAnnotation as Marker
        let deltaPosition = targetLatLng.subtract(this._targetStart)
        let currentPosition = marker.getPosition()
        let newPosition = currentPosition.add(deltaPosition)
        marker.setPosition(newPosition)
        this._targetStart = targetLatLng
      }
      else if (this._draggableAnnotation instanceof Polyline) {
        let polyline = this._draggableAnnotation as Polyline
        let target = targetLatLng.subtract(this._targetStart)
        let path = polyline.getPath()
        path.forEach((position, index) => {
          let newPosition = position.add(target)
          path[index] = newPosition
        })
        polyline.setPath(path)
        this._targetStart = targetLatLng
      }
      else if (this._draggableAnnotation instanceof  Polygon) {
        let polygon = this._draggableAnnotation as Polygon
        let paths = polygon.getPaths()
        let coordinates = []
        let scale = this._mapView.mapState.getScale()
        paths.forEach(path => {
          let pathCoordinates = []
          path.forEach(latLng => {

            let currentMousePixelCoordinate  = ProjectionMercator.projectScale(targetLatLng, scale)

            let preMousePixelCoordinate = ProjectionMercator.projectScale(this._targetStart, scale)

            let delta = currentMousePixelCoordinate.subtract(preMousePixelCoordinate)

            let pixelScale  = ProjectionMercator.projectScale(latLng, scale)
            pixelScale = pixelScale.add(new Vector2( delta.x, delta.y))

            let newLatLng = ProjectionMercator.unproject(pixelScale, scale)
            pathCoordinates.push(newLatLng)
          })
          coordinates.push(pathCoordinates)
        })
        polygon.setPaths(coordinates)
        this._targetStart = targetLatLng
      }
      else if (this._draggableAnnotation instanceof Circle) {
        let circle = this._draggableAnnotation as Circle
        let deltaCenter = targetLatLng.subtract(this._targetStart)
        let currentCenter = circle.getCenter()
        let newCenter = currentCenter.add(deltaCenter)
        circle.setCenter(newCenter)
        this._targetStart = targetLatLng
      }
    }
    else {
      let delta = new Point(-data.dx, data.dy)
      this._mapView.moveBy(delta)
    }
  }

  onDragEnd(data: MouseState) {
    if (this._draggableAnnotation == null && this._draggableObjectId == null) {
      let delta = new Vector2(-data.dx, data.dy)
      this._mapView.moveByVelocity(new Vector2(delta.x, delta.y).multiply(
        MapConstants.flingSpeedMultiply * MapUtils.pixelScale))
    }
    this._draggableObjectId = null
    this._draggableAnnotation = null
  }

  get draggableObject(): MapObject {
    if (!this._draggableObjectId) {
      return null
    }
    let mapObject = new MapObject()
    let building = this._mapView.getBuildingResourceManager().getBuilding(this._draggableObjectId)
    if (building == null) {
      this._draggableObjectId = null
      return null
    }
    mapObject.initWithBuilding(building)
    return mapObject
  }

  get draggableAnnotation(): Annotation {
    return this._draggableAnnotation
  }
}
