import {Vector2} from "../engine/Vector2"

export class Rasterize {
  static scanTriangle(a: Vector2, b: Vector2, c: Vector2, ymin: number, ymax: number, scanLine: Function) {
    let ab = new Edge(a, b)
    let bc = new Edge(b, c)
    let ca = new Edge(c, a)

    // sort edges by y-length
    if (ab.dy > bc.dy) {
      let t = ab
      ab = bc
      bc = t
    }
    if (ab.dy > ca.dy) {
      let t = ab
      ab = ca
      ca = t
    }
    if (bc.dy > ca.dy) {
      let t = bc
      bc = ca
      ca = t
    }

    // scan span! scan span!
    if (ab.dy) scanSpans(ca, ab, ymin, ymax, scanLine)
    if (bc.dy) scanSpans(ca, bc, ymin, ymax, scanLine)
  }
}

export class Edge {
  x0: number = 0;

  y0: number = 0;

  x1: number = 0;

  y1: number = 0;

  dx: number = 0;

  dy: number = 0;

  constructor(a: Vector2, b: Vector2) {
    if (a.y > b.y) {
      let t = a
      a = b
      b = t
    }
    this.x0 = a.x
    this.y0 = a.y
    this.x1 = b.x
    this.y1 = b.y
    this.dx = b.x - a.x
    this.dy = b.y - a.y
  }
}

// scan-line conversion
function scanSpans(e0: Edge, e1: Edge, ymin: number, ymax: number, scanLine: Function): void {
  let y0 = Math.max(ymin, Math.floor(e1.y0)),
    y1 = Math.min(ymax, Math.ceil(e1.y1))

  // sort edges by x-coordinate
  if ((e0.x0 == e1.x0 && e0.y0 == e1.y0)
    ? (e0.x0 + e1.dy / e0.dy * e0.dx < e1.x1)
    : (e0.x1 - e1.dy / e0.dy * e0.dx < e1.x0)) {
    let t = e0
    e0 = e1
    e1 = t
  }

  // scan lines!
  let m0 = e0.dx / e0.dy
  let m1 = e1.dx / e1.dy

  // use y + 1 to compute x0
  let d0 = e0.dx > 0 ? 1 : 0

  // use y + 1 to compute x1
  let d1 = e1.dx < 0 ? 1 : 0

  for (let y = y0; y < y1; ++y) {
    let x0 = m0 * Math.max(0, Math.min(e0.dy, y + d0 - e0.y0)) + e0.x0,
      x1 = m1 * Math.max(0, Math.min(e1.dy, y + d1 - e1.y0)) + e1.x0
    scanLine(Math.floor(x1), Math.ceil(x0), y)
  }
}
