import {LatLng, ILatLng} from "./LatLng"
import {Map4D} from "./Map"
import {Annotation} from "../annotation/Annotation"

import {Icon} from "./Icon"
import {MapUtils} from "../util/MapUtils"
import {Point, IPoint} from "./Point"
import {MarkerOptions} from "./options/MarkerOptions"
import {MarkerLabel} from "./MarkerLabel"
import {SymbolConstants} from "../map/MapConstants"

export class Marker extends Annotation {

  private position: LatLng

  private visible: boolean

  private anchor: Point

  private labelAnchor: Point

  private icon: Icon | string

  private title: string

  private snippet: string

  private windowAnchor: Point

  private infoContents?: string | Node

  private infoWindow?: string | Node

  private label: string | MarkerLabel

  private draggable: boolean

  private iconView: string | Node

  private rotation: number

  private static readonly defaultMarkerOptions = {
    visible: true,
    anchor: new Point(0.5, 1.0),
    labelAnchor: new Point(0.5, 0.5),
    icon: SymbolConstants.defaultSymbol,
    elevation: 0,
    rotation: 0,
    windowAnchor: new Point(0.5, 0),
    zIndex: 0,
    draggable: false,
    iconView: null,
    userInteractionEnabled: true
  }

  constructor(options: MarkerOptions) {
    super()
    let markerOptions: any = {}
    MapUtils.extendOptions(markerOptions, Marker.defaultMarkerOptions, options)
    this.position = LatLng.convert(markerOptions.position)
    this.anchor = Point.convert(markerOptions.anchor)
    this.labelAnchor = Point.convert(markerOptions.labelAnchor)
    this.visible = markerOptions.visible
    this.icon = markerOptions.icon
    this.elevation = markerOptions.elevation
    this.title = markerOptions.title
    this.snippet = markerOptions.snippet
    this.windowAnchor = markerOptions.windowAnchor
    this.zIndex = markerOptions.zIndex
    this.label = markerOptions.label
    this.draggable = markerOptions.draggable
    this.iconView = markerOptions.iconView
    this.userInteractionEnabled = markerOptions.userInteractionEnabled
    this.rotation = markerOptions.rotation
    if (this.position == null || this.anchor == null || this.windowAnchor == null || this.visible == null ||
      this.elevation == null || this.zIndex == null || this.draggable == null) {
      throw Error("Parameters can not be null")
    }
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeAnnotation(this.id)
      this.map.mapView.hideInfoWindow(this.id)
      this.id = -1
    }
    this.map = map
    if (this.map != null) {
      this.id = this.map.mapView.addAnnotation(this)
    }
  }

  setInfoWindow(infoWindow: string | Node): void {
    if (infoWindow === this.infoWindow) {
      return
    }
    this.infoWindow = infoWindow
  }

  setInfoContents(infoContents: string | Node): void {
    if (infoContents === this.infoContents) {
      return
    }
    this.infoContents = infoContents
  }

  setIcon(icon: Icon | string) {
    if (this.getIconView()) {
      console.warn("Icon view markers has the priority over other markers")
      return
    }
    if (icon == null) {
      icon = SymbolConstants.defaultSymbol
    }
    if (icon === this.icon) {
      return
    }
    this.icon = icon
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id, true)
    }
  }

  setTitle(title: string): void {
    if (this.title === title) {
      return
    }
    this.title = title
    if (this.map != null) {
      this.map.mapView.updateInfoContents(this.id)
    }
  }

  setSnippet(snippet: string): void {
    if (this.snippet === snippet) {
      return
    }
    this.snippet = snippet
    if (this.map != null) {
      this.map.mapView.updateInfoContents(this.id)
    }
  }

  setLabel(label: string | MarkerLabel): void {
    if (this.label === label) {
      return
    }
    this.label = label
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setWindowAnchor(anchor: IPoint) {
    if (anchor == null) {
      console.warn("Parameter anchor can not be null")
      return
    }
    let windowAnchor = Point.convert(anchor)
    if (this.windowAnchor.equals(windowAnchor)) {
      return
    }
    this.windowAnchor = windowAnchor
  }

  setPosition(position: ILatLng) {
    if (position == null) {
      console.warn("Parameter position can not be null")
      return
    }
    let markerPosition = LatLng.convert(position)
    if (this.position.equals(markerPosition)) {
      return
    }
    this.position = markerPosition
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id, true)
    }
  }

  setVisible(visible: boolean) {
    if (visible == null) {
      console.warn("Parameter visible can not be null")
      return
    }
    if (this.visible === visible) {
      return
    }
    this.visible = visible
    if (this.map != null) {
      if (!visible) {
        if (this.isInfoWindowShown()) {
          this.map.mapView.hideInfoWindow(this.id)
        }
      }
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setAnchor(anchor: IPoint) {
    if (anchor == null) {
      console.warn("Parameter anchor can not be null")
      return
    }
    let markerAnchor = Point.convert(anchor)
    if (this.anchor.equals(markerAnchor)) {
      return
    }
    this.anchor = markerAnchor
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setLabelAnchor(labelAnchor: IPoint): void {
    if (labelAnchor == null) {
      console.warn("Parameter labelAnchor can not be null")
      return
    }
    let markerLabelAnchor = Point.convert(labelAnchor)
    if (this.labelAnchor.equals(markerLabelAnchor)) {
      return
    }
    this.labelAnchor = markerLabelAnchor
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setDraggable(draggable: boolean): void {
    if (draggable == null) {
      console.warn("Parameter draggable can not be null")
      return
    }
    this.draggable = draggable
  }

  getTitle(): string {
    return this.title
  }

  getSnippet(): string {
    return this.snippet
  }

  getWindowAnchor(): Point {
    if (this.windowAnchor == null) {
      return null
    }
    return new Point(this.windowAnchor.x, this.windowAnchor.y)
  }

  getPosition(): LatLng {
    return this.position
  }

  isVisible(): boolean {
    return this.visible
  }

  getAnchor(): Point {
    if (this.anchor == null){
      return null
    }
    return new Point(this.anchor.x, this.anchor.y)
  }

  getLabelAnchor(): Point {
    if (this.labelAnchor == null) {
      return null
    }
    return new Point(this.labelAnchor.x, this.labelAnchor.y)
  }

  getIcon(): Icon | string {
    return this.icon
  }

  getLabel(): MarkerLabel | string {
    return this.label
  }

  isDraggable(): boolean {
    return this.draggable
  }

  getInfoWindow(): string | Node {
    return this.infoWindow
  }

  getInfoContents(): string | Node {
    return this.infoContents
  }

  isInfoWindowShown(): boolean {
    if (this.map == null) {
      return false
    }
    return this.map.mapView.isInfoWindowShown(this.id)
  }

  showInfoWindow(): void {
    if (this.map == null || !this.visible || this.isInfoWindowShown()) {
      return
    }
    this.map.mapView.showInfoWindow(this.id)
  }

  hideInfoWindow(): void {
    if (this.map == null) {
      return
    }
    if (this.isInfoWindowShown()) {
      this.map.mapView.hideInfoWindow(this.id)
    }
  }

  getIconView():  string | Node {
    return this.iconView
  }

  setIconView(iconView: string | Node ) {
    if (this.iconView === iconView) {
      return
    }
    this.iconView = iconView
    if (this.map != null) {
      this.map.mapView.updateInfoContents(this.id)
    }
  }

  getRotation(): number {
    return this.rotation
  }

  setRotation(rotation: number): void {
    if (rotation == null) {
      console.warn("Parameter rotation can not be null")
      return
    }
    if (this.rotation === rotation) {
      return
    }
    this.rotation = rotation
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }
}
