import {MapUtils} from "../util/MapUtils"
import {MarkerLabelOptions} from "./options/MarkerLabelOptions"
import {Point} from "./Point"
import {MarginOptions} from "./options/MarginOptions"

export class MarkerLabel {

  private readonly color: string

  private readonly fontWeight: string

  private readonly fontSize: number

  private readonly maxWidth: number

  private readonly line: number

  private readonly text: string

  private readonly halo: boolean

  private readonly haloColor: string

  private readonly anchor: Point

  private readonly marginOptions: MarginOptions

  private static readonly defaultMarkerLabelOptions = {
    text: "",
    color: "#000000",
    fontWeight: "normal",
    fontSize: 13,
    maxWidth: 200,
    line: 1,
    halo: true,
    haloColor: "#ffffff",
    anchor: new Point(0.5, 0.5),
    margin: {top: 0, bottom: 0, left: 0, right: 0}
  }

  private static readonly defaultMarginOptions = {
    top: 0,
    bottom: 0,
    left: 0,
    right: 0
  }

  constructor(options: MarkerLabelOptions){
    let markerLabelOptions: any = {}
    MapUtils.extendOptions(markerLabelOptions, MarkerLabel.defaultMarkerLabelOptions, options)
    for (const option of markerLabelOptions) {
      if (option == null) {
        throw Error("Parameters can not be null")
      }
    }
    if (markerLabelOptions.fontWeight != "normal" && markerLabelOptions.fontWeight != "bold") {
      this.fontWeight = "normal"
    }
    else {
      this.fontWeight = markerLabelOptions.fontWeight
    }
    this.color = markerLabelOptions.color
    this.fontSize = markerLabelOptions.fontSize * MapUtils.pixelScale
    this.maxWidth = markerLabelOptions.maxWidth * MapUtils.pixelScale
    this.line = markerLabelOptions.line
    this.text = markerLabelOptions.text
    this.halo = markerLabelOptions.halo
    this.haloColor = markerLabelOptions.haloColor
    this.anchor = Point.convert(markerLabelOptions.anchor)
    let marginOptions: any = {}
    MapUtils.extendOptions(marginOptions, MarkerLabel.defaultMarginOptions, markerLabelOptions.margin)
    this.marginOptions = marginOptions
  }

  getColor(): string {
    return this.color
  }

  getFontWeight(): string {
    return this.fontWeight
  }

  getFontSize(): number {
    return this.fontSize
  }

  getMaxWidth(): number {
    return this.maxWidth
  }

  getLine(): number {
    return this.line
  }

  getText(): string {
    return this.text
  }

  hasHalo(): boolean {
    return this.halo
  }

  getHaloColor(): string {
    return this.haloColor
  }

  getAnchor(): Point {
    return this.anchor
  }

  getMargin(): MarginOptions {
    return this.marginOptions
  }
}
