import {LayerData} from "./LayerData"
import {Model} from "../../engine/Model"
import {GeometryTileLayer, GeometryType} from "../../tile/data/GeometryTileData"
import {LayerType} from "../../layer/Layer"
import {LineCapType, LineJoinType, PolylineBuilderContext} from "../../util/PolyBuilders"
import {Vector3} from "../../engine/Vector3"
import {MapUtils} from "../../util/MapUtils"
import {Vector2} from "../../engine/Vector2"
import {AttributeInfo} from "../../engine/Shader"
import {Color} from "../../engine/Color"
import {Range} from "../../util/Range"
import {VectorTileLayer} from "../../tile/data/VectorTileData"

export class LineLayerData extends LayerData {

  public static readonly defaultStrokeWidth = 5

  private static readonly  defaultStrokeColor = Color.fromHex("#000000")

  private readonly gl: WebGLRenderingContext

  private lineModel: Model

  private linePartials: Range[]

  private lineLayerProperties: {strokeColor: Color, strokeWidth: number}[]

  constructor(geometryTileLayer: GeometryTileLayer, gl: WebGLRenderingContext, lineWidth: number = null) {
    super(LayerType.LINE)
    this.gl = gl
    this.linePartials = []
    this.lineLayerProperties = []
    this.buildPolyline(geometryTileLayer, lineWidth)
    this.initializeProperties(geometryTileLayer)
  }

  getLinePartials(): Range[] {
    return this.linePartials
  }

  getModel(): Model {
    return this.lineModel
  }

  getLineLayerDataProperties():  {strokeColor: Color, strokeWidth: number}[] {
    return this.lineLayerProperties
  }

  private initializeProperties(geometryTileLayer: GeometryTileLayer): void {
    if (!(geometryTileLayer instanceof VectorTileLayer)) {
      return
    }
    let featureCount = geometryTileLayer.featureCount()
    for (let featureIndex = 0; featureIndex < featureCount; ++featureIndex) {
      let feature = geometryTileLayer.getFeature(featureIndex)
      let properties = feature.getProperties()
      if (properties) {
        let fill = properties.get("stroke") as string || "#000000"
        let fillOpacity = properties.has("stroke-opacity") ? properties.get("stroke-opacity") as number : 1
        let strokeColor = Color.fromHex(fill)
        strokeColor.alpha = fillOpacity
        let strokeWidth = properties.has("stroke-width") ?
          properties.get("stroke-width") as number : LineLayerData.defaultStrokeWidth
        strokeWidth *= MapUtils.pixelScale
        this.lineLayerProperties.push({strokeColor: strokeColor, strokeWidth: strokeWidth})
      }
      else {
        this.lineLayerProperties.push({strokeColor: LineLayerData.defaultStrokeColor,
          strokeWidth: LineLayerData.defaultStrokeWidth * MapUtils.pixelScale})
      }
    }
  }

  private buildPolyline(geometryTileLayer: GeometryTileLayer, lineWidth: number = null): void {
    let vertices: any = []
    let polylineBuilderContext: PolylineBuilderContext = new PolylineBuilderContext()
    polylineBuilderContext.addVertex = (coord: Vector2, extrude: Vector2, uv: Vector2) => {
      vertices.push(coord.x, coord.y, extrude.x, extrude.y, uv.x, uv.y)
    }
    polylineBuilderContext.lineCapType = LineCapType.Butt
    polylineBuilderContext.lineJoinType = LineJoinType.Round

    let tileSize = MapUtils.getTileSize()
    let featureCount = geometryTileLayer.featureCount()
    for (let featureIndex = 0; featureIndex < featureCount; ++featureIndex) {
      let feature = geometryTileLayer.getFeature(featureIndex)
      if (feature.getType() != GeometryType.LineString) {
        console.warn("Invalid geometry type: " + feature.getType())
        return
      }
      let geometries = feature.getGeometries().geometryCoordinates
      let rangeStart = polylineBuilderContext.indices.length
      for (let i = 0; i < geometries.length; ++i) {
        let points: Vector2[] = []
        for (let j = 0; j < geometries[i].length; ++j) {
          let point = new Vector2(geometries[i][j][0], geometries[i][j][1])
          point.x = point.x * tileSize
          point.y = point.y * tileSize
          points.push(point)
        }
        polylineBuilderContext.buildPolyLine(points, null, lineWidth)
      }
      let rangeLength = polylineBuilderContext.indices.length - rangeStart
      this.linePartials.push(new Range(rangeStart, rangeLength))
    }
    this.lineModel = new Model(this.gl)
    this.lineModel
      .init(vertices,  [
        new AttributeInfo("a_position", 2, 0, 6),
        new AttributeInfo("a_extrude", 2, 2, 6),
        new AttributeInfo("a_uv", 2, 4, 6)
      ],
      polylineBuilderContext.indices)
  }

  destroy(): void {
    this.lineModel && this.lineModel.destroy()
  }
}
