import {ShadowMap} from "./ShadowMap"

export class EffectManager {

  private _shadowMap: ShadowMap

  private _enableShadowEffect: boolean

  private _enableWaterEffect: boolean

  constructor(shadowMap: ShadowMap) {
    this._shadowMap = shadowMap
    this._enableShadowEffect = false
    this._enableWaterEffect = false
  }

  get enableWaterEffect(): boolean {
    return this._enableWaterEffect
  }

  set enableWaterEffect(value: boolean) {
    this._enableWaterEffect = value
  }

  get enableShadowEffect(): boolean {
    return this._enableShadowEffect
  }

  set enableShadowEffect(value: boolean) {
    this._enableShadowEffect = value
  }

  get shadowMap(): ShadowMap {
    return this._shadowMap
  }

  resize(width: number, height: number): void {
    this._shadowMap.resize(width, height)
  }

  dirty() {
    return this.enableWaterEffect
  }

  destroy(): void {
    this._shadowMap.destroy()
    this._shadowMap = null
  }
}