import {AnnotationId} from "./Annotation"
import {LatLngBounds} from "../public/LatLngBounds"
import {AnnotationManager} from "./AnnotationManager"
import {ProjectionMercator} from "../map/ProjectionMercator"
import {Vector2} from "../engine/Vector2"
import {MapView} from "../map/MapView"
import {LayerType} from "../layer/Layer"
import {CircleLayer} from "../layer/CircleLayer"
import {Circle} from "../public/Circle"
import {MapConstants} from "../map/MapConstants"

export class CircleAnnotationData {

  private readonly _id: AnnotationId

  private readonly _layerId: string

  private readonly _circleAnnotation: Circle

  // Rbush properties
  private _minX: number

  private _minY: number

  private _maxX: number

  private _maxY: number

  constructor(annotationId: AnnotationId, circleAnnotation: Circle) {
    this._id = annotationId
    this._layerId = AnnotationManager.annotationLayerId + annotationId.toString()
    this._circleAnnotation = circleAnnotation
    this.updateBounds()
  }

  updateLayer(map: MapView): void {
    let layer = map.getLayer(this._layerId)
    if (layer == null) {
      layer = map.createLayer(this._layerId, LayerType.CIRCLE, this._circleAnnotation.getZIndex())
    }
    let circleLayer = layer as CircleLayer
    circleLayer.setCenter(this._circleAnnotation.getCenter())
    circleLayer.setRadius(this._circleAnnotation.getRadius())
    circleLayer.setFillColor(this._circleAnnotation.getFillColor())
    circleLayer.setFillOpacity(this._circleAnnotation.getFillOpacity())
    circleLayer.setVisible(this._circleAnnotation.isVisible())
    circleLayer.setElevation(this._circleAnnotation.getElevation())
    circleLayer.setStrokeColor(this._circleAnnotation.getStrokeColor())
    circleLayer.setStrokeWidth(this._circleAnnotation.getStrokeWidth())
  }

  updateBounds(): void {
    let center = this._circleAnnotation.getCenter()
    let radius = this._circleAnnotation.getRadius()
    let centerInMeters = ProjectionMercator.convertToMeterFromLatLng(center)
    let southwestInMeters = new Vector2(centerInMeters.x - radius, centerInMeters.y - radius)
    let northeastInMeters = new Vector2(centerInMeters.x + radius, centerInMeters.y + radius)
    let bounds = new LatLngBounds(ProjectionMercator.convertToLatLngFromMeter(southwestInMeters),
      ProjectionMercator.convertToLatLngFromMeter(northeastInMeters))
    this._minX = bounds.getSouthwest().lng
    this._minY = bounds.getSouthwest().lat
    this._maxX = bounds.getNortheast().lng
    this._maxY = bounds.getNortheast().lat
  }

  get id(): AnnotationId {
    return this._id
  }

  get layerId(): string {
    return this._layerId
  }

  getAnnotation(): Circle {
    return this._circleAnnotation
  }

  get minX(): number {
    return this._minX
  }

  get minY(): number {
    return this._minY
  }

  get maxX(): number {
    return this._maxX
  }

  get maxY(): number {
    return this._maxY
  }
}
