import {ControlOptions} from "../public/options/ControlOptions"

export class Style {

  static getStyleForControlOptions(option: ControlOptions): string {
    switch (option) {
    case ControlOptions.TOP_LEFT:
      return "mf-ui-top-left"
    case ControlOptions.TOP_RIGHT:
      return "mf-ui-top-right"
    case ControlOptions.BOTTOM_LEFT:
      return "mf-ui-bottom-left"
    case ControlOptions.BOTTOM_RIGHT:
      return "mf-ui-bottom-right"
    }
  }

  static bgColor = "rgba(255,255,255,1)";

  static size = "29px";

  static controlDistance = "10px";

  // language=SCSS
  static content = `
.mf-map4d {
  height: 100%;
  width: 100%;
  position: relative;
  box-sizing: content-box;
  overflow: hidden;
}

.mf-mapgl {    
    position: absolute;
    height: 100%;
    width: 100%;
    box-sizing: content-box;  
  }
  
.mf-mapgl canvas {
  width: inherit;
  height: inherit;
  position: absolute;
  display: inherit;
}

.mf-map4d .mf-ui {
  position: absolute;
  display: flex;
  flex-direction: column;
}

.mf-map4d .mf-ui .vertical {
  display: flex;
  flex-direction: column;
}

.mf-map4d .mf-ui .horizontal {
    display: block;
    align-self: flex-end;
    height: 35px;
}

.mf-ui.mf-ui-top-left {
  top: ${Style.controlDistance};
  left: ${Style.controlDistance};
  flex-direction: inherit;
}
.mf-ui.mf-ui-top-right {
  top: ${Style.controlDistance};
  right: ${Style.controlDistance};
  flex-direction: row-reverse;
}
.mf-ui.mf-ui-bottom-right {
  bottom: ${Style.controlDistance};
  right: ${Style.controlDistance};
  flex-direction: row-reverse;
}
.mf-ui.mf-ui-bottom-left {
  left: ${Style.controlDistance};
  bottom: ${Style.controlDistance};
  flex-direction: inherit;
}

.mf-map4d .mf-ui.mf-ui-top-left .vertical {
  flex-direction: column-reverse;
}

.mf-map4d .mf-ui.mf-ui-top-left .horizontal {
  align-self: flex-start;
}

.mf-map4d .mf-ui.mf-ui-top-right .vertical {
  flex-direction: column-reverse;
}

.mf-map4d .mf-ui.mf-ui-top-right .horizontal {
  align-self: flex-start;
}

.mf-map4d .mf-ui .mf-button {
  margin: 3px;
    display: inline-flex;
    align-items: center;
    justify-content: space-around;
    width: ${Style.size};
    height: ${Style.size};
    border-radius: 2px;
    border: 1px solid darken(${Style.bgColor}, 5%);
    background-color: ${Style.bgColor};
    cursor: pointer;
    user-select: none;
    -moz-user-select: none;
    box-shadow: 0px 0px 4px #666;
    background-color: #fff;
  }

.mf-map4d .mf-ui .mf-button:hover {
    background-color: darken(${Style.bgColor}, 5%);
  }

.mf-map4d .mf-ui .mf-button:active {
    background-color: darken(${Style.bgColor}, 10%);
  }
 
.mf-map4d .mf-ui.mf-ui-bottom-right .mf-location-box,
.mf-map4d .mf-ui.mf-ui-bottom-left .mf-location-box {
    margin-bottom: 9px;
}

.mf-map4d .mf-ui.mf-ui-top-right .mf-location-box,
.mf-map4d .mf-ui.mf-ui-top-left .mf-location-box {
    margin-top: 9px;
}

.mf-map4d .mf-ui a{
  background-position: center !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
  opacity: 0.5;
  width: 29px;
  height: 29px;
}

.mf-scrollbar::-webkit-scrollbar {
  width: 10px;
}
 
.mf-scrollbar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
  border-radius: 10px;
}
 
.mf-scrollbar::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
  background-color: #555;
}

.mf-map4d .mf-ui .mf-zoom-in-icon { 
  background-image: url(data:image/png;base64,);       
}

.mf-map4d .mf-ui .mf-zoom-out-icon { 
    background-image: url(data:image/png;base64,);  
}

.mf-map4d .mf-ui .mf-location {
    background-image: url(data:image/png;base64,);    
}

.mf-map4d .mf-ui .mf-mode-icon { 
  background-image: url(data:image/png;base64,);    
}

.mf-map4d .mf-ui .mf-button.mf-mode-2d .mf-mode-icon {
  background-image: url(data:image/png;base64,);
}

.mf-map4d .mf-ui .mf-direct-icon { 
  background-image: url(data:image/png;base64,);     
}

.mf-map4d .mf-ui a:hover {
  opacity: 1;  
}

.mf-logo-container {
  position: absolute;
  bottom: 10px;  
}

.mf-logo-container.mf-right {
  right: 10px;
}

.mf-logo-container.mf-left {
  left: 10px;
}

.mf-logo {
  cursor: pointer;
  width: 75px;
  opacity: 1;
}

.mf-info-window-container {
  position: relative;
  display: inline-block;
  transform: translateY(-5px);
  border: 1px solid #ddd;
  border-radius: 5px;
}

.mf-info-window-content:after, .mf-info-window-content:before {
	top: 100%;
	left: 50%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.mf-info-window-content:after {
	border-color: rgba(255, 255, 255, 0);
	border-top-color: #ffffff;
	border-width: 7px;
	margin-left: -7px;
}

.mf-info-window-content:before {
	border-color: rgba(221, 221, 221, 0);
	border-top-color: #ddd;
	border-width: 8px;
	margin-left: -8px;
}

.mf-info-window-content p, h4{
  margin-bottom: 0;
  margin-top: 0;
}

.mf-info-window-content p:empty, h4:empty{
  display: none;
}

.mf-info-window-content {
  background-color: #ffffff;
  overflow: auto;
  max-width: 636px;
  max-height: 298px;
  text-align: left;
  padding: 5px;
}

.mf-info-window-content h4{
  font-family: Georgia, serif;
  font-size: 14px;
  font-weight: normal;
  text-align: center;
}

.mf-info-window-content p{
  font-family: Georgia, serif;
  font-size: 12px;  
}

.mf-map4d .mf-ui .mf-button-mode {
  position: fixed;
  bottom: 130px;
  right :13px;
    display: inline-flex;
    align-items: center;
    justify-content: space-around;
    width: ${Style.size};
    height: ${Style.size};
    border-radius: 2px;
    border: 1px solid darken(${Style.bgColor}, 5%);
    background-color: ${Style.bgColor};
    cursor: pointer;
    user-select: none;
    -moz-user-select: none;
}
.mf-iconview-marker-container {
  position: absolute;
  display: inline-block;
  border: 1px solid #ddd;
  border-radius: 5px;
}
`
}