import * as TinySDF from "@mapbox/tiny-sdf"
import {Texture} from "../engine/Texture"
import {FontConstant} from "../map/MapConstants"

export interface FontMetric {
  x: number,
  y: number
}

export class FontSDF {

  private _fontSize: number

  private _tinySDFGenerator: TinySDF

  private _texture: Texture

  private gl: WebGLRenderingContext

  private fontImageData: number[]

  private _metrics: FontMetric[]

  private readonly chars = " !&quot;#$%&amp;'()*+,-./0123456789:;<=>?@ABCDEFGHIJKLMNOPQRSTUVWXYZ[]^_`\\abcdefghijklmnopqrstuvwxyz{|}~"

  constructor(gl: WebGLRenderingContext, fontSize, cutOff: number, fontFamily: string, fontWeight: number) {
    this.gl = gl
    this._fontSize = fontSize
    this.fontImageData = []
    this._metrics = new Array(this.chars.length)
    this.update(fontSize, cutOff, fontFamily, fontWeight)
  }

  update(fontSize: number, cutOff: number, fontFamily: string, fontWeight: number): void {
    this.fontImageData = new Array(FontConstant.fontTextureWidth * FontConstant.fontTextureHeight * 4).fill(0)
    let buffer = fontSize / 8
    let radius = fontSize / 3
    let width = FontConstant.fontTextureWidth
    let height = FontConstant.fontTextureHeight
    this._tinySDFGenerator = new TinySDF(fontSize, buffer, radius, cutOff, fontFamily, fontWeight)
    let sdf = this._tinySDFGenerator
    for (let y = 0, i = 0; y + sdf.size <= height && i < this.chars.length; y += sdf.size) {
      for (let x = 0; x + sdf.size <= width && i < this.chars.length; x += sdf.size) {
        let data = this.makeRGBAImageData(sdf.draw(this.chars[i]), sdf.size)
        if (data) {
          this.putImageData(data, x, y)
        }
        this._metrics[this.chars[i]] = {x: x, y: y}
        i++
      }
    }
    let sdfData = new Uint8Array(this.fontImageData)
    this._texture = new Texture(this.gl)
    this._texture.loadImageDataArray(sdfData, width, height, this.gl.LINEAR)
  }

  /**
 * Convert alpha-only to RGBA so we can use convenient
 * putImageData` for building the composite bitmap
 */

  makeRGBAImageData(alphaChannel, size): number[] {
    let data = new Array(size * size * 4)
    for (let i = 0; i < alphaChannel.length; i++) {
      data[4 * i + 0] = alphaChannel[i]
      data[4 * i + 1] = alphaChannel[i]
      data[4 * i + 2] = alphaChannel[i]
      data[4 * i + 3] = 255
    }
    return data
  }

  putImageData(data: number[], x: number, y: number): void {
    let width = FontConstant.fontTextureWidth
    let size = this._tinySDFGenerator.size
    for (let i = 0; i < size; ++i) {
      for (let j = 0; j < size; ++j) {
        this.fontImageData[(y + i) * 4 * width + (4 * x + 4 * j)] = data[i * 4 * size + (4 * j)]
        this.fontImageData[(y + i) * 4 * width + (4 * x + 4 * j + 1)] = data[i * 4 * size + (4 * j + 1)]
        this.fontImageData[(y + i) * 4 * width + (4 * x + 4 * j + 2)] = data[i * 4 * size + (4 * j + 2)]
        this.fontImageData[(y + i) * 4 * width + (4 * x + 4 * j + 3)] = data[i * 4 * size + (4 * j + 3)]
      }
    }
  }

  get texture(): Texture {
    return this._texture
  }

  get fontSize(): number {
    return this._fontSize
  }

  get tinySDFGenerator(): TinySDF {
    return this._tinySDFGenerator
  }

  get metrics(): FontMetric[] {
    return this._metrics
  }

  destroy() {
    this._texture.destroy()
  }
}
