import {TileManager} from "../tile/TileManager"
import {MapState} from "../map/MapState"
import {LayerRenderer} from "../renderer/LayerRenderer"

export enum LayerType {
  RASTER,
  BUILDING,
  LINE,
  FILL,
  CIRCLE,
  SYMBOL,
  ORDERED,
  GEOJSON
}

export class LayerId {
  id: string;

  /**
   * The order of layer. The map will choose which layer will be rendered
   * first base on this value.
   */
  order: number;

  constructor(id: string, order: number = 0) {
    this.id = id
    this.order = order
  }

  equal(rhs: LayerId): boolean {
    return this.id == rhs.id
  }

  lessThan(rhs: LayerId): boolean {
    if (this.order == rhs.order) {
      return this.id < rhs.id
    }
    return this.order < rhs.order
  }
}

export function compareLayerId(a: LayerId, b: LayerId) {
  if (a.order == b.order) {
    if (a.id == b.id) {
      return 0
    }
    return a.id > b.id
  }
  return a.order > b.order
}

export abstract class Layer {

  layerId: LayerId

  layerType: LayerType

  tileManager: TileManager

  features: string

  // @TODO: Weak reference?
  layerRenderer: LayerRenderer

  protected constructor(layerId: string, order: number, layerType: LayerType, tileManager: TileManager) {
    this.layerId = new LayerId(layerId, order)
    this.layerType = layerType
    this.tileManager = tileManager
  }

  abstract update(mapState: MapState): void;

  getOrder(): number {
    return this.layerId.order
  }

  getLayerId(): string {
    return this.layerId.id
  }

  getLayerType(): LayerType {
    return this.layerType
  }

  setOrder(order: number): void {
    this.layerId.order = order
  }

  setLayerRenderer(layerRenderer: LayerRenderer) {
    this.layerRenderer = layerRenderer
  }
}
