import {ParticleSystem} from "./ParticleSystem"
import {Vector2} from "../engine/Vector2"

export class Particle {

  /**
   *  The emitter that controls this particle.
   */
  private _emitter: ParticleSystem

  /**
   * The position of the particle.
   */
  private _position: Vector2

  /**
   * The scale value of the particle.
   */
  private _scale: number

  /**
   * The rotation value of the particle.
   */
  private _rotation: number

  /**
   * The rotation speed of the particle.
   */
  private _rotationSpeed: number

  /**
   * The velocity of the particle.
   */
  private _velocity: Vector2

  /**
   * Acceleration to apply to the particle.
   */
  private _acceleration: Vector2

  /**
   * The maximum lifetime of this particle, in seconds
   */
  private _maxLife: number

  /**
   * An extra data for custom particle.
   */
  private _extraData: any

  /**
   * The life time function of this particle, if this one is set then _maxLife value will be ignored
   */
  private _lifetimeFunc: (Particle) => boolean

  /**
   * The custom update function if it's not set then we will use the default one
   */
  private _updateFunc: (Particle, number) => void

  /**
   * The current age of the particle, in seconds.
   */
  private _age: number

  /**
   * Reference to the next particle in the list.
   */
  private _next: Particle

  /**
   * Reference to the previous particle in the list.
   */
  private _prev: Particle

  constructor(emitter: ParticleSystem) {
    this._emitter = emitter
    this._position = new Vector2(0, 0)
    this._scale = 1
    this._velocity = new Vector2(0, 0)
    this._rotation = 0
    this._rotationSpeed = 0
    this._acceleration = null
    this._extraData = null
    this._updateFunc = null
    this._maxLife = 0
    this._age = 0
  }

  public update(delta: number): void {

    // increase age
    this._age += delta

    // recycle particle if it is too old
    if (this._lifetimeFunc) {
      if (this._lifetimeFunc(this)) {
        this.kill()
        return
      }
    }
    else if(this._age >= this._maxLife || this._age < 0) {
      this.kill()
      return
    }
    if (this._updateFunc) {
      this._updateFunc(this, delta)
    }
    else {
      if (this._acceleration) {
        this._velocity.x += this._acceleration.x * delta
        this._velocity.y += this._acceleration.y * delta
      }
      if (this._rotationSpeed !== 0) {
        this._rotation += this._rotationSpeed * delta
      }

      // adjust position based on velocity
      this._position.x += this._velocity.x * delta
      this._position.y += this._velocity.y * delta
    }
  }

  public init(): void {
    this._age = 0
    this._velocity = this._velocity.rotateBy(this._rotation)
  }

  public destroy(): void {
    this._emitter = null
    this._lifetimeFunc = null
    this._extraData = null
    this._updateFunc = null
    this._next = null
    this._prev = null
  }

  private kill() {
    this._emitter.recycle(this)
  }

  set emitter(value: ParticleSystem) {
    this._emitter = value
  }

  set velocity(value: Vector2) {
    this._velocity = value
  }

  set position(value: Vector2) {
    this._position = value
  }

  set scale(value: number) {
    this._scale = value
  }

  set rotation(value: number) {
    this._rotation = value
  }

  set rotationSpeed(value: number) {
    this._rotationSpeed = value
  }

  set maxLife(value: number) {
    this._maxLife = value
  }

  set lifetimeFunc(value: (Particle) => boolean) {
    this._lifetimeFunc = value
  }

  set updateFunc(value: (Particle, number) => void) {
    this._updateFunc = value
  }

  set acceleration(value: Vector2) {
    this._acceleration = value
  }

  set extraData(value: any) {
    this._extraData = value
  }

  set next(value: Particle) {
    this._next = value
  }

  set prev(value: Particle) {
    this._prev = value
  }

  get position(): Vector2 {
    return this._position
  }

  get scale(): number {
    return this._scale
  }

  get velocity(): Vector2 {
    return this._velocity
  }

  get acceleration(): Vector2 {
    return this._acceleration
  }

  get rotation(): number {
    return this._rotation
  }

  get rotationSpeed(): number {
    return this._rotationSpeed
  }

  get extraData(): any {
    return this._extraData
  }

  get next(): Particle {
    return this._next
  }

  get prev(): Particle {
    return this._prev
  }
}