import {UUID} from "../util/UUID"
import {TaskWorker} from "./TaskWorker"

export enum Resource {
  Json = 0,
  RasterTile = Json + 1,
  CustomModel = RasterTile + 1,
  CustomTexture = CustomModel + 1,
  TileOverlay = CustomTexture + 1,
  TileData = TileOverlay + 1,
  Model = TileData + 1,
  Texture = Model + 1,
  MAX = Texture + 1
}

export class RequestTask {
  id: string

  url: string

  method: string = "GET"

  params: any = null

  type: Resource

  worker: TaskWorker

  completion?: Function

  constructor(type: Resource) {
    this.id = UUID.generate()
    this.type = type
  }
}

export class ResponseTask {
  success: boolean

  data: any

  constructor(public id: string){
    this.success = false
    this.data = null
  }
}
