import {Layer, LayerType} from "./Layer"
import {TileManager} from "../tile/TileManager"
import {RasterLayer} from "./RasterLayer"
import {LineLayer} from "./LineLayer"
import {FillLayer} from "./FillLayer"
import {CircleLayer} from "./CircleLayer"
import {BuildingLayer} from "./BuildingLayer"
import {SymbolLayer} from "./SymbolLayer"
import {GeoJsonLayer} from "./GeoJsonLayer"

export class LayerFactory {
  constructor() {}

  static createLayer(layerId: string, layerType: LayerType, tileManager: TileManager, order: number): Layer {
    switch (layerType) {
    case LayerType.RASTER:
      return new RasterLayer(layerId, order, tileManager)
    case LayerType.BUILDING:
      return new BuildingLayer(layerId, order, tileManager)
    case LayerType.LINE:
      return new LineLayer(layerId, order, tileManager)
    case LayerType.FILL:
      return new FillLayer(layerId, order, tileManager)
    case LayerType.CIRCLE:
      return new CircleLayer(layerId, order, tileManager)
    case LayerType.SYMBOL:
      return new SymbolLayer(layerId, order, tileManager)
    case LayerType.GEOJSON:
      return new GeoJsonLayer(layerId, order, tileManager)
    default:
      console.log("Un-support layer type")
      return null
    }
  }
}
