import {Layer, LayerType} from "./Layer"
import {Color} from "../engine/Color"
import {TileManager} from "../tile/TileManager"
import {MapState} from "../map/MapState"
import {RenderTile} from "../renderer/RenderTile"
import {LatLng} from "../public/LatLng"
import {MapUtils} from "../util/MapUtils"
import {PolyConstants} from "../map/MapConstants"

export class CircleLayer extends Layer {

  private _center: LatLng

  private _radius: number

  private _fillColor: Color

  private _fillOpacity: number

  private _strokeColor: Color

  private _strokeWidth: number

  private _elevation: number

  private _hypotenuseStrokeWidth: number

  private _visible: boolean

  private _renderTiles: Map<string, RenderTile>

  constructor(layerId: string, order: number, tileManager: TileManager) {
    super(layerId, order, LayerType.CIRCLE, tileManager)
    this._center = new LatLng(0, 0)
    this._radius = 0
    this._fillColor = new Color(0, 0, 0, 1)
    this._fillOpacity = 1.0
    this._strokeColor = new Color( 0, 0, 0, 1)
    this._strokeWidth = 5.0
    this._elevation = 0
    this._visible = true
    this._renderTiles = new Map<string, RenderTile>()
  }

  update(mapState: MapState): void {

  }

  setCenter(center: LatLng): void {
    this._center = center
  }

  setRadius(radius: number): void {
    this._radius = radius
  }

  setFillColor(fillColor?: string): void {
    if (fillColor !== null) {
      this._fillColor = Color.fromHex(fillColor)
      this.fillColor.alpha = this.fillOpacity
    }
    else {
      this._fillColor = new Color(255, 0, 0, this.fillOpacity)
    }
  }

  setFillOpacity(fillOpacity: number) {
    this._fillOpacity = fillOpacity
    this.fillColor.alpha = this.fillOpacity
  }

  setStrokeColor(strokeColor?: string): void {
    if (strokeColor !== null) {
      this._strokeColor = Color.fromHex(strokeColor)
      this._strokeColor.alpha = 1.0
    }
    else {
      this._strokeColor = new Color(0.0, 1.0, 0.0, 1.0)
    }
  }

  setStrokeWidth(strokeWidth?: number): void {
    let width = 5.0 * MapUtils.pixelScale
    if (strokeWidth !== null) {
      width = strokeWidth * MapUtils.pixelScale
      if (width < 1.0) {
        width = 0.0
      }
    }
    this._strokeWidth = width
    this._hypotenuseStrokeWidth = width / Math.cos(Math.PI / PolyConstants.numTrianglesPerCircle)
  }

  setElevation(elevation: number): void {
    this._elevation = elevation
  }

  setVisible(visible: boolean): void {
    this._visible = visible
  }

  get center(): LatLng {
    return this._center
  }

  get radius(): number {
    return this._radius
  }

  get fillColor(): Color {
    return this._fillColor
  }

  get fillOpacity(): number {
    return this._fillOpacity
  }

  get strokeColor(): Color {
    return this._strokeColor
  }

  get strokeWidth(): number {
    return this._strokeWidth
  }

  get hypotenuseStrokeWidth(): number {
    return this._hypotenuseStrokeWidth
  }

  get elevation(): number {
    return this._elevation
  }

  get visible(): boolean {
    return this._visible
  }

  get renderTiles(): Map<string, RenderTile> {
    return this._renderTiles
  }

  addRenderTile(tileCoordinate: string, renderTile: RenderTile) {
    if (!this._renderTiles.has(tileCoordinate)) {
      this._renderTiles.set(tileCoordinate, renderTile)
    }
  }

  removeRenderTile(tileCoordinate: string) {
    this._renderTiles.delete(tileCoordinate)
  }

}
