import {MapConstants} from "../map/MapConstants"
import {MapUtils} from "../util/MapUtils"
import {TileOverlayOptions} from "./options/TileOverlayOptions"
import {LayerOverlay, OverlayType} from "./LayerOverlay"

export class TileOverlay extends LayerOverlay {

  private static readonly defaultTileOverlay = {

    getUrl: (x, y, z, _3dMode) => {
      return null
    },

    minZoom: MapConstants.defaultMinZoom,

    maxZoom: MapConstants.defaultMaxZoom

  }

  constructor(options: TileOverlayOptions) {
    super()
    let optionDefault: any = {}
    MapUtils.extendOptions(optionDefault, TileOverlay.defaultTileOverlay, options)
    this._getUrl = optionDefault.getUrl
    this.minZoom = optionDefault.minZoom
    this.maxZoom = optionDefault.maxZoom
    this.type = OverlayType.Tile
  }

  getUrl(x: number, y: number, z: number, _3dMode: boolean): string {
    return this._getUrl(x, y, z, _3dMode)
  }
}
