import {LatLng} from "../public/LatLng"

export class ConvexHull {
  static makeHullPresorted(points: LatLng[]): LatLng[] {
    if (points.length <= 1)
      return points.slice()

    // Andrew's monotone chain algorithm. Positive y coordinates correspond to "up"
    // as per the mathematical convention, instead of "down" as per the computer
    // graphics convention. This doesn't affect the correctness of the result.

    let upperHull: Array<LatLng> = []
    for (let i = 0; i < points.length; i++) {
      const p: LatLng = points[i]
      while (upperHull.length >= 2) {
        const q: LatLng = upperHull[upperHull.length - 1]
        const r: LatLng = upperHull[upperHull.length - 2]
        if ((q.lng - r.lng) * (p.lat - r.lat) >= (q.lat - r.lat) * (p.lng - r.lng))
          upperHull.pop()
        else
          break
      }
      upperHull.push(p)
    }
    upperHull.pop()

    let lowerHull: Array<LatLng> = []
    for (let i = points.length - 1; i >= 0; i--) {
      const p: LatLng = points[i]
      while (lowerHull.length >= 2) {
        const q: LatLng = lowerHull[lowerHull.length - 1]
        const r: LatLng = lowerHull[lowerHull.length - 2]
        if ((q.lng - r.lng) * (p.lat - r.lat) >= (q.lat - r.lat) * (p.lng - r.lng))
          lowerHull.pop()
        else
          break
      }
      lowerHull.push(p)
    }
    lowerHull.pop()

    if (upperHull.length == 1 && lowerHull.length == 1 && upperHull[0].lng == lowerHull[0].lng && upperHull[0].lat == lowerHull[0].lat)
      return upperHull
    else
      return upperHull.concat(lowerHull)
  }

  static POINT_COMPARATOR(a: LatLng, b: LatLng): number {
    if (a.lng < b.lng)
      return -1
    else if (a.lng > b.lng)
      return 1
    else if (a.lat < b.lat)
      return -1
    else if (a.lat > b.lat)
      return 1
    else
      return 0
  }

  static makeHull(points: LatLng[]): LatLng[] {
    let newPoints = points.slice()
    newPoints.sort(ConvexHull.POINT_COMPARATOR)
    return ConvexHull.makeHullPresorted(newPoints)
  }
}
