import {Map4D} from "../public/Map"

export type AnnotationId = number

export abstract class Annotation {

  protected id: AnnotationId

  protected zIndex: number

  protected elevation: number

  protected map: Map4D

  protected userData: any

  protected bringFrontBuilding: boolean

  protected userInteractionEnabled: boolean

  protected constructor() {
    this.id = null
    this.map = null
    this.zIndex = 0
    this.bringFrontBuilding = false
  }

  getId(): AnnotationId {
    return this.id
  }

  getMap(): Map4D {
    return this.map
  }

  getUserData(): any {
    return this.userData
  }

  getZIndex(): number {
    return this.zIndex
  }

  setUserData(data: any) {
    this.userData = data
  }

  setZIndex(zIndex: number): void {
    if (zIndex == null) {
      console.warn("Parameter index can not be null")
      return
    }
    if (zIndex == Number.MAX_SAFE_INTEGER || zIndex == Number.MIN_SAFE_INTEGER) {
      console.warn("zIndex value is not allowed!!!")
      return
    }
    if (zIndex == this.zIndex) {
      return
    }
    if (this.map != null) {
      this.map.mapView.updateAnnotationZIndex(this.id, this.zIndex, zIndex)
    }
    this.zIndex = zIndex
  }

  getElevation(): number {
    return this.elevation
  }

  setElevation(elevation: number) {
    if (elevation == null) {
      console.warn("Parameter elevation can not be null")
      return
    }
    if (this.elevation === elevation) {
      return
    }
    this.elevation = elevation
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  isUserInteractionEnabled(): boolean {
    return this.userInteractionEnabled
  }

  setUserInteractionEnabled(value: boolean) {
    this.userInteractionEnabled = value
  }


  abstract setMap(map: Map4D): void

}