import {TextLabelData} from "./TextLabelData"
import {MarkerLabel} from "../public/MarkerLabel"
import {TextLabelFactory} from "./TextLabelFactory"
import {TextLabel} from "./TextLabel"

export class TextLabelResourceManager {

  private static instances: Map<string, TextLabelResourceManager> = null

  private textLabels: Map<string, TextLabelData>

  private gl: WebGLRenderingContext

  private constructor(gl: WebGLRenderingContext) {
    this.gl = gl
    this.textLabels = new Map<string, TextLabelData>()
  }

  public static getInstance(gl: WebGLRenderingContext) {
    let id = gl["id"]
    if (this.instances == null) {
      this.instances = new Map<string, TextLabelResourceManager>()
    }
    let instance = this.instances.get(id)
    if (!instance) {
      instance = new this(gl)
      this.instances.set(id, instance)
    }
    return instance
  }

  public static releaseInstance(gl: WebGLRenderingContext) {
    let id = gl["id"]
    if (this.instances == null) {
      return
    }
    let instance = this.instances.get(id)
    if (!instance) {
      return
    }
    instance.textLabels.forEach(textLabel => textLabel.destroy())
    this.instances.delete(id)
  }

  acquire(markerLabel: MarkerLabel) {
    let markerLabelString = this.markerLabelToString(markerLabel)
    let textLabel = this.textLabels.get(markerLabelString)
    if (textLabel != null) {
      textLabel.model.retain()
      return textLabel
    }
    let textLabelData =
      TextLabelFactory.getInstance(this.gl).createText(new TextLabel(markerLabel.getText(), markerLabel.getFontSize()),
        markerLabel.getFontWeight(), markerLabel.getMaxWidth(), markerLabel.getLine())
    this.textLabels.set(markerLabelString, textLabelData)
    return textLabelData
  }

  release(markerLabel: MarkerLabel): void {
    if (!this.gl) {
      console.error("Please initialize SymbolResourceManager before using it")
    }
    let markerLabelString = this.markerLabelToString(markerLabel)
    let textLabel = this.textLabels.get(markerLabelString)
    if (textLabel == null) {
      //console.error("Can not find any text label with id: " + markerLabelString + " to decrease reference!")
      return
    }
    textLabel.model.release()
    if (textLabel.model.reference <= 0) {
      this.textLabels.delete(markerLabelString)
    }
  }

  private markerLabelToString(markerLabel: MarkerLabel): string {
    return markerLabel.getText() + " " + markerLabel.getFontSize() + " " + markerLabel.getFontWeight() + " " +
           markerLabel.getMaxWidth() + " " + markerLabel.getLine()
  }
}
