import * as MBPoint from "@mapbox/point-geometry"
import {Point} from "../public/Point"

export class Box {

  private readonly _min: MBPoint

  private readonly _max: MBPoint

  constructor(min: Point, max: Point) {
    this._min = new MBPoint(min.x, min.y)
    this._max = new MBPoint(max.x, max.y)
  }

  get min(): MBPoint {
    return this._min
  }

  get max(): MBPoint {
    return this._max
  }

}