import {Model} from "../engine/Model"
import {Texture} from "../engine/Texture"
import {Matrix4} from "../engine/Matrix"
import {MapState} from "../map/MapState"
import {ResourceReference} from "../engine/ResourceReference"

export abstract class Object3D extends ResourceReference{

  model: Model

  texture: Texture

  modelMatrix: Matrix4

  mvpMatrix: Matrix4

  protected constructor() {
    super()
  }

  setModel(model: Model): void {
    this.model = model
  }

  setTexture(texture: Texture): void {
    this.texture = texture
  }

  getModel(): Model {
    return this.model
  }

  getTexture(): Texture {
    return this.texture
  }

  hasTexture(): boolean {
    return this.texture != null
  }

  getModelMatrix(): Matrix4 {
    return this.modelMatrix
  }

  getMvpMatrix(): Matrix4 {
    return this.mvpMatrix
  }

  abstract updateMatrices(mapState: MapState): void
}
