import {LatLng, ILatLng} from "./LatLng"
import {Map4D} from "./Map"
import {MapUtils} from "../util/MapUtils"
import {Annotation} from "../annotation/Annotation"
import {CircleOptions} from "./options/CircleOptions"

export class Circle extends Annotation {

  private center: LatLng

  private radius: number

  private fillColor: string

  private fillOpacity: number

  private strokeColor: string

  private strokeWidth: number

  private visible: boolean

  private draggable: boolean

  private static readonly defaultCircleOptions = {
    radius: 0,
    fillColor: "#0000ff",
    fillOpacity: 1.0,
    visible: true,
    strokeColor: "#00ff00",
    strokeWidth: 0.0,
    draggable: false,
    zIndex: 0,
    elevation: 0,
    userInteractionEnabled: true
  }

  constructor(options: CircleOptions) {
    super()
    let circleOptions: any = {}
    MapUtils.extendOptions(circleOptions, Circle.defaultCircleOptions, options)
    for (const option of circleOptions) {
      if (option == null) {
        throw Error("Parameters can not be null")
      }
    }
    this.center = LatLng.convert(circleOptions.center)
    this.radius = circleOptions.radius
    this.fillColor = circleOptions.fillColor
    this.fillOpacity = circleOptions.fillOpacity
    this.visible = circleOptions.visible
    this.strokeColor = circleOptions.strokeColor
    this.strokeWidth = circleOptions.strokeWidth
    this.draggable = circleOptions.draggable
    this.zIndex = circleOptions.zIndex
    this.elevation = circleOptions.elevation
    this.userInteractionEnabled = circleOptions.userInteractionEnabled
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeAnnotation(this.id)
      this.id = -1
    }
    this.map = map
    if (this.map != null) {
      this.id = this.map.mapView.addAnnotation(this)
    }
  }

  setCenter(center: ILatLng): void {
    if (center == null)  {
      console.warn("Parameter center can not be null")
      return
    }
    let newCenter = LatLng.convert(center)
    if (this.center.equals(newCenter)) {
      return
    }
    this.center = newCenter
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id, true)
    }
  }

  setRadius(radius: number): void {
    if (radius == null) {
      console.warn("Parameter radius can not be null")
      return
    }
    if (this.radius == radius) {
      return
    }
    this.radius = radius
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id, true)
    }
  }

  setFillColor(fillColor: string): void {
    if (fillColor == null) {
      console.warn("Parameter fillColor can not be null")
      return
    }
    if (this.fillColor == fillColor) {
      return
    }
    this.fillColor = fillColor
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setFillOpacity(fillOpacity: number): void {
    if (fillOpacity == null) {
      console.warn("Parameter fillOpacity can not be null")
      return
    }
    if (this.fillOpacity == fillOpacity) {
      return
    }
    this.fillOpacity = fillOpacity
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setStrokeColor(strokeColor: string): void {
    if (strokeColor == null) {
      console.warn("Parameter strokeColor can not be null")
      return
    }
    if (this.strokeColor == strokeColor) {
      return
    }
    this.strokeColor = strokeColor
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setStrokeWidth(strokeWidth: number): void {
    if (strokeWidth == null) {
      console.warn("Parameter strokeWidth can not be null")
      return
    }
    if (this.strokeWidth == strokeWidth) {
      return
    }
    this.strokeWidth = strokeWidth
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setVisible(visible: boolean): void {
    if (visible == null) {
      console.warn("Parameter visible can not be null")
      return
    }
    if (this.visible === visible) {
      return
    }
    this.visible = visible
    if (this.map != null) {
      this.map.mapView.updateAnnotation(this.id)
    }
  }

  setDraggable(draggable: boolean): void {
    if (draggable == null) {
      console.warn("Parameter draggable can not be null")
      return
    }
    this.draggable = draggable
  }

  getCenter(): LatLng {
    return this.center
  }

  getRadius(): number {
    return this.radius
  }

  getFillColor(): string {
    return this.fillColor
  }

  getFillOpacity(): number {
    return this.fillOpacity
  }

  getStrokeColor(): string {
    return this.strokeColor
  }

  getStrokeWidth(): number {
    return this.strokeWidth
  }

  isVisible(): boolean {
    return this.visible
  }

  isDraggable(): boolean {
    return this.draggable
  }

}
