import {Layer, LayerType} from "../layer/Layer"
import {MapState} from "../map/MapState"
import {LayerRenderer} from "./LayerRenderer"
import {SymbolLayer} from "../layer/SymbolLayer"
import {ModelEnum, ResourceManager, ShaderEnum} from "../util/ResourceManager"
import {Kind} from "../tile/Tile"
import {GeometryUtils} from "../util/GeometryUtils"
import {Matrix4} from "../engine/Matrix"
import {Shader, UniformInfo} from "../engine/Shader"
import {TextLabelRenderer} from "./TextLabelRenderer"
import {Color} from "../engine/Color"
import {Vector2} from "../engine/Vector2"
import {MapUtils} from "../util/MapUtils"
import {SymbolResourceManager} from "../annotation/SymbolResourceManager"
import {Texture} from "../engine/Texture"
import {Point} from "../public/Point"
import {TextLabelData} from "../text/TextLabelData"
import {MarkerLabel} from "../public/MarkerLabel"
import {Model} from "../engine/Model"
import {MapConstants, SymbolConstants} from "../map/MapConstants"
import {VectorTile} from "../tile/VectorTile"
import {LatLng} from "../public/LatLng"
import {TextLabelResourceManager} from "../text/TextLabelResourceManager"
import {UrlUtils} from "../util/UrlUtils"
import {MapSettings} from "../map/MapSettings"

export class SymbolLayerRenderer extends LayerRenderer {

  private symbolShader: Shader

  private model: Model

  private flag: boolean

  constructor(mapState: MapState, layer: Layer) {
    super(mapState, layer)
    let resourceManager = ResourceManager.getInstance(this.layer.tileManager.mapView.gl)
    this.symbolShader = resourceManager.getShader(ShaderEnum.SYMBOL_SHADER)
    this.model = resourceManager.getModel(ModelEnum.SYMBOL_MODEL)
  }

  render(): void {
    if (this.layer.getLayerType() != LayerType.SYMBOL) {
      console.log("Invalid layer type")
      return
    }
    let symbolLayer = this.layer as SymbolLayer
    let gl = this.layer.tileManager.mapView.gl
    gl.enable(gl.BLEND)
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
    this.flag = true
    let textInformations = []
    let renderOrder = 0
    let symbolResourceManager = SymbolResourceManager.getInstance(gl)
    let cameraPosition = this.mapState.cameraPosition
    let screenSize = this.mapState.getScreenSize()
    symbolLayer.renderTiles.forEach(renderTile => {
      let tile = renderTile.getTile()
      let tileKind = tile.getKind()
      if (tileKind != Kind.ANNOTAION && tileKind != Kind.VECTOR) {
        console.error("Invalid tile")
        return
      }
      if (tileKind == Kind.ANNOTAION) {
        let iconTexture = symbolResourceManager.acquire(symbolLayer.url)
        let symbolWidth = symbolLayer.width < 0 ? iconTexture.width : symbolLayer.width
        let symbolHeight = symbolLayer.height < 0 ? iconTexture.height : symbolLayer.height
        let elevation = this.mapState.is3dMode() ? symbolLayer.elevation : 0.0
        let coordinate = GeometryUtils.latLngToScreenCoordinate(symbolLayer.position, elevation, cameraPosition, screenSize)
        let anchor = symbolLayer.anchor
        let textLabelData = symbolLayer.textLabelData
        this.renderSymbol(gl, iconTexture, coordinate, symbolWidth, symbolHeight, anchor, symbolLayer.rotation)
        symbolResourceManager.release(symbolLayer.url)
        if (textLabelData) {
          let labelAnchor = symbolLayer.labelAnchor
          let markerLabel = symbolLayer.markerLabel
          this.renderSymbolLabel(gl, coordinate, symbolWidth, symbolHeight, anchor, textLabelData, markerLabel, labelAnchor)
          this.flag = true
        }
      }
      else {
        this.symbolShader.bind()
        this.model.bind()
        this.symbolShader.setAttributes(this.model.attributeInfos)
        let vectorTile = renderTile.tile as VectorTile
        let tileData = vectorTile.getTileData()
        tileData.getLayerIds().forEach(id => {
          let tileLayer = tileData.getLayer(id)
          tileLayer.features.forEach(feature => {
            let properties = feature.getProperties()
            let symbolVisible = properties.get("symbolVisible") as boolean
            /** Still render user POI although @placesEnable = false  **/
            if ((!this.mapState.mapSettings.placesEnabled
              && !(properties.get("userFeature") === true || properties.get("overlayPlaces") === true))
                  || !symbolVisible) {
              return
            }
            let selected = properties.get("selected") as boolean
            let iconType = properties.get("url") as string
            let iconUrl = properties.get("userFeature") === true ? UrlUtils.getPOIImageUrl(MapConstants.poiImageUrl, 1, iconType, "1x") : iconType
            let iconTexture = selected === true ?
              symbolResourceManager.acquire(SymbolConstants.placeSelectedSymbol, false) :
              symbolResourceManager.acquire(iconUrl, false)
            let symbolWidth = (selected === true ?
              MapConstants.placeSelectedWidth :
              MapConstants.placeWidth) * MapUtils.pixelScale
            let symbolHeight = (selected === true ?
              MapConstants.placeSelectedHeight :
              MapConstants.placeHeight) * MapUtils.pixelScale
            let coordinate =
              GeometryUtils.latLngToScreenCoordinate(new LatLng(properties.get("lat") as number,
                properties.get("lng") as number), 0, cameraPosition, screenSize)
            let anchor = MapConstants.placeAnchor
            properties.set("renderOrder", ++renderOrder)
            let wasPlaceRendered = this.renderPlace(gl, iconTexture, coordinate, symbolWidth, symbolHeight, anchor)
            let textVisible = properties.get("textVisible") as boolean
            if (wasPlaceRendered && textVisible === true) {
              let color = properties.get("color")
              if (!color) {
                color = iconTexture.color ? iconTexture.color : MapConstants.placeDefaultColor
              }
              textInformations.push({
                text: properties.get("name") as string, coordinate: coordinate,
                color: selected === true ? MapConstants.placeSelectedColor : color,
                symbolWidth: symbolWidth, symbolHeight: symbolHeight, anchor: anchor
              })
            }
          })
        })
      }
    })
    textInformations.forEach(textInformation => {
      let markerLabel = new MarkerLabel({
        text: textInformation.text,
        anchor: [0.0, 0.5],
        fontSize: MapConstants.placeFontSize,
        fontWeight: MapConstants.placeFontWeight,
        line: MapConstants.placeNumLine,
        maxWidth: MapConstants.placeMaxWidth,
        color: textInformation.color
      })
      let textLabelData = TextLabelResourceManager.getInstance(gl).acquire(markerLabel)
      let labelAnchor = MapConstants.placeLabelAnchor
      this.renderSymbolLabel(gl, textInformation.coordinate, textInformation.symbolWidth,
        textInformation.symbolHeight, textInformation.anchor, textLabelData, markerLabel, labelAnchor)
      TextLabelResourceManager.getInstance(gl).release(markerLabel)
    })
    this.model.unbind()
    gl.disable(gl.BLEND)
  }

  private renderSymbol(gl: WebGLRenderingContext,
    iconTexture: Texture, coordinate: Vector2,
    symbolWidth: number, symbolHeight: number, anchor: Point, rotation: number = 0): void {
    if (!iconTexture.isLoaded) {
      return
    }
    if (this.flag) {
      this.symbolShader.bind()
      this.model.bind()
      this.symbolShader.setAttributes(this.model.attributeInfos)
      this.flag = false
    }
    let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0)
    let matrixTranslate =
      Matrix4.translate((0.5 - anchor.x) * symbolWidth, (0.5 - anchor.y) * symbolHeight, 0)
    modelMatrix = matrixTranslate.concat(modelMatrix)
    let matrixScale = Matrix4.scale(symbolWidth / 2, symbolHeight / 2, 1)
    let matrixRotate = Matrix4.rotateZ(rotation)
    modelMatrix = matrixRotate.concat(modelMatrix)

    modelMatrix = matrixScale.concat(modelMatrix)
    let mvpMatrix = modelMatrix.concat(this.mapState.getOrthoProjectionMatrix())
    this.symbolShader.setUniforms([
      new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix),
      new UniformInfo("u_texture", "texture", iconTexture)
    ])

    this.model.render(gl.TRIANGLES)
    this.symbolShader.resetTextureUnit()
  }

  private renderSymbolLabel(gl: WebGLRenderingContext, coordinate: Vector2,
    symbolWidth: number, symbolHeight: number, anchor: Point,
    textLabelData: TextLabelData,
    markerLabel: MarkerLabel, labelAnchor: Point) {
    let margin = markerLabel.getMargin()
    let symbolLabelAnchor = markerLabel.getAnchor()
    let pixelScale = MapUtils.pixelScale
    let textLabelWidth = (textLabelData.widthPerLine == MapConstants.none ? textLabelData.width : textLabelData.widthPerLine)
    let labelDeltaWidth = (labelAnchor.x - anchor.x) * symbolWidth
      + (margin.left - margin.right) * pixelScale
      + (textLabelWidth * (0.5 - symbolLabelAnchor.x))
    let labelDeltaHeight = (labelAnchor.y - anchor.y) * symbolHeight
      + (margin.top - margin.bottom) * pixelScale
      + (textLabelData.height * (0.5 - symbolLabelAnchor.y))

    let coordinateLabel = coordinate.add(
      new Vector2(labelDeltaWidth, labelDeltaHeight))
    TextLabelRenderer.render(gl, this.mapState, textLabelData, coordinateLabel,
      Color.fromHex(markerLabel.getColor()),
      markerLabel.hasHalo(),
      Color.fromHex(markerLabel.getHaloColor()))
  }

  private renderPlace(gl: WebGLRenderingContext,
    iconTexture: Texture,
    coordinate: Vector2,
    symbolWidth: number, symbolHeight: number, anchor: Point): boolean {
    if (!iconTexture.isLoaded) {
      return false
    }
    let modelMatrix = Matrix4.translate(coordinate.x, coordinate.y, 0)
    let matrixTranslate =
      Matrix4.translate((0.5 - anchor.x) * symbolWidth, (0.5 - anchor.y) * symbolHeight, 0)
    modelMatrix = matrixTranslate.concat(modelMatrix)
    let matrixScale = Matrix4.scale(symbolWidth / 2, symbolHeight / 2, 1)
    modelMatrix = matrixScale.concat(modelMatrix)
    let mvpMatrix = modelMatrix.concat(this.mapState.getOrthoProjectionMatrix())
    this.symbolShader.setUniforms([
      new UniformInfo("u_mvpMatrix", "matrix", mvpMatrix),
      new UniformInfo("u_texture", "texture", iconTexture),
    ])

    this.model.render(gl.TRIANGLES)
    this.symbolShader.resetTextureUnit()
    return true
  }
}
