import {LatLngBounds, ILatLngBounds} from "./LatLngBounds"
import {Map4D} from "./Map"
import {MapUtils} from "../util/MapUtils"
import {MapConstants} from "../map/MapConstants"
import {Annotation} from "../annotation/Annotation"
import {TileAreaOptions} from "./options/TileAreaOptions"

export type TileAreaId = number

export class TileArea extends Annotation {

  private bounds: LatLngBounds

  private url: string

  private minZoom: number

  private maxZoom: number

  private static readonly defaultTileArea = {

    url: MapConstants.baseMapUrl,

    minZoom: MapConstants.defaultMinZoom,

    maxZoom: MapConstants.defaultMaxZoom

  }

  constructor(options: TileAreaOptions) {
    super()
    let optionDefault: any = {}
    MapUtils.extendOptions(optionDefault, TileArea.defaultTileArea, options)
    this.bounds = LatLngBounds.convert(optionDefault.bounds)
    this.url = optionDefault.url
    this.minZoom = optionDefault.minZoom
    this.maxZoom = optionDefault.maxZoom
  }

  getBounds(): LatLngBounds {
    if (this.bounds == null){
      return null
    }
    return new LatLngBounds(this.bounds.getSouthwest(), this.bounds.getNortheast())
  }

  getUrl(): string {
    return this.url
  }

  getMinZoom(): number {
    return this.minZoom
  }

  getMaxZoom(): number {
    return this.maxZoom
  }

  setBounds(bounds: ILatLngBounds): void {
    if (bounds == null) {
      console.warn("Parameter bounds can not be null")
      return
    }
    this.bounds = LatLngBounds.convert(bounds)
    if (this.map != null) {
      this.map.mapView.reload()
    }
  }

  setMinZoom(minZoom: number): void {
    if (minZoom == null) {
      console.warn("Parameter minZoom can not be null")
      return
    }
    if (this.minZoom == minZoom) {
      return
    }
    this.minZoom = minZoom
    if (this.map != null) {
      this.map.mapView.reload()
    }
  }

  setMaxZoom(maxZoom: number): void {
    if (maxZoom == null) {
      console.warn("Parameter maxZoom can not be null")
      return
    }
    if (this.maxZoom == maxZoom) {
      return
    }
    this.maxZoom = maxZoom
    if (this.map != null) {
      this.map.mapView.reload()
    }
  }

  setUrl(url: string): void {
    if (url == null) {
      console.warn("Parameter url can not be null")
      return
    }
    if (this.url == url) {
      return
    }
    this.url = url
    if (this.map != null) {
      this.map.mapView.reload()
    }
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeTileArea(this.id)
      this.id = -1
    }
    this.map = map
    if (this.map != null) {
      this.id = this.map.mapView.addTileArea(this)
    }
  }
}
