import {Tile} from "./Tile"

export class TileCache {
  tiles: Map<string, Tile>;

  orderedKeys: string[] = [];

  size: number;

  constructor(size: number) {
    this.size = size
    this.tiles = new Map<string, Tile>()
  }

  setSize(size: number): void {
    this.size = size
    while (this.orderedKeys.length > size) {
      let key = this.orderedKeys[0]
      this.orderedKeys.shift()
      this.tiles.delete(key.toString())
    }
  }

  add(key: string, tile: Tile): void {
    if (!tile.isRenderable() || !this.size) {
      tile.destroy()
      return
    }

    // insert new or query existing tile
    if (this.tiles.has(key)) {
      let index = this.orderedKeys.indexOf(key, 0)
      if (index > -1) {
        this.orderedKeys.splice(index, 1)
      }
      let oldTile = this.tiles.get(key)
      if (oldTile != tile) {
        oldTile.destroy()
      }
    }
    this.tiles.set(key, tile)

    // (re-)insert tile key as newest
    this.orderedKeys.push(key)

    // Remove the old key in the case cache size exceed maximum value
    if (this.orderedKeys.length > this.size - 1) {
      let removeTile = this.pop(this.orderedKeys[0])
      removeTile.destroy()
    }
  }

  get(key: string): Tile {
    return this.tiles.get(key)
  }

  pop(key: string): Tile {
    let tile = this.tiles.get(key)
    if (this.tiles.delete(key)) {
      let index = this.orderedKeys.indexOf(key, 0)
      if (index > -1) {
        this.orderedKeys.splice(index, 1)
      }
    }
    return tile
  }

  has(key: string): boolean {
    return this.tiles.get(key) != null
  }

  clear(): void {
    this.orderedKeys = []
    this.tiles.forEach(tile => {
      tile.destroy()
    })
    this.tiles.clear()
  }
}
