import {Model} from "../engine/Model"
import {FontSDF} from "./FontSDF"

export class TextLabelData {

  private readonly _text: string

  private readonly _size: number

  private readonly _width: number

  private readonly _height: number

  private readonly _widthPerLine: number

  private _model: Model

  private _font: FontSDF

  constructor(text: string, font: FontSDF, size: number, width: number, height: number, widthPerLine: number, model: Model) {
    this._text = text
    this._font = font
    this._size = size
    this._width = width
    this._height = height
    this._widthPerLine = widthPerLine
    this._model = model
  }

  destroy(): void {
    if (this._model) {
      this._model.destroy()
      this._model = null
    }
  }

  get text(): string {
    return this._text
  }

  get font(): FontSDF {
    return this._font
  }

  get size(): number {
    return this._size
  }

  get width(): number {
    return this._width
  }

  get height(): number {
    return this._height
  }

  get widthPerLine(): number {
    return this._widthPerLine
  }

  get model(): Model {
    return this._model
  }
}
