import {LayerData} from "./LayerData"
import {Model} from "../../engine/Model"
import {GeometryTileLayer, GeometryType} from "../../tile/data/GeometryTileData"
import {LayerType} from "../../layer/Layer"
import {
  buildPolygon,
  LineCapType,
  LineJoinType,
  PolygonBuilderContext,
  PolylineBuilderContext
} from "../../util/PolyBuilders"
import {MapUtils} from "../../util/MapUtils"
import {Vector2} from "../../engine/Vector2"
import {AttributeInfo} from "../../engine/Shader"
import {Color} from "../../engine/Color"
import {VectorTileFeature, VectorTileLayer} from "../../tile/data/VectorTileData"
import {Range} from "../../util/Range"
import {Vector3} from "../../engine/Vector3"
import {LatLng} from "../../public/LatLng"

export class GeoJsonLayerData extends LayerData {

  private static readonly defaultFillColor = Color.fromHex("#555555")

  private static readonly  defaultStrokeColor = Color.fromHex("#555555")

  public static readonly defaultStrokeWidth = 5

  private vertices: number[] = []

  private indices: number[] = []

  private partials: Map<string, Range[]>

  private model: Model

  private fillOutlinedPartials: Map<string, Range[][]>

  private fillProperties: Map<string, {fillColor: Color}>

  private lineProperties: Map<string, {strokeColor: Color, strokeWidth: number}>

  private fillOutlinedProperties: Map<string, {strokeColor: Color}>

  private pointCoordinates: Map<string, LatLng[]>

  private readonly gl: WebGLRenderingContext

  constructor(geometryTileLayer: GeometryTileLayer, gl: WebGLRenderingContext) {
    super(LayerType.GEOJSON)
    this.gl = gl
    this.partials = new Map<string, Range[]>()
    this.fillOutlinedPartials = new Map<string, Range[][]>()
    this.fillProperties = new Map<string, {fillColor: Color}>()
    this.lineProperties = new Map<string, {strokeColor: Color, strokeWidth: number}>()
    this.fillOutlinedProperties = new Map<string, {strokeColor: Color}>()
    this.pointCoordinates = new Map<string, LatLng[]>()

    // We must build the stroke model first
    this.buildFillStroke(geometryTileLayer)
    this.buildModel(geometryTileLayer)
    this.model = new Model(this.gl)
    this.model.init(this.vertices,
      [new AttributeInfo("a_position", 2, 0, 4),
        new AttributeInfo("a_extrude", 2, 2, 4)], this.indices)
    this.vertices = []
    this.indices = []
    this.initializeProperties(geometryTileLayer)
  }

  getModel(): Model {
    return this.model
  }

  getPartials(): Map<string, Range[]> {
    return this.partials
  }

  getFillOutlinedPartials(): Map<string, Range[][]> {
    return this.fillOutlinedPartials
  }

  getFillProperties(): Map<string, {fillColor: Color}> {
    return this.fillProperties
  }

  getLineProperties(): Map<string, {strokeColor: Color, strokeWidth: number}> {
    return this.lineProperties
  }

  getFillOutlinedProperties(): Map<string, {strokeColor: Color}> {
    return this.fillOutlinedProperties
  }

  getPointCoordinates(): Map<string, LatLng[]> {
    return this.pointCoordinates
  }

  private buildFillStroke(geometryTileLayer: GeometryTileLayer): void {
    let tileSize = MapUtils.getTileSize()
    let featureCount = geometryTileLayer.featureCount()
    let number = 0
    let size = 0
    let ranges: Range[] = []
    for (let count = 0; count < featureCount; ++count) {
      let feature = geometryTileLayer.getFeature(count)
      if (feature.getType() != GeometryType.LineString || feature.getProperties().get("fill-outlined") !== true) {
        continue
      }
      let geometries = feature.getGeometries().geometryCoordinates
      let rangeStart = this.indices.length
      for (let i = 0; i < geometries.length; ++i) {
        size += geometries[i].length
        for (let j = 0; j < geometries[i].length ; ++j) {
          let point = new Vector2(geometries[i][j][0], geometries[i][j][1])
          point.x = point.x * tileSize
          point.y = point.y * tileSize
          this.vertices.push(point.x)
          this.vertices.push(point.y)
          this.vertices.push(0.0)
          this.vertices.push(0.0)
          if (number == size - 1) {
            number++
            continue
          }
          this.indices.push(number++)
          this.indices.push(number)

        }
      }
      let rangeLength = this.indices.length - rangeStart
      ranges.push(new Range(rangeStart, rangeLength))
      let featureId = ""
      if (feature instanceof VectorTileFeature) {
        featureId = (feature as VectorTileFeature).getId()
      }
      let linePartials = this.fillOutlinedPartials.get(featureId)
      if (linePartials) {
        linePartials.push(ranges)
      }
      else {
        this.fillOutlinedPartials.set(featureId, [ranges])
      }

      ranges = []
    }
  }

  private initializeProperties(geometryTileLayer: GeometryTileLayer): void {
    if (!(geometryTileLayer instanceof VectorTileLayer)) {
      return
    }
    let featureCount = geometryTileLayer.featureCount()
    for (let featureIndex = 0; featureIndex < featureCount; ++featureIndex) {
      let feature = geometryTileLayer.getFeature(featureIndex)
      let featureType = feature.getType()
      let properties = feature.getProperties()
      let featureId = (feature as VectorTileFeature).getId() || ""

      if (featureType == GeometryType.Point) {

        // Point
        let lat = properties.get("coordinate-lat") as number
        let lng = properties.get("coordinate-lng") as number
        if (lat == null || lng == null) {
          console.warn("Point feature does not have coordinate")
          continue
        }
        if (this.pointCoordinates.has(featureId)) {
          this.pointCoordinates.get(featureId).push(new LatLng(lat, lng))
        }
        else {
          this.pointCoordinates.set(featureId, [new LatLng(lat, lng)])
        }
      }
      else if (featureType == GeometryType.LineString) {

        // Polyline
        if (properties == null) {
          this.lineProperties.set(featureId, {strokeColor: GeoJsonLayerData.defaultStrokeColor,
            strokeWidth: GeoJsonLayerData.defaultStrokeWidth * MapUtils.pixelScale})
        }
        else if (properties.has("fill-outlined")) {
          let stroke = properties.get("stroke") as string || "#555555"
          let strokeOpacity = properties.has("stroke-opacity") ? properties.get("stroke-opacity") as number : 1.0
          let strokeColor = Color.fromHex(stroke)
          strokeColor.alpha = strokeOpacity
          this.fillOutlinedProperties.set(featureId, {strokeColor: strokeColor})
        }
        else {
          let fill = properties.get("stroke") as string || "#555555"
          let fillOpacity = properties.has("stroke-opacity") ? properties.get("stroke-opacity") as number : 1.0
          let strokeColor = Color.fromHex(fill)
          strokeColor.alpha = fillOpacity
          let strokeWidth = properties.has("stroke-width") ?
            properties.get("stroke-width") as number : GeoJsonLayerData.defaultStrokeWidth
          strokeWidth *= MapUtils.pixelScale
          this.lineProperties.set(featureId, {strokeColor: strokeColor, strokeWidth: strokeWidth})
        }
      }
      else {

        // Polygon
        if (properties) {
          let fill = properties.get("fill") as string || "#555555"
          let fillOpacity = properties.has("fill-opacity") ? properties.get("fill-opacity") as number : 0.5
          let fillColor = Color.fromHex(fill)
          fillColor.alpha = fillOpacity
          this.fillProperties.set(featureId, {fillColor: fillColor})
        }
        else {
          this.fillProperties.set(featureId, {fillColor: GeoJsonLayerData.defaultFillColor})
        }
      }
    }
  }

  private buildModel(geometryTileLayer: GeometryTileLayer): void {
    let buf = this.vertices.length / 4
    let count = 0

    let polygonBuilderContext = new PolygonBuilderContext()
    polygonBuilderContext.addVertex = (coord: Vector2) => {
      this.vertices.push(coord.x, coord.y, 0, 0)
      ++count
    }

    let polylineBuilderContext: PolylineBuilderContext = new PolylineBuilderContext()
    polylineBuilderContext.addVertex = (coord: Vector3, extrude: Vector3) => {
      this.vertices.push(coord.x, coord.y, extrude.x, extrude.y)
      ++count
    }
    polylineBuilderContext.lineCapType = LineCapType.Butt
    polylineBuilderContext.lineJoinType = LineJoinType.Round

    let tileSize = MapUtils.getTileSize()
    let featureCount = geometryTileLayer.featureCount()
    for (let featureIndex = 0; featureIndex < featureCount; ++featureIndex) {
      let feature = geometryTileLayer.getFeature(featureIndex)
      let properties = feature.getProperties()
      let featureType = feature.getType()
      if (featureType == GeometryType.Point) {

        // Ignore
      }
      else if (featureType == GeometryType.Polygon) {
        // Polygon
        let rangeStart = this.indices.length
        let geometries = feature.getGeometries().geometryCoordinates
        let paths: Vector2[][] = []
        for (let i = 0; i < geometries.length; ++i) {
          let path: Vector2[] = []
          for (let j = 0; j < geometries[i].length; ++j) {
            let point = new Vector2(geometries[i][j][0], geometries[i][j][1])
            point.x = point.x * tileSize
            point.y = point.y * tileSize
            path.push(point)
          }
          paths.push(path)
        }
        polygonBuilderContext.clear()
        buildPolygon(paths, polygonBuilderContext)
        let featureId = ""
        if (feature instanceof VectorTileFeature) {
          featureId = (feature as VectorTileFeature).getId()
        }
        polygonBuilderContext.indices.forEach(index => this.indices.push(index + buf))
        buf += count
        count = 0
        let rangeLength = this.indices.length - rangeStart
        if (this.partials.has(featureId)) {
          this.partials.get(featureId).push(new Range(rangeStart, rangeLength))
        }
        else {
          this.partials.set(featureId, [new Range(rangeStart, rangeLength)])
        }
      }
      else {

        // Polyline
        if (properties == null || properties.has("fill-outlined")) {
          continue
        }
        else {
          // Polyline
          polylineBuilderContext.clear()
          let rangeStart = this.indices.length
          let geometries = feature.getGeometries().geometryCoordinates
          for (let i = 0; i < geometries.length; ++i) {
            let points: Vector2[] = []
            for (let j = 0; j < geometries[i].length; ++j) {
              let point = new Vector2(geometries[i][j][0], geometries[i][j][1])
              point.x = point.x * tileSize
              point.y = point.y * tileSize
              points.push(point)
            }

            polylineBuilderContext.buildPolyLine(points)
          }
          polylineBuilderContext.indices.forEach(index => this.indices.push(index + buf))
          buf += count
          count = 0
          let rangeLength = this.indices.length - rangeStart
          let featureId = ""
          if (feature instanceof VectorTileFeature) {
            featureId = (feature as VectorTileFeature).getId()
          }
          this.partials.set(featureId, [new Range(rangeStart, rangeLength)])
        }
      }
    }
  }

  destroy(): void {
    this.model && this.model.destroy()
  }
}
