import {MapConstants} from "../map/MapConstants"
import {LatLng} from "../public/LatLng"
import {MathUtils} from "../engine/MathUtils"
import {Point} from "../public/Point"
import {Vector2} from "../engine/Vector2"
import {CameraPosition} from "../public/CameraPosition"
import {LatLngBounds} from "../public/LatLngBounds"
import {MapState} from "../map/MapState"
import {MapSettings} from "../map/MapSettings"
import {PaddingOptions} from "../public/options/PaddingOptions"
import {ProjectionMercator} from "../map/ProjectionMercator"

export class MapUtils {
  private static _pixelScale = 1.0

  private static _touchBoxSize = 1.0

  static get pixelScale(): number {
    return MapUtils._pixelScale
  }

  static set pixelScale(pixelScale: number) {
    MapUtils._pixelScale = pixelScale
  }

  static get touchBoxSize(): number {
    return MapUtils._touchBoxSize
  }

  static set touchBoxSize(touchBoxSize: number) {
    MapUtils._touchBoxSize = touchBoxSize
  }

  static get resourceScale(): number {
    return Math.round(MapUtils._pixelScale)
  }

  static getTileSize(): number {
    return MapConstants.baseTileSize * MapUtils.pixelScale
  }

  static extendOptions(dest: any, ...sources: Array<any>): any {
    for (const src of sources) {
      for (const k in src) {
        dest[k] = src[k]
      }
    }
    return dest
  }

  static pointToPixel(point: Point): Point {
    return new Point(point.x * MapUtils.pixelScale, point.y * MapUtils.pixelScale)
  }

  static pixelToPoint(pixel: Point): Point {
    return new Point(pixel.x / MapUtils.pixelScale, pixel.y / MapUtils.pixelScale)
  }

  static getTimePanTo(src: LatLng, desc: LatLng, zoom: number): number{
    let distance = src.distanceTo(desc)

    // Time By Zoom
    // 500 km = 1000 ms
    let time = distance / 500
    if (zoom < 10){
      time *= 0.01
    }
    else if (zoom < 14){
      time *= 0.5
    }
    else {
      time *= 1.5
    }
    time = MathUtils.clamp(time, MapConstants.minTimeSpanTo, MapConstants.maxTimeSpanTo)
    return Math.round(time)
  }

  static getCameraPositionForLatLngBounds(bounds: LatLngBounds,
    padding: PaddingOptions,
    mapState: MapState,
    mapSettings: MapSettings) {
    let screenSize = mapState.getScreenSize()
    let zoom = mapState.getZoom()
    let scale = 1<<zoom
    let botLeft = ProjectionMercator.projectScale(bounds.getSouthwest(), scale)
    let topRight = ProjectionMercator.projectScale(bounds.getNortheast(), scale)
    let width = topRight.x - botLeft.x
    let height = botLeft.y - topRight.y
    width = width <= 0 ? 1.0 : width
    height = height <= 0 ? 1.0 : height

    let minScale = Number.MAX_VALUE
    if (width > 0 || height > 0) {
      let scaleX = screenSize.x / width
      let scaleY = screenSize.y / height
      scaleX -= (padding.left + padding.right)/width
      scaleY -= (padding.top + padding.bottom)/height
      minScale = Math.min(scaleX, scaleY)
    }

    let targetZoom = zoom
    if (minScale > 0) {
      targetZoom = MathUtils.clamp(zoom + Math.log2(minScale), mapSettings.getMinZoom(), mapSettings.getMaxZoom())
    }
    else {
      console.warn("Cannot calculate zoom level for bounds. Vertical or horizontal padding is greater than map's height or width")
      targetZoom = mapSettings.getMinZoom()
    }
    targetZoom = Math.floor(targetZoom)
    let targetScale = Math.pow(2, targetZoom)
    let centerPixel = botLeft.add(topRight).multiply(0.5)

    let target = ProjectionMercator.unproject(centerPixel, scale)
    let targetCenterPixel = ProjectionMercator.projectScale(target, targetScale)

    let screenTopRightPixel = targetCenterPixel.add(screenSize.multiply(0.5))
    let screenBotLeftPixel = targetCenterPixel.add(screenSize.multiply(-0.5))

    //apply padding
    screenTopRightPixel = screenTopRightPixel.subtract(new Vector2(padding.left, padding.top))
    screenBotLeftPixel = screenBotLeftPixel.add(new Vector2(padding.right, padding.bottom))
    targetCenterPixel = screenTopRightPixel.add(screenBotLeftPixel).multiply(0.5)
    return new CameraPosition(ProjectionMercator.unproject(targetCenterPixel, targetScale), 0.0, 0.0, targetZoom)
  }
}
