import {LayerData} from "./LayerData"
import {Model} from "../../engine/Model"
import {GeometryTileLayer, GeometryType} from "../../tile/data/GeometryTileData"
import {LayerType} from "../../layer/Layer"
import {buildPolygon, PolygonBuilderContext} from "../../util/PolyBuilders"
import {MapUtils} from "../../util/MapUtils"
import {Vector2} from "../../engine/Vector2"
import {AttributeInfo} from "../../engine/Shader"
import {Color} from "../../engine/Color"
import {VectorTileFeature, VectorTileLayer} from "../../tile/data/VectorTileData"
import {Range} from "../../util/Range"

export class FillLayerData extends LayerData {

  private static readonly defaultFillColor = Color.fromHex("#000000")

  private vertices: number[] = []

  private indices: number[] = []

  private fillPartials: Map<string, Range[]>

  private model: Model

  private linePartials: Map<string, Range[][]>

  private fillProperties: Map<string, {fillColor: Color}>

  private strokeProperties: Map<string, {strokeColor: Color}>

  private readonly gl: WebGLRenderingContext

  constructor(geometryTileLayer: GeometryTileLayer, gl: WebGLRenderingContext) {
    super(LayerType.FILL)
    this.gl = gl
    this.fillPartials = new Map<string, Range[]>()
    this.linePartials = new Map<string, Range[][]>()
    this.fillProperties = new Map<string, {fillColor: Color}>()
    this.strokeProperties = new Map<string, {strokeColor: Color}>()

    // We must build the stroke model first
    this.buildStroke(geometryTileLayer)
    this.buildPolygon(geometryTileLayer)
    this.model = new Model(this.gl)
    this.model.init(this.vertices,
      [new AttributeInfo("a_position", 2, 0, 2)], this.indices)
    this.vertices = []
    this.indices = []
    this.initializeProperties(geometryTileLayer)
  }

  getModel(): Model {
    return this.model
  }

  getFillPartials(): Map<string, Range[]> {
    return this.fillPartials
  }

  getLinePartials(): Map<string, Range[][]> {
    return this.linePartials
  }

  getFillProperties(): Map<string, {fillColor: Color}> {
    return this.fillProperties
  }

  getStrokeProperties(): Map<string, {strokeColor: Color}> {
    return this.strokeProperties
  }

  private buildStroke(geometryTileLayer: GeometryTileLayer): void {
    let tileSize = MapUtils.getTileSize()
    let featureCount = geometryTileLayer.featureCount()
    let number = 0
    let size = 0
    let ranges: Range[] = []
    for (let count = 0; count < featureCount; ++count) {
      let feature = geometryTileLayer.getFeature(count)
      if (feature.getType() != GeometryType.LineString) {
        continue
      }
      let geometries = feature.getGeometries().geometryCoordinates
      let rangeStart = this.indices.length
      for (let i = 0; i < geometries.length; ++i) {
        size += geometries[i].length
        for (let j = 0; j < geometries[i].length ; ++j) {
          let point = new Vector2(geometries[i][j][0], geometries[i][j][1])
          point.x = point.x * tileSize
          point.y = point.y * tileSize
          this.vertices.push(point.x)
          this.vertices.push(point.y)
          if (number == size - 1) {
            number++
            continue
          }
          this.indices.push(number++)
          this.indices.push(number)

        }
      }
      let rangeLength = this.indices.length - rangeStart
      ranges.push(new Range(rangeStart, rangeLength))
      let featureId = ""
      if (feature instanceof VectorTileFeature) {
        featureId = (feature as VectorTileFeature).getId()
      }
      let linePartials = this.linePartials.get(featureId)
      if (linePartials) {
        linePartials.push(ranges)
      }
      else {
        this.linePartials.set(featureId, [ranges])
      }

      ranges = []
    }
  }

  private initializeProperties(geometryTileLayer: GeometryTileLayer): void {
    if (!(geometryTileLayer instanceof VectorTileLayer)) {
      return
    }
    let featureCount = geometryTileLayer.featureCount()
    for (let featureIndex = 0; featureIndex < featureCount; ++featureIndex) {
      let feature = geometryTileLayer.getFeature(featureIndex)
      let featureType = feature.getType()
      let properties = feature.getProperties()
      let featureId = (feature as VectorTileFeature).getId() || ""
      if (featureType != GeometryType.Polygon) {
        let stroke = properties.get("stroke") as string || "#000000"
        let strokeOpacity = properties.has("stroke-opacity") ? properties.get("stroke-opacity") as number : 1
        let strokeColor = Color.fromHex(stroke)
        strokeColor.alpha = strokeOpacity
        this.strokeProperties.set(featureId, {strokeColor: strokeColor})
        continue
      }
      if (properties) {
        let fill = properties.get("fill") as string || "#000000"
        let fillOpacity = properties.has("fill-opacity") ? properties.get("fill-opacity") as number : 1
        let fillColor = Color.fromHex(fill)
        fillColor.alpha = fillOpacity
        this.fillProperties.set(featureId, {fillColor: fillColor})
      }
      else {
        this.fillProperties.set(featureId, {fillColor: FillLayerData.defaultFillColor})
      }
    }
  }

  private buildPolygon(geometryTileLayer: GeometryTileLayer): void {
    let buf = this.vertices.length / 2
    let polygonBuilderContext = new PolygonBuilderContext()
    polygonBuilderContext.addVertex = (coord: Vector2) => {
      this.vertices.push(coord.x, coord.y)
    }
    let tileSize = MapUtils.getTileSize()
    let featureCount = geometryTileLayer.featureCount()
    for (let featureIndex = 0; featureIndex < featureCount; ++featureIndex) {
      let feature = geometryTileLayer.getFeature(featureIndex)
      let featureType = feature.getType()
      if (featureType != GeometryType.Polygon) {
        continue
      }
      let properties = feature.getProperties()
      if (properties && properties.get("multi-polygon") === true) {
        let geometries = feature.getGeometries().geometryCoordinates
        let paths: Vector2[][] = []
        let ranges: Range[] = []
        for (let i = 0; i < geometries.length; ++i) {
          let rangeStart = this.indices.length
          let path: Vector2[] = []
          for (let j = 0; j < geometries[i].length; ++j) {
            let point = new Vector2(geometries[i][j][0], geometries[i][j][1])
            point.x = point.x * tileSize
            point.y = point.y * tileSize
            path.push(point)
          }
          paths.push(path)
          buildPolygon(paths, polygonBuilderContext)
          polygonBuilderContext.indices.forEach(index => this.indices.push(index + buf))
          buf = this.vertices.length / 2
          paths = []
          let rangeLength = this.indices.length - rangeStart
          ranges.push(new Range(rangeStart, rangeLength))
        }
        let featureId = ""
        if (feature instanceof VectorTileFeature) {
          featureId = (feature as VectorTileFeature).getId()
        }
        this.fillPartials.set(featureId, ranges)
      }
      else {
        let rangeStart = this.indices.length
        let geometries = feature.getGeometries().geometryCoordinates
        let paths: Vector2[][] = []
        for (let i = 0; i < geometries.length; ++i) {
          let path: Vector2[] = []
          for (let j = 0; j < geometries[i].length; ++j) {
            let point = new Vector2(geometries[i][j][0], geometries[i][j][1])
            point.x = point.x * tileSize
            point.y = point.y * tileSize
            path.push(point)
          }
          paths.push(path)
        }
        buildPolygon(paths, polygonBuilderContext)
        let featureId = ""
        if (feature instanceof VectorTileFeature) {
          featureId = (feature as VectorTileFeature).getId()
        }
        polygonBuilderContext.indices.forEach(index => this.indices.push(index + buf))
        buf = this.vertices.length / 2
        let rangeLength = this.indices.length - rangeStart
        this.fillPartials.set(featureId, [new Range(rangeStart, rangeLength)])
      }
    }
  }

  destroy(): void {
    this.model && this.model.destroy()
  }
}
