import * as RBush from "rbush"
import {VectorTileFeature} from "../tile/data/VectorTileData"
import {TileCoordinate} from "../tile/TileCoordinate"
import {GeometryUtils} from "../util/GeometryUtils"
import {LatLng} from "../public/LatLng"
import {MapState} from "./MapState"
import {TileManager} from "../tile/TileManager"
import {UrlUtils} from "../util/UrlUtils"
import {MapConstants} from "./MapConstants"
import {MapSettings} from "./MapSettings"
import {SymbolResourceManager} from "../annotation/SymbolResourceManager"
import {TextLabelResourceManager} from "../text/TextLabelResourceManager"
import {MarkerLabel} from "../public/MarkerLabel"

type FeatureIndex = {
  feature: VectorTileFeature

  // Rbush properties
  minX: number
  minY: number
  maxX: number
  maxY: number
}

export class FeatureManager {

  private gl: WebGLRenderingContext

  private readonly featuresTree: rbush.RBush<FeatureIndex>

  private features: Map<string, FeatureIndex>

  private tileManager: TileManager

  private mapState: MapState

  constructor(gl: WebGLRenderingContext, tileManager: TileManager, mapState: MapState) {
    this.gl = gl
    this.featuresTree = RBush<FeatureIndex>()
    this.features = new Map<string, FeatureIndex>()
    this.tileManager = tileManager
    this.mapState = mapState
  }

  hasFeature(featureId: string): boolean {
    return this.features.has(featureId)
  }

  addFeature(feature: VectorTileFeature): void {
    if (this.features.has(feature.getId())) {
      console.error("Feature id exist: " + feature.getId())
      return
    }
    let properties = feature.getProperties()
    let lat = properties.get("lat") as number
    let lng = properties.get("lng") as number
    let placeName = properties.get("name") as string
    if (placeName != null) {
      let markerLabel = new MarkerLabel({
        text: placeName,
        fontSize: MapConstants.placeFontSize,
        fontWeight: MapConstants.placeFontWeight,
        line: MapConstants.placeNumLine,
        maxWidth: MapConstants.placeMaxWidth
      })
      TextLabelResourceManager.getInstance(this.gl).acquire(markerLabel)
    }
    let iconType = properties.get("url") as string || MapConstants.placeDefaultIcon
    let iconUrl = UrlUtils.getPOIImageUrl(MapConstants.poiImageUrl, 1, iconType, "1x")
    SymbolResourceManager.getInstance(this.gl).acquire(iconUrl)
    let tileCoordinate =
      TileCoordinate.fromLatLng(new LatLng(lat, lng), this.mapState.getZoom(), this.mapState.is3dMode())
    let featureIndex = {feature: feature, minX: lng, maxX: lng, minY: lat, maxY: lat}
    this.featuresTree.insert(featureIndex)
    this.features.set(feature.getId(), featureIndex)
    this.tileManager.reloadTileData(tileCoordinate)
  }

  removeFeature(featureId: string): void {
    let featureIndex = this.features.get(featureId)
    if (featureIndex != null) {
      let properties = featureIndex.feature.getProperties()
      let lat = properties.get("lat") as number
      let lng = properties.get("lng") as number
      let placeName = properties.get("name") as string
      if (placeName != null) {
        let markerLabel = new MarkerLabel({
          text: placeName,
          fontSize: MapConstants.placeFontSize,
          fontWeight: MapConstants.placeFontWeight,
          line: MapConstants.placeNumLine,
          maxWidth: MapConstants.placeMaxWidth
        })
        TextLabelResourceManager.getInstance(this.gl).release(markerLabel)
      }
      let iconType = properties.get("icon") as string || MapConstants.placeDefaultIcon
      let iconUrl = UrlUtils.getPOIImageUrl(MapConstants.poiImageUrl, 1, iconType, "1x")
      SymbolResourceManager.getInstance(this.gl).release(iconUrl)
      let tileCoordinate =
        TileCoordinate.fromLatLng(new LatLng(lat, lng), this.mapState.getZoom(), this.mapState.is3dMode())
      this.featuresTree.remove(featureIndex)
      this.features.delete(featureId)
      this.tileManager.reloadTileData(tileCoordinate)
    }
    else {
      !featureId.startsWith(MapConstants.placeSelectedIdPrefix) &&
      console.warn("Feature id not found: " + featureId)
    }
  }

  queryFeatures(tileCoordinate: TileCoordinate): VectorTileFeature[] {
    let bounds = GeometryUtils.tileCoordinateToLatLngBounds(tileCoordinate)
    let bBox = {
      minX: bounds.getSouthwest().lng,
      minY: bounds.getSouthwest().lat,
      maxX: bounds.getNortheast().lng,
      maxY: bounds.getNortheast().lat
    }
    let features = this.featuresTree.search(bBox)
    let visibleFeatures = []
    features.forEach(feature => {
      let featureProperties = feature.feature.getProperties()
      featureProperties.set("userFeature", true)
      visibleFeatures.push(feature.feature)
    })

    return visibleFeatures
  }

}
