export class BoundingBox {

  private _minX: number

  private _minY: number

  private _maxX: number

  private _maxY: number

  constructor(minX: number, minY: number, maxX: number, maxY: number) {
    this._minX = minX
    this._minY = minY
    this._maxX = maxX
    this._maxY = maxY
  }

  get minX(): number {
    return this._minX
  }

  get minY(): number {
    return this._minY
  }

  get maxX(): number {
    return this._maxX
  }

  get maxY(): number {
    return this._maxY
  }
}
