import {Vector2} from "../engine/Vector2"
import {MapView} from "../map/MapView"
import {MapUtils} from "../util/MapUtils"
import {MapEvent} from "../public/MapEvent"
import {ScrollZoomHandler} from "./ScrollZoomHandler"
import {DragRotateHandler} from "./DragRotateHandler"
import {DragPanHandler} from "./DragPanHandler"
import {MathUtils} from "../engine/MathUtils"

/**
 * the mouse state state
 */
export interface MouseState {
  dx?:number,
  dy?:number,
  x?: number,
  y?: number,
  action?: MouseAction,
  button?: MouseButton
}

/**
 * list of actions
 */
export enum MouseAction {
  Down,
  Up,
  Drag,
  DragEnd,
  Wheel,
  Move,
  Over,
  Out,
  Double,
  None
}

export enum MouseButton {
  Left = 0,
  Wheel = 1,
  Right = 2,
  None = 3
}

/**
 * the event handler for the map
 */
export class MouseEventHandler {

  static readonly TIME_DRAG_DETECT = 300.0

  static readonly MAX_TOUCH_POINTS = 2.0

  static readonly STEP_DETECT_ZOOM = 100.0

  static readonly TIME_DETECT_DOUBLE_TOUCH = 300.0

  /**
   * Use to check when start drag
   */
  checkDragStart: boolean = true

  /**
   * the mouse position
   */
  mousePosition: Vector2;

  /**
   * the mouse down position
   */
  mouseDownPosition: Vector2;

  /**
   * the mouse down time
   */
  mouseDownTime: number;

  /**
   * the mouse down time
   */
  mouseUpTime: number;

  /**
   * Mouse down
   */
  preMouseDown: MouseEvent;

  mouseOverTimeoutHandler: number

  // milisecond
  mouseOverTimeout = 250

  longClickTimeoutHandler: number

  // milisecond
  longClickTimeout = 500

  mouseOutside: boolean

  scrollZoomHandler: ScrollZoomHandler

  dragRotateHandler: DragRotateHandler

  dragPanHandler: DragPanHandler

  longClickIsCalled = false

  private _isHandling: boolean

  /**
   * Implement touch
   */
  private lastTimeTouch: number = 0.0

  private currentTouchZoom: number = 0.0

  private touchPrevDistance: number

  private touchStartAngle: number

  private anchorZoom: Vector2

  private touchEvent: any

  private isMultiTouch: boolean

  private isMultiTouchZoom: boolean

  private isMultiTouchTilt: boolean

  private touchX1Prev: number

  private touchY1Prev: number

  private touchX2Prev: number

  private touchY2Prev: number

  private inertia: Array<[number, number]> = []

  /**
   * create a mouse handle
   * @param map
   */
  constructor(private canvas: HTMLCanvasElement, private mapView: MapView) {
    canvas.addEventListener("mousedown", this.mouseDown.bind(this))
    canvas.addEventListener("mousemove", this.mouseMove.bind(this))
    canvas.addEventListener("mouseup", this.mouseUp.bind(this))
    canvas.addEventListener("wheel", this.mouseWheel.bind(this))
    canvas.addEventListener("mouseover", this.mouseOver.bind(this))
    canvas.addEventListener("mouseout", this.mouseOut.bind(this))
    canvas.addEventListener("dblclick", this.doubleClick.bind(this))
    canvas.addEventListener("contextmenu", event => event.preventDefault())

    canvas.addEventListener("touchstart", this.touchStart.bind(this), false)
    canvas.addEventListener("touchmove", this.touchMove.bind(this), false)
    canvas.addEventListener("touchcancel", this.touchCancel.bind(this), false)
    canvas.addEventListener("touchend", this.touchEnd.bind(this), false)

    this.scrollZoomHandler = new ScrollZoomHandler(this.mapView)
    this.dragRotateHandler = new DragRotateHandler(this.mapView)
    this.dragPanHandler = new DragPanHandler(this.mapView)
    this._isHandling = false
  }

  private touchStart(touches: TouchEvent) {
    touches.preventDefault()
    let mouseEvent: any = touches.touches.item(0)
    this.touchEvent = mouseEvent
    if (mouseEvent != null) {
      if (touches.touches.length < MouseEventHandler.MAX_TOUCH_POINTS) {
        mouseEvent.button = MouseButton.Left
        let currentTimeTouch = Date.now()
        let deltaTimeTouch = currentTimeTouch - this.lastTimeTouch
        this.lastTimeTouch = currentTimeTouch
        if (deltaTimeTouch < MouseEventHandler.TIME_DETECT_DOUBLE_TOUCH) {
          this.doubleClick(mouseEvent)
          return
        }
        this.handleGestureStop()
      }
      else {
        let secondMouseEvent: any = touches.touches.item(1)
        let posA = this.mapView.getCurrentMousePosition(mouseEvent)
        let posB = this.mapView.getCurrentMousePosition(secondMouseEvent)
        this.handleGestureStart(posA.x, posA.y, posB.x, posB.y)
        mouseEvent.button = MouseButton.Right
      }
      this.mouseDown(mouseEvent)
    }
  }

  private touchMove(touches: TouchEvent) {
    touches.preventDefault()
    let mouseEvent: any = touches.touches.item(0)
    this.touchEvent = mouseEvent
    if (mouseEvent != null) {
      if (touches.touches.length < MouseEventHandler.MAX_TOUCH_POINTS && !this.isMultiTouch) {
        mouseEvent.button = MouseButton.Left
        this.mouseMove(mouseEvent)
      }
      else if (touches.touches.length >= MouseEventHandler.MAX_TOUCH_POINTS) {
        let secondMouseEvent: any = touches.touches.item(1)
        let posA = this.mapView.getCurrentMousePosition(mouseEvent)
        let posB = this.mapView.getCurrentMousePosition(secondMouseEvent)
        this.handleGesture(posA.x, posA.y, posB.x, posB.y)
      }
    }
  }

  private touchCancel(touches: TouchEvent) {
    console.log("touch cancel")
  }

  private touchEnd(touches: TouchEvent) {
    touches.preventDefault()
    if (touches.touches.length == 0) {
      let button: number
      if (this.isMultiTouch) {
        button = MouseButton.Right
        this.handleGestureStop()
      }
      else {
        button = MouseButton.Left
      }
      let touchPos = new Vector2(this.touchEvent.clientX, this.touchEvent.clientY)
      let mouseEvent: any = new MouseEvent("mouseup", {
        view: window,
        bubbles: true,
        cancelable: true,
        clientX: touchPos.x,
        clientY: touchPos.y,
        button: button
      })
      this.mouseUp(mouseEvent)
    }
  }

  /**
   * double click
   * @param event
   */
  doubleClick(event: MouseEvent) {
    this.longClickIsCalled = false
    let position = this.mapView.getCurrentMousePosition(event)
    if (!this.mapView.mapsEventManager.onDoubleClickEvent(position, event)) {
      this.mapView.mapsEventManager.notify(MapEvent.dblClick, event)
      this.dispatch({
        dx: 0,
        dy: 0,
        x: position.x,
        y: position.y,
        action: MouseAction.Double,
        button: event.button
      })
    }
  }

  mouseOut(event: MouseEvent){
    this.mouseOutside = true
    this.longClickIsCalled = false
    clearTimeout(this.longClickTimeoutHandler)
    clearTimeout(this.mouseOverTimeoutHandler)
    let position = this.mapView.getCurrentMousePosition(event)
    this.mapView.mapsEventManager.notify(MapEvent.mouseOut, event)
    this.dispatch({
      dx: 0,
      dy: 0,
      x: position.x,
      y: position.y,
      action: MouseAction.Out,
      button: event.button
    })

    this.mousePosition = null
  }

  /**
   * the mouse wheel event
   * @param event
   */
  mouseWheel(event: WheelEvent) {
    this.longClickIsCalled = false
    if (event.deltaY == 0) {
      return
    }
    let position = this.mapView.getCurrentMousePosition(event)
    this.dispatch({
      dx: event.deltaX,
      dy: event.deltaMode === 0x01 /* DOM_DELTA_LINE */ ? event.deltaY * 40 : event.deltaY,
      x: position.x,
      y: position.y,
      action: MouseAction.Wheel,
      button: event.button
    }, event)

  }

  /**
   * the mouse down event
   * @param event
   */
  mouseDown(event: MouseEvent) {

    if (window.getSelection) {

      // Chrome
      if (window.getSelection().empty) {
        window.getSelection().empty()
      }

      // Firefox
      else if (window.getSelection().removeAllRanges) {
        window.getSelection().removeAllRanges()
      }
    }
    this.longClickIsCalled = false
    this.mapView.stopAnimation()
    this.mousePosition = this.mapView.getCurrentMousePosition(event)
    this.mouseDownPosition = this.mousePosition
    this.mouseDownTime = Date.now()
    if (event.button == MouseButton.Right) {
      this.dragRotateHandler.onDragStart(this.mousePosition)
    }
    else {
      this.dragPanHandler.onDragStart(this.mousePosition)
    }

    this.dispatch({
      x: this.mousePosition.x,
      y: this.mousePosition.y,
      dx: 0,
      dy: 0,
      action: MouseAction.Down,
      button: event.button
    })

    if (this.mapView.mapState.isStill()){
      clearTimeout(this.longClickTimeoutHandler)
      this.longClickTimeoutHandler = setTimeout(() => {
        if (event.button == MouseButton.Left) {
          if (!this.mapView.mapsEventManager.onLongClickEvent(this.mousePosition, event)) {
            this.longClickIsCalled = true
            this.mapView.mapsEventManager.notify(MapEvent.longClick, event)
          }
        }

      }, this.longClickTimeout)
    }
    this.preMouseDown = event
    return false
  }

  /**
   * mouse move
   * @param event
   */
  mouseMove(event: MouseEvent) {
    this.longClickIsCalled = false
    clearTimeout(this.longClickTimeoutHandler)
    let newPosition = this.mapView.getCurrentMousePosition(event)
    let button = this.preMouseDown != null ? this.preMouseDown.button: event.button
    if (this.mousePosition) {
      let delta = newPosition.subtract(this.mousePosition)
      this.mousePosition = newPosition
      if (delta.x == 0 && delta.y == 0){
        return
      }
      let mouseState = {
        x: newPosition.x,
        y: newPosition.y,
        dx: delta.x,
        dy: delta.y,
        action: MouseAction.Drag,
        button: button
      }
      if (button == MouseButton.Left) {
        this.dragPanHandler.onDrag(mouseState)
      }
      let draggable = this.dragPanHandler.draggableAnnotation || this.dragPanHandler.draggableObject
      if (this.checkDragStart) {
        if (draggable) {
          this.mapView.mapsEventManager.onDragEvent(MapEvent.dragStart, event, draggable)
        }
        else {
          this.mapView.mapsEventManager.notify(MapEvent.dragStart, event)
        }
        this.checkDragStart = false
      }
      if (draggable) {
        this.mapView.mapsEventManager.onDragEvent(MapEvent.drag, event, draggable)
      }
      else {
        this.mapView.mapsEventManager.notify(MapEvent.drag, event)
      }
      this.dispatch(mouseState)
    }
    else {
      if (this.mapView.mapState.isStill()){
        clearTimeout(this.mouseOverTimeoutHandler)
        this.mouseOverTimeoutHandler = setTimeout(() => {
          let touchPoint  = this.mapView.getCurrentMousePosition(event)
          if (!this.mapView.mapsEventManager.onHoverEvent(touchPoint, event)) {
            this.mapView.mapsEventManager.notify(MapEvent.hover, event, false)
          }
        }, this.mouseOverTimeout)
      }

      this.mapView.mapsEventManager.notify(MapEvent.mouseMove, event)
      this.dispatch({
        x: newPosition.x,
        y: newPosition.y,
        dx: 0,
        dy: 0,
        action: MouseAction.Move,
        button: button
      })
    }
    return false
  }

  /**
   * mouse over
   * @param event
   */
  mouseOver(event: MouseEvent) {
    this.mouseOutside = false
    this.longClickIsCalled = false
    let newPosition = this.mapView.getCurrentMousePosition(event)
    this.mapView.mapsEventManager.notify(MapEvent.mouseOver, event)
    this.dispatch({
      x: newPosition.x,
      y: newPosition.y,
      dx: 0,
      dy: 0,
      action: MouseAction.Over,
      button: event.button
    })
  }

  /**
   * mouse up event handler
   */
  mouseUp(event: MouseEvent) {
    clearTimeout(this.longClickTimeoutHandler)
    let newPosition = this.mapView.getCurrentMousePosition(event)
    this.mouseUpTime = Date.now()
    let delta = newPosition.subtract(this.mouseDownPosition)
    let deltaTime = this.mouseUpTime - this.mouseDownTime
    if (delta.length < MapUtils.touchBoxSize) {
      this.dispatch({
        x: newPosition.x,
        y: newPosition.y,
        dx: 0,
        dy: 0,
        action: MouseAction.Up,
        button: event.button
      })

      if (this.mapView.mapState.isStill() && !this.longClickIsCalled){
        if (event.button == MouseButton.Left) {
          let touchPoint  = this.mapView.getCurrentMousePosition(event)
          if (!this.mapView.mapsEventManager.onClickEvent(touchPoint, event)) {
            this.mapView.mapsEventManager.notify(MapEvent.click, event)
          }
        }
        else if (event.button == MouseButton.Right) {
          let touchPoint  = this.mapView.getCurrentMousePosition(event)
          if (!this.mapView.mapsEventManager.onRightClickEvent(touchPoint, event)) {
            this.mapView.mapsEventManager.notify(MapEvent.rightClick, event)
          }
        }
      }
    }
    else {
      let draggable = this.dragPanHandler.draggableAnnotation || this.dragPanHandler.draggableObject
      if (draggable) {
        this.mapView.mapsEventManager.onDragEvent(MapEvent.dragEnd, event, draggable)
      }
      else {
        this.mapView.mapsEventManager.notify(MapEvent.dragEnd, event)
      }
      this.checkDragStart = true
      this.dispatch({
        x: newPosition.x,
        y: newPosition.y,
        dx: deltaTime < MouseEventHandler.TIME_DRAG_DETECT ? delta.x : 0,
        dy: deltaTime < MouseEventHandler.TIME_DRAG_DETECT ? delta.y : 0,
        action: MouseAction.DragEnd,
        button: event.button
      })
    }

    this.mousePosition = null
    this.preMouseDown = null
    this.longClickIsCalled = false
    return false
  }

  /**
   * dispatch an action
   * @param type
   * @param data
   */
  private dispatch(data?: MouseState, e?: WheelEvent) {
    this.mouseEventHandler(data, e)
  }

  /**
   * Handler all event from mouse
   * @param data
   */
  mouseEventHandler(data?: MouseState, e?: WheelEvent ) {

    switch (data.action) {
    case MouseAction.Drag:
      if (data.button == MouseButton.Right) {
        this.dragRotateHandler.onDrag(data)
      }
      this._isHandling = true
      break

    case MouseAction.Wheel:
      this.scrollZoomHandler.onWheel(data, e)
      this._isHandling = true
      break

    case MouseAction.DragEnd:

      if (data.button == MouseButton.Left) {
        this.dragPanHandler.onDragEnd(data)
      }
      if (data.button == MouseButton.Right) {
        this.dragRotateHandler.onDragEnd()
      }
      this._isHandling = false
      break

    case MouseAction.Double:

      if (data.button == MouseButton.Left) {
        let zoom = this.mapView.mapState.getZoom() + 1
        let anchor = new Vector2(data.x, data.y)
        this.mapView.setZoom(zoom, anchor, 0, true)
      }
      this._isHandling = true
      break

    default:
      this._isHandling = false
      break
    }

  }

  get isHandling(): boolean {
    return this._isHandling
  }

  handleGestureStart(x1: number, y1: number, x2: number, y2: number): void {

    /** calculate distance and angle between fingers */
    let dx = x2 - x1
    let dy = y2 - y1
    this.inertia = [[Date.now(), this.mapView.mapState.getBearing()]]
    this.touchX1Prev = x1
    this.touchY1Prev = y1
    this.touchX2Prev = x2
    this.touchY2Prev = y2
    this.anchorZoom = new Vector2((x1 + x2) / 2.0, (y1 + y2) / 2.0)
    this.touchPrevDistance = Math.sqrt(dx * dx + dy * dy)
    this.touchStartAngle = Math.atan2(dy, dx)
    this.isMultiTouch = true
    this.isMultiTouchZoom = false
    this.isMultiTouchTilt = false
  }

  /**
   * Handle Gesture to detect user Zoom or Rotate Map. Return TRUE when user ZOOM, FALSE when user ROTATE
   * @param mouseEvent
   * @param x1
   * @param y1
   * @param x2
   * @param y2
   */
  handleGesture(x1: number, y1: number, x2: number, y2: number): void {

    /** calculate distance and angle between fingers */
    let dx = x2 - x1
    let dy = y2 - y1
    let touchDistance = Math.sqrt(dx * dx + dy * dy)

    let deltaX1 = x1 - this.touchX1Prev
    let deltaY1 = y1 - this.touchY1Prev
    let deltaX2 = x2 - this.touchX2Prev
    let deltaY2 = y2 - this.touchY2Prev
    this.touchX1Prev = x1
    this.touchY1Prev = y1
    this.touchX2Prev = x2
    this.touchY2Prev = y2

    if (deltaX1 * deltaX2 >= 0.0 && deltaY1 * deltaY2 > 0.0) {
      let tiltChange = Math.max(deltaY1, deltaY2) / MapUtils.pixelScale
      this.mapView.setTilt(this.mapView.mapState.cameraPosition.getTilt() - tiltChange, true)
      this.isMultiTouchTilt = true
    }

    /** calculate the difference between current touch values and the start values */
    this.currentTouchZoom = (touchDistance - this.touchPrevDistance) / MapUtils.pixelScale

    if (Math.abs(this.currentTouchZoom) > MouseEventHandler.STEP_DETECT_ZOOM) {
      this.inertia = [[Date.now(), this.mapView.mapState.getBearing()]]
      let zoomStep = 1.0
      if (this.currentTouchZoom < 0) {
        zoomStep = -1.0
      }
      this.mapView.setZoom(this.mapView.mapState.getZoom() + zoomStep, this.anchorZoom, 200)
      this.mapView.notifyCameraIsChanging()
      this.touchPrevDistance = touchDistance
      this.isMultiTouchZoom = true
      return
    }

    let currentTouchAngle = Math.atan2(dy, dx)
    let angleChange = currentTouchAngle - this.touchStartAngle
    if (Math.abs(angleChange) > (Math.PI / 180.0) && !this.isMultiTouchZoom && !this.isMultiTouchTilt) {
      let currentBearing = this.mapView.mapState.getBearing() - (angleChange * 180.0 / Math.PI)
      this.inertia.push([Date.now(), -currentBearing])
      this.mapView.setBearing(currentBearing, 0, true)
      this.mapView.notifyCameraIsChanging()
      this.touchStartAngle = currentTouchAngle
    }
  }

  handleGestureStop(): void {
    this.isMultiTouch = false
    this.isMultiTouchZoom = false
    this.isMultiTouchTilt = false
    this.currentTouchZoom = 0.0
    const inertia = this.inertia, now = Date.now(), cutoff = 160  /* msec */
    while (inertia.length > 0 && now - inertia[0][0] > cutoff) {
      inertia.shift()
    }
    if (this.inertia.length > 2) {
      let length = inertia.length
      const last = inertia[length - 1], previous = inertia[length - 2]
      let  direction = last[1] - previous[1] > 0 ? 1 : -1
      const bearingDiff = last[1] - previous[1]
      let offset = Math.abs(bearingDiff) * 3.0
      if (Math.abs(offset) > 360) {
        offset = 360
      }
      offset *= direction
      let duration = Math.abs(offset) / 90.0
      duration = MathUtils.clamp(duration, 0.5, 1.0)
      this.mapView.setBearing(this.mapView.mapState.getBearing() - offset, duration, true)
    }
  }
}
