import {Map4D} from "./Map"

export type LayerOverlayId = number

export enum OverlayType {
  Tile = 0,
  Building = 1,
  POI = 2,
}

export class LayerOverlay {

  protected id: LayerOverlayId

  protected _getUrl: Function

  protected minZoom: number

  protected maxZoom: number

  protected type: OverlayType

  protected map: Map4D

  constructor() {
    this.id = null
    this.map = null
  }

  getId(): LayerOverlayId {
    return this.id
  }

  getType(): OverlayType {
    return this.type
  }

  getMinZoom(): number {
    return this.minZoom
  }

  getMaxZoom(): number {
    return this.maxZoom
  }

  setMinZoom(minZoom: number): void {
    if (minZoom == null) {
      console.warn("Parameter minZoom can not be null")
      return
    }
    if (this.minZoom == minZoom) {
      return
    }
    this.minZoom = minZoom
    if (this.map != null) {
      this.map.mapView.reload()
    }
  }

  setMaxZoom(maxZoom: number): void {
    if (maxZoom == null) {
      console.warn("Parameter maxZoom can not be null")
      return
    }
    if (this.maxZoom == maxZoom) {
      return
    }
    this.maxZoom = maxZoom
    if (this.map != null) {
      this.map.mapView.reload()
    }
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeLayerOverlay(this.id)
      this.id = -1
    }
    this.map = map
    if (this.map != null) {
      this.id = this.map.mapView.addLayerOverlay(this)
    }
  }
}
