import {MapUtils} from "../util/MapUtils"
import {Point} from "../public/Point"
import {MapState} from "../map/MapState"
import {GeometryUtils} from "../util/GeometryUtils"
import {MapConstants} from "../map/MapConstants"
import {SymbolAnnotationData} from "./SymbolAnnotationData"
import {LatLng} from "../public/LatLng"

export class InfoWindow {

  private content: HTMLElement

  private windowAnchor: Point

  constructor(content: HTMLElement, windowAnchor: Point) {
    this.content = content
    this.windowAnchor = windowAnchor
  }

  getContent(): HTMLElement {
    return this.content
  }

  getPosition(symbolPosition: LatLng, symbolElevation: number,
    symbolWidth: number, symbolHeight: number, symbolAnchor: Point, mapState: MapState): Point {
    if (this.content == null) {
      return null
    }

    symbolElevation = mapState.is3dMode() ? symbolElevation : 0.0
    let position = GeometryUtils.latLngToScreenCoordinate(symbolPosition, symbolElevation, mapState.cameraPosition, mapState.getScreenSize())
    position.x /= MapUtils.pixelScale
    position.y /= MapUtils.pixelScale
    position.x -= this.content.offsetWidth / 2
    position.y -= this.content.offsetHeight
    let width = symbolWidth / MapUtils.pixelScale
    let height = symbolHeight / MapUtils.pixelScale

    let archon = this.windowAnchor || new Point(0.5, 0.5)

    position.x += (0.5 - symbolAnchor.x) * width
      - (0.5 - archon.x) * width
    position.y += (0.5 - symbolAnchor.y) * height
      - (0.5 - archon.y) * height - MapConstants.infoWindowPadding

    return new Point(position.x, position.y)
  }

  getPositionBySymbolAnnotationData(symbolAnnotationData: SymbolAnnotationData, mapState: MapState): Point {
    let symbolAnnotation = symbolAnnotationData.symbolAnnotation
    return this.getPosition(symbolAnnotation.getPosition(), symbolAnnotation.getElevation(),
      symbolAnnotationData.width, symbolAnnotationData.height, symbolAnnotation.getAnchor(), mapState)
  }

  setContent(content: HTMLElement): void {
    this.content = content
  }
}
