export class MapView extends CameraAnimationObserver implements MapModeChangedObserver{

  private static idContextCounting: number = 0

  gl: WebGLRenderingContext = null

  width: number

  height: number

  shader: Shader

  mapState: MapState

  tileManager: TileManager

  httpTaskManager: TaskManager

  mapSettings: MapSettings

  mapObserver: MapObserver

  mapRenderer: MapRenderer

  layers: Map<string, Layer>

  cameraAnimation: CameraAnimation

  _annotationManager: AnnotationManager

  tileAreaManager: TileAreaManager

  layerOverlayManager: LayerOverlayManager

  buildingResourceManager: BuildingResourceManager

  buildingLayer: BuildingLayer

  vectorPointLayer: SymbolLayer

  userVectorPointLayer: SymbolLayer

  overlayVectorPointLayer: SymbolLayer

  dirty: boolean

  _hasPOIOverlay: boolean

  mapsEventManager: MapsEventManager

  skyObject: SkyObject

  skyRenderer: SkyRenderer

  frameBuffer: FrameBuffer

  timeEffectManager: TimeEffectManager

  private weatherManager: WeatherManager

  private effectManager: EffectManager

  private disableShadowMapByTime: boolean = false

  private geolocateControl: GeolocateControl

  private zoomInButton: HTMLElement

  private zoomOutButton: HTMLElement

  private modeButton: HTMLElement

  private bearingButton: HTMLElement

  private compassImage: HTMLElement

  private canvas: HTMLCanvasElement

  private mapContainer: HTMLElement

  private container: HTMLElement

  private mouseEventHandler: MouseEventHandler

  private options: MapOptions

  private renderTaskQueue: TaskQueue

  private buildingIconSelectionManager: BuildingIconSelectionManager

  private customBuildingManager: CustomBuildingManager

  private geoJsonManager: GeoJsonManager

  private placement: Placement

  private featureManager: FeatureManager

  readonly defaultMapOptions = {

    center: [-1, -1],

    zoom: 12,

    minZoom: MapConstants.defaultMinZoom,

    maxZoom: MapConstants.defaultMaxZoom,

    maxNativeZoom: MapConstants.defaultMaxNativeZoom,

    geolocate: false,

    controls: false,

    controlOptions: ControlOptions.BOTTOM_RIGHT,

    tilt: 0,

    bearing: 0,

    switchMode: SwitchMode.Manual
  }

  readonly defaultAnimationOptions = {

    duration: null,

    easing: null,

    animate: false
  }

  readonly defaultPaddingOptions = {

    top: 0,

    bottom: 0,

    left: 0,

    right: 0
  }

  constructor(public clientContainer: HTMLElement, options?: MapOptions) {
    super()
    let optionDefault: any = {}
    MapUtils.extendOptions(optionDefault, this.defaultMapOptions, options)
    this.options = optionDefault

    this.container = DOMUtils.create("div", "mf-map4d", this.clientContainer)

    this.createStyleNode()
    this.init()
    this.createMapControlNode(this.container, this.options)
    this.createLogoNode(this.container)
  }

  private init() {
    this.setupMapViews(this.container, this.options)
    this.setupComponents(this.options)

    this.renderTaskQueue = new TaskQueue()
    this.loop()
  }

  requestRenderFrame(callback: () => void): TaskID {
    this.dirty = true
    return this.renderTaskQueue.add(callback)
  }

  cancelRenderFrame(id: TaskID) {
    this.renderTaskQueue.remove(id)
  }

  private loop(): void {
    let startTime = Date.now()
    this.resize()
    if (this.dirty) {
      this.dirty = false
      this.update()
      this.render()
    }
    if (this.weatherManager.dirty() ||
      this.timeEffectManager.dirty() ||
      this.mapState.dirty() ||
      (this.effectManager.dirty() && this.mapState.is3dMode())) {
      this.dirty = true
    }
    let timeLimit = 1000.0 / MapConstants.FPS
    let endTime = Date.now()
    let deltaTime = endTime - startTime
    let sleepTime = timeLimit - deltaTime
    if (sleepTime > 0 && !this.mouseEventHandler.isHandling) {
      setTimeout(() => {
        requestAnimationFrame(this.loop.bind(this))
      }, sleepTime)
    }
    else {
      requestAnimationFrame(this.loop.bind(this))
    }
  }

  private update() {

    // Camera animation must be updated first otherwise shapes will be shake
    this.cameraAnimation.updateTransitions()
    this.annotationManager.update()
    this.geoJsonManager.update()
    this.layers.forEach(layer => {
      layer.update(this.mapState)
    })

    if (this.mapSettings.is3dMode()) {
      this.weatherManager.update()
      if (this.mapState.getTilt() > MapConstants.maxTiltIntersectWithScreen) {
        this.skyObject.updateMatrices(this.mapState)
      }
    }

    if (this.effectManager.enableShadowEffect
      && !this.disableShadowMapByTime
      && (this.timeEffectManager.getTimeEffect() == TimeEffect.Evening
        || this.timeEffectManager.isEveningTime())) {
      this.disableShadowMapByTime = true
      this.effectManager.enableShadowEffect = false
    }
    else if (this.disableShadowMapByTime
      && this.timeEffectManager.getTimeEffect() != TimeEffect.Evening
      && !this.timeEffectManager.isEveningTime()) {
      this.disableShadowMapByTime = false
      this.effectManager.enableShadowEffect = true
    }

    if (this.compassImage) {
      this.compassImage.style.transform = `rotate(${this.mapState.getBearing()}deg)`
    }
  }

  private hasTouch() {
    return (('ontouchstart' in window)       // html5 browsers
      || (navigator.maxTouchPoints > 0)      // future IE
      || (navigator.msMaxTouchPoints > 0))   // current IE10
  }

  private resize(): void {
    if (this.canvas == null) {
      return
    }

    MapUtils.pixelScale = window.devicePixelRatio || 1
    let width = this.canvas.offsetWidth * MapUtils.pixelScale
    let height = this.canvas.offsetHeight * MapUtils.pixelScale

    if (this.width == width && this.height == height) {
      return
    }

    this.width = width
    this.height = height
    this.canvas.width = this.width
    this.canvas.height = this.height
    this.gl.viewport(0, 0, this.width, this.height)
    this.mapState.setScreenSize(this.width, this.height)
    this.weatherManager.resize(width, height)
    this.effectManager.resize(width, height)

    if (this.frameBuffer != null){
      this.frameBuffer.destroy()
      this.frameBuffer = null
    }
    this.frameBuffer = new FrameBuffer(this.gl, this.width, this.height)
    this.tileManager.updateVisibleTiles()
    this.buildingLayer.setDirty()
    this.mapsEventManager.notify(MapEvent.boundsChanged)
    this.placement.updatePlacement()
    this.dirty = true
  }

  private render() {
    let gl = this.gl
    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT | gl.STENCIL_BUFFER_BIT)
    this.mapRenderer.render()
    this.renderTaskQueue.run()
    if (this.mapSettings.is3dMode()) {
      gl.enable(gl.BLEND)
      gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA)
      this.buildingIconSelectionManager.render(this.mapState)
      this.weatherManager.render(this.mapState)
      gl.disable(gl.BLEND)
    }
  }

  private renderSelection(){
    let gl = this.gl
    gl.clearColor(1.0, 1.0, 1.0, 1.0)
    gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT)
    this.mapRenderer.renderSelection()
    let backgroundColor = Color.fromHex(this.mapState.is3dMode() ? MapConstants.mapBackgroundColor3D :
      MapConstants.mapBackgroundColor2D)
    gl.clearColor(backgroundColor.red, backgroundColor.green, backgroundColor.blue, 1.0)
  }

  private getSelectionCode(touchBox: Box): number {
    if (this.frameBuffer != null) {
      this.frameBuffer.bind()
      this.renderSelection()

      let boxSize = MapUtils.touchBoxSize
      let lengthColor = 4 * boxSize * boxSize
      let colors = new Uint8Array(lengthColor).fill(0)
      this.gl.readPixels(touchBox.min.x, this.height - touchBox.min.y - boxSize, boxSize, boxSize, this.gl.RGBA, this.gl.UNSIGNED_BYTE, colors)
      this.frameBuffer.unbind()

      let listColors = new Map<number, number>()
      let tempColorCode: number = 0

      for (let i = 0; i < lengthColor; i += 4) {
        let red = colors[i]
        let green = colors[i + 1]
        let blue = colors[i + 2]
        let colorCode = (red << 16) | (green << 8) | blue
        let currentColor = listColors.get(colorCode) ? listColors.get(colorCode) : 0
        listColors.set(colorCode, currentColor + 1)
        tempColorCode = colorCode
      }

      listColors.forEach((value, key) => {
        if (value > listColors.get(tempColorCode)) {
          tempColorCode = key
        }
      })
      return tempColorCode
    }
    return -1
  }

  createLayer(layerId: string, layerType: LayerType, order: number): Layer {
    if (this.layers.has(layerId)) {
      console.log("Cannot create layer which layer id already exist")
      return null
    }
    let layer = LayerFactory.createLayer(layerId, layerType, this.tileManager, order)
    if (layer != null) {
      this.mapRenderer.addLayer(layer)
      this.layers.set(layerId, layer)
      return layer
    }
    return null
  }

  removeLayer(layerId: string, layerOrder: number): void {
    this.mapRenderer.removeLayer(layerId, layerOrder)
    this.layers.delete(layerId)
  }

  static createCameraAnimationOptions(animationOptions: AnimationOptions) : CameraAnimationOptions {
    let cameraAnimationOptions = new CameraAnimationOptions()
    if (animationOptions.animate) {
      if (animationOptions.duration != null && animationOptions.duration < 0) {
        throw new Error("Animation duration must be greater or equal to 0")
      }

      cameraAnimationOptions.easing = animationOptions.easing
    }
    cameraAnimationOptions.duration = animationOptions.duration
    cameraAnimationOptions.gesture = false
    return cameraAnimationOptions
  }

  panBy(offset: Point, animationOptions?: AnimationOptions): void {
    animationOptions = MapUtils.extendOptions({}, this.defaultAnimationOptions, animationOptions)
    this.cameraAnimation.moveBy(offset, MapView.createCameraAnimationOptions(animationOptions))

  }

  panTo(dest: LatLng, animationOptions?: AnimationOptions): void {
    animationOptions = MapUtils.extendOptions({}, this.defaultAnimationOptions, animationOptions)
    if (animationOptions.duration == null) {

      // Set Time Duration
      animationOptions.duration = MapUtils.getTimePanTo(this.mapState.getTarget(), dest, this.mapState.getZoom())
    }
    this.cameraAnimation.moveTo(
      {target: dest, zoom: this.mapState.getZoom()}, MapView.createCameraAnimationOptions(animationOptions))
  }

  moveCamera(position: ICameraPosition, animationOptions?: AnimationOptions): void {
    let cameraPosition = CameraPosition.convert(position)

    let cameraOptions = new CameraOptions()

    if (cameraPosition.getZoom() != null) {
      cameraOptions.zoom = MathUtils.clamp(
        cameraPosition.getZoom(), this.mapSettings.getMinZoom(), this.mapSettings.getMaxZoom())
    }
    else {
      cameraOptions.zoom = this.mapState.getZoom()
    }

    if (cameraPosition.getTilt() != null) {
      let zoom = cameraOptions.zoom || this.mapState.getZoom()
      let maxTilt = zoom < MapConstants.defaultMinZoom3d ? MapConstants.maxTilt2D : this.mapSettings.getPreferMaxTilt()
      cameraOptions.tilt = MathUtils.clamp(cameraPosition.getTilt(), this.mapSettings.getMinTilt(), maxTilt)
    }
    else {

      //default 3d tilt is MapConstants.maxTilt2D, default 2d tilt is 0
      let zoom = cameraOptions.zoom || this.mapState.getZoom()
      cameraOptions.tilt = this.mapSettings.getPreferMaxTilt() == MapConstants.maxTilt2D
      || zoom < MapConstants.defaultMinZoom3d ? 0: MapConstants.maxTilt2D
    }
    let bearing = cameraPosition.getBearing()
    cameraOptions.bearing = bearing != null ? bearing : this.mapState.getBearing()
    cameraOptions.target = cameraPosition.getTarget() || this.mapState.getTarget()

    if (animationOptions == null || !animationOptions.animate) {
      this.cameraAnimation.moveTo(cameraOptions)
      return
    }

    let target = this.latLngToScreenCoordinate(cameraOptions.target)
    let center = new Vector2(this.mapState.screenWidth / 2.0, this.mapState.screenHeight / 2.0)
    let distance = MathUtils.distance(target, center)

    animationOptions = MapUtils.extendOptions({}, this.defaultAnimationOptions, animationOptions)
    if (distance < center.x) {
      if (animationOptions.duration == null) {
        animationOptions.duration = MapUtils.getTimePanTo(this.mapState.getTarget(), cameraOptions.target, this.mapState.getZoom())
      }
      this.cameraAnimation.moveTo(cameraOptions, MapView.createCameraAnimationOptions(animationOptions))
    }
    else {
      this.cameraAnimation.flyTo(cameraOptions, MapView.createCameraAnimationOptions(animationOptions))
    }
  }

  fitBounds(bounds: LatLngBounds, padding?: PaddingOptions, animationOptions?: AnimationOptions): void {
    let cameraPosition = this.getCameraPositionForLatLngBounds(
      bounds, padding)
    this.moveCamera(cameraPosition, animationOptions)
  }

  moveBy(offset: Point) {
    this.cameraAnimation.moveBy(offset, null, true)
  }

  moveByVelocity(velocity: Vector2): void {
    /** Fling animation should check whether the map is panning / zooming ? */
    if (!this.mapState.isPanning() && !this.mapState.isZoomIn() && !this.mapState.isZoomOut()) {
      this.cameraAnimation.moveByVelocity(velocity)
    }
  }

  getCameraPositionForLatLngBounds(bounds : LatLngBounds, padding?: PaddingOptions): CameraPosition {
    padding = MapUtils.extendOptions({}, this.defaultPaddingOptions, padding)
    if (padding.top < 0 || padding.bottom < 0 || padding.left < 0 || padding.right < 0) {
      throw new Error("Padding value must be greater or equal to 0")
    }
    let ppadding = {
      top: padding.top * MapUtils.pixelScale,
      bottom: padding.bottom * MapUtils.pixelScale,
      left: padding.left * MapUtils.pixelScale,
      right: padding.right * MapUtils.pixelScale
    }
    return MapUtils.getCameraPositionForLatLngBounds(bounds, ppadding, this.mapState, this.mapSettings)
  }

  setZoom(zoom: number, anchor: Vector2, duration: number = 0, gesture: boolean = false): void {
    let cameraAnimationOptions = new CameraAnimationOptions()
    cameraAnimationOptions.duration = duration
    cameraAnimationOptions.gesture = gesture
    zoom = MathUtils.clamp(zoom, this.mapSettings.getPreferMinZoom(), this.mapSettings.getMaxZoom())
    this.cameraAnimation.zoom(zoom, anchor, cameraAnimationOptions)
  }

  setTilt(degrees: number, gesture: boolean = false): void {
    this.cameraAnimation.setTilt(degrees, gesture)
  }

  setBearing(degrees: number, duration = 0, gesture: boolean = false): void {
    this.cameraAnimation.setBearing( degrees, duration, gesture)
  }

  stopAnimation(): void {
    this.cameraAnimation.stopAnimation()
  }

  setTileUrl(mapUrl: string, is3dMode: boolean): void {
    if (this.mapSettings.getTileUrl(is3dMode) === mapUrl) {
      return
    }

    this.mapSettings.setTileUrl(mapUrl, is3dMode)

    if (is3dMode && this.mapState.is3dMode()) {
      this.buildingResourceManager.removeAllResources()
      this.buildingLayer.clear()
    }
    this.tileManager.reload()
  }

  getTileUrl(is3dMode: boolean) {
    return this.mapSettings.getTileUrl(is3dMode)
  }

  setWeather(weather: Weather): void {
    this.weatherManager.setWeather(weather)
    this.dirty = true
  }

  getWeather(): Weather {
    return this.weatherManager.getWeather()
  }

  enable3dMode(enabled: boolean): void {

    this.mapState.setForce2d(!enabled)

    if (this.mapState.enable3dMode(enabled)) {
      if (this.mapState.getZoom() >= MapConstants.defaultMinZoom3d) {
        this.tileManager.reload()
      }
      if (!enabled) {

        // 2D mode
        this.buildingResourceManager.removeAllResources()
        this.buildingLayer.clear()
      }
      else if (this.mapState.getZoom() < MapConstants.defaultMinZoom3d) {
        this.moveCamera({target: this.mapState.getTarget(), zoom: MapConstants.defaultMinZoom3d})
      }
      let backgroundColor = Color.fromHex(this.mapState.is3dMode() ? MapConstants.mapBackgroundColor3D :
        MapConstants.mapBackgroundColor2D)
      this.gl.clearColor(backgroundColor.red, backgroundColor.green, backgroundColor.blue, 1.0)
    }
  }

  getBounds(paddingOptions?: PaddingOptions): LatLngBounds {

    paddingOptions = MapUtils.extendOptions({}, this.defaultPaddingOptions, paddingOptions)
    if (paddingOptions.top < 0 || paddingOptions.bottom < 0 || paddingOptions.left < 0 || paddingOptions.right < 0) {
      throw new Error("Padding value must be greater or equal to 0")
    }
    paddingOptions = {
      top: paddingOptions.top * MapUtils.pixelScale,
      bottom: paddingOptions.bottom * MapUtils.pixelScale,
      left: paddingOptions.left * MapUtils.pixelScale,
      right: paddingOptions.right * MapUtils.pixelScale,
    }

    let screenSize = this.mapState.getScreenSize()

    if (!this.getCamera().getTarget() || !this.mapState.getZoom() || screenSize.x <= 0 || screenSize.y <= 0) {
      return null
    }

    /** Save last tilt to restore if tilt > maxTiltIntersectWithScreen */
    let lastTilt = this.mapState.getTilt()
    let needRestoreTilt = false
    if (lastTilt > MapConstants.maxTiltIntersectWithScreen) {
      this.mapState.setTilt(MapConstants.maxTiltIntersectWithScreen)
      needRestoreTilt = true
    }

    let cameraPosition = this.mapState.cameraPosition

    let northWest = GeometryUtils.screenCoordinateToLatLng(
      new Vector2(paddingOptions.left, paddingOptions.top), cameraPosition, screenSize).normalize()
    let northEast = GeometryUtils.screenCoordinateToLatLng(
      new Vector2(screenSize.x - paddingOptions.right, paddingOptions.top), cameraPosition, screenSize).normalize()

    /** Restore last tilt */
    if (needRestoreTilt) {
      this.mapState.setTilt(lastTilt)
    }

    let southEast = GeometryUtils.screenCoordinateToLatLng(
      new Vector2(screenSize.x - paddingOptions.right, screenSize.y - paddingOptions.bottom), cameraPosition, screenSize).normalize()
    let southWest = GeometryUtils.screenCoordinateToLatLng(
      new Vector2(paddingOptions.left, screenSize.y - paddingOptions.bottom), cameraPosition, screenSize).normalize()

    if (!southEast.valid() || !southWest.valid()) {
      //max bounds
      return LatLngBounds.world()
    }
    let latLngBounds = new LatLngBounds()
    latLngBounds.extend(northEast).extend(southWest).extend(northWest).extend(southEast)

    return latLngBounds
  }

  getCurrentMousePosition(mouseEvents: MouseEvent | Touch): Vector2 {
    return new Vector2((mouseEvents.clientX  - this.canvas.getBoundingClientRect().left) * MapUtils.pixelScale,
      (mouseEvents.clientY  - this.canvas.getBoundingClientRect().top) * MapUtils.pixelScale)
  }

  hideInfoWindow(annotationId?: AnnotationId): void {
    this.annotationManager.hideInfoWindow(annotationId)
  }

  isInfoWindowShown(annotationId: AnnotationId): boolean {
    return this.annotationManager.isInfoWindowShown(annotationId)
  }

  showInfoWindow(annotationId: AnnotationId): void {
    this.annotationManager.showInfoWindow(annotationId)
  }

  setMinZoom(minZoom: number): void {
    this.mapSettings.setMinZoom(minZoom)
  }

  setMaxZoom(maxZoom: number): void {
    this.mapSettings.setMaxZoom(maxZoom)
  }

  setDataLayerMinZoom(minZoom: number): void {
    this.dirty = true
    this.mapSettings.dataLayerMinZoom = minZoom
  }

  setDataLayerMaxZoom(maxZoom: number): void {
    this.dirty = true
    this.mapSettings.dataLayerMaxZoom = maxZoom
  }

  getDataLayerMinZoom(): number {
    return this.mapSettings.dataLayerMinZoom
  }

  getDataLayerMaxZoom(): number {
    return this.mapSettings.dataLayerMaxZoom
  }

  setMaxNativeZoom(maxNativeZoom: number) {
    maxNativeZoom = MathUtils.clamp(maxNativeZoom, MapConstants.defaultMinZoom, MapConstants.defaultMaxZoom)
    if (this.getMaxNativeZoom() == maxNativeZoom) {
      return
    }
    this.mapSettings.setMaxNativeZoom(maxNativeZoom)
    this.tileManager.reload()
    this.annotationManager.clearShapeIndexer()
  }

  getMaxNativeZoom(): number {
    return this.mapSettings.getMaxNativeZoom()
  }

  addTileArea(tileArea: TileArea): TileAreaId {
    return this.tileAreaManager.addTileArea(tileArea)
  }

  removeTileArea(tileAreaId: TileAreaId): void {
    this.tileAreaManager.removeTileArea(tileAreaId)
  }

  addLayerOverlay(layerOverlay: LayerOverlay): LayerOverlayId {
    return this.layerOverlayManager.addLayerOverlay(layerOverlay)
  }

  removeLayerOverlay(overlayId: LayerOverlayId): void {
    this.layerOverlayManager.removeLayerOverlay(overlayId)
  }

  setPOIOverlay(hasPOIOverlay: boolean): void {
    if (this._hasPOIOverlay == hasPOIOverlay) {
      return
    }
    this._hasPOIOverlay = hasPOIOverlay
  }

  hasPOIOverlay(): boolean {
    return this._hasPOIOverlay
  }

  addAnnotation(annotation: Annotation): AnnotationId {
    this.dirty = true
    return this.annotationManager.addAnnotation(annotation)
  }

  updateAnnotationZIndex(annotationId: AnnotationId, oldZIndex: number, newZIndex: number): void {
    this.annotationManager.updateAnnotationZIndex(annotationId, oldZIndex, newZIndex)
    this.dirty = true
  }

  updateAnnotation(annotationId: AnnotationId, reloadLayout: boolean = false): void {
    this.annotationManager.updateAnnotation(annotationId, reloadLayout)
    this.dirty = true
  }

  removeAnnotation(annotationId: AnnotationId): void {
    this.annotationManager.removeAnnotation(annotationId)
    this.dirty = true
  }

  addMapObject(mapObject: MapObject): void {
    this.customBuildingManager.addMapObject(mapObject)
    this.dirty = true
  }

  removeMapObject(id: string): void {
    this.customBuildingManager.removeMapObject(id)
    this.dirty = true
  }

  screenCoordinateToLatLng(screenCoordinate: Vector2, elevation: number = 0): LatLng {
    elevation = this.mapState.is3dMode() ? elevation : 0.0
    return GeometryUtils.screenCoordinateToLatLng(screenCoordinate, this.mapState.cameraPosition, this.mapState.getScreenSize(), elevation)
  }

  latLngToScreenCoordinate(latLng: LatLng, elevation: number = 0): Vector2 {
    elevation = this.mapState.is3dMode() ? elevation : 0.0
    return GeometryUtils.latLngToScreenCoordinate(latLng, elevation, this.mapState.cameraPosition, this.mapState.getScreenSize())
  }

  getCamera(): CameraPosition {
    return this.mapState.getCamera()
  }

  setSwitchMode(mode: SwitchMode): void {
    this.mapState.setSwitchMode(mode)
  }

  setShadowEffect(enabled: boolean) {
    this.disableShadowMapByTime = false
    this.effectManager.enableShadowEffect = enabled
    this.dirty = true
  }

  setWaterEffect(enabled: boolean): void {
    this.effectManager.enableWaterEffect = enabled
    this.dirty = true
  }

  setObjectsEnabled(enabled: boolean): void {
    if (this.mapSettings.objectsEnabled == enabled){
      return
    }
    this.mapSettings.objectsEnabled = enabled
    if (this.mapState.is3dMode()) {
      if (!enabled) {
        this.buildingResourceManager
          .removeBuildings(building => {
            let buildingType =  building.getBuildingType()
            /** Don't remove custom building and building Overlay **/
            return buildingType != BuildingType.External && buildingType != BuildingType.ExternalExtrude
              && this.customBuildingManager.getMapObjects().get(building.getId()) == null
              && building.getId().indexOf(MapConstants.buildingOverlayIdPrefix) < 0
          })
      }
      else {
        this.tileManager.enableBuilding(buildingJson => {
          /** Don't add custom building and building Overlay **/
          return this.customBuildingManager.getMapObjects().get(buildingJson.id) == null
            && buildingJson.id.indexOf(MapConstants.buildingOverlayIdPrefix) < 0
        })
      }
      this.effectManager.shadowMap.clear()
      this.dirty = true
    }
  }

  isObjectsEnabled(): boolean {
    return this.mapSettings.objectsEnabled
  }

  setPlacesEnabled(enabled: boolean): void {
    if (this.mapSettings.placesEnabled == enabled){
      return
    }
    if (enabled === true) {
      this.placement.updatePlacement()
    }
    this.dirty = true
    this.mapSettings.placesEnabled = enabled
  }

  isPlacesEnabled(): boolean {
    return this.mapSettings.placesEnabled
  }

  getRestrictionBounds(): LatLngBounds {
    return this.mapSettings.getRestrictionBounds()
  }

  setRestrictionBounds(bounds: ILatLngBounds): void {
    if (bounds == null) {
      this.mapSettings.setRestrictionBounds(null)
    }
    else {
      this.mapSettings.setRestrictionBounds(LatLngBounds.convert(bounds))
      this.mapState.constraintCameraWithRestrictionBounds()
    }
  }

  reload() {
    this.tileManager.reload()
  }

  // Setup
  private setupComponents(options: MapOptions): void {
    let optionDefault: any = {}
    MapUtils.extendOptions(optionDefault, this.defaultMapOptions, options)

    // Eager load
    ResourceManager.getInstance(this.gl)
    TextLabelFactory.getInstance(this.gl)
    TextLabelResourceManager.getInstance(this.gl)

    this.layers = new Map<string, Layer>()
    this.httpTaskManager = new TaskManager()
    this.mapsEventManager = new MapsEventManager(this)
    this.mapSettings = new MapSettings(this)
    this.mapState = new MapState(this.mapSettings)
    this.timeEffectManager = new TimeEffectManager(this.mapState)
    this.mapRenderer = new MapRenderer(this.gl, this.mapState)
    this.skyRenderer = new SkyRenderer(this.gl, this.timeEffectManager)
    this.cameraAnimation = new CameraAnimation(this.mapState, this, this.mapSettings)
    this.dirty = true
    this._hasPOIOverlay = false
    this.mapObserver = new MapObserver()

    this.mapState.setScreenSize(this.width, this.height)

    // Initialize map state
    if (optionDefault.restrictionBounds) {
      this.setRestrictionBounds(optionDefault.restrictionBounds)
    }
    this.mapState.setZoom(optionDefault.zoom)
    this.mapState.setBearing(optionDefault.bearing)
    this.mapState.setTarget(LatLng.convert(optionDefault.center))

    this.mapSettings.setMinZoom(optionDefault.minZoom)
    this.mapSettings.setMaxZoom(optionDefault.maxZoom)
    this.mapSettings.setMaxNativeZoom(optionDefault.maxNativeZoom)

    if (optionDefault.zoom >= MapConstants.defaultMinZoom3d && optionDefault.tilt > MapConstants.maxTilt2D) {
      optionDefault.tilt = MathUtils.clamp(optionDefault.tilt, MapConstants.minTilt, MapConstants.maxTilt3D)
      this.mapState.setTilt(optionDefault.tilt)
      this.mapState.enable3dMode(true)
    }
    else {
      optionDefault.tilt = MathUtils.clamp(optionDefault.tilt, MapConstants.minTilt, MapConstants.maxTilt2D)
      this.mapState.setTilt(optionDefault.tilt)
    }

    let requestRender = () => {
      this.dirty = true
    }

    SymbolResourceManager.getInstance(this.gl).setRequestRender(requestRender)
    SymbolResourceManager.getInstance(this.gl).setTaskManager(this.httpTaskManager)

    /**
     * Always keep the reference of marker cluster icon so the zoom-in/zoom-out animation with cluster will be
     * more smooth
     */
    SymbolResourceManager.getInstance(this.gl).acquire("markerClusterIcon")
    SymbolResourceManager.getInstance(this.gl).acquire(SymbolConstants.placeSelectedSymbol)

    this.buildingIconSelectionManager = new BuildingIconSelectionManager(this.gl, this)
    this.buildingResourceManager =
      new BuildingResourceManager(this.gl, this.httpTaskManager, this.mapState, this.mouseEventHandler, requestRender)

    this.customBuildingManager = new CustomBuildingManager(this.buildingResourceManager, this.mapState,
      this.httpTaskManager, requestRender)

    let createAnnotationTile = (tileCoordinate: TileCoordinate) => {
      return new AnnotationTile(tileCoordinate, this.annotationManager, this.gl)
    }

    this.tileManager = new TileManager(this, this.httpTaskManager, this.mapState,
      this.mapSettings, this.buildingResourceManager, this.buildingIconSelectionManager,
      this.customBuildingManager,
      createAnnotationTile, requestRender)
    this.tileAreaManager = new TileAreaManager(this.tileManager)
    this.layerOverlayManager = new LayerOverlayManager(this.tileManager)
    this.customBuildingManager.setTileManager(this.tileManager)

    // Add layers
    /**
     * Note: Raster layer must be updated first otherwise exception will be thrown.
     * To make sure raster layer update first I will set the name of raster layer start with 1
     */

    // Raster layer is always the lowest layer
    let rasterLayer =
      this.createLayer("1.rasterLayer", LayerType.RASTER, MapConstants.baseMapLowestOrder) as RasterLayer

    // SDK POI is always the seconds lowest layer
    this.vectorPointLayer =
        this.createLayer(TileManager.pointLayerId, LayerType.SYMBOL, MapConstants.baseMapLowestOrder) as SymbolLayer

    // GeoJson is always the third lowest layer
    this.createLayer(GeoJsonManager.geoJsonLayerId, LayerType.GEOJSON, MapConstants.baseMapLowestOrder)

    this.overlayVectorPointLayer =
      this.createLayer(TileManager.overlayPointLayerId, LayerType.SYMBOL, MapConstants.baseMapLowestOrder) as SymbolLayer

    this.userVectorPointLayer =
        this.createLayer(TileManager.userPointLayerId, LayerType.SYMBOL, MapConstants.userFeatureLayerOrder) as SymbolLayer

    this.buildingLayer =
        this.createLayer("2.buildingLayer", LayerType.BUILDING, MapConstants.buildingLayerOrder) as BuildingLayer

    this.buildingLayer.setBuildingResourceManager(this.buildingResourceManager)

    this.placement = new Placement(this.gl, this.mapState, this.mapSettings)
    this.featureManager = new FeatureManager(this.gl, this.tileManager, this.mapState)

    let lightManager = new LightingManager(this.timeEffectManager)
    this.buildingLayer.timeEffectManager = this.timeEffectManager
    this.buildingLayer.setLightingManager(lightManager)

    let shadowMap = new ShadowMap(this.gl, lightManager.sunLight, this.mapState)
    this.effectManager = new EffectManager(shadowMap)
    this.mapRenderer.setEffectManager(this.effectManager)

    rasterLayer.effectManager = this.effectManager
    rasterLayer.timeEffectManager = this.timeEffectManager
    rasterLayer.setLightingManager(lightManager)

    this.buildingLayer.effectManager = this.effectManager

    this._annotationManager = new AnnotationManager(this, this.tileManager)
    this.weatherManager = new WeatherManager(this.gl, this.mapState, this.httpTaskManager)
    lightManager.setOnLightPositionChanged(() => {
      shadowMap.updateMatrices()
    })

    this.geoJsonManager = new GeoJsonManager(this)

    // Sky object
    this.skyObject = new SkyObject()

    rasterLayer.skyObject = this.skyObject
    rasterLayer.skyRenderer = this.skyRenderer

    this.frameBuffer = new FrameBuffer(this.gl, this.width, this.height)

    this.canvas.width = this.width
    this.canvas.height = this.height
    this.dirty = true
    this.tileManager.updateVisibleTiles()
  }

  // Create style node
  private createStyleNode(): void {
    let head = document.getElementsByTagName("head")[0] as HTMLElement
    let styleNodes = head.getElementsByClassName("map-style")
    if (styleNodes && styleNodes.length > 0) {
      return
    }
    // Create style node
    let style = document.createElement("style")
    style.innerHTML = Style.content
    style.className = "map-style"
    head.appendChild(style)
  }

  private setupMapViews(container: HTMLElement, options: MapOptions): void {
    let mapContainer = DOMUtils.create("div", "mf-mapgl", container)
    this.mapContainer = mapContainer
    this.canvas = DOMUtils.create("canvas", "", mapContainer) as HTMLCanvasElement
    this.gl = this.canvas.getContext("experimental-webgl", {stencil: true}) as WebGLRenderingContext
    ++MapView.idContextCounting
    this.gl["id"] = MapView.idContextCounting.toString()
    let backgroundColor = Color.fromHex(MapConstants.mapBackgroundColor2D)
    this.gl.clearColor(backgroundColor.red, backgroundColor.green, backgroundColor.blue, backgroundColor.alpha)
    this.gl.stencilOp(this.gl.KEEP, this.gl.REPLACE, this.gl.REPLACE)
    MapUtils.pixelScale = window.devicePixelRatio || 1
    if (this.hasTouch()) {
      MapUtils.touchBoxSize = MapConstants.touchBoxSize * MapUtils.pixelScale
    }
    this.width = this.canvas.offsetWidth * MapUtils.pixelScale
    this.height = this.canvas.offsetHeight * MapUtils.pixelScale
    this.mouseEventHandler = new MouseEventHandler(this.canvas, this)
    window.addEventListener("resize", this.resize, false)
  }

  private createMapControlNode(mapContainer: HTMLElement, options: MapOptions) {
    let mapUiContainer =
      DOMUtils.create("div", `mf-ui ${
        Style.getStyleForControlOptions(options.controlOptions)}`, mapContainer)

    let mapUIVerticalContainer = null
    let mapUIHorizontalContainer = null
    if (options.geolocate) {
      this.geolocateControl = new GeolocateControl(this)
    }
    if (options.controls) {

      //control vertical
      mapUIVerticalContainer =
        DOMUtils.create("div", "vertical", mapUiContainer)

      this.geolocateControl && this.geolocateControl.updateMapUI(mapUIVerticalContainer)

      //style css button
      this.zoomInButton = DOMUtils.create("div", "mf-button", mapUIVerticalContainer)
      this.zoomInButton.innerHTML = "<a class='mf-zoom-in-icon'></a>"

      this.zoomOutButton = DOMUtils.create("div", "mf-button", mapUIVerticalContainer)
      this.zoomOutButton.innerHTML = "<a class='mf-zoom-out-icon'></a>"

      this.modeButton = DOMUtils.create("div", "mf-button", mapUIVerticalContainer)
      this.modeButton.innerHTML = "<a class='mf-mode-icon'></a>"

      //control horizontal
      mapUIHorizontalContainer =
        DOMUtils.create("div", "horizontal", mapUiContainer)

      //style css button
      this.bearingButton = DOMUtils.create("div", "mf-button", mapUIHorizontalContainer)
      this.compassImage = DOMUtils.create("a", "mf-direct-icon", this.bearingButton)
      this.compassImage.style.transform = null
    }

    this.addControlEvents()

  }

  private createLogoNode(mapContainer: HTMLElement) {
    let position = this.options.controlOptions == ControlOptions.BOTTOM_LEFT? "mf-right" : "mf-left"
    let map4dLogoContainer = DOMUtils.create("div", `mf-logo-container ${position}`, mapContainer)
    let imageLogoSrc = Images.get("logoIcon")

    map4dLogoContainer.innerHTML = `<a target='_blank' href='https://www.map4d.vn/' aria-label='Map4D Logo'>
      <img src=${imageLogoSrc} class='mf-logo'/>
      </a>`
  }

  private addControlEvents(): void {
    if (this.options.controls) {
      this.zoomInButton.addEventListener("click", () => {
        this.setZoom(this.mapState.getZoom() + 1, new Vector2(this.width / 2, this.height / 2),
          0, false)
      })
      this.zoomOutButton.addEventListener("click", () => {
        this.setZoom(this.mapState.getZoom() - 1, new Vector2(this.width / 2, this.height / 2),
          0, false)
      })

      this.modeButton.addEventListener("click", () => {

        this.enable3dMode(!this.mapState.is3dMode())
      })
      this.bearingButton.addEventListener("click", () => {
        this.moveCamera({
          target: this.mapState.getTarget(),
          bearing: 0.0},
        {animate: true})
      })
    }
  }

  addListener(event: string | MapEvent, func: Function, options: EventOptions): MapsEventListener {
    return this.mapsEventManager.addEvent(event, func, options)
  }

  addGeoJsonListener(event: string | MapEvent, func: Function): MapsEventListener {
    return this.mapsEventManager.addGeoJsonListener(event, func)
  }

  updateInfoContents(annotationId: AnnotationId): void {
    this.annotationManager.updateInfoContents(annotationId)
  }

  // Camera animation observer
  notifyCameraWillChange(gesture: boolean): void {
    this.dirty = true
    this.mapsEventManager.notify(MapEvent.cameraWillChange, null, gesture)
  }

  notifyCameraIsChanging(): void {
    this.dirty = true
    this.mapsEventManager.notify(MapEvent.cameraChanging)
    this.mapsEventManager.notify(MapEvent.boundsChanged)
    let force2d = this.mapState.getForce2d()
    this.mapState.setForce2d(force2d && this.mapState.getZoom() >= MapConstants.defaultMinZoom3d)
    this.buildingLayer.setDirty()
    this.tileManager.updateVisibleTiles()
    if (this.mapSettings.placesEnabled || this.hasPOIOverlay()) {
      this.placement.updatePlacement()
    }
  }

  notifyCameraDidChange(): void {
    this.dirty = true
    this.mapState.setZoom(Math.round(this.mapState.getZoom()))
    this.buildingLayer.setDirty()
    this.tileManager.updateVisibleTiles()
    this.mapState.handleSwitchMode()
    this.mapsEventManager.notify(MapEvent.idle)
    this.mapState.willChange = false
  }

  getLayer(name: string): Layer {
    if (this.layers.has(name)) {
      return this.layers.get(name)
    }
    return null
  }

  queryMapObjectAndAnnotation(touchPoint: Vector2): Annotation | MapObject {
    return this.annotationManager.queryMapObjectAndAnnotation(touchPoint)
  }

  queryFeature(touchPoint: Vector2): VectorTileFeature {
    return this.placement.queryFeature(touchPoint)
  }

  queryBuildingInfo(touchBox: Box): MapObject {
    let selectionCode = this.getSelectionCode(touchBox)
    return this.buildingLayer.getBuilding(selectionCode)
  }

  setDate(date: Date): void {
    this.mapSettings.setDate(date)
    this.reload()
    if (this.mapSettings.is3dMode()) {
      this.buildingResourceManager.removeAllResources()
      this.buildingLayer.clear()
    }
  }

  getMapContainer(): HTMLElement {
    return this.mapContainer
  }

  destroy(): void {
    this.frameBuffer && this.frameBuffer.destroy()
    this.shader && this.shader.destroy()
    this.tileManager && this.tileManager.destroy()
    this.mapsEventManager && this.mapsEventManager.destroy()
    this.weatherManager && this.weatherManager.destroy()
    this.layers && this.layers.clear()
    this.buildingLayer && this.buildingLayer.clear()
    this.effectManager && this.effectManager.destroy()
    this.buildingIconSelectionManager && this.buildingIconSelectionManager.destroy()
    this.customBuildingManager && this.customBuildingManager.destroy()
    ResourceManager.releaseInstance(this.gl)
    SymbolResourceManager.releaseInstance(this.gl)
    TextLabelFactory.releaseInstance(this.gl)
    TextLabelResourceManager.releaseInstance(this.gl)
    this.gl = null
    this.frameBuffer = null
    this.effectManager = null
  }

  get annotationManager(): AnnotationManager {
    return this._annotationManager
  }

  moveToMyLocation(animate: boolean = true): void {
    this.geolocateControl && this.geolocateControl.moveToMyLocation(animate)
  }

  setTimeEffect(timeEffect : TimeEffect): void {
    this.timeEffectManager.setTimeEffect(timeEffect)
    this.buildingLayer.setDirty()
    this.dirty = true
  }

  getTimeEffect(): TimeEffect {
    return this.timeEffectManager.getTimeEffect()
  }

  setFilterPlaces(placeTypes: string[]): void {
    this.mapSettings.filterPlaceTypes = placeTypes
    if (!this.mapState.is3dMode()) {
      this.placement.updatePlacement()
      this.dirty = true
    }
  }

  getFilterPlaces(): string[] {
    return this.mapSettings.filterPlaceTypes
  }

  setFilterObjects(objectTypes: string[]): void {
    this.buildingLayer.filterObjectTypes = objectTypes
    this.effectManager.shadowMap.clear()
    this.dirty = true
  }

  getFilterObjects(): string[] {
    return this.buildingLayer.filterObjectTypes
  }

  setSelectedObjects(ids: string[]): void {
    this.buildingIconSelectionManager.deselectInfoWindow()
    this.buildingLayer.setSelectedObjectIds(ids)
    this.dirty = true
  }

  setSelectedPlace(id: string): void {
    if (id && id.startsWith(MapConstants.placeSelectedIdPrefix)) {
      return
    }
    let oldSelectedPlaceFeatureId = this.placement.getSelectedFeature()
    if (oldSelectedPlaceFeatureId == id) {
      return
    }

    // Remove old selected feature
    if (oldSelectedPlaceFeatureId && !this.featureManager.hasFeature(oldSelectedPlaceFeatureId) /* Not user feature */) {
      this.featureManager.removeFeature(MapConstants.placeSelectedIdPrefix + oldSelectedPlaceFeatureId)
      this.placement.removeFeatures([MapConstants.placeSelectedIdPrefix + oldSelectedPlaceFeatureId])
    }

    if (!this.featureManager.hasFeature(MapConstants.placeSelectedIdPrefix + id) &&
        !this.featureManager.hasFeature(id) /* NOt user feature */) {

      // Add new selected feature
      let placeFeature = this.placement.getFeatures().get(id)
      if (placeFeature != null && placeFeature.feature != null) {
        let properties = new Map<string, string | boolean | number>()
        let placeFeatureProperties = placeFeature.feature.properties
        properties.set("lat", placeFeatureProperties.get("lat"))
        properties.set("lng", placeFeatureProperties.get("lng"))
        properties.set("icon", placeFeatureProperties.get("icon"))
        properties.set("name", placeFeatureProperties.get("name"))
        let feature = new VectorTileFeature(MapConstants.placeSelectedIdPrefix + id,
          GeometryType.Point, null, properties)
        this.featureManager.addFeature(feature)
      }
    }

    this.placement.setSelectedFeature(id)
    this.dirty = true
  }

  getSelectedPlace(): string {
    return this.placement.getSelectedFeature()
  }

  setHiddenObject(id: string): void {
    this.buildingResourceManager.setHiddenObject(id)
    this.dirty = true
  }

  setUnhiddenObject(id: string): void {
    this.buildingResourceManager.setUnhiddenObject(id)
    this.tileManager.updateVisibleTiles()
  }

  getSelectedObjects(): string[] {
    return this.buildingLayer.getSelectedObjectIds()
  }

  getBuildingSelectionIconManager(): BuildingIconSelectionManager {
    return this.buildingIconSelectionManager
  }

  getBuildingResourceManager() {
    return this.buildingResourceManager
  }

  getCustomBuildingManager() {
    return this.customBuildingManager
  }

  getBuildingIconSelectionManager(): BuildingIconSelectionManager {
    return this.buildingIconSelectionManager
  }

  getGeoJsonManager(): GeoJsonManager {
    return this.geoJsonManager
  }

  getPlacement(): Placement {
    return this.placement
  }

  addFeature(feature: VectorTileFeature) {
    this.featureManager.addFeature(feature)
    this.dirty = true
  }

  removeFeature(featureId: string) {
    this.featureManager.removeFeature(featureId)
    this.dirty = true
  }

  getFeatureManager(): FeatureManager {
    return this.featureManager
  }

  onMapModeChanged(): void {
    this.mapsEventManager.notify(MapEvent.modeChanged)
    let is3dMode = this.mapSettings.is3dMode()
    if (!is3dMode) {
      this.effectManager.shadowMap.clear()
      this.buildingResourceManager.removeAllResources()
    }
    if (this.vectorPointLayer) {
      //FIXME: Do we really need clear render tiles at vector point layer
      this.vectorPointLayer.renderTiles.clear()
      this.userVectorPointLayer.renderTiles.clear()
      this.overlayVectorPointLayer.renderTiles.clear()
    }
    if (this.modeButton) {
      this.modeButton.className = this.mapSettings.is3dMode() ?  "mf-button mf-mode-2d": "mf-button"
    }
  }
}
