export class ReflectUtils {

  private constructor() {
  }

  static renameProp(props: any, obj: any): any {
    return Object
      .keys(obj)
      .reduce((acc, key) => {
        const renamedObject = {
          [props[key] || key]: obj[key]
        }
        return {
          ...acc,
          ...renamedObject
        }
      }, {})
  }

}