import {LatLng} from "./LatLng"
import {MapUtils} from "../util/MapUtils"
import {PlaceOptions} from "./options/PlaceOptions"
import {Map4D} from "./Map"
import {VectorTileFeature} from "../tile/data/VectorTileData"
import {GeometryType} from "../tile/data/GeometryTileData"

export class Place {

  private static readonly defaultPlaceOptions = {
    name: "Default icon name",

    icon: "iconDefault"
  }

  private id: string

  private name: string

  private url: string

  private location: LatLng

  private map: Map4D

  constructor(options?: PlaceOptions) {
    let optionDefault: any = {}
    MapUtils.extendOptions(optionDefault, Place.defaultPlaceOptions, options)
    for (const option of optionDefault) {
      if (option == null) {
        throw Error("Parameters can not be null")
      }
    }
    this.id = optionDefault.id
    this.url = optionDefault.icon
    this.location = LatLng.convert(optionDefault.location)
    this.name = optionDefault.name
  }

  getId(): string {
    return this.id
  }

  getName(): string {
    return this.name
  }

  getLocation(): LatLng {
    return this.location
  }

  getMap(): Map4D {
    return this.map
  }

  setMap(map: Map4D): void {
    if (this.map === map) {
      return
    }
    if (this.map != null) {
      this.map.mapView.removeFeature(this.id)
    }
    this.map = map
    if (this.map != null) {
      let properties = new Map<string, string | boolean | number>()
      properties.set("lat", this.location.lat)
      properties.set("lng", this.location.lng)
      properties.set("url", this.url)
      properties.set("name", this.name)
      let feature = new VectorTileFeature(this.id, GeometryType.Point, null, properties)
      this.map.mapView.addFeature(feature)
    }
  }
}
