import {ParticleSystemQuad} from "./ParticleSystemQuad"
import {ResourceManager, ShaderEnum, TextureEnum} from "../util/ResourceManager"
import {Vector2} from "../engine/Vector2"
import {MathConstants, ParticleConstants} from "../map/MapConstants"
import {ParticlePool} from "./ParticlePool"
import {Particle} from "./Particle"

export class ParticleSnow extends ParticleSystemQuad {

  constructor(gl: WebGLRenderingContext, particlePool: ParticlePool) {
    let resourceManager = ResourceManager.getInstance(gl)
    let snowShader = resourceManager.getShader(ShaderEnum.PARTICLE_SHADER)
    let snowTexture = resourceManager.getTexture(TextureEnum.SNOW_TEXTURE)
    super(gl, snowShader, snowTexture, particlePool)
  }

  init(): void {
    super.init({ "frequency": ParticleConstants.snowFrequency,
      "maxParticles": 1000,
      "acceleration": (new Vector2(0, 50)),
      "minSpeed": 50,
      "maxSpeed": 200,
      "minScale": 0.25,
      "maxScale": 0.35,
      "maxRotation": 0,
      "minRotation": 0,
      "minRotationSpeed": 0,
      "maxRotationSpeed": 200,
      "extraData": { counter: 0 },
      "updateFunc": (particle: Particle, delta: number) => {
        if (particle.acceleration) {
          particle.velocity.y += particle.acceleration.y * delta
        }
        if (particle.rotationSpeed !== 0) {
          particle.rotation += particle.rotationSpeed * delta
        }
        particle.extraData.counter += 4
        particle.position.x += 2 * Math.cos(particle.extraData.counter * MathConstants.deg2Rad)
        particle.position.y += particle.velocity.y * delta
      }
    })
  }

}