import {DOMUtils} from "../util/DOMUtils"
import {ILatLng, LatLng} from "../public/LatLng"
import {MapView} from "../map/MapView"
import {AnnotationId} from "../annotation/Annotation"
import {Icon} from "../public/Icon"
import {Images} from "../resources/Images"
import {Marker} from "../public/Marker"
import {Point} from "../public/Point"

export class GeolocateControl {
  private userLocationDotId: AnnotationId = -1

  private userLocationDot: Marker = null

  private lastKnownLocation: LatLng = null

  private myLocationButton: HTMLElement

  private readonly mapView: MapView

  private myLocationZoom = 17

  private animate = false

  private defaultPositionOptions = {
    enableHighAccuracy: true,
    maximumAge: 0,
    timeout: 6000
  }

  constructor(mapView: MapView) {
    this.mapView = mapView
  }

  updateMapUI(container: HTMLElement) {
    if (container != null){
      this.myLocationButton = DOMUtils.create("div", "mf-button", container)

      this.myLocationButton.innerHTML = "<a class='mf-location'></a>"

      this.myLocationButton.addEventListener("click", () => {
        this.onMyLocationButtonClick()
      })
    }
  }

  moveToMyLocation(animate: boolean = true): void {
    if (!location.protocol.startsWith("https") && !location.host.startsWith("localhost")) {
      alert("This feature is available only in secure contexts (HTTPS)")
      return
    }
    this.animate = animate
    window.navigator.geolocation.getCurrentPosition(this.onGetCurrentPositionSuccess.bind(this),
      this.onGetCurrentPositionError.bind(this),
      this.defaultPositionOptions)
  }

  private onMyLocationButtonClick() {
    this.moveToMyLocation()
  }

  private onGetCurrentPositionSuccess(position: Position): void {
    let location = new LatLng(position.coords.latitude, position.coords.longitude)
    this.mapView.moveCamera({target: location, zoom: this.myLocationZoom}, {animate: this.animate})
    this.lastKnownLocation = location
    if (this.userLocationDot == null) {
      let myLocationIcon = new Icon(32, 32, "myPosition")
      this.userLocationDot
        = new Marker({position: location, visible: true, anchor: new Point(0.5, 0.5), icon: myLocationIcon})
      this.userLocationDotId = this.mapView.addAnnotation(this.userLocationDot)
    }
    else {
      this.userLocationDot.setPosition(location)
      this.userLocationDot.setVisible(true)
    }
  }

  private onGetCurrentPositionError(error: PositionError): void {
    if (this.userLocationDotId != -1) {
      this.mapView.removeAnnotation(this.userLocationDotId)
      this.userLocationDotId = -1
      this.userLocationDot = null
    }
  }

}
