import {Layer, LayerType} from "../layer/Layer"
import {MapState} from "../map/MapState"

import {LayerRenderer} from "./LayerRenderer"
import {RasterLayerRenderer} from "./RasterLayerRenderer"
import {LineLayerRenderer} from "./LineLayerRenderer"
import {FillLayerRenderer} from "./FillLayerRenderer"
import {CircleLayerRenderer} from "./CircleLayerRenderer"
import {BuildingLayerRenderer} from "./BuildingLayerRenderer"
import {SymbolLayerRenderer} from "./SymbolLayerRenderer"
import {GeoJsonLayerRenderer} from "./GeoJsonLayerRenderer"

export class LayerRenderFactory {
  static create(gl: WebGLRenderingContext, mapState: MapState, layer: Layer): LayerRenderer {
    switch (layer.getLayerType()) {
    case LayerType.RASTER:
      return new RasterLayerRenderer(mapState, layer)
    case LayerType.BUILDING:
      return new BuildingLayerRenderer(gl, mapState, layer)
    case LayerType.LINE:
      return new LineLayerRenderer(mapState, layer)
    case LayerType.FILL:
      return new FillLayerRenderer(mapState, layer)
    case LayerType.CIRCLE:
      return new CircleLayerRenderer(mapState, layer)
    case LayerType.SYMBOL:
      return new SymbolLayerRenderer(mapState, layer)
    case LayerType.GEOJSON:
      return new GeoJsonLayerRenderer(mapState, layer)
    default:
      console.log("Invalid layer type")
    }
  }
}
