import {MapConstants} from "./MapConstants"
import {SwitchMode} from "../public/SwitchMode"
import {MapModeChangedObserver} from "./MapModeChangedObserver"
import {LatLngBounds} from "../public/LatLngBounds"

export class MapSettings {

  private map2dUrl: string

  private tile2dUrl: string

  private map3dUrl: string

  private tile3dUrl: string

  private minZoom: number = MapConstants.defaultMinZoom

  private maxZoom: number = MapConstants.defaultMaxZoom

  private maxNativeZoom: number = MapConstants.defaultMaxNativeZoom

  private _3dMode: boolean = false

  private _objectsEnabled: boolean = true

  private _placesEnabled: boolean = true

  private _filterPlaceTypes: string[] = []

  private date?: Date

  private _dataLayerMinZoom: number = MapConstants.defaultMinZoom

  private _dataLayerMaxZoom: number = MapConstants.defaultMaxZoom

  private static accessKey: string = "__ACCESS_KEY__"

  private switchMode: SwitchMode

  private restrictionBounds: LatLngBounds

  private mapModeChangedObserver: MapModeChangedObserver

  constructor(mapModeChangedObserver: MapModeChangedObserver) {
    this.switchMode = SwitchMode.Manual
    this.mapModeChangedObserver = mapModeChangedObserver
    this.tile2dUrl = MapConstants.defaultMap2dUrl
    this.map2dUrl = MapConstants.baseMapUrl + "?tileUrl=" + MapConstants.defaultMap2dUrl + "&key=" + MapSettings.accessKey
    this.tile3dUrl = MapConstants.defaultMap3dUrl
    this.map3dUrl = MapConstants.baseMapUrl + "?tileUrl=" + MapConstants.defaultMap3dUrl + "&mode=3d&key=" + MapSettings.accessKey
  }

  setTileUrl(tileUrl: string, is3dMode: boolean = false): void {
    if (tileUrl == null) {
      tileUrl = is3dMode ? MapConstants.defaultMap3dUrl : MapConstants.defaultMap2dUrl
    }

    if (is3dMode) {
      this.map3dUrl = MapConstants.baseMapUrl + "?tileUrl=" + tileUrl + "&mode=3d&key=" + MapSettings.accessKey
      this.tile3dUrl = tileUrl
    }
    else {
      this.map2dUrl = MapConstants.baseMapUrl + "?tileUrl=" + tileUrl + "&key=" + MapSettings.accessKey
      this.tile2dUrl = tileUrl
    }
  }

  getTileUrl(is3dMode: boolean): string {
    return is3dMode ? this.tile3dUrl : this.tile2dUrl
  }

  setMinZoom(minZoom: number): void {
    this.minZoom = minZoom
    if (this.minZoom > this.maxZoom) {

      /** swap */
      this.minZoom += this.maxZoom
      this.maxZoom = this.minZoom - this.maxZoom
      this.minZoom -= this.maxZoom
    }
  }

  setMaxZoom(maxZoom: number): void {
    this.maxZoom = maxZoom
    if (this.minZoom > this.maxZoom) {

      /** swap */
      this.minZoom += this.maxZoom
      this.maxZoom = this.minZoom - this.maxZoom
      this.minZoom -= this.maxZoom
    }
  }

  getMapUrl(): string {
    return this._3dMode ? this.map3dUrl : this.map2dUrl
  }

  getPreferMaxTilt(): number {
    if (this.is3dMode()) {
      return MapConstants.maxTilt3D
    }
    switch (this.switchMode) {
    case SwitchMode.Auto:
    case SwitchMode.Auto2DTo3D:
      return MapConstants.maxTilt3D
    default:
      break
    }
    return MapConstants.maxTilt2D
  }

  getPreferMinZoom() {
    let limitMinZoom = this.minZoom
    let limitZoom = 0
    switch (this.switchMode) {
    case SwitchMode.Auto:
    case SwitchMode.Auto3DTo2D:
      limitMinZoom = this.minZoom > MapConstants.defaultMinZoom ? this.minZoom : MapConstants.defaultMinZoom
      break
    default:
      limitZoom = this.is3dMode() ? MapConstants.defaultMinZoom3d : MapConstants.defaultMinZoom
      limitMinZoom = this.minZoom > limitZoom ? this.minZoom : limitZoom
      break
    }
    return limitMinZoom
  }

  getMinZoom(): number {
    return this.minZoom
  }

  getMaxZoom(): number {
    return this.maxZoom
  }

  getMinTilt(): number {
    return MapConstants.minTilt
  }

  getMaxTilt(): number {
    return this._3dMode ? MapConstants.maxTilt3D : MapConstants.maxTilt2D
  }

  getMaxNativeZoom(): number {
    return this.maxNativeZoom
  }

  setDate(date: Date): void{
    this.date = date
  }

  getDate(): Date{
    return this.date || new Date()
  }

  setMaxNativeZoom(maxNativeZoom: number) {
    this.maxNativeZoom = maxNativeZoom
  }

  static getAccessKey(): string {
    return this.accessKey
  }

  static setAccessKey(value: string) {
    this.accessKey = value
  }

  enable3dMode(enabled: boolean): boolean {
    if (enabled != this._3dMode) {
      this._3dMode = enabled
      this.mapModeChangedObserver.onMapModeChanged()
      return true
    }
    return false
  }

  is3dMode(): boolean {
    return this._3dMode
  }

  setRestrictionBounds(bounds: LatLngBounds): void {
    this.restrictionBounds = bounds
  }

  getRestrictionBounds(): LatLngBounds {
    return this.restrictionBounds
  }

  setSwitchMode(mode: SwitchMode): void {
    this.switchMode = mode
  }

  getSwitchMode(): SwitchMode {
    return this.switchMode
  }

  get objectsEnabled(): boolean {
    return this._objectsEnabled
  }

  set objectsEnabled(value: boolean) {
    this._objectsEnabled = value
  }

  get placesEnabled(): boolean {
    return this._placesEnabled
  }

  set placesEnabled(value: boolean) {
    this._placesEnabled = value
  }

  get filterPlaceTypes(): string[] {
    return this._filterPlaceTypes
  }

  set filterPlaceTypes(value: string[]) {
    this._filterPlaceTypes = value
  }

  get dataLayerMinZoom(): number {
    return this._dataLayerMinZoom
  }

  set dataLayerMinZoom(value: number) {
    this._dataLayerMinZoom = value
    if (this._dataLayerMinZoom > this._dataLayerMaxZoom) {

      // Swap
      this._dataLayerMinZoom += this._dataLayerMaxZoom
      this._dataLayerMaxZoom = this._dataLayerMinZoom - this._dataLayerMaxZoom
      this._dataLayerMinZoom -= this._dataLayerMaxZoom
    }
  }

  get dataLayerMaxZoom(): number {
    return this._dataLayerMaxZoom
  }

  set dataLayerMaxZoom(value: number) {
    this._dataLayerMaxZoom = value
    if (this._dataLayerMinZoom > this._dataLayerMaxZoom) {

      // Swap
      this._dataLayerMinZoom += this._dataLayerMaxZoom
      this._dataLayerMaxZoom = this._dataLayerMinZoom - this._dataLayerMaxZoom
      this._dataLayerMinZoom -= this._dataLayerMaxZoom
    }
  }
}
